export var downloadExcel =  function(tableId, filename){
	console.log(tableId,filename);
	var idTmr;
	var getExplorer = function(){
		var explorer = window.navigator.userAgent ;
	    //ie 
	    if (explorer.indexOf("MSIE") >= 0) {
	        return 'ie';
	    }
	    //firefox 
	    else if (explorer.indexOf("Firefox") >= 0) {
	        return 'Firefox';
	    }
	    //Chrome
	    else if(explorer.indexOf("Chrome") >= 0){
	        return 'Chrome';
	    }
	    //Opera
	    else if(explorer.indexOf("Opera") >= 0){
	        return 'Opera';
	    }
	    //Safari
	    else if(explorer.indexOf("Safari") >= 0){
	        return 'Safari';
	    }
	};
	var exportExcel = function(tableId, filename) {
		//������񿽱���EXCEL��
	    if(getExplorer()=='ie') {
	        var curTbl = document.getElementById(tableId);
	        var oXL = new ActiveXObject("Excel.Application");
	        //����AX����excel 
	        var oWB = oXL.Workbooks.Add();
	        //��ȡworkbook���� 
	        var xlsheet = oWB.Worksheets(1);
	        //���ǰsheet 
	        var sel = document.body.createTextRange();
	        sel.moveToElementText(curTbl);
	        //�ѱ���е������Ƶ�TextRange�� 
	        sel.select();
	        //ȫѡTextRange������ 
	        sel.execCommand("Copy");
	        //����TextRange������  
	        xlsheet.Paste();
	        //ճ�������EXCEL��       
	        oXL.Visible = true;
	        //����excel�ɼ�����
	        try {
	            var fname = oXL.Application.GetSaveAsFilename("Excel.xls", "Excel Spreadsheets (*.xls), *.xls");
	        } catch (e) {
	            print("Nested catch caught " + e);
	        } finally {
	            oWB.SaveAs(fname);
	            oWB.Close(savechanges = false);
	            //xls.visible = false;
	            oXL.Quit();
	            oXL = null;
	            //����excel���̣��˳����
	            //window.setInterval("Cleanup();",1);
	            idTmr = window.setInterval("Cleanup();", 1);
	        }
	    }else{
	        tableToExcel(tableId, filename);
	    }
	};
	var Cleanup = function() {
	    window.clearInterval(idTmr);
	    CollectGarbage();
	};
	var tableToExcel = function(tableId, filename){
		var _this = this;
		var temp = function(){
			var uri = 'data:application/vnd.ms-excel;base64,',
		    	template = '<html xmlns:o="urn:schemas-microsoft-com:office:office" xmlns:x="urn:schemas-microsoft-com:office:excel" xmlns="http://www.w3.org/TR/REC-html40"><head><!--[if gte mso 9]><xml><x:ExcelWorkbook><x:ExcelWorksheets><x:ExcelWorksheet><x:Name>{worksheet}</x:Name><x:WorksheetOptions><x:DisplayGridlines/></x:WorksheetOptions></x:ExcelWorksheet></x:ExcelWorksheets></x:ExcelWorkbook></xml><![endif]--></head><body><table>{table}</table></body></html>',
		    	base64 = function(s) {
		    		return window.btoa(unescape(encodeURIComponent(s))) 
		    	},
		    	format = function(s, c) {
		        	return s.replace(/{(\w+)}/g, function(m, p) { return c[p]; }) 
		        };
            return function(tableId, filename) {
    //         	for(var i=0; i<billList.length; i++){
    //         		_this.showTableIndex = i + 1;
				// 	var table = document.getElementById(billList[i].id);
				// 	var ctx = {worksheet: name || 'Worksheet', table: table.innerHTML};
				// 	var aEl = document.getElementById('doExport' + i);
				// 	aEl.href = uri + base64(format(template, ctx));
				// 	aEl.download = billList[i].billName;
				// 	aEl.click();
				// 	_this.billList[i].downloadState = 1;
				// }
                var table = document.getElementById(tableId);
                var ctx = {worksheet: name || 'Worksheet', table: table.innerHTML}
            	var bodyEl = document.getElementsByTagName('body')[0];
            	var aEl = document.createElement('a');
            	aEl.href = uri + base64(format(template, ctx));
            	aEl.download = filename;
            	bodyEl.appendChild(aEl);
            	aEl.click();
            	bodyEl.removeChild(aEl);
            }
		}
        var temp1 = temp();
		temp1(tableId, filename);
	};
	exportExcel(tableId, filename);
}


