<template>
	<div class="page userAction">
		<el-row>
			<el-col :xs="24" :lg="12">
				<div class="table-wrapper">
					<div class="table-header">
						<span class="title">验证码</span>
						<span class="btn" @click="downloadExcelFun('table1', '验证码')"><i class="fa fa-download"></i></span>
					</div>
					<div class="table-action">
						<div class="toolbar">
							<el-select size="mini" v-model="tableInfo1.selected" filterable default-first-option placeholder="选择产品">
							    <el-option v-for="(item,index) in appInfo.list" :key="index" :label="item.name" :value="item.id"></el-option>
							</el-select>
							<!-- <el-select size="mini" v-model="tableInfo1.channel" clearable filterable default-first-option placeholder="渠道号" @change="channelChange">
							    <el-option v-for="(item,index) in channelInfo.list" :key="index" :label="item" :value="item"></el-option>
							</el-select> -->
							<div class="myRangeMonth">
								<label for="">开始</label>
								<el-date-picker size="mini" v-model="tableInfo1.rangeDate.minDate" type="date" placeholder="开始日期" value-format="yyyy-MM-dd"></el-date-picker>
								<label for="">结束</label>
								<el-date-picker size="mini" v-model="tableInfo1.rangeDate.maxDate" type="date" placeholder="结束日期" value-format="yyyy-MM-dd"></el-date-picker>
								<el-button size="mini" type="primary" @click="doSearch(tableInfo1, 1)"><i class="fa fa-search"></i></el-button>
							</div>
						</div>
					</div>
					<div class="table-main">
						<el-table id="table1" :data="tableInfo1.data" size="small" height="342" border style="width: 100%" v-loading="tableInfo1.loading">
						    <el-table-column prop="date" label="日期" sortable align="center">
						    	<template slot-scope="scope">
			                         <span v-if="scope.row.page_name == '登录页'">{{scope.row.date}}</span>
			                    </template>
						    </el-table-column>
						    <el-table-column prop="page_name" label="页面" class-name="myClass" align="center"></el-table-column>
					    	<el-table-column align="center" prop="getTime" label="获取"></el-table-column>
					    	<el-table-column align="center" prop="notGet" label="未获取"></el-table-column>
					    	<el-table-column align="center" prop="times" label="平均时长">
					    		<template slot-scope="scope">
			                         <span>{{scope.row.times}}s</span>
			                    </template>
					    	</el-table-column>
					    	<el-table-column align="center" prop="time10" label="0~10s"></el-table-column>
					    	<el-table-column align="center" prop="time20" label="11~20s"></el-table-column>
					    	<el-table-column align="center" prop="time30" label="21~30s"></el-table-column>
					    	<el-table-column align="center" prop="timeOther" label="30s↑"></el-table-column>
					  	</el-table>
					</div>
				</div>
			</el-col>
			<el-col :xs="24" :lg="12">
				<div class="table-wrapper marginR">
					<div class="table-header">
						<span class="title">页面停留</span>
						<span class="btn" @click="downloadExcelFun('table2', '页面停留')"><i class="fa fa-download"></i></span>
					</div>
					<div class="table-action">
						<div class="toolbar">
							<el-select size="mini" v-model="tableInfo2.selected" filterable default-first-option placeholder="选择产品">
							    <el-option v-for="(item,index) in appInfo.list" :key="index" :label="item.name" :value="item.id"></el-option>
							</el-select>
							<!-- <el-select size="mini" v-model="tableInfo2.channel" clearable filterable default-first-option placeholder="渠道号" @change="channelChange">
							    <el-option v-for="(item,index) in channelInfo.list" :key="index" :label="item" :value="item"></el-option>
							</el-select> -->
							<div class="myRangeMonth">
								<label for="">开始</label>
								<el-date-picker size="mini" v-model="tableInfo2.rangeDate.minDate" type="date" placeholder="开始日期" value-format="yyyy-MM-dd"></el-date-picker>
								<label for="">结束</label>
								<el-date-picker size="mini" v-model="tableInfo2.rangeDate.maxDate" type="date" placeholder="结束日期" value-format="yyyy-MM-dd"></el-date-picker>
								<el-button size="mini" type="primary" @click="doSearch(tableInfo2, 2)"><i class="fa fa-search"></i></el-button>
							</div>
						</div>
					</div>
					<div class="table-main">
						<el-table id="table2" :data="tableInfo2.data" size="small" height="342" border style="width: 100%" v-loading="tableInfo2.loading">
						    <el-table-column prop="date" label="日期" sortable align="center">
						    	<template slot-scope="scope" align="center">
			                         <span v-if="scope.row.page_name == '登录页'">{{scope.row.date}}</span>
			                    </template>
						    </el-table-column>
						    <el-table-column prop="page_name" label="页面" width='120' class-name="myClass" align="center"></el-table-column>
					    	<el-table-column align="center" prop="times" label="平均时长">
					    		<template slot-scope="scope">
			                         <span>{{scope.row.times}}s</span>
			                    </template>
					    	</el-table-column>
					    	<el-table-column align="center" prop="time30" label="0~30s"></el-table-column>
					    	<el-table-column align="center" prop="time60" label="31~60s"></el-table-column>
					    	<el-table-column align="center" prop="time120" label="61~120s"></el-table-column>
					    	<el-table-column align="center" prop="timeOther" label="120s↑"></el-table-column>
					  	</el-table>
					</div>
				</div>
			</el-col>
		</el-row>
	</div>
</template>
<script>
import {
  getAppListReg,
  actionYamTimeReg,
  actionPageTimeReg
} from "../../api/api.js";
import { thisWeek } from "../../api/formatter";
import { downloadExcel } from "../../common/common";
export default {
  data() {
    return {
      // 登陆信息
      userperadmin: "",
      // App信息
      appInfo: {
        list: []
      },
      // 验证码数据
      tableInfo1: {
        loading: false,
        selected: 1, //选择APP值
        // 表格数据
        data: [],
        // 渠道号
        channel: "",
        // 日期选择
        rangeDate: {
          minDate: "",
          maxDate: ""
        }
      },
      //页面停留
      tableInfo2: {
        loading: false,
        selected: 1, //选择APP值
        // 表格数据
        data: [],
        // 渠道号
        channel: "",
        // 日期选择
        rangeDate: {
          minDate: "",
          maxDate: ""
        }
      }
    };
  },
  mounted() {
    setTimeout(() => {
      this.userperadmin = JSON.parse(localStorage.getItem("userperadmin"));
      // 获取所有APP产品
      this.getAppListFun();
      // 验证码时间默认选择昨天和今天
      this.tableInfo1.rangeDate.minDate = thisWeek()[0];
      this.tableInfo1.rangeDate.maxDate = thisWeek()[1];
      this.getTableDataFun(this.tableInfo1, 1);
      // 页面停留时间默认选择昨天和今天
      this.tableInfo2.rangeDate.minDate = thisWeek()[0];
      this.tableInfo2.rangeDate.maxDate = thisWeek()[1];
      setTimeout(() => {
        this.getTableDataFun(this.tableInfo2, 2);
      }, 1000);
    }, 300);
  },
  methods: {
    // 点击查询：查询表格
    doSearch(tableInfo, index) {
      var minDate = tableInfo.rangeDate.minDate;
      var maxDate = tableInfo.rangeDate.maxDate;
      if (!minDate) minDate = "";
      if (!maxDate) maxDate = "";
      if (minDate != "" && maxDate == "") {
        this.toast("请选择结束日期", "error");
        return;
      }
      if (minDate == "" && maxDate != "") {
        this.toast("请选择开始日期", "error");
        return;
      }
      if (minDate > maxDate) {
        this.toast("开始日期不能大于结束日期", "error");
        return;
      }
      this.getTableDataFun(tableInfo, index);
    },
    // 搜索请求数据
    getTableDataFun(tableData, index) {
      tableData.data = [];
      tableData.loading = true;
      var requireInterface = null;
      var parameter = {
        sessionid: this.userperadmin.sessionid,
        minDate: tableData.rangeDate.minDate,
        maxDate: tableData.rangeDate.maxDate,
        appId: tableData.selected
      };
      console.log(parameter);
      switch (index) {
        case 1:
          requireInterface = actionYamTimeReg;
          break;
        case 2:
          console.log("-----------------");
          requireInterface = actionPageTimeReg;
          break;
        default:
          // statements_def
          break;
      }
      let _this = this;
      console.log(requireInterface);
      requireInterface(parameter).then(res => {
        console.log(res.data);
        if (res.data.status === 0) {
          if (index === 1) {
            _this.verificationCode(res.data.actionYamList);
          } else if (index === 2) {
            _this.pageDetentionTime(res.data.actionYamList);
          }
          tableData.loading = false;
        }
        if (res.data.status === 1) {
          _this.$router.replace("/login");
        }
      });
    },
    // 验证码数据
    verificationCode(data) {
      console.log("验证码数据", data);
      if (!data) {
        return;
      }
      data.map(item => {
        if (item.page_name == "loginPage") {
          return (item.page_name = "登录页");
        } else if (item.page_name == "registerPage") {
          return (item.page_name = "注册页");
        } else if (item.page_name == "yinhangkaPage") {
          return (item.page_name = "绑卡");
        }
      });
      this.tableInfo1.data = data;
    },
    // 页面停留数据
    pageDetentionTime(data) {
      console.log("页面停留数据", data);
      if (!data) {
        return;
      }
      let newData = [];
      // for (var i = 0; i < data.length; i++) {
      // 	for (var j = 0; j < data.length; j++) {
      // 		if (data[i].date<data[j].date) {
      // 			if (data[i].page_name == "loginPage") {
      // 				if (data[i].type == "0") {
      // 				    newData.push(data[i])
      // 				}els
      // 			}
      // 		}
      // 	}
      // }
      console.log("-----------");
      console.log(newData);
      data.map(item => {
        if (item.page_name == "loginPage") {
          if (item.type == "0") {
            return (item.page_name = "登录页");
          } else if (item.type == "1") {
            return (item.page_name = "登录成功");
          }
        } else if (item.page_name == "registerPage") {
          if (item.type == "0") {
            return (item.page_name = "注册页");
          } else if (item.type == "1") {
            return (item.page_name = "注册成功");
          }
        } else if (item.page_name == "shenfenzhengPage") {
          if (item.type == "0") {
            return (item.page_name = "身份认证");
          } else if (item.type == "1") {
            return (item.page_name = "身份认证提交");
          }
        } else if (item.page_name == "yinhangkaPage") {
          if (item.type == "0") {
            return (item.page_name = "绑卡");
          } else if (item.type == "1") {
            return (item.page_name = "绑卡提交");
          }
        } else if (item.page_name == "jibenxingxiPage") {
          if (item.type == "0") {
            return (item.page_name = "基本信息");
          } else if (item.type == "1") {
            return (item.page_name = "基本信息提交");
          }
        } else if (item.page_name == "shipingPage") {
          if (item.type == "0") {
            return (item.page_name = "视频认证");
          } else if (item.type == "1") {
            return (item.page_name = "视频认证提交");
          }
        }
      });
      this.tableInfo2.data = data;
    },
    // 获取App信息
    getAppListFun() {
      // 获取app列表
      var parameter = {
        sessionid: this.userperadmin.sessionid
      };
      let _this = this;
      getAppListReg(parameter)
        .then(res => {
          // console.log('getAppListFun', res);
          if (res.data.status === 0) {
            _this.appInfo.list = res.data.appList;
          } else if (res.data.status === 1) {
            _this.$router.replace("/login");
          } else {
            _this.toast(res.data.statusMsg, "error");
          }
        })
        .catch(err => {
          _this.toast("请求APP列表错误，请联系管理员", "error");
        });
    },
    toast(msg, type) {
      this.$message({
        type: type,
        message: msg
      });
    },
    // 下载
    downloadExcelFun(tableId, filename) {
      downloadExcel(tableId, filename);
    }
  }
};
</script>
<style scoped>
.page {
  text-align: left;
}
.table-wrapper {
  margin-bottom: 25px;
  border: 1px solid #dee5e7;
  box-shadow: 0 0 6px 0 #dcdcdc;
}
.table-header {
  padding: 10px 15px;
  font-size: 16px;
  border-bottom: 1px solid #dee5e7;
  background: #e5e9f2;
  position: relative;
}
.table-action {
  padding: 10px;
  font-size: 12px;
  box-sizing: border-box;
}
.toolbar {
  overflow: hidden;
  margin-bottom: 15px;
}
.myRangeMonth {
  float: right;
}
.table-header > .title {
  font-weight: 700;
}
.table-header > .btn {
  float: right;
  cursor: pointer;
}
.table-header > .btn:hover {
  color: #409eff;
}
@media only screen and (min-width: 1200px) {
  .marginR {
    margin-left: 20px;
  }
}
</style>
<style>
.userAction .el-table--small td,
.el-table--small th {
  padding: 4px 0;
}
.userAction .el-table .cell,
.el-table--border td:first-child .cell,
.el-table th div,
.el-table--border th:first-child .cell {
  padding: 0 4px;
}
.userAction .el-checkbox__label {
  padding-left: 5px;
}
.userAction .el-checkbox + .el-checkbox {
  margin-left: 10px;
}
.userAction .el-date-editor.el-input {
  width: 100px;
}
.userAction .el-select .el-input--suffix .el-input__inner {
  padding-right: 6px;
  padding-left: 6px;
}
.userAction .el-input--suffix .el-input__inner {
  padding-right: 6px;
}
.userAction .el-select > .el-input {
  width: 100px;
}
@media only screen and (min-width: 1200px) {
  .marginR {
    margin-left: 20px;
  }
}
</style>


