<template>
  <el-row class="container">
    <el-col :span="24" class="header">
      <el-col :span="10" class="logo" :class="collapsed?'logo-collapse-width':'logo-width'">
        {{collapsed?'':sysName}}
      </el-col>
      <el-col :span="4">
        <div class="tools" @click.prevent="collapse">
          <i class="el-icon-menu"></i>
        </div>
      </el-col>
      <el-col :span="10" class="userinfo">
        <el-dropdown trigger="hover">
          <span class="el-dropdown-link userinfo-inner">{{sysUserName}}</span>
          <el-dropdown-menu slot="dropdown">
           <!--  <el-dropdown-item>我的消息</el-dropdown-item>
            <el-dropdown-item>设置</el-dropdown-item> -->
            <el-dropdown-item  @click.native="logout">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-col>
    </el-col>
    <el-col :span="24" class="main">
      <aside :class="collapsed?'menu-collapsed':'menu-expanded'">
        <!--导航菜单-->
        <el-menu :default-active="$route.path" id="lastclass" class="el-menu-vertical-demo" @open="handleopen" @close="handleclose" @select="handleselect" unique-opened router v-show="!collapsed"  background-color="#324057" text-color="#fff" active-text-color="#ffd04b">
          <template v-for="(item,index) in $router.options.routes" v-if="!item.hidden">
            <el-submenu :index="index+''" v-if="!item.leaf">
              <template slot="title"><i :class="item.iconCls"></i>{{item.name}}</template>
              <el-menu-item v-for="child in item.children" :index="child.path" :key="child.path" v-if="!child.hidden && !child.children">
                {{child.name}}
              </el-menu-item>
              <el-submenu :index="child.path" :key="child.path" v-if="child.children && !child.hidden" v-for="(child, index) in item.children">
                <template slot="title">{{child.name}}</template>
                <el-menu-item :index="subchild.path" :key="subchild.path"  v-if="!subchild.hidden" v-for="subchild in child.children">{{subchild.name}}</el-menu-item>
              </el-submenu>
            </el-submenu>
            <el-menu-item v-if="item.leaf&&item.children.length>0" :index="item.children[0].path"><i :class="item.iconCls"></i>{{item.children[0].name}}</el-menu-item>
          </template>
        </el-menu>

        <!--导航菜单-折叠后-->
        <ul class="el-menu el-menu-vertical-demo collapsed" v-show="collapsed" ref="menuCollapsed">
          <li v-for="(item,index) in $router.options.routes" v-if="!item.hidden" class="el-submenu item">
            <template v-if="!item.leaf">
              <div class="el-submenu__title" style="padding-left: 20px;" @mouseover="showMenu(index,true)" @mouseout="showMenu(index,false)"><i :class="item.iconCls"></i></div>
              <ul class="el-menu submenu" :class="'submenu-hook-'+index" @mouseover="showMenu(index,true)" @mouseout="showMenu(index,false)"> 
                <li v-for="child in item.children" v-if="!child.hidden" :key="child.path" class="el-menu-item" style="padding-left: 40px;" :class="$route.path==child.path?'is-active':''" @click="$router.push(child.path)">{{child.name}}</li>
              </ul>
            </template>
            <template v-else>
              <li class="el-submenu">
                <div class="el-submenu__title el-menu-item" style="padding-left: 20px;height: 56px;line-height: 56px;padding: 0 20px;" :class="$route.path==item.children[0].path?'is-active':''" @click="$router.push(item.children[0].path)"><i :class="item.iconCls"></i></div>
              </li>
            </template>
          </li>
        </ul>
      </aside>
      <section class="content-container">
        <div class="grid-content bg-purple-light">
          <!-- <el-col :span="24" class="breadcrumb-container">
            <strong class="title">{{$route.name}}</strong>
            <el-breadcrumb separator="/" class="breadcrumb-inner">
              <el-breadcrumb-item v-for="item in $route.matched" :key="item.path">
                {{ item.name }}
              </el-breadcrumb-item>
            </el-breadcrumb>
          </el-col> -->
          <el-col :span="24" class="content-wrapper">
            <keep-alive><router-view></router-view></keep-alive>
<!--           <router-view></router-view> -->
          </el-col>
        </div>
      </section>
    </el-col>
  </el-row>
</template>

<script>
  export default {
    data() {
      return {
        sysName:'运营后台管理系统',
        collapsed:false,
        sysUserName: '',
      }
    },
    methods: {
      handleopen() {
        //console.log('handleopen');
      },
      handleclose() {
        //console.log('handleclose');
      },
      handleselect: function (a, b) {
      },
      //退出登录
      logout: function () {
        var _this = this;
        this.$confirm('确认退出吗?', '提示', {
          //type: 'warning'
        }).then(() => {
          sessionStorage.removeItem('user');
          localStorage.removeItem('loginStatus');
          localStorage.removeItem('userperadmin');
          sessionStorage.removeItem('userPermiss');
          _this.$router.push('/login');
        }).catch(() => {

        });
      },
      //折叠导航栏
      showMenu(i,status){
        this.$refs.menuCollapsed.getElementsByClassName('submenu-hook-'+i)[0].style.display=status?'block':'none';
      },
      collapse:function(){
        this.collapsed=!this.collapsed;
        var uiwidth = document.getElementById('lastclass');
        if(uiwidth.offsetWidth===0){
        uiwidth.style.width = "230px"
        }
      }
    },
    mounted() {
      var userperadmin = JSON.parse(localStorage.getItem("userperadmin"));
        // 获取保存在本地的用户权限信息userPermiss
      let userPermission = JSON.parse(unescape(sessionStorage.getItem('userPermiss')));
      console.log(userPermission)
       // 如果保存本地的数据被删除刷新跳转登录页
      if (!userperadmin || !userPermission) {
        this.$router.replace("/login")
      };
      this.sysUserName = userperadmin.admin.email || '';
      console.log(userPermission)
      console.log(this.$router.options.routes);
      let router = this.$router.options.routes;
      let _this = this;
      try {
           // 导航权限
          for (var i = 0; i < userPermission.data.length; i++) {
             for (var j = 0; j < router.length; j++) {
                 if (userPermission.data[i].meta === router[j].meta) {
                    router[j].hidden = userPermission.data[i].show =='0'?true:false;
                    // 根据后台数据改变路由名字 
                    // router[j].name = userPermission.data[i].meta_name;
                    // 导航孩子路由
                    for (var m = 0; m < userPermission.data[i].children.length; m++) {
                       for (var k = 0; k <router[j].children.length; k++) {
                          if (userPermission.data[i].children[m].meta == router[j].children[k].meta) {
                            router[j].children[k].hidden = userPermission.data[i].children[m].show == '0'?true:false;
                            // console.log(router[j].children[k])
                            // 对报表中心用户行为的概况，分析进行单独权限处理
                            if (router[j].children[k].meta === '0204') {
                              if (router[j].children[k].children && router[j].children[k].children.length>0) {
                                  for (var n = 0; n <router[j].children[k].children.length; n++) {
                                    for (var s = 0; s <userPermission.data[i].children[m].children.length; s++) {
                                      if (router[j].children[k].children[n].meta === userPermission.data[i].children[m].children[s].meta) {
                                         router[j].children[k].children[n].hidden = userPermission.data[i].children[m].children[s].show == '0'?true:false;
                                      }
                                    } 
                                  }
                              }
                            }
                            // 根据后台数据改变路由名字
                            // router[j].children[k].name = userPermission.data[i].children[m].meta_name;
                          };
                       };
                    };
                  };
             };
          };
      } catch(e) {
        // statements
        console.log(e);
      }
    }
  }

</script>

<style scoped lang="scss">
  .container {
    position: absolute;
    top: 0px;
    bottom: 0px;
    width: 100%;
    .header {
      height: 60px;
      line-height: 60px;
      background: #1f2d3d;
      color:#fff;
      .userinfo {
        text-align: right;
        padding-right: 35px;
        float: right;
        .userinfo-inner {
          cursor: pointer;
          color:#fff;
          img {
            width: 40px;
            height: 40px;
            border-radius: 20px;
            margin: 10px 0px 10px 10px;
            float: right;
          }
        }
      }
      .logo {
        //width:230px;
        height:60px;
        font-size: 18px;
        padding-left:20px;
        padding-right:20px;
        border-color: rgba(238,241,146,0.3);
        border-right-width: 1px;
        border-right-style: solid;
        img {
          width: 40px;
          float: left;
          margin: 10px 10px 10px 18px;
        }
        .txt {
          color:#fff;
        }
      }
      .logo-width{
        width:230px;
      }
      .logo-collapse-width{
        width:60px
      }
      .tools{
        padding: 0px 23px;
        width:14px;
        height: 60px;
        line-height: 60px;
        cursor: pointer;
      }
    }
    .main {
      display: flex;
      // background: #324057;
      position: absolute;
      top: 60px;
      bottom: 0px;
      overflow: hidden;
      aside {
        flex:0 0 230px;
        width: 230px;
        // position: absolute;
        // top: 0px;
        // bottom: 0px;
        .el-menu{
          height: 100%;
          text-align: left;
        }
        .collapsed{
          width:60px;
          .item{
            position: relative;
          }
          .submenu{
            position:absolute;
            top:0px;
            left:60px;
            z-index:99999;
            height:auto;
            display:none;
          }

        }
      }
      .menu-collapsed{
        flex:0 0 60px;
        width: 60px;
        background-color:rgb(50, 64, 87);
      }
      .menu-expanded{
        flex:0 0 230px;
        width: 230px;
        background-color:rgb(50, 64, 87);
      }
      .content-container {
        // background: #f1f2f7;
        flex:1;
        // position: absolute;
        // right: 0px;
        // top: 0px;
        // bottom: 0px;
        // left: 230px;
        overflow-y: scroll;
        padding: 20px;
        .breadcrumb-container {
          //margin-bottom: 15px;
          .title {
            width: 200px;
            float: left;
            color: #475669;
          }
          .breadcrumb-inner {
            float: right;
          }
        }
        .content-wrapper {
          background: #fff;
          box-sizing: border-box;
        }
      }
    }
    .el-menu{
      background-color:#324057;
    }
    .el-menu .el-submenu__title{
      text-align: left;
    }
    .el-menu-item{
      color:#ffffff;
    }
    .el-menu-item:hover{
      color: #000000;

    }
  }
</style>


