<template>
	<div class="dailyHistor">
		<!-- 头部选择信息部分 -->
		<div class="header">
			<!-- 选择日期 -->
		  	<span class="hraderFont">起止时间:</span>
		    <el-date-picker
		      v-model="chooseDate"
		      type="daterange"
		      range-separator="至"
		      start-placeholder="开始日期"
		      end-placeholder="结束日期"
		      size="small"
		      unlink-panels
	          value-format="yyyy-MM-dd"
	          :editable="false"
		      >
		    </el-date-picker>
		    <!-- 选择APP -->
			<el-select v-model="appValue" placeholder="选择APP" size='small' class='dateStyle marginLeft'>
			    <el-option
			      v-for="item in appOptions"
			      :key="item.id"
			      :label="item.name"
			      :value="item.id">
			    </el-option>
			</el-select>
			<el-button type="info" icon="el-icon-search" size='small' class='marginLeft' @click='searchHistory'></el-button>
			<el-button type="primary" class="el-icon-download" size='small' @click='tableDown'></el-button>
		</div>
		<div class="content" v-loading="loading" element-loading-text="拼命加载中...">
			<el-tabs v-model="activeName2" type="card" @tab-click="handleClick">
			    <el-tab-pane label="运营数据" name="6">
			    	<dailyHistoryTable :operationData='getNeedData' :operationIndex='6' @sendParentcommentRow="clickRowComment"/></dailyHistoryTable>
			    </el-tab-pane>
			    <el-tab-pane label="续贷数据" name="7">
			    	<dailyHistoryTable :operationData='refinanceData' :operationIndex='7' @sendParentcommentRow="clickRowComment"/></dailyHistoryTable>
			    </el-tab-pane>
			    <el-tab-pane label="BD数据" name="2">
			    	<dailyHistoryTable :operationData='BDData' :operationIndex='2' @sendParentcommentRow="clickRowComment" @sendDeilRow='clickDeil'/></dailyHistoryTable>
			    </el-tab-pane>
			    <el-tab-pane label="市场数据" name="1">
			    	<dailyHistoryTable :operationData='bazaarData' :operationIndex='1' @sendParentcommentRow="clickRowComment" @sendDeilRow='clickDeil'/></dailyHistoryTable>
			    </el-tab-pane>
			</el-tabs>
		</div>
		<!-- 模态框 -->
	     <el-dialog title="评论" :visible.sync="commentVisible" width="60%" style='overflow: hidden;min-width: 50%;'>
	     	<div class="commentInfo" v-show='commentInfoLength'>
	     		<div class="boxcomment clearfix" v-for='item in getCommentInfo'>
	     			<div class="commentUserNameDate">
	     				<div class='uesrName'><span>账号:</span><span>{{item.name}}</span></div>
	     				<div class='commentdate'><span>{{item.commentTime}}</span></div>
	     			</div>
		     		<div class="commentline"></div>
		     		<div class="commentConter">{{item.comment}}</div>
	     		</div>
	     	</div>
	     	<div class="statrComment">
	     		<div class="tltleComment">添加评论</div>
	     		<div class="inputInfoContent">
	     			<el-input
					  type="textarea"
					  :autosize="{ minRows: 4,maxRows: 6}"
					  placeholder="请输入评论内容"
					  v-model="textareaComment">
					</el-input>
	     		</div>
	     	</div>
	         <span slot="footer">
		          <el-button @click="commentVisible = false" size="small">取 消</el-button>
		          <el-button type="primary" size="small" @click="saveComment">提交</el-button>
	        </span>   
	    </el-dialog>
	    <!-- 模态框BD表格详情 -->
	     <el-dialog title="详情" :visible.sync="deilBDVisible" width="90%" style='overflow: hidden;min-width: 50%;'>
	     	<el-button type="primary" size="small" @click="BDDeilDown" class='deilDownStyle'>下载</el-button>
	     	<div style="height: 50px"></div>
	     	<el-table :data="OPBDData"  style="width: 100%" :height="OPBDData&&OPBDData.length>0?350:100" border  size="mini" v-loading="tableloading" element-loading-text="拼命加载中..." :highlight-current-row="true">
                 <el-table-column type="index" label="排序"></el-table-column>
                 <el-table-column prop="date" label="日期"  width='120'></el-table-column>
                 <el-table-column prop="channelName" label="推广渠道"></el-table-column>
                 <el-table-column prop="channel" label="渠道号(一级)" width='120'></el-table-column>
                 <el-table-column prop="register" label="注册"></el-table-column>
                 <el-table-column prop="upload" label="进件"></el-table-column>
                 <el-table-column prop="uploadConversion" label="进件转化率">
                    <template slot-scope="scope">
                         <span>{{scope.row.uploadConversion}}%</span>
                    </template>
                 </el-table-column>
                 <el-table-column prop="account" label="开户"></el-table-column>
                 <el-table-column prop="accountConversion" label="开户转化率">
                    <template slot-scope="scope">
                         <span>{{scope.row.accountConversion}}%</span>
                    </template>
                 </el-table-column>
                 <el-table-column prop="firstGetPer" label="渠道首提人数"></el-table-column>
                 <el-table-column prop="firstGetSum" label="首提总金额"></el-table-column>
                 <el-table-column prop="outFirstGetSum" label="外部首提总金额"></el-table-column>
                 <el-table-column prop="loaner" label="渠道放款总笔数"></el-table-column>
                 <el-table-column prop="channelSum" label="渠道提现总金额"></el-table-column>
                 <el-table-column prop="perCapitaCredit" label="渠道笔均金额"></el-table-column>
                 <el-table-column prop="income" label="收入"></el-table-column>
                 <el-table-column prop="cost" label="成本"></el-table-column>
                 <el-table-column prop="grossProfit" label="毛利">
                    <template slot-scope="scope">
                         <span :class="scope.row.grossProfit<0?'colorFont':''">{{scope.row.grossProfit}}</span>
                    </template>
                 </el-table-column>
                 <el-table-column prop="grossProfitRate" label="毛利率">
                     <template slot-scope="scope">
                         <span :class="scope.row.grossProfitRate<0?'colorFont':''">{{scope.row.grossProfitRate}}%</span>
                    </template>
                 </el-table-column>
                 <el-table-column prop="cost2" label="优化后成本"></el-table-column>
                 <el-table-column prop="grossProfit2" label="优化后毛利">
                    <template slot-scope="scope">
                         <span :class="scope.row.grossProfit2<0?'colorFont':''">{{scope.row.grossProfit2}}</span>
                    </template>
                 </el-table-column>
                 <el-table-column prop="grossProfitRate2" label="优化后毛利率">
                     <template slot-scope="scope">
                         <span :class="scope.row.grossProfitRate2<0?'colorFont':''">{{scope.row.grossProfitRate2}}%</span>
                    </template>
                 </el-table-column>
                 <el-table-column prop="ROI" label="roi"></el-table-column>
                 <el-table-column prop="firstIncome" label="首贷收入"></el-table-column>
                 <el-table-column prop="ROIFirst" label="roi首贷"></el-table-column>
                 <el-table-column  label="评论">
                 	<template slot-scope="scope">
                         <el-button type="text" size="small"   @click='detailsCommentClick(scope.row,scope.$index)'><i class="fa fa-align-justify"></i></el-button>
                    </template>
                 </el-table-column>
            </el-table>
	    </el-dialog>
	    <!-- 模态框市场表格详情 -->
	     <el-dialog title="详情" :visible.sync="deilbazaarVisible" width="90%" style='overflow: hidden;min-width: 50%;'>
	     	<el-button type="primary" size="small" @click="deilbazaarDown" class='deilDownStyle'>下载</el-button>
	     	<div style="height: 50px"></div>
	     	 <el-table :data="opbazaarDData"  style="width: 100%" :height="opbazaarDData&&opbazaarDData.length>0?350:100" border  size="mini" v-loading="tableloading" element-loading-text="拼命加载中..." :highlight-current-row="true">
                 <el-table-column type="index" label="排序"></el-table-column>
                 <el-table-column prop="date" label="日期"  width='120'></el-table-column>
                 <el-table-column prop="channelName" label="推广渠道"></el-table-column>
                 <el-table-column prop="channel" label="渠道号"></el-table-column>
                 <el-table-column prop="register" label="注册"></el-table-column>
                 <el-table-column prop="upload" label="进件"></el-table-column>
                 <el-table-column prop="account" label="开户"></el-table-column>
                 <el-table-column prop="firstGetPer" label="首提人数"></el-table-column>
                 <el-table-column prop="perCapitaCredit" label="提现笔均金额"></el-table-column>
                 <el-table-column prop="loaner" label="渠道放款总笔数"></el-table-column>
                 <el-table-column prop="channelSum" label="渠道提现总金额"></el-table-column>
                 <el-table-column prop="income" label="收入"></el-table-column>
                 <el-table-column prop="cost" label="成本"></el-table-column>
                 <el-table-column prop="grossProfit" label="毛利"></el-table-column>
                 <el-table-column prop="grossProfitRate" label="毛利率">
                    <template slot-scope="scope">
                         <span>{{scope.row.grossProfitRate}}%</span>
                    </template>
                 </el-table-column>
                 <el-table-column prop="uploadConversion" label="进件转化率">
                    <template slot-scope="scope">
                         <span>{{scope.row.uploadConversion}}%</span>
                    </template>
                 </el-table-column>
                 <el-table-column prop="accountConversion" label="开户转化率">
                    <template slot-scope="scope">
                         <span>{{scope.row.accountConversion}}%</span>
                    </template>
                 </el-table-column>
                 <el-table-column prop="registerCpa" label="注册CPA"></el-table-column>
                 <el-table-column prop="accountCpa" label="开户CPA"></el-table-column>
                 <el-table-column prop="ROI" label="ROI"></el-table-column>
                 <el-table-column  label="评论"  >
                    <template slot-scope="scope">
                         <el-button type="text" size="small"  @click='bazaarCommentClick(scope.row,scope.$index)'><i class="fa fa-align-justify"></i></el-button>
                    </template>
                 </el-table-column>
            </el-table>
	    </el-dialog>
	</div>
</template>
<script>
// 运营数据 '6'  续贷数据 '7' BD数据 '2' 市场数据 '1'
    import {yesterDay,toDay} from '../../api/formatter.js'
    import dailyHistoryTable from '../ConclusionTheCenter/tableComponent/dailyHistoryTable.vue'
    import {getOperateAndSecondDataReg,dailyHistoryReg,getBdAndMarketDataReg,getCommentListReg,addCommentReg,getBdAndMarketDeatailDataReg,exportOperateAndSecondDataReg,exportgetBdAndMarketDataReg,exportBdAndMarketDeatailDataReg} from '../../api/api.js'
	export default {
		data() {
			return {
				userInfo:null,// 用户信息
				loading:false,//加载动画
	            chooseDate:[],//选择日期值
	            appValue:1,//选择APP值
	            appOptions:[],//APP获取的信息
	            activeName2: '6',//默认table切换
	            getNeedData:[],//后台获取运营的数据
	            refinanceData:[],//后台获取续贷的数据
	            BDData:[],//后台获取BD的数据
	            bazaarData:[],//后台获取市场的数据
	            // 评论
	            componentGetInfo:null,//点击评论获取点击此行的信息
	            getCommentInfo:[],//后台获取存在的评论信息
	            commentInfoLength:false,//获取到的评论信息为空时候不显示存在的评论样式
	            commentVisible:false,//评论显示状态
	            textareaComment:'',//输入的评论内容
	            deilBDVisible:false,//点击查看BD详情模态框
	            deilBDRow:null,//点击详情查看时候某行数据
	            OPBDData:[],//BD详情返回的数据
	            deilbazaarVisible:false,//点击查看市场详情模态框
	            opbazaarDData:[],//市场详情返回的数据
	            tableloading:false,//详情加载动画
			}
		},
		components:{
			dailyHistoryTable
		},
		mounted() {
			setTimeout(()=>{
				// 获取数据信息
				this.userInfo = JSON.parse(localStorage.getItem("userperadmin"));
				this.chooseDate = [yesterDay()[0],toDay()[0]];//默认选择昨天到今天
				this.getAppInfo(this.userInfo.sessionid);
			},300)
			
		},
		methods: {
	      handleClick(tab, event) {
	        // console.log(tab, event);
	        console.log(this.activeName2)
	        this.loading = true;//进行切换时候启动加载动画
	        this.searchHistory()
	      },
	      // 首次进入获取APP产品信息
	      getAppInfo(sessionid) {
	      	 this.loading = true;
	      	 let userSessionid = {
	      	 	sessionid:sessionid
	      	 };
	      	 let _this = this;
	      	 dailyHistoryReg(userSessionid).then(res=>{
	      	 	console.log('-----')
	      	 	console.log(res.data)
	      	 	if (res.data.status === 0) {
	      	 		_this.appOptions = res.data.appList;
	      	 		_this.$nextTick(function() {
	      	 		   _this.searchHistory()
	      	 		})
	      	 	};
	      	 	if (res.data.status === 1) {
	      	 		_this.$router.replace("/login");
	      	 	};
	      	 })
	      },
	      // 点击搜索事件
	      searchHistory() {
	      	this.getNeedData = [];//table切换时候传入子组件的报表数据置空再执行搜索事件
	      	this.refinanceData = [];//table切换时候传入子组件的报表数据置空再执行搜索事件
	      	this.BDData = [];//table切换时候传入子组件的报表数据置空再执行搜索事件
	      	this.bazaarData = [];//table切换时候传入子组件的报表数据置空再执行搜索事件
	      	let sendSearch = {
	      		sessionid:this.userInfo.sessionid,
	      		channelType:this.activeName2,
	      		appId:this.appValue,
	      		minDate:this.chooseDate.length>0?this.chooseDate[0]:'',
	      		maxDate:this.chooseDate.length>0?this.chooseDate[1]:'',
	      	};
	      	console.log('点击搜索发送信息',sendSearch)
	      	let _this = this;
	      	if (this.activeName2 ==='6' ||this.activeName2 ==='7') {
	      		getOperateAndSecondDataReg(sendSearch).then(res=>{
		      		console.log('点击搜索运营续贷后台返回数据',res.data)
		      		if (res.data.status === 0) {
		      			switch (this.activeName2) {
		      				case '6':
		      					_this.getNeedData = res.data.reportforms_admin;
		      					break;
		      				case '7':
		      					_this.refinanceData = res.data.reportforms_admin;
		      					break;
		      				default:
		      					// statements_def
		      					break;
		      			}
		      			
		      			_this.loading = false;
		      		};
		      		if (res.data.status === 1) {
		      			_this.$router.replace("/login");
		      		};
		      	})

	      	}
	      	if (this.activeName2 ==='2' || this.activeName2 ==='1') {
	      		getBdAndMarketDataReg(sendSearch).then(res=>{
		      		console.log('点击搜索BD市场后台返回数据',res.data)
		      		if (res.data.status === 0) {
		      			switch (this.activeName2) {
		      				case '2':
		      					_this.BDData = res.data.reportforms_admin;
		      					break;
		      				case '1':
		      					_this.bazaarData = res.data.reportforms_admin;
		      					break;
		      				default:
		      					// statements_def
		      					break;
		      			}
		      			
		      			_this.loading = false;
		      		};
		      		if (res.data.status === 1) {
		      			_this.$router.replace("/login");
		      		};
		      	})
	      	}
	      	
	      },
	     clickRowComment(msg){
	      	// 把点击评论获取的点击此行信息保存用与读取
	    	this.componentGetInfo = msg;
	    	// 初始话输入的评论内容和获取评论的信息
	    	this.textareaComment = '';
	    	this.getCommentInfo = [];
	    	let getComment = {
	    		sessionid:this.userInfo.sessionid,
                appId: this.appValue,
                date: msg.date,
                type:this.activeName2,
                channelName: msg.channelName?msg.channelName:'',
                channel:msg.channel?msg.channel:''
	    	};
	    	console.log('评论发送的参数',getComment)
		    let _this = this;
		    getCommentListReg(getComment).then(res=>{
	    		console.log('评论得到的数据',res.data)
	    		if (res.data.status === 0) {
	    			if (res.data.reportforms_admin && res.data.reportforms_admin.length>0) {
	    				_this.commentInfoLength = true;
	    				_this.getCommentInfo = res.data.reportforms_admin;

	    			}else {
	    				_this.commentInfoLength = false;
	    			}
	    			_this.commentVisible = true;
	    		};
	    		if (res.data.status === 1) {
	    			_this.$router.replace("/login");
	    		};
		    })
	    },
	      // 保存评论信息事件
		saveComment(){	
		    	let sendSaveComment = {
		    		sessionid:this.userInfo.sessionid,
                    appId: this.appValue,
                    date: this.componentGetInfo.date,
                    type:this.activeName2,
                    channelName: this.componentGetInfo.channelName?this.componentGetInfo.channelName:'',
                    channel:this.componentGetInfo.channel?this.componentGetInfo.channel:'',
                    comment:this.textareaComment
		    	};
		    	console.log('保存评论时发送数据',sendSaveComment)
		    	let _this = this;
		    	addCommentReg(sendSaveComment).then(res=>{
		    		console.log(res.data)
		    		if (res.data.status === 0) {
		    			 _this.$message({
				          message: '评论保存成功',
				          type: 'success'
				        });
		    			_this.commentVisible = false;
		    		};
		    		if (res.data.status === 1) {
		    			_this.$router.replace("/login");
		    		};
		    	})
		},
	    clickDeil(msg){
	    	this.tableloading = true;
	        //点击详情获取此行数据
	        this.deilBDRow = msg;
	    	// 点击详情获取信息接口
	      	if (this.activeName2 ==='2' || this.activeName2 ==='1') {
	      		let deatailSend = {
	      			sessionid:this.userInfo.sessionid,
	      			date:msg.date,
		      		channelType:this.activeName2,
		      		appId:this.appValue
	      		};
	      		console.log('点击BD或市场某行获取详细信息发送参数',deatailSend)
	      		let _this = this;
	      		getBdAndMarketDeatailDataReg(deatailSend).then(res=>{
	      			console.log(res.data)
	      			if (res.data.status === 0) {
	      				//  当点击为BD时候
	      				if (this.activeName2 ==='2') {
	      					_this.OPBDData = res.data.reportforms_admin;
	      					_this.deilBDVisible = true;	
	      				};
	      				// 当点击为市场时候
	      				if (this.activeName2 ==='1') {
	      					_this.opbazaarDData = res.data.reportforms_admin;
	      					_this.deilbazaarVisible = true;
	      				};
	      				_this.tableloading = false;
	      			};
	      			if (res.data.status === 1) {
	      				_this.$router.replace("/login");
	      			};
	      		})
	      	};
	      	 // console.log(msg)
	      },
	      // BD详情评论事件
	      detailsCommentClick(row,dataIndex) {
              this.clickRowComment(row)
	      },
	      // 市场点击详情评论事件
	      bazaarCommentClick(row,dataIndex) {
	      	 this.clickRowComment(row)
	      },
	      // 点击下载事件
	      tableDown() {
	      	    console.log('点击下载')
		    	console.log(this.activeName2)
		    	var downFunction;
		    	let downName;
		    	let sendDown = {
		    		sessionid:this.userInfo.sessionid,
                    appId: this.appValue,
                    minDate:this.chooseDate.length>0?this.chooseDate[0]:'',
	      		    maxDate:this.chooseDate.length>0?this.chooseDate[1]:'',
                    channelType:this.activeName2
		    	};
		    	switch (this.activeName2) {
		    		case '6':
		    		    downName = '日报历史运营数据'
		    			downFunction = exportOperateAndSecondDataReg
		    			break;
		    		case '7':
		    			downName = '日报历史续贷数据'
		    			downFunction = exportOperateAndSecondDataReg
		    			break;
		    		case '2':
		    			downName = '日报历史BD数据'
		    			downFunction = exportgetBdAndMarketDataReg
		    			break;
		    		case '1':
		    		    downName = '日报历史市场数据'
		    			downFunction = exportgetBdAndMarketDataReg
		    			break;
		    		default:
		    			// statements_def
		    			break;
		    	};
		    	console.log("发送参数",sendDown)
		    	let _this = this;
		    	downFunction(sendDown).then(res=>{
		    		console.log(res.data)
		    		// if (res.data.status === 0) {
		    			const content = res.data;
				        const blob = new Blob([content])
				        const fileName = downName+'统计表.xls'
				        if ('download' in document.createElement('a')) { // 非IE下载
				            const elink = document.createElement('a')
				            elink.download = fileName
				            elink.style.display = 'none'
				            elink.href = URL.createObjectURL(blob)
				            document.body.appendChild(elink)
				            elink.click()
				            URL.revokeObjectURL(elink.href) // 释放URL 对象
				            document.body.removeChild(elink)
				        } else { // IE10+下载
				            navigator.msSaveBlob(blob, fileName)
				        }
		    		// };
		    		// if (res.data.status === 1) {
		    		// 	_this.$router.replace("/login");
		    		// };
		    	})
	      },
	      //点击BD详情下载报表
	      BDDeilDown() {
	      	console.log(this.deilBDRow)
	      	let sendDeilDown = {
	    		sessionid:this.userInfo.sessionid,
                appId: this.appValue,
                date:this.deilBDRow.date,
                channelType:this.activeName2
	    	};
	    	console.log('sendDeilDown',sendDeilDown)
	    	let _this = this;
