<template>
	<div class="page userAction">
		<el-row>
			<el-col :xs="24" :lg="24">
				<div class="table-wrapper">
					<div class="table-header">
						<span class="title">返回分析</span>
						<span class="btn" @click="downloadExcelFun"><i class="fa fa-download"></i></span>
					</div>
					<div class="table-action clearfix">
						<div class="toolbar">
								<el-select size="mini" v-model="tableInfo1.selected" filterable default-first-option placeholder="选择产品">
							    	<el-option v-for="(item,index) in appInfo.list" :key="index" :label="item.name" :value="item.id"></el-option>
								</el-select>
								<!-- 版本 -->
								<el-select size="mini" v-model="tableInfo1.versions" filterable clearable default-first-option placeholder="选择版本" class='marginLeft'>
							    	<el-option v-for="(item,key,index) in versionsOp.list" :key="index" :label="item.app_version_name" :value="item.app_version_name"></el-option>
								</el-select>
								<!-- 系统 -->
								<el-select size="mini" v-model="tableInfo1.androidSystem" filterable clearable default-first-option placeholder="选择系统" class='marginLeft'>
							    	<el-option v-for="(item,key,index) in androidSystemop.list" :key="index" :label="item.platform" :value="item.platform"></el-option>
								</el-select>
								<!-- 页面 -->
								<el-select size="mini" v-model="tableInfo1.page" filterable clearable default-first-option placeholder="选择页面" class='marginLeft'>
							    	<el-option v-for="(item,key,index) in pageName.list" :key="index" :label="key" :value="item"></el-option>
								</el-select>
							<span class="myRangeMonth">
								<label for="">开始</label>
								<el-date-picker size="mini" v-model="tableInfo1.rangeDate.minDate" type="date" placeholder="开始日期" value-format="yyyy-MM-dd"></el-date-picker>
								<label for="">结束</label>
								<el-date-picker size="mini" v-model="tableInfo1.rangeDate.maxDate" type="date" placeholder="结束日期" value-format="yyyy-MM-dd"></el-date-picker>
								<el-button size="mini" type="primary" @click="doSearch(tableInfo1, 1)"><i class="fa fa-search"></i></el-button>
							</span>
						</div>
					</div>
					<div class="table-main">
						<el-table id="table1" :data="tableInfo1.data" size="small" :height="clientHeight" border style="width: 100%" v-loading="tableInfo1.loading">
							<el-table-column align="center" type="index" :index="indexFormatter1" label="序号"></el-table-column>
						    <el-table-column prop="date" label="日期" sortable align="center">
						    	<template slot-scope="scope">
			                         <span>{{scope.row.date}}</span>
			                    </template>
						     </el-table-column>
					    	<el-table-column align="center" prop="page_name" label="页面"></el-table-column>
						    <el-table-column prop="error" label="错误类型" class-name="myClass" align="center"></el-table-column>
					    	<el-table-column align="center" prop="app_version_name" label="版本"></el-table-column>
					    	<el-table-column align="center" prop="model" label="机型"></el-table-column>
					    	<el-table-column align="center" prop="platform" label="系统"></el-table-column>
					    	<el-table-column align="center" prop="cou" label="合计"></el-table-column>
					  	</el-table>
					  	<br>
					  	<el-pagination
			                background
			                @size-change="pageSizeChange"
			                @current-change="currentPageChange"
			                :current-page="currentPage"
			                :page-sizes="pageSizes"
			                :page-size="pagesize"
			                layout="total, sizes, prev, pager, next, jumper"
			                :total="dataSize" class="paginationWrapper">
			            </el-pagination>
					</div>
				</div>
			</el-col>
		</el-row>
	</div>
</template>
<script>
    import {getAppListReg,actionErrorPageSearchReg,actionErrorPageReg,exportErrorPageReg} from '../../api/api.js';
    import {yesterDay,toDay} from '../../api/formatter';
	export default {
		data() {
			return {
				// 登陆信息
			    userperadmin: '',
			    clientHeight:'500',//屏幕可视区域高度
			    // App信息
			    appInfo:{
			    	list:[]
			    },
			    // 版本信息
			    versionsOp:{
			    	list:[]
			    },
			    // 系统信息
			    androidSystemop:{
			    	list:[]
			    },
			    // 页面信息
			    pageName:{
			    	list:[]
			    },
			    // 验证码数据
				tableInfo1: {
					loading: false,
					selected:null,//选择APP值
					versions:'',//选择版本
					androidSystem:'',//选择手机系统
					page:'',
					// 表格数据
					data: [],
					// 日期选择
					rangeDate: {
						minDate: '',
						maxDate: ''
					}
				},
				//分页
				 // 分页信息
		        currentPage: 1,
		        pageSizes: [100,300,500,1000],
		        dataSize: 0,
		        pagesize: 100
			}
		},
		mounted() {
			setTimeout(()=>{
				this.userperadmin = JSON.parse(localStorage.getItem('userperadmin'));
				this.screenclientHeight();
				// 获取所有APP产品
				this.getAppListFun();
				// 验证码时间默认选择昨天和今天
				this.tableInfo1.rangeDate.minDate = yesterDay()[0];
			    this.tableInfo1.rangeDate.maxDate = toDay()[1];
			    this.getTableDataFun(this.tableInfo1, 1);
			},300)
		},
		methods:{
			// 获取可视区域高度
			screenclientHeight() {
				var clientHeight = document.documentElement.clientHeight;
				this.clientHeight = clientHeight-300;
			},
			// 点击查询：查询表格
			doSearch(tableInfo, index) {
				var minDate = tableInfo.rangeDate.minDate;
				var maxDate = tableInfo.rangeDate.maxDate;
				if(!minDate) minDate = '';
				if(!maxDate) maxDate = '';
				if (minDate != '' && maxDate == '') {
					this.toast('请选择结束日期', 'error');
					return;
				}
				if (minDate == '' && maxDate != '') {
					this.toast('请选择开始日期', 'error');
					return;
				}
				if (minDate > maxDate) {
					this.toast('开始日期不能大于结束日期', 'error');
					return;
				}
				this.getTableDataFun(tableInfo, index);
			},
			// 搜索请求数据
			getTableDataFun(tableData,index) {
				console.log(tableData)
				tableData.data = [];
				tableData.loading = true;
				var requireInterface = null;
				var parameter = {
					sessionid: this.userperadmin.sessionid,
					minDate: tableData.rangeDate.minDate,
					maxDate: tableData.rangeDate.maxDate,
					appId: tableData.selected,
					platform:tableData.androidSystem,
					app_version_name:tableData.versions,
					page_name:tableData.page,
					pageId:this.currentPage-1,
					pageSize: this.pagesize
				};
				console.log(parameter)
				let _this = this;
				console.log(requireInterface)
				actionErrorPageReg(parameter).then(res=>{
					console.log(res.data)
					if (res.data.status === 0) {
						for (var i = 0; i <res.data.actionYamList.length; i++) {
							if (res.data.actionYamList[i].page_name == "registerPage") {
								res.data.actionYamList[i].page_name = "注册页面"
							} else if (res.data.actionYamList[i].page_name == "shenfenzhengPage") {
								res.data.actionYamList[i].page_name = "身份证页面"
							} else if (res.data.actionYamList[i].page_name == "loginPage") {
								res.data.actionYamList[i].page_name = "登录页面"
							} else if (res.data.actionYamList[i].page_name == "huizongPage") {
								res.data.actionYamList[i].page_name = "汇总页面"
							} else if (res.data.actionYamList[i].page_name == "jibenxingxiPage") {
								res.data.actionYamList[i].page_name = "基本信息页面"
							} else if (res.data.actionYamList[i].page_name == "yinhangkaPage") {
								res.data.actionYamList[i].page_name = "银行卡页面"
							}
						}
                        _this.tableInfo1.data = res.data.actionYamList;
                        _this.dataSize = parseInt(res.data.listSize);
						tableData.loading = false;
					};
					if (res.data.status === 1) {
						_this.$router.replace("/login");
					};
				})
			},
			// 改变显示条数
			pageSizeChange(res){
				this.pagesize = res;
				this.getTableDataFun(this.tableInfo1,1);
			},
			// 点击当前页
			currentPageChange(res){
				this.currentPage = res;
				this.getTableDataFun(this.tableInfo1,1);
			},
			// 获取App信息
			getAppListFun() {
				// 获取app列表
				var parameter = {
					sessionid: this.userperadmin.sessionid
				};
				let _this = this;
				actionErrorPageSearchReg(parameter).then((res) => {
					console.log('getAppListFun', res);
					if(res.data.status === 0){
						_this.appInfo.list = res.data.appList;
						_this.pageName.list = res.data.basicPage.actionMap;
						_this.versionsOp.list = res.data.basicPage.appVersionName;
						_this.androidSystemop.list = res.data.basicPage.platform;
						_this.tableInfo1.selected = 1;
					}else if(res.data.status === 1){
						_this.$router.replace("/login");
					}else{
						_this.toast(res.data.statusMsg, 'error');
					}
				}).catch((err) => {
					_this.toast('请求APP列表错误，请联系管理员', 'error');
				});
			},
			// 序号
			indexFormatter1(index) {
				var newIndex;
				newIndex = this.pagesize * (this.currentPage - 1) + 1 + index;
				return newIndex;
			},
			toast(msg, type) {
				this.$message({
		            type: type,
		            message: msg
		        });
			},
			// 下载
			downloadExcelFun() {
				let sendDownData = {
				    sessionid: this.userperadmin.sessionid,
					minDate: this.tableInfo1.rangeDate.minDate,
					maxDate: this.tableInfo1.rangeDate.maxDate,
					appId: this.tableInfo1.selected,
					platform:this.tableInfo1.androidSystem,
					app_version_name:this.tableInfo1.versions,
					page_name:this.tableInfo1.page
				}
				console.log(sendDownData)
				let _this = this;
				exportErrorPageReg(sendDownData).then(res=>{
					console.log(res.data)
					  const content = res.data;
			          const blob = new Blob([content])
			          const fileName = '返回分析报表.xls'
			          if ('download' in document.createElement('a')) { // 非IE下载
			            const elink = document.createElement('a')
			            elink.download = fileName
			            elink.style.display = 'none'
			            elink.href = URL.createObjectURL(blob)
			            document.body.appendChild(elink)
			            elink.click()
			            URL.revokeObjectURL(elink.href) // 释放URL 对象
			            document.body.removeChild(elink)
			          } else { // IE10+下载
			            navigator.msSaveBlob(blob, fileName)
			          }
				})
			},
		}
	}
</script>
<style scoped>
.page{
	text-align: left;
}
.table-wrapper{
	margin-bottom: 25px;
	border: 1px solid #DEE5E7;
	box-shadow: 0 0 6px 0 #DCDCDC;
}
.table-header{
	padding: 10px 15px;
	font-size: 16px;
	border-bottom: 1px solid #DEE5E7;
	background: #E5E9F2;
	position: relative;
}
.table-action{
	padding: 10px;
	font-size: 12px;
	box-sizing: border-box;
}
.toolbar{
	overflow: hidden;
	margin-bottom: 15px;
}
.table-header>.title{
	font-weight: 700;
}
.table-header>.btn{
	float: right;
	cursor: pointer;
}
.table-header>.btn:hover{
	color: #409EFF;
}
@media only screen and (min-width: 1200px){
	.marginR{
		margin-left: 20px;
	}
}
</style>
<style>
.userAction .el-table--small td,.el-table--small th{
	padding: 4px 0;
}
.userAction .el-table .cell,.el-table--border td:first-child .cell,.el-table th div,.el-table--border th:first-child .cell{
	padding: 0 4px;
}
.userAction .el-checkbox__label{
	padding-left: 5px;
}
.userAction .el-checkbox+.el-checkbox{
	margin-left: 10px;
}
.userAction .el-date-editor.el-input{
	width: 120px;
}
.userAction .el-select .el-input--suffix .el-input__inner{
	padding-right: 6px;
	padding-left: 6px;
}
.userAction .el-input--suffix .el-input__inner{
	padding-right: 6px;
}
.userAction .el-select>.el-input{
	width: 80px;
}
.marginLeft{margin-left: 10px;}
</style>



