<template>
	<div>
		<div  v-loading="Statrloading" element-loading-text="拼命加载中...">
	         <el-row :span="24">
		        <!-- 公司选择 -->
		        <el-col :span="3">
			         <el-select v-model="company" filterable clearable allow-create default-first-option size="small" placeholder="公司名称" class='templeMaring' @focus="focuschooseCompany">
				          <el-option
				            v-for="(item,index) in chooseCompanyArry"
				            :key="index"
				            :label="item.company"
				            :value="item.company">
				          </el-option>
			         </el-select>
		        </el-col>
		        <!-- 合作方式 -->
		        <el-col :span="3" :offset='1'>
			         <el-select v-model="cooperationType" filterable clearable allow-create default-first-option size="small" placeholder="合作方式" class='templeMaring' @focus="focuswaysOfCooperation">
				          <el-option
				            v-for="(item,index) in waysOfCooperationArry"
				            :key="index"
				            :label="item.name"
				            :value="item.id">
				          </el-option>
			         </el-select>
		        </el-col>
		        <el-col :span="4" :offset='1'>
		        	<el-input v-model="contentOfCooperation" clearable size="small" placeholder="合作内容"></el-input>
		        </el-col>
		        <el-col :span="4" :offset='1'>
		        	<el-button type="success" icon="el-icon-plus" size="small" @click='addComponyInfo'></el-button>
		        	<el-button type="primary" icon="el-icon-search" size="small" style="margin-left: 1rem;" @click="searchInfo"></el-button>
		        </el-col>
		    </el-row>
		</div>
		<!-- table表格 -->
		<div class="table">
			<el-table :data="fbsData" style="width: 90%" :height='fbsData&&fbsData.length>0?480:120' border size="small"  v-loading="tableLoding" element-loading-text="拼命加载中..." :highlight-current-row='true'>
	                <el-table-column type="index" label="序号" ></el-table-column>
	                <el-table-column prop="company" label="公司" ></el-table-column>
	                <el-table-column prop="contacts" label="联系人" ></el-table-column>
	                <el-table-column prop="mobileno" label="联系方式"  ></el-table-column>
	                <el-table-column prop="name" label="合作方式"></el-table-column>
	                <el-table-column prop="cooperationContent" label="合作内容"></el-table-column>
	                <el-table-column label="操作">
	                    <template slot-scope="scope">
	                        <el-button  type="text" size="small" @click='reviseComponyInfo(scope.row,scope.$index)'><i class="fa fa-wrench"></i>修改</el-button>
	                        <el-button  type="text" size="small" @click="deleteRow(scope.row,scope.$index)" v-show='deleteStatus'><i class="el-icon-delete"></i>删除</el-button>
	                    </template>
	                </el-table-column>
	        </el-table>
		</div>
		<!-- 添加修改模态框 -->
		<div class="model">
			<el-dialog :title="modelTitle" :visible.sync="reviseVisible" width="60%" :before-close="closeDialog">
	            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" :status-icon='true' label-width="120px" class="demo-ruleForm modelStyle" >
	              <el-row :span="24">
	              	  <el-col :span="10">
	              	  	  <el-form-item label="公司名称:" prop="company">
						    <el-input v-model="ruleForm.company" clearable size="small" :maxlength='20'></el-input>
						  </el-form-item>  
	              	  </el-col>
	              	  <el-col :span="10">
	              	  	 <el-form-item label="我方公司名称:" prop="ourCompanyId">
						     <el-select v-model="ruleForm.ourCompanyId" size="small" placeholder="请选择公司" style='width: 15rem;' @change="chooseMycomony(ruleForm.ourCompanyId)">
				                 <el-option
				                    v-for="item in myComponyName"
				                    :key="item.value"
				                    :label="item.name"
				                    :value="item.id"
				                    >
				                </el-option>
				             </el-select>
						 </el-form-item>  
	              	  </el-col>
	              </el-row>
	              <el-row :span="24">
	              	  <el-col :span="10">
	              	  	  <el-form-item label="公司简称:" prop="abbreviation">
						    <el-input v-model="ruleForm.abbreviation" clearable size="small" :maxlength='20'></el-input>
						  </el-form-item>  
	              	  </el-col>
	              	  <el-col :span="10">
	              	  	  <el-form-item label="银行账号:" prop="myComponyBankNum">
						    <el-input v-model="ruleForm.myComponyBankNum" size="small" :disabled="true"></el-input>
						  </el-form-item>  
	              	  </el-col>
	              	  <el-col :span="10">
	              	  	 <el-form-item label="银行账号:" prop="bankAccount">
						    <el-input v-model="ruleForm.bankAccount" clearable size="small" :maxlength='20'></el-input>
						  </el-form-item>  
	              	  </el-col>
	              	  <el-col :span="10">
	              	  	  <el-form-item label="开户银行:" prop="myComponyOpenBank">
						    <el-input type="textarea" :rows="2" v-model="ruleForm.myComponyOpenBank" size="small" :disabled="true"></el-input>
						  </el-form-item>  
	              	  </el-col>
	              </el-row>
	              <el-row :span="24">
	              	  <el-col :span="10">
	              	  	 <el-form-item label="开户银行:" prop="bank">
						    <el-input v-model="ruleForm.bank" clearable size="small" :maxlength='20'></el-input>
						  </el-form-item>  
	              	  </el-col>
	              	  <el-col :span="10">
	              	  	  <el-form-item label="税号:" prop="myComponydutyNum">
						    <el-input v-model="ruleForm.myComponydutyNum" size="small" :disabled="true"></el-input>
						  </el-form-item>  
	              	  </el-col>
	              	  <el-col :span="10">
	              	  	 <el-form-item label="税号:" prop="dutyParagraph">
						    <el-input v-model="ruleForm.dutyParagraph" clearable size="small"></el-input>
						  </el-form-item>  
	              	  </el-col>
	              	  <el-col :span="10">
	              	  	  <el-form-item label="公司电话:" prop="myComponyTel">
						    <el-input v-model="ruleForm.myComponyTel"  size="small" :disabled="true"></el-input>
						  </el-form-item>  
	              	  </el-col>
	              	  <el-col :span="10">
	              	  	 <el-form-item label="税收编码:" prop="taxcode">
						    <el-input v-model="ruleForm.taxcode" clearable size="small"></el-input>
						  </el-form-item>  
	              	  </el-col>
	              	  <el-col :span="10">
	              	  	  <el-form-item label="公司地址:" prop="myComponyAddress">
						    <el-input type="textarea" :rows="2"  v-model="ruleForm.myComponyAddress" size="small" :disabled="true"></el-input>
						  </el-form-item>  
	              	  </el-col>
	              	  <el-col :span="10">
	              	  	 <el-form-item label="公司电话:" prop="phone">
						    <el-input v-model="ruleForm.phone" clearable size="small"></el-input>
						  </el-form-item>  
	              	  </el-col>
	              	  <el-col :span="10">
	              	  	  <el-form-item label="发票类型:" prop="myComponyInvoiceType">
						    <el-input v-model="ruleForm.myComponyInvoiceType" size="small" :disabled="true"></el-input>
						  </el-form-item>  
	              	  </el-col>
	              	  <el-col :span="10">
	              	  	 <el-form-item label="公司地址:" prop="address">
						    <el-input type="textarea" :rows="2" v-model="ruleForm.address" clearable size="small"></el-input>
						  </el-form-item>  
	              	  </el-col>
	              	  <el-col :span="10">
	              	  	  <el-form-item label="发票内容:" prop="invoiceContentId">
						    <el-select v-model="ruleForm.invoiceContentId" clearable size="small" placeholder="发票内容" style='width: 11rem;'>
				                 <el-option
				                    v-for="item in InvoiceContentOptions"
				                    :key="item.id"
				                    :label="item.name"
				                    :value="item.id">
				                </el-option>
				             </el-select>
						  </el-form-item>  
	              	  </el-col> 
	              </el-row>
	              <el-row :span="24">
	              	  <el-col :span="10">
	              	  	 <el-form-item label="联系人:" prop="contacts">
						    <el-input v-model="ruleForm.contacts" clearable size="small"></el-input>
						  </el-form-item>  
	              	  </el-col>
	              </el-row>
	               <el-row :span="24">
	              	  <el-col :span="10">
	              	  	 <el-form-item label="联系人电话:" prop="mobileno">
						    <el-input v-model="ruleForm.mobileno" clearable size="small"></el-input>
						  </el-form-item>  
	              	  </el-col>
	              </el-row>
	               <el-row :span="24">
	              	  <el-col :span="10">
	              	  	 <el-form-item label="联系人邮箱:" prop="email">
						    <el-input type="textarea" :rows="2" v-model="ruleForm.email"  clearable size="small"></el-input>
						  </el-form-item>  
	              	  </el-col>
	              </el-row>
	              <el-row :span="24">
	              	  <el-col :span="10">
	              	  	 <el-form-item label="账号初始密码:" prop="pwd">
						    <el-input v-model="ruleForm.pwd" clearable size="small"></el-input>
						  </el-form-item>  
	              	  </el-col>
	              </el-row>
	              <el-row :span="24">
	              	  <el-col :span="10">
	              	  	 <el-form-item label="合作内容:" prop="cooperationContent">
						    <el-input v-model="ruleForm.cooperationContent" clearable size="small"></el-input>
						  </el-form-item>  
	              	  </el-col>
	              </el-row>
	              <el-row :span="24">
	              	  <el-col :span="10">
	              	  	 <el-form-item label="合作方式:" prop="cooperationType">
						    <el-select v-model="ruleForm.cooperationType" clearable size="small" placeholder="合作方式" style='width: 11rem;'>
				                 <el-option
				                    v-for="item in selectways"
				                    :key="item.id"
				                    :label="item.name"
				                    :value="item.id">
				                </el-option>
				             </el-select>
						  </el-form-item>  
	              	  </el-col>
	              </el-row>
				</el-form>
		        <span slot="footer">
		          <el-button type="primary" size="small" @click="submitForm('ruleForm')">提交</el-button>
		        </span>
	        </el-dialog>
		</div>
	</div>
</template>
<script>
    import {partnerManagerReg,getOurCompanyReg,addOrUpdatePartnerReg,deletePartnerReg} from "../../api/api.js"
	export default {
		data() {
			return {
			   userloginInfo:null,//用户登录信息
			   deleteStatus:false,//删除按钮权限
			   Statrloading:false,//获取搜索信息状态加载动画
			   //选择输入公司
               company:'',//选择公司的值
               chooseCompanyArry:[],//选择公司获取的所有公司信息
               //选择输入合作方式
               cooperationType:'',//合作方式的值
               waysOfCooperationArry:[{name:'供应商',id:1},{name:'客户',id:2}],//选择合作方式获取的所有信息
               //合作内容
               contentOfCooperation:'',//输入的合作内容的值
               //table表格
               tableLoding:false,//加载表格动画状态
               fbsData:[],//获取表格显示数据
               //添加修改模态框
               reviseVisible:false,//模态框状态
               infoStatus:null,//添加0 修改1
               modelTitle:'',//模态框标题
               ruleForm: { 
		          company: '', //公司名称
		          ourCompanyId:'',//我方公司id     三个默认的公司供选择
		          abbreviation:'',//公司简称
		          bankAccount:'', //银行账号
		          myComponyBankNum:'',
		          bank:'',  //开户行
		          myComponyOpenBank:'',
		          dutyParagraph:'', //税号
		          myComponydutyNum:'',
		          taxcode:'', //税收编码
		          myComponyTel:'',
		          phone:'',//公司电话
		          myComponyAddress:'',
		          address:'', //公司地址
                  myComponyInvoiceType:'',
                  contacts:'',//联系人
                  invoiceContentId:'',//发票内容值
                  mobileno:'',//联系人电话
                  email:'', //联系人邮箱
                  pwd:'',//初始化密码
                  cooperationContent:'',//合作内容
                  cooperationType:''//合作方式    1  表示  供应商   2表示  客户
		        },
		        InvoiceContentOptions:[],// 发票内容
                rules:{
                	company: [
			            { required: true, message: '公司名称为必填项', trigger: 'change' },
			          ],
			        bankAccount: [
		                { required: true, message: '银行账号为必填项', trigger: 'change' }
		            ],
		            abbreviation:[
		                { required: true, message: '公司简称为必填项', trigger: 'change' }
		            ],
		            ourCompanyId:[
		                { required: true, message: '我方公司名称为必选项', trigger: 'change' }
		            ],
		            bank: [
		                { required: true, message: '开户行为必填项', trigger: 'change' }
		            ],
		            dutyParagraph: [
		                 { required: true, message: '税号为必填项', trigger: 'change' }
		            ],
		            taxcode: [
		                 { required: true, message: '税收编码为必填项', trigger: 'change' }
		            ],
		            phone: [
		                 { required: true, message: '公司电话为必填项', trigger: 'change' }
		            ],
		            address: [
		                 { required: true, message: '公司地址为必填项', trigger: 'change' }
		            ],
		            contacts: [
		                 { required: true, message: '联系人为必填项', trigger: 'change' }
		            ],
		            mobileno: [
		                 { required: true, message: '联系人电话为必填项', trigger: 'change' }
		            ],
		            email: [
		                 { required: true, message: '联系人邮箱为必填项', trigger: 'change' },
		                 { type:'email', message: '联系人邮箱不合法', trigger: 'change' }
		            ],
		            pwd: [
		                 { required: true, message: '账号初始密码为必填项', trigger: 'change' }
		            ],
		            cooperationContent: [
		                 { required: true, message: '合作内容为必填项', trigger: 'change' }
		            ],
		            cooperationType: [
		                 { required: true, message: '合作方式为必选项', trigger: 'change' }
		            ],
		            invoiceContentId:[
		                 { required: true, message: '发票内容为必选项', trigger: 'change' }
		            ]
                },
                selectways:[{name:'供应商',id:'48'},{name:'客户',id:'49'}],//合作方式选择
                myComponyName:[{name:'上海麦广互娱文化传媒股份有限公司',id:'1'},{name:'上海麦广网络科技有限公司',id:'2'},{name:'霍尔果斯财马科技',id:'3'}]//我方公司名称
			}
		},
		mounted() {
			try {
				 // 获取对操作是否限权
				 let userPermission = JSON.parse(unescape(sessionStorage.getItem('userPermiss')));
	             console.log(userPermission);
	             let _this = this;
	             // 对渠道商管理中操作限权进行循环读取状态
	             for (var i = 0; i <userPermission.data.length; i++) {
	             	if (userPermission.data[i].meta == '06') {
	             		for (var j = 0; j <userPermission.data[i].children.length; j++) {
	             			if (userPermission.data[i].children[j].meta == '0601') {
	             				for (var m = 0; m < userPermission.data[i].children[j].children.length; m++) {
	             					console.log(userPermission.data[i].children[j].children[m].meta)
	             					if (userPermission.data[i].children[j].children[m].meta == '060101') {
	             						// 添加功能组权限
	             						_this.deleteStatus = userPermission.data[i].children[j].children[m].show == '0'?false:true;
	             					} 
	             				}
	             				
	             			}
	             		}
	             	}
               }
			} catch(e) {
				// statements
				console.log(e);
			}
			this.userloginInfo = JSON.parse(localStorage.getItem("userperadmin"));
			// 获取搜索内容信息
			this.getSelections()
		},
		methods:{
			// 获取搜索内容信息
			getSelections() {
				let sendSessionid = {sessionid:this.userloginInfo.sessionid};
				this.Statrloading = true;
				console.log('发送参数获取搜索条件')
				console.log(sendSessionid)
				let _this = this;
                partnerManagerReg(sendSessionid).then(res=>{
                	console.log("获取到的数据")
                	console.log(res.data)
                	if (res.data.status === 0) {
                		_this.chooseCompanyArry = res.data.reportforms_admin;
                		_this.Statrloading = false;
                		_this.searchInfo();
                	};
                	if (res.data.status === 1) {
                       _this.$router.replace("/login");
                	};
                })
			},
			// 选择公司获取焦点事件
			focuschooseCompany() {
				this.company = '';
			},
			focuswaysOfCooperation() {
				this.cooperationType = '';
			},
			// 添加点击事件
			addComponyInfo() {
				this.addDialog();
				this.infoStatus = 0;
				this.ruleForm = { 
		          company: '', //公司名称
		          ourCompanyId:'',//我方公司id     三个默认的公司供选择
		          bankAccount:'', //银行账号
		          abbreviation:'',//公司简称
		          myComponyBankNum:'',
		          bank:'',  //开户行
		          myComponyOpenBank:'',
		          dutyParagraph:'', //税号
		          myComponydutyNum:'',
		          taxcode:'', //税收编码
		          myComponyTel:'',
		          phone:'',//公司电话
		          myComponyAddress:'',
		          address:'', //公司地址
                  myComponyInvoiceType:'',
                  contacts:'',//联系人
                  invoiceContentId:'',//发票内容值
                  mobileno:'',//联系人电话
                  email:'', //联系人邮箱
                  pwd:'',//初始化密码
                  cooperationContent:'',//合作内容
                  cooperationType:''//合作方式    1  表示  供应商   2表示  客户
		        },
				this.modelTitle = '新增合作方账户信息'
				this.reviseVisible = true;
			},
			// 搜索信息
			searchInfo() {
				let sendSearchInfo = {
					sessionid:this.userloginInfo.sessionid,
					company: this.company,
					cooperationType:this.cooperationType,
					cooperationContent: this.contentOfCooperation
				};
				this.tableLoding = true;
				console.log(sendSearchInfo)
				let _this = this;
				partnerManagerReg(sendSearchInfo).then(res=>{
					console.log('返回数据')
					console.log(res.data)
					if (res.data.status === 0) {
                		_this.fbsData = res.data.reportforms_admin;
                		_this.InvoiceContentOptions = res.data.invoiceContentList;
                		_this.tableLoding = false;
                	};
                	if (res.data.status === 1) {
                       _this.$router.replace("/login");
                	};
				})

			},
			// 点击添加清除存在的验证
	        addDialog() {
	        	let _this = this;
				this.$nextTick(()=>{
					try {
						_this.$refs.ruleForm.resetFields();
					} catch(e) {
						console.log(e)
					}
				});
	        },
			//增加取消初始化验证
			closeDialog(done){
				let _this = this;
				this.$nextTick(()=>{
					_this.$refs.ruleForm.resetFields();
					document.getElementsByClassName('modelStyle')[0].scrollTop = 0;
				    done();
				})
			},
			// 选择我方公司
			chooseMycomony(res) {
				console.log(res)
				let sendMyCompony = {
					sessionid:this.userloginInfo.sessionid,
					ourCompanyId: res
				};
				console.log(sendMyCompony)
				let _this = this;
				getOurCompanyReg(sendMyCompony).then(res=>{
                   console.log(res.data)
                   if (res.data.status === 0) {
                   	   _this.ruleForm.myComponyBankNum = res.data.reportforms_admin[0].accountNo;
                   	   _this.ruleForm.myComponyOpenBank = res.data.reportforms_admin[0].bank;
                   	   _this.ruleForm.myComponydutyNum = res.data.reportforms_admin[0].taxpayerNo;
                   	   _this.ruleForm.myComponyTel = res.data.reportforms_admin[0].phone;
                   	   _this.ruleForm.myComponyAddress = res.data.reportforms_admin[0].address;
                   	   _this.ruleForm.myComponyInvoiceType = res.data.reportforms_admin[0].name;
                   	   _this.InvoiceContentOptions = res.data.appList;
                   	   console.log(_this.ruleForm)
                   };
                   if (res.data.status === 1) {
                      _this.$router.replace("/login");
                   };
				}).catch(res=>{

				})
			},
			// 点击修改事件
			reviseComponyInfo(row,index) {
				console.log(row)
			   this.infoStatus = 1;
			   this.ruleForm = {
			   	  id: row.id,//修改行的id
		          company: row.company, //公司名称
		          ourCompanyId:row.ourCompanyId,//我方公司id     三个默认的公司供选择
		          abbreviation:row.abbreviation,//公司简称
		          bankAccount:row.bankAccount, //银行账号
		          myComponyBankNum:row.ourAccountNo,
		          bank:row.bank,  //开户行
		          myComponyOpenBank:row.ourBank,
		          dutyParagraph:row.dutyParagraph, //税号
		          myComponydutyNum:row.taxpayerNo,
		          taxcode:row.taxcode, //税收编码
		          myComponyTel:row.ourPhone,
		          phone:row.phone,//公司电话
		          myComponyAddress:row.ourAddress,
		          address:row.address, //公司地址
                  myComponyInvoiceType:row.ourInvoiceType,
                  contacts:row.contacts,//联系人
                  invoiceContentId:parseInt(row.invoiceContentId),//发票内容值
                  mobileno:row.mobileno,//联系人电话
                  email:row.email, //联系人邮箱
                  pwd:row.pwd,//初始化密码
                  cooperationContent:row.cooperationContent,//合作内容
                  cooperationType:row.cooperationType//合作方式    1  表示  供应商   2表示  客户
		        },
               this.modelTitle = '修改合作方账户信息'
			   this.reviseVisible = true;
			},
			// 点击删除
			deleteRow(row,index) {
               let _this = this;
               let showTitle = '是否删除 '+ row.company +' (公司名称),删除后不可恢复,是否要删除?'
		       this.$confirm(showTitle, '提示', {
		          //type: 'warning'
		       }).then(() => {
		           console.log('确认')
		           let deteleInfo = {
		           	 sessionid:this.userloginInfo.sessionid,
		           	 ruleForm:{
		           	 	id: row.id,
		           	 	company: row.company,
			           	contacts: row.contacts,
			           	mobileno: row.mobileno,
			           	cooperationType: row.cooperationType,
			           	cooperationContent: row.cooperationContent
		           	 }
		           }
		           console.log(deteleInfo)
		           let _this = this;
		           deletePartnerReg(deteleInfo).then(res=>{
		           	  console.log(res.data)
		           	  if (res.data.status === 0) {
		           	  	_this.$message({
					          message: '删除数据成功',
					          type: 'success'
					    });
					    _this.searchInfo();
		           	  };
		           	  if (res.data.status === 1) {
		           	  	 _this.$router.replace("/login");
		           	  };
		           })
		        }).catch(() => {
		        	console.log('取消')
		        })
			},
			// 保存事件
			 submitForm(formName) {
		        this.$refs[formName].validate((valid) => {
		          if (valid) {
		            console.log(this.ruleForm)
		            let saveSendForm = {
		            	sessionid:this.userloginInfo.sessionid,
		            	infoStatus:this.infoStatus,
		            	ruleForm: this.ruleForm
		            };
		            console.log(saveSendForm)
		            let _this = this;
		            addOrUpdatePartnerReg(saveSendForm).then(res=>{
		            	console.log(res.data)
		            	if (res.data.status === 0) {
		            		_this.$message({
					          message: '提交保存成功',
					          type: 'success'
					        });
					        document.getElementsByClassName('modelStyle')[0].scrollTop = 0;
					        _this.reviseVisible = false;
					        _this.searchInfo();
		            	};
		            	if (res.data.status === 1) {
		            	   _this.$router.replace("/login");
		            	};
		            })
		          } else {
		            console.log('error submit!!');
		            console.log(this.infoStatus);
		            return false;
		          }
		        });
		     }

		}
	}
</script>
<style lang="scss" scoped>
   .table{margin-top: 1rem;}
   .modelStyle{
   	  height: 16rem;
   	  overflow-y: auto;
   }
</style>


