// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
// import ElementUI from 'element-ui'
import '../src/common/rem.js'
import store from './store'
import 'font-awesome/css/font-awesome.css'
// import XLSX from 'xlsx';
// import FileSaver from 'file-saver'
import XLSX from 'xlsx'
// 引入 ECharts 主模块
var echarts = require('echarts/lib/echarts');
// 引入柱状图
require('echarts/lib/chart/bar');
require("echarts/lib/chart/line");
// 引入提示框和标题组件
require('echarts/lib/component/tooltip');
require('echarts/lib/component/toolbox');
require('echarts/lib/component/title');
require('echarts/lib/component/legend')
Vue.prototype.$echarts = echarts
import {Button,Form,FormItem,Input,Checkbox,Row,Col,Dropdown,DropdownMenu,DropdownItem,Menu,Submenu,MenuItem,
        DatePicker,Select,tabs,TabPane,table,TableColumn,RadioButton,RadioGroup,CheckboxGroup,Dialog,Loading,
        option,pagination,radio,Switch,Message,MessageBox,collapse,CollapseItem,Transfer,TimePicker
} from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
Vue.use(Button);
Vue.use(Form);
Vue.use(FormItem);
Vue.use(Input);
Vue.use(Checkbox);
Vue.use(Row);
Vue.use(Col);
Vue.use(Dropdown);
Vue.use(DropdownMenu);
Vue.use(DropdownItem);
Vue.use(Menu);
Vue.use(Submenu);
Vue.use(MenuItem);
Vue.use(DatePicker);
Vue.use(Select);
Vue.use(tabs);
Vue.use(TabPane);
Vue.use(table);
Vue.use(TableColumn);
Vue.use(RadioButton);
Vue.use(RadioGroup);
Vue.use(CheckboxGroup);
Vue.use(Dialog);
Vue.use(Loading.directive);
Vue.use(option);
Vue.use(pagination);
Vue.use(radio);
Vue.use(Switch);
Vue.use(collapse);
Vue.use(CollapseItem);
Vue.use(Transfer);
Vue.use(TimePicker);
Vue.prototype.$confirm = MessageBox.confirm
Vue.prototype.$message = Message
Vue.prototype.$loading = Loading.service
Vue.config.productionTip = false
router.beforeEach((to, from, next) => {
  console.log('beforeEach');
  if (to.path == '/login') {
    sessionStorage.removeItem('loginStatus');
  }
  let userstatus = JSON.parse(sessionStorage.getItem('loginStatus'));
  if (!userstatus && to.path != '/login') {
    next({ path: '/login' })
  } else {
    next()
  }
})
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})
// 时间戳转换时间公共函数
Vue.prototype.formatDate = function(now) {
   var now = new Date(now)
   var year=now.getFullYear(); 
   var month=now.getMonth()+1; 
   var date=now.getDate(); 
   var hour=now.getHours(); 
   var minute=now.getMinutes(); 
   var second=now.getSeconds(); 
   return year+"-"+month+"-"+date+" "+hour+":"+minute+":"+(second>=10?second:'0'+second); 
}
// 标准时间转时间戳
Vue.prototype.dataToMs = function(data) {
   let result = new Date(data).getTime();
   return result;
}
