/**
 * 
 */
export var dateFormatter = function(date, time) {
    if(date === undefined || date === '' || typeof date == 'string'){
        return '';
    }
	var y = date.getFullYear();  
    var m = date.getMonth() + 1;  
    m = m < 10 ? '0' + m : m;  
    var d = date.getDate();  
    d = d < 10 ? ('0' + d) : d;
    if (time !== undefined) {
        var hours = date.getHours();
        hours = hours < 10 ? ('0' + hours) : hours;
        var minute = date.getMinutes();
        minute = minute < 10 ? ('0' + minute) : minute;
        var second = date.getSeconds();
        second = second < 10 ? ('0' + second) : second;
        return y + '-' + m + '-' + d + ' ' + hours + ':' + minute + ':' + second; 
    }
    return y + '-' + m + '-' + d; 
}

/**
 * 比较两个日期的大小
 */
export var dateCompare = function(date1, date2) {
	var date1 = new Date(date1); 
	var date2 = new Date(date2); 
    if (date1 > date2) {
    	return true;
    } else {
    	return false;
    }
}
/**
 * 时间戳转YYYY-MM-DD
 */
export var timeStempToDate = function(stemp) {
	var date = new Date(stemp);
	var mydate = dateFormatter(date);
	return mydate;
}
/**
 * 时间戳转YYYY-MM-DD HH:MM:SS
 */
export var timeStempToDatetime = function(stemp) {
    var date = new Date(stemp);
    var mydate = dateFormatter(date, 1);
    return mydate;
}

/**
 * 获取昨天日期
 */ 
export var yesterDay = function(data) {
    let arrResultTime = [];
    let day1 = new Date();
    day1.setTime(day1.getTime()-24*60*60*1000);
    let  year = day1.getFullYear();
    let month = (day1.getMonth()+1)>=10?(day1.getMonth()+1):'0'+(day1.getMonth()+1);
    let date = day1.getDate()>=10?day1.getDate():'0'+day1.getDate();
    let resultTime = year+"-" + month + "-" + date;
    arrResultTime = [resultTime,resultTime]
    return arrResultTime;
}
/**
 * 获取今天日期
 */ 
export var toDay = function(data) {
    let arrResultTime = [];
    let day1 = new Date();
    day1.setTime(day1.getTime());
    let  year = day1.getFullYear();
    let month = (day1.getMonth()+1)>=10?(day1.getMonth()+1):'0'+(day1.getMonth()+1);
    let date = day1.getDate()>=10?day1.getDate():'0'+day1.getDate();
    let resultTime = year+"-" + month + "-" + date;
    arrResultTime = [resultTime,resultTime]
    return arrResultTime;
}
/**
 * 获取本周日期
 */ 
 export var thisWeek = function() {
     let arrResultTime = [];
     let day1 = new Date();
     day1.setTime(day1.getTime());
     let  year1 = day1.getFullYear();
     let month1 = (day1.getMonth()+1)>=10?(day1.getMonth()+1):'0'+(day1.getMonth()+1);
     let date1 = day1.getDate()>=10?day1.getDate():'0'+day1.getDate();
     let resultTime1 = year1+"-" + month1 + "-" + date1;
     let day2=new Date();
     let myddy=day2.getDay();//获取存储当前日期
     let newDay = myddy;//用于判断日期如果是周日就把0换成7
     if (myddy==0) {
        newDay = 7;
     };
    day2.setTime(day2.getTime()-(newDay-1)*24*60*60*1000);
    let  year = day2.getFullYear();
    let month = (day2.getMonth()+1)>=10?(day2.getMonth()+1):'0'+(day2.getMonth()+1);
    let date = day2.getDate()>=10?day2.getDate():'0'+day2.getDate();
    let resultTime2 = year+"-" + month + "-" + date;
    arrResultTime = [resultTime2,resultTime1];
    return arrResultTime;
 }
 /**
 * 获取本月日期
 */ 
export var thisMonth = function() {
     let aarrResultTime = [];
     let day1 = new Date();
     day1.setTime(day1.getTime());
     let  year1 = day1.getFullYear();
     let month1 = (day1.getMonth()+1)>=10?(day1.getMonth()+1):'0'+(day1.getMonth()+1);
     let date1 = day1.getDate()>=10?day1.getDate():'0'+day1.getDate();
     let resultTime1 = year1+"-" + month1 + "-" + date1;
     let day2=new Date();
     let  year = day2.getFullYear();
     let myddy=day2.getMonth()+1>=10?(day2.getMonth()+1):'0'+(day2.getMonth()+1);//获取存储当前月
     let resultTime2 = year+'-'+myddy+'-'+'01';
     aarrResultTime = [resultTime2,resultTime1];
     return aarrResultTime;
 }
 /**
 * 获取上个月日期
 */ 
 export var lastMonth = function() {
    var nowdays = new Date();
    var year = nowdays.getFullYear();
    var month = nowdays.getMonth();
    if(month==0)
    {
        month=12;
        year=year-1;
    }
    if (month < 10) {
        month = "0" + month;
    }
    var firstDay = year + "-" + month + "-" + "01";//上个月的第一天
    var myDate = new Date(year, month, 0);
    var lastDay = year + "-" + month + "-" + myDate.getDate();//上个月的最后一天
    let resultTime = [firstDay,lastDay];
    return resultTime;
 }
  /**
 * 获取当前月
 */ 
 export var newMonth = function() {
    var resultTime;
    var nowdays = new Date();
    var year = nowdays.getFullYear();
    var month = (nowdays.getMonth()+1)>=10?(nowdays.getMonth()+1):'0'+(nowdays.getMonth()+1);
    resultTime = year+'-'+month;
    return resultTime;
 }

  /**
 * 转换月
 */ 
 export var getMonth = function(date) {
    var resultTime;
    var nowdays = new Date(date)
    var year = nowdays.getFullYear();
    var month = (nowdays.getMonth()+1)>=10?(nowdays.getMonth()+1):'0'+(nowdays.getMonth()+1);
    resultTime = year+'-'+month;
    return resultTime;
 }

