<template>
	<div class="groupBox">
		<div class="clickBtn">
			 <el-button type="primary" class="btnStyleone" size="small" @click="addGroup" v-show="addGroupUser">添加功能组</el-button>
			 <el-button type="primary" class="btnStyletow" size="small" @click="addFun" v-show="addFunctionUser">添加功能</el-button>
		</div>
		<div class="tablesys">
            <el-table :data="fbsData"  style="width: 80%"  border  size="mini" v-loading="tableloading" element-loading-text="拼命加载中..." >
                 <el-table-column type="index" label="排序"></el-table-column>
                 <!-- <el-table-column prop="id" label="ID"></el-table-column> -->
                 <el-table-column  label="创建时间"  >
                 	<template slot-scope="scope">
                         <span>{{scope.row.updateTime | formatDate}}</span>
                    </template>
                 </el-table-column>
                 <el-table-column prop="adminName" label="创建人"  ></el-table-column>
                 <el-table-column prop="name" label="组名"  ></el-table-column>
                 <el-table-column  label="管理"  >
                 	<template slot-scope="scope">
                         <el-button @click="useManage(scope.row,scope.$index)" type="text" size="small" v-show="lookFunSet"><i class="fa fa-align-justify"></i></el-button>
                    </template>
                 </el-table-column>
            </el-table>
		</div>
		<!-- modelgroup -->
	      <el-dialog title="添加一个功能组" :visible.sync="reviseVisible" width="50%">
	        <el-form>
	             <el-row style="padding-bottom: 20px">
	                  <el-col :span="4">功能组名称:</el-col>
	                  <el-col :span="17"><el-input placeholder="请输入内容" v-model="funGroupName" @input="funManageName"></el-input></el-col> 
	             </el-row>
	        </el-form>
	        <span slot="footer">
	          <el-button @click="reviseVisible = false" size="small">取 消</el-button>
	          <el-button type="primary" size="small" @click="saveFunGroup">提交</el-button>
	        </span>
	    </el-dialog>
	    <!-- modelFunction -->
	      <el-dialog title="添加一个功能" :visible.sync="FunctionVisible" width="50%">
	        <el-form>
	             <el-row>
	             	<div class="showPadding">
	             		 <el-col :span="4">归属功能组:</el-col>
	                     <el-col :span="17">
	                     	<el-select v-model="addFunInfo.changeFunGroup" placeholder="选择功能组">
	                           <el-option
	                                v-for="(item,index) in options"
	                                :key="index"
	                                :label="item.label"
	                                :value="item.value">
	                            </el-option>
	                        </el-select>
	                     </el-col>
	             	</div>
	             	<div class="showPadding">
	             		<el-col :span="4">功能名称:</el-col>
	                    <el-col :span="17"><el-input placeholder="请输入名称" v-model="addFunInfo.funName" @input="funNameRep"></el-input></el-col> 
	             	</div>
	             	<div class="showPadding">
	             		<el-col :span="4">ID:</el-col>
	                    <el-col :span="17">
	                    	<el-input  :maxlength="6" placeholder="请输入唯一ID(2或4或6位)" v-model="addFunInfo.meta" @input="funIDRep"></el-input>
	                    	<span class="fontSizeSty" v-show="isFunMetaRepetSpan">功能ID名称重复</span>
	                    </el-col>
	             	</div>
	             	<div class="showPadding">
	             		<el-col :span="4">在列表显示:</el-col>
	                    <el-col :span="17">
	                    	<el-radio v-model="addFunInfo.isShow" label="1">是</el-radio>
	                    	<el-radio v-model="addFunInfo.isShow" label="0">否</el-radio>
	                    </el-col> 
	             	</div>
	             	<div>
	             		<el-col :span="4">是否限权:</el-col>
	                    <el-col :span="17">
	                    	<el-radio v-model="addFunInfo.isgetUse" label="1">是</el-radio>
	                    	<el-radio v-model="addFunInfo.isgetUse" label="0">否</el-radio>
	                    </el-input></el-col> 
	             	</div>
	             </el-row>
	        </el-form>
	        <span slot="footer">
	          <el-button @click="FunctionVisible = false" size="small">取 消</el-button>
	          <el-button type="primary" size="small" @click="saveAddFunction">提交</el-button>
	        </span>
	    </el-dialog>
	    <!-- useManagemodel -->
	     <el-dialog :title="manageTile" :visible.sync="useManageVisible" width="50%">
	            <el-table :data="setPermissData"  style="width: 100%"  border  size="mini" v-loading="tableloading" element-loading-text="拼命加载中..." >
	                 <el-table-column  type="index" label="序号"></el-table-column>
	                 <el-table-column  label="功能名称"  >
	                 	<template slot-scope="scope">
	                 		 <span v-if="clickIndex!==scope.$index"  class="clickSpanStyle">{{scope.row.name}}</span>
	                 		 <!-- <span v-if="clickIndex!==scope.$index" @click="clickBtnFunName(scope.row,scope.$index,$event)" class="clickSpanStyle">{{scope.row.name}}</span> -->
                             <!-- <el-input :value="scope.row.name" ref="Autofocus" v-model="changeInputValue" v-if="clickIndex==scope.$index"  @blur="inputBlur(scope.row,scope.$index)"></el-input> -->
	                    </template>
	                 </el-table-column>
	                 <el-table-column prop="className" label="是否显示在列表">
	                 	<template slot-scope="scope">
	                        <el-switch
	                          v-model="scope.row.allshow"
							  active-color="#13ce66"
							  inactive-color="#999999"
							  @change="clickShowList(scope.row,scope.$index)"
							   v-if="(scope.row.name !='总结中心')&&(scope.row.name !='日报')"
							   v-show="saveSetFun"
							  >
							</el-switch>
	                    </template>
	                 </el-table-column>
	                 <el-table-column prop="className" label="是否限权">
	                 	<template slot-scope="scope" >
	                         <el-switch
	                          v-model="scope.row.isuse"
							  active-color="#13ce66"
							  inactive-color="#999999"
							  @change="clickShowuse(scope.row,scope.$index)"
							  v-if="(scope.row.name !='总结中心')&&(scope.row.name !='日报')"
							  v-show="saveSetFun"
							  >
							</el-switch>
	                    </template>
	                 </el-table-column>
	                <!--  <el-table-column  label="删除"  >
	                 	<template slot-scope="scope">
	                         <el-button @click="manageDel(scope.row,scope.$index)" type="text" size="small"><i class="fa fa-trash-o"></i></el-button>
	                    </template>
	                 </el-table-column> -->
	            </el-table>
	    </el-dialog>
	</div>
</template>
<script>
    // 此模块现在在总结中心和日报的开关中进行了隐藏，默认两个都显示 使用语句v-if="(scope.row.name !='总结中心')&&(scope.row.name !='日报')"
    import {getPermissionTypeReg,addPermissionTypeReg,checkPermissionTypeReg,addPermissionReg,checkPermissionReg,getPermissionReg,checkMetaReg,updatePermissionReg} from '../../api/api.js'
	export default {
		data() {
			return {
			   sessionidInfo:null,// 用户sessionid等信息
               tableloading: false,// 表格加载动画
               fbsData: [], // 获取所有功能组表格数据
               reviseVisible: false, // 添加功能组模态框状态
               FunctionVisible: false,  // 添加功能模态框状态  
               useManageVisible: false, // 管理模态框状态   
               manageTile: '',  // 管理模态title   
               funGroupName: '', // 添加功能组名字   
               isRepetition: null,// 记录功能组重复状态0不重复1重复默认为null
               isFunRepetition: null,// 记录功能重复状态0不重复1重复默认为null
               isFunMetaRepetition: null,// 记录功能meta重复状态0不重复1重复默认为null
               isFunMetaRepetSpan:false,//记录功能meta重复span提示是否显示
               // 添加一个功能的信息
               addFunInfo: {
               	 changeFunGroup: '',
               	 funName: '',
               	 isShow:'',//0否1是
               	 isgetUse: '',//0否1是
               	 meta:''
               },
               // 添加功能时选择功能组，功能组内容
                options: [],
                setPermissData:[],//点击管理获取相应的信息
                clickIndex:null,//点击可编辑
                changeInputValue:'',
                addGroupUser: false,//添加功能组权限
                addFunctionUser: false,//添加功能权限
                lookFunSet: false,//查看功能管理权限
                saveSetFun: false//查看功能管理->修改保存权限
	        }
		},
		mounted() {
			// 获取对操作是否限权
			 let userPermission = JSON.parse(unescape(sessionStorage.getItem('userPermiss')));
             console.log(userPermission);
             let _this = this;
             // 对渠道商管理中操作限权进行循环读取状态
             for (var i = 0; i <userPermission.data.length; i++) {
             	if (userPermission.data[i].meta == '05') {
             		for (var j = 0; j <userPermission.data[i].children.length; j++) {
             			if (userPermission.data[i].children[j].meta == '0501') {
             				for (var m = 0; m < userPermission.data[i].children[j].children.length; m++) {
             					console.log(userPermission.data[i].children[j].children[m].meta)
             					if (userPermission.data[i].children[j].children[m].meta == '050101') {
             						// 添加功能组权限
             						_this.addGroupUser = userPermission.data[i].children[j].children[m].show == '0'?false:true;
             					} else if (userPermission.data[i].children[j].children[m].meta == '050102') {
             						// 获取添加功能权限值
             						_this.addFunctionUser = userPermission.data[i].children[j].children[m].show == '0'?false:true;
             					} else if (userPermission.data[i].children[j].children[m].meta == '050103') {
             						// 获取查看功能管理权限值
             						_this.lookFunSet = userPermission.data[i].children[j].children[m].show == '0'?false:true;
             					} else if (userPermission.data[i].children[j].children[m].meta == '050104') {
             						// 查看功能管理->修改保存权限
             						_this.saveSetFun = userPermission.data[i].children[j].children[m].show == '0'?false:true;
             					};
             				};
             				
             			};
             		};
             	};
             	
             };
			this.sessionidInfo = JSON.parse(localStorage.getItem('userperadmin'));
			// console.log(this.sessionidInfo)
			// 执行函数获取存在的功能组信息
			this.getAllFunGroup();
		},
		filters: {
           formatDate(value,index) {
           	// console.log(value)
           	 var now = new Date(value)
			 var year=now.getFullYear(); 
			 var month=now.getMonth()+1; 
			 var date=now.getDate(); 
			 var hour=now.getHours(); 
			 var minute=now.getMinutes(); 
			 var second=now.getSeconds(); 
			 return year+"-"+month+"-"+date+" "+hour+":"+minute+":"+(second>=10?second:'0'+second); 
           }
		},
		methods: {
			// 获取所有功能组
			getAllFunGroup() {
			   let _this = this;
			   let params = {sessionid: this.sessionidInfo.sessionid};
               getPermissionTypeReg(params).then(res=>{
                   console.log(res.data)
                   if (res.data.status === 0) {
                   	  _this.fbsData = res.data.permissionTypeList;
                   };
                   if (res.data.status === 1) {
                   	  _this.$router.replace("/login");
                   };
               })
			},
			// 添加功能组
			addGroup() {
				// 模态框数据初始化
		       this.funGroupName = '';
		       this.reviseVisible = true;
			},
			// 添加功能
			addFun() {
				// 模态框数据初始化
               this.addFunInfo.changeFunGroup = '';
               this.addFunInfo.funName = '';
               // 默认选择是
               this.addFunInfo.isShow = '1';
               this.addFunInfo.isgetUse = '1';
               this.addFunInfo.meta = '';
               this.options = [];
               for (var i = 0; i < this.fbsData.length; i++) {
               	   this.options.push({value:this.fbsData[i].id,label:this.fbsData[i].name})
               };
               this.FunctionVisible = true;
			},
			// 验证功能组名字是否重复
			funManageName() {
				let addGroupInfo = {
					sessionid: this.sessionidInfo.sessionid,
					name: this.funGroupName
				};
				var that = this;
				if (that.funGroupName) {
					checkPermissionTypeReg(addGroupInfo).then(res=>{
						// console.log(res.data)
						if (res.data.status === 0) {
							that.isRepetition = 0;
						} else if (res.data.status === 1) {
							that.$router.replace("/login");
						}else if (res.data.status === 2) {
							that.$message.error('功能组名称不能重复');
							that.isRepetition = 1;
							return;
						} else {
							that.isRepetition = null;
						}
					})
				};
			},
			// 保存添加的功能组
			saveFunGroup() {
				if (!this.funGroupName) {
					  this.$message.error('添加功能组名称不能为空');
					  return;	
				};
				// 为防止验证名字重复时候接口报错，点击提交时候再执行一次请求是否名字重复
				if (this.funGroupName == null) {
					this.funManageName();
				}
				let addGroupInfo = {
						sessionid: this.sessionidInfo.sessionid,
						name: this.funGroupName
					};
				var that = this;
			    addPermissionTypeReg(addGroupInfo).then(res=>{
				  console.log(res.data)
				  if (res.data.status === 0) {
					  that.$message({
				          message: '添加功能组成功',
				          type: 'success'
				        });
					 that.reviseVisible = false;
					 that.getAllFunGroup();
				  };
				  if (res.data.status === 1) {
					 that.$router.replace("/login");
				  }
			    });
			},
			// 验证功能名字是否重复
			funNameRep() {
				let checkPermission = {
					sessionid: this.sessionidInfo.sessionid,
					name: this.addFunInfo.funName
				}
				let _this = this;
				checkPermissionReg(checkPermission).then(res=>{
					console.log(res.data)
					if (res.data.status === 0) {
                        _this.isFunRepetition = 0;
					}else if (res.data.status === 1) {
						_this.$router.replace("/login");
					} else if (res.data.status === 2) {
						_this.$message.error('功能名称重复');
						_this.isFunRepetition = 1;
					} else {
						_this.isFunRepetition = null;
					}
				})
			},
			// 验证功能中meat是否重复
			funIDRep() {
				let checkPermission = {
					sessionid: this.sessionidInfo.sessionid,
					meta: this.addFunInfo.meta
				};
				let _this = this;
				console.log(checkPermission)
				checkMetaReg(checkPermission).then(res=>{
					console.log(res.data)
					if (res.data.status === 0) {
                        _this.isFunMetaRepetition = 0;
                        _this.isFunMetaRepetSpan = false;
					}else if (res.data.status === 1) {
						_this.$router.replace("/login");
					} else if (res.data.status === 2) {
						_this.isFunMetaRepetSpan = true;
						_this.isFunMetaRepetition = 1;
					} else {
						_this.isFunMetaRepetition = null;
						_this.isFunMetaRepetSpan = false;
					}
				})
			},
			// 保存添加的功能
			saveAddFunction() {
              // console.log(this.addFunInfo)
              if (!this.addFunInfo.changeFunGroup) {
              	 this.$message.error('请选择归属功能组');
              	 return;
              };
               if (!this.addFunInfo.funName) {
              	 this.$message.error('功能名称不能为空');
				 return;	
              };
               if (!this.addFunInfo.meta) {
              	 this.$message.error('功能ID不能为空');
				 return;	
              };
              if (this.addFunInfo.meta && this.addFunInfo.meta.length!==2 && this.addFunInfo.meta.length!==4 && this.addFunInfo.meta.length!==6) {
              	 this.$message.error('功能ID长度必须2位/4位/6位');
				 return;
              };
          	 let sendAddFunInfo = {
          	 	sessionid: this.sessionidInfo.sessionid,
          	 	name: this.addFunInfo.funName, 
          	 	type: this.addFunInfo.changeFunGroup,
          	 	meta: this.addFunInfo.meta,
          	 	allshow: parseInt(this.addFunInfo.isShow),
          	 	isuse: parseInt(this.addFunInfo.isgetUse)
          	 };
          	 console.log('-----------------------')
          	 console.log(sendAddFunInfo);
          	 console.log('-------------------------------')
          	 let _this = this;
          	 addPermissionReg(sendAddFunInfo).then(res=>{
          	 	console.log(res.data);
          	 	if (res.data.status === 0) {
          	 		_this.$message({
			          message: '添加功能成功',
			          type: 'success'
			        });
			        _this.FunctionVisible = false;
          	 	};
          	 	if (res.data.status === 1) {
          	 		_this.$router.replace("/login");
          	 	};
          	 })
		},
			// 用户管理事件
			useManage(row,index) {
				console.log(row)
				console.log(index)
				let _this = this;
				this.clickIndex = null;
				let setPermission = {
                  sessionid: this.sessionidInfo.sessionid,
                  type: row.id
				};
				// _this.useManageVisible = true;
				getPermissionReg(setPermission).then(res=>{

                    console.log(res.data);
                    if (res.data.status === 0) {
                    	_this.setPermissData = res.data.permissions;
                    	for (var i = 0; i < _this.setPermissData.length; i++) {
                    		_this.setPermissData[i].allshow = _this.setPermissData[i].allshow===0?false:true;
                    		_this.setPermissData[i].isuse = _this.setPermissData[i].isuse===0?false:true;
                    	}
                    	_this.useManageVisible = true;
				        _this.manageTile = row.name+"功能管理";
                    };
                    if (res.data.status === 1) {
                    	_this.$router.replace("/login");
                    };
				});
			},
			clickShowList(row,index,res) {
				// console.log(row)
				// console.log(index)
				// console.log(res)
				this.setFunName(row);
			},
			clickShowuse(res) {
				this.setFunName(res);
			},
			// 点击span input显示可编辑
			// clickBtnFunName(row,index) {
			// 	// alert(1)
   //             this.clickIndex = index;
   //             this.$nextTick(function(){
               	 
   //             	  this.changeInputValue = '';
   //             	 this.$refs.Autofocus.focus()
   //             })
			// },
			// 修改列表名称失去焦点事件
			// inputBlur(row,index) {
			// 	console.log(row)
			// 	console.log(index)
			// 	console.log(this.changeInputValue)
			// 	let titleInfo = '是否更改 '+ row.name+'(旧功能名称)为 '+ this.changeInputValue+"(新功能名称)";
			// 	if (this.changeInputValue) {
   //                  console.log(titleInfo)
			// 		this.$confirm(titleInfo, {
			//           confirmButtonText: '确定',
			//           cancelButtonText: '取消',
			//           type: 'warning'
			//         }).then(() => {
			//            this.setFunName(row,this.changeInputValue);
   //      //                row.name = this.changeInputValue;
			// 		   // this.clickIndex = null;
			//         }).catch(() => {
			//             this.clickIndex = null;       
			//         });
			// 		}else {
			// 			this.clickIndex = null;
			// 		}
			// },
			// 管理对应列表保存事件
			setFunName(row) {
               console.log(row)
               let updatePer = {
               	 sessionid: this.sessionidInfo.sessionid,
               	 id:row.id,
               	 name:row.name,
               	 type:row.type,
               	 allshow: row.allshow,
               	 isuse: row.isuse,
               	 meta: row.meta
               };
               console.log(updatePer)
               let _this = this;
               updatePermissionReg(updatePer).then(res=>{
               	  console.log(res.data)
               	  if (res.data.status === 0) {
               	  	_this.$message({
			          message: '修改'+row.name+'保存功能成功',
			          type: 'success'
			        });
               	  };
               	  if (res.data.status === 1) {
               	  	_this.$router.replace("/login");
               	  };
               })
			}
		}
	}
</script>
<style scoped lang="scss">
	.groupBox {
		text-align: left;
		margin-left:2rem;
	}
	.clickBtn .btnStyletow {
		margin-left: 2rem;
	}
	.tablesys{
		margin-top: 1rem;
	}
	.showPadding{
		padding-bottom: 3rem;
	}
	.clickSpanStyle{
		display: inline-block;
		width:100%;
		height: 100%;
	}
	.fontSizeSty{
		color: red;
	}
</style>


