<template>
	<div class="page page-partner" v-loading="pageLoading">
		<div class="bread-nav">
			<span class="current-text">当前位置：</span>
			<span v-if="currentPosition.page >= item.id" :class="{menuActive: currentPosition.page > item.id}" v-for="(item, index) in currentPosition.menu" @click="toggleCurrentMenu(item.id)"><span v-if="index > 0">></span> {{item.name}}</span>
		</div>
		<transition name="slide-fade">
			<div class="page1" v-show="showPage1">
				<el-row class="search-wrapper">
					<el-col :xs="24" :sm="20" class="handler-panel">
						<el-col :xs="24" :sm="20">
							<el-select class="px360W mrgB" v-model="partnerInfo.selected" clearable filterable default-first-option placeholder="选择合作方">
							    <el-option v-for="(item,index) in partnerInfo.list" :key="index" :label="item.company" :value="item.company"></el-option>
							</el-select>
							<el-select class="px360W mrgB" v-model="companyInfo.selected" clearable filterable default-first-option placeholder="选择我方公司">
							    <el-option v-for="(item,index) in companyInfo.list" :key="index" :label="item.company" :value="item.company"></el-option>
							</el-select>
							<el-select class="mrgB" v-model="appInfo.selected" clearable filterable default-first-option placeholder="选择产品">
							    <el-option v-for="(item,index) in appInfo.list" :key="index" :label="item.name" :value="item.id"></el-option>
							</el-select>
							<el-select class="mrgB" v-model="cooperationInfo.selected" clearable filterable default-first-option placeholder="选择合作方式">
							    <el-option v-for="(item,index) in cooperationInfo.list" :key="index" :label="item.name" :value="item.name"></el-option>
							</el-select>
							<el-select class="mrgB" v-model="billStateInfo.selected" clearable filterable default-first-option placeholder="选择账单状态">
							    <el-option v-for="(item,index) in billStateInfo.list" :key="index" :label="item.name" :value="item.id"></el-option>
							</el-select>
							<div class="myRangeMonth">
								<label for="">起始月份：</label>
								<el-date-picker  v-model="rangeDate.minDate" type="month" placeholder="起始月份"></el-date-picker>
								<label for="">终止月份：</label>
								<el-date-picker  v-model="rangeDate.maxDate" type="month" placeholder="终止月份"></el-date-picker>
							</div>
						</el-col>
						<el-col :xs="24" :sm="4">
							<button class="btn primary" @click="searchAction"><i class="fa fa-search"></i>搜索</button>
						</el-col>
					</el-col>
					<el-col :xs="24" :sm="4" class="btn-panel">
						<button class="btn primary download" @click="batchDownload"><i class="fa fa-cloud-download"></i></button>
					</el-col>
				</el-row>
				<div class="table-wrapper" style="height: 500px">
					<el-table :data="billTableInfo.table" size="small" height="90%" border style="width: 100%" v-loading="billTableInfo.loading">
						<el-table-column type="selection" width="40"></el-table-column>
					    <el-table-column type="index" label="序号" width="60" :index="indexFormatter"></el-table-column>
					    <el-table-column prop="id" label="账单ID"></el-table-column>
					    <el-table-column prop="app" label="产品"></el-table-column>
					    <el-table-column prop="company" label="合作方" width="220"></el-table-column>
					    <el-table-column prop="cooperationType" label="合作方式"></el-table-column>
					    <el-table-column prop="ourCompany" label="我方公司" width="220"></el-table-column>
					    <el-table-column prop="month" label="归属期间"></el-table-column>
					    <el-table-column prop="cost" label="含税金额(元)" width="100"></el-table-column>
					    <el-table-column prop="actualCost" label="实际金额(元)" width="100"></el-table-column>
					    <el-table-column prop="createTime" label="生成时间" width="90"></el-table-column>
					    <el-table-column label="状态">
			                <template slot-scope="scope">
			                	<a class="tableA" :class="scope.row | tableStatusStyleFilter" @click="openStateDialog(scope.row)">
			                	<i class="fa" :class="scope.row.status | tableStatusStyleFilter1"></i>
			                	{{scope.row.status | tableStatusFilter}}</a>
			                </template>
			            </el-table-column>
					    <el-table-column label="账单详情" width="110">
			                <template slot-scope="scope">
			                    <el-button type="text" size="small" @click="viewDetailFun(scope.row)">明细</el-button>
			                    <el-button v-if="scope.row.cooperationType !== '客户'" type="text" size="small" @click="previewBill(scope.row)">预览</el-button>
			                </template>
			            </el-table-column>
				  	</el-table>
				</div>
			</div>
		</transition>
		<transition name="slide-fade">
			<div class="page2 form-wrapper" v-show="showPage2" v-loading="billDetailInfo.loading">
				<div class="page-title">
					<p>{{billDetailInfoTitle}} 账单详情</p>
					<p class="small">（产品名称 - 合作方名称）</p>
					<button v-if="cooperationType" class="btn primary download" @click="downloadBillExcel"><i class="fa fa-cloud-download"></i></button>
					<span class="back" @click="closePage2Fun"><i class="fa fa-chevron-left" aria-hidden="true"></i></i></span>
				</div>
				<el-row :gutter="20" v-if="cooperationType">
					<el-col :sx="24" :sm="12" class="block distributorInfo">
						<div class="block-title">
							<span>合作方信息</span>
						</div>
						<div class="content">
							<div class="input-group">
								<label for="proxyName">合作方名称：</label>
								<input type="text" id="proxyName" readonly="true" v-model="billDetailInfo.partner.company">
							</div>
							<div class="input-group">
								<label for="bankAccount">银行账户：</label>
								<input type="text" id="bankAccount" readonly="true" v-model="billDetailInfo.partner.bankAccount">
							</div>
							<div class="input-group">
								<label for="bank">开户行：</label>
								<input type="text" id="bank" readonly="true" v-model="billDetailInfo.partner.bank">
							</div>
							<div class="input-group">
								<label for="dutyParagraph">税号：</label>
								<input type="text" id="dutyParagraph" readonly="true" v-model="billDetailInfo.partner.dutyParagraph">
							</div>
							<div class="input-group">
								<label for="dutyParagraph">税收编码：</label>
								<input type="text" id="dutyParagraph" readonly="true" v-model="billDetailInfo.partner.taxcode">
							</div>
							<div class="input-group">
								<label for="phone">公司电话：</label>
								<input type="text" id="phone" readonly="true" v-model="billDetailInfo.partner.phone">
							</div>
							<div class="input-group textarea-group">
								<label for="address">公司地址：</label>
								<textarea rows="2" disabled="true">{{billDetailInfo.partner.address}}</textarea>
							</div>
						</div>
					</el-col>
					<el-col :sx="24" :sm="12" class="block companyInfo">
						<div class="block-title">
							<span>我方公司信息</span>
						</div>
						<div class="content">
							<div class="input-group">
								<label for="company">公司名称：</label>
								<input type="text" id="company" readonly="true" v-model="billDetailInfo.balanceAccount.company">
							</div>
							<div class="input-group">
								<label for="accountNo">银行账号：</label>
								<input type="text" id="accountNo" readonly="true" v-model="billDetailInfo.balanceAccount.accountNo">
							</div>
							<div class="input-group">
								<label for="bank">开户行：</label>
								<input type="text" id="bank" readonly="true" v-model="billDetailInfo.balanceAccount.bank">
							</div>
							<div class="input-group">
								<label for="taxpayerNo">税号：</label>
								<input type="text" id="taxpayerNo" readonly="true" v-model="billDetailInfo.balanceAccount.taxpayerNo">
							</div>
							<div class="input-group">
								<label for="phone">公司电话：</label>
								<input type="text" id="phone" readonly="true" v-model="billDetailInfo.balanceAccount.phone">
							</div>
							<div class="input-group textarea-group">
								<label for="address">公司地址：</label>
								<textarea rows="2" disabled="true">{{billDetailInfo.balanceAccount.address}}</textarea>
							</div>
						</div>
					</el-col>
				</el-row>
				<el-row :gutter="20" class="block">
					<el-col :sx="24" class="distributorInfo">
						<div class="block-title">
							<span>账单信息</span>
						</div>
						<el-row class="content">
							<el-col :sx="24" :sm="12" class="block">
								<div class="input-group">
									<label for="appId">产品名称：</label>
									<input type="text" id="appId" readonly="true" :value="billDetailInfo.billInfo.appName">
								</div>
								<div class="input-group">
									<label for="adminname">合作方式：</label>
									<input type="text" id="adminname" readonly="true" v-model="billDetailInfo.cooperationType">
								</div>
							</el-col>
							<el-col :sx="24" :sm="12" class="block">
								<div class="input-group">
									<label for="cost">申请金额(元)：</label>
									<input type="text" id="cost" readonly="true" v-model="billDetailInfo.billInfo.cost">
								</div>
								<div class="input-group">
									<label for="month">归属期间：</label>
									<input type="text" id="month" readonly="true" v-model="billDetailInfo.billInfo.month">
								</div>
							</el-col>
						</el-row>
					</el-col>
				</el-row>
				<el-row :gutter="20" class="block">
					<el-col :sx="24" class="distributorInfo">
						<div class="block-title">
							<span>详细信息</span>
						</div>
						<div class="content table-wrapper">
							<el-table :data="billDetailInfo.billDetail" size="small" height="300" border style="width: 100%">
							    <el-table-column type="index" label="序号" width="80" :index="indexFormatter"></el-table-column>
							    <el-table-column prop="cost" label="含税成本金额(元)"></el-table-column>
							    <el-table-column prop="dateNew" label="归属期间(账期)"></el-table-column>
						  	</el-table>
						</div>
					</el-col>
				</el-row>
				<!-- <el-row :gutter="20" class="block">
					<el-col :sx="24" class="distributorInfo">
						<div class="block-title">
							<span>审批路径</span>
						</div>
						<div class="content table-wrapper">
							<el-table :data="billDetailInfo.billStepList" size="small" border style="width: 100%">
								 <el-table-column type="index" label="流程" width="80" align="center" :index="indexFormatter"></el-table-column>
							    <el-table-column prop="adminName" label="审核人" align="center"></el-table-column>
							    <el-table-column prop="date" label="审核时间" align="center"></el-table-column>
							    <el-table-column prop="status" label="审核状态" align="center"></el-table-column>
						  	</el-table>
						</div>
					</el-col>
				</el-row> -->
				<div class="btn-group">
					<button class="btn primary" @click="closePage2Fun">关闭</button>
				</div>
			</div>
		</transition>
		<transition name="slide-fade">
			<div class="page3 previewBill" v-if="showPage3" v-loading="batchLoading">
				<download-excel v-on:close="closePreview" :billList="billExcel"></download-excel>
			</div>
		</transition>
		<el-dialog title="修改账单状态" :visible.sync="billDetailDialog.visible" width="550px" center v-loading="billDetailDialog.loading">
			<form class="form-wrapper">
				<div class="input-group">
					<label for="company">合作方：</label>
					<input type="text" id="company" readonly="true" v-model="billDetailDialog.form.company">
				</div>
				<div class="input-group">
					<label for="cooperationType">合作方式：</label>
					<input type="text" id="cooperationType" readonly="true" v-model="billDetailDialog.form.cooperationType">
				</div>
				<div class="input-group">
					<label for="ourCompany">我方公司：</label>
					<input type="text" id="ourCompany" readonly="true" v-model="billDetailDialog.form.ourCompany">
				</div>
				<div class="input-group">
					<label for="month">归属期间：</label>
					<input type="text" id="month" readonly="true" v-model="billDetailDialog.form.month">
				</div>
				<div class="input-group">
					<label for="cost">含税成本金额(元)：</label>
					<input type="text" id="cost" readonly="true" v-model="billDetailDialog.form.cost">
				</div>
				<div class="input-group">
					<label for="cost">实际金额(元)：</label>
					<input type="text" id="cost" :readonly="actualCostModify" v-model="billDetailDialog.form.actualCost">
				</div>
				<div class="input-group">
					<label for="status">账单当前状态：</label>
					<input type="text" id="status" readonly="true" :value="billDetailDialog.form.status | tableStatusFilter">
				</div>
				<div class="input-group" v-show="canSelectedStatus.length > 0">
					<label for="billState">审批操作：</label>
					<el-select class="mrgB billState" size="small" v-model="billDetailDialog.form.action" filterable default-first-option placeholder="选择账单状态">
					    <el-option v-for="item in canSelectedStatus" :key="item.id" :label="item.name" :value="item.id"></el-option>
					</el-select>
				</div>
			</form>
			<span slot="footer" class="dialog-footer">
			    <el-button @click="billDetailDialog.visible = false">取 消</el-button>
			    <el-button type="primary" @click="updateBillState">确 定</el-button>
		  	</span>
		</el-dialog>
	</div>
</template>
<script>
	import {getPartnerBillParaReg, getPartnerBillReg, exportPartnerBillReg, updatePartnerBillReg} from '../../api/api';
	import {dateFormatter} from '../../api/formatter';
	import DownloadExcel from './PartnerExcel';
	var AppList = [];
	var BillState = [];
	export default {
		components: {
			'download-excel': DownloadExcel
		},
		data() {
			return {
				userperadmin: '',
				// 页面loading
				pageLoading: false,
				// 当前显示的页面
				currentPosition: {
					page: 1,
					menu: [
						{id: 1 ,name: '合作方账单'},
						{id: 2 ,name: '账单详情'},
						{id: 3 ,name: '账单预览'}
					]
				},
				// 合作方
				partnerInfo: {
					selected: '',
					list: []
				},
				// 我方公司
				companyInfo: {
					selected: '',
					list: []
				},
				// 产品
				appInfo: {
					selected: '',
					list: []
				},
				//合作方式
				cooperationInfo: {
					selected: '',
					list: []
				},
				//账单状态
				billStateInfo: {
					selected: '',
					list: []
				},
				// 可审核状态列表
				billAdminStatusList: [],
				// 归属期间
				rangeDate: {
					minDate: '',
					maxDate: ''
				},
				// 账单表格
				billTableInfo: {
					loading: false,
					table: []
				},
				// 对话框
				billDetailDialog: {
					visible: false,
					loading: false,
					form: {
						billId: '',
						company: '',
						cooperationType: '',
						ourCompany: '',
						month: '',
						cost: '',
						actualCost: '',
						status: '',
						action: ''
					}
				},
				// 账单详情
				billDetailInfo: {
					loading: false,
					curApp: '',
					curPartner: '',
					partner: {},        // 合作方信息billDetailInfo
					billInfo: {},       // 账单信息
					balanceAccount: {}, // 合作方信息
					billDetail: [],     // 账单详细信息
					billStepList: [],
					cooperationType: ''
				},
				batchLoading: false,
				// 批量下载账单
				billLists: [],
				// 单个下载账单
				singleBill: [],
				// 准备下载账单
				billExcel: []
			}
		},
		mounted() {
			this.userperadmin = JSON.parse(localStorage.getItem('userperadmin'));
			this.getSearchParams();
		},
		filters: {
			tableStatusFilter(val) {
				var cellValueStr = val;
				for(var i=0; i<BillState.length; i++){
					if(BillState[i].id == val){
						cellValueStr = BillState[i].name;
						break;
					}
				}
				return cellValueStr;
			},
			tableStatusStyleFilter(row) {
				var className = '';
				// 未审核
				if(row.status == 55) {
					className = 'unChenck';
				}
				// 已审核
				if(row.status == 56) {
					className = 'checking';
				}
				// 已收款
				if(row.status == 57) {
					className = 'getMoney';
				}
				// 已付款
				if(row.status == 58) {
					className = 'pay';
				}
				if(row.isUpdate == 1){
					className += ' isUpdate';
				}
				return className;
			},
			tableStatusStyleFilter1(val) {
				// 未审核
				if(val == 55) {
					return '';
				}
				// 已审核
				if(val == 56){
					return 'fa-spinner';
				}
				// 已收款
				if(val == 57) {
					return 'fa-check';
				}
				// 已付款
				if(val == 58) {
					return 'fa-check-square-o';
				}
			}
		},
		computed: {
			showPage1() {
				if(this.currentPosition.page === 1){
					return true;
				}else{
					return false;
				}
			},
			showPage2() {
				if(this.currentPosition.page === 2){
					return true;
				}else{
					return false;
				}
			},
			showPage3() {
				if(this.currentPosition.page === 3){
					return true;
				}else{
					return false;
				}
			},
			billDetailInfoTitle() {
				// 账单详情标题
				return this.billDetailInfo.curApp + '-' + this.billDetailInfo.curPartner;
			},
			cooperationType() {
				if (this.billDetailInfo.cooperationType == '供应商') {
					return true;
				} else {
					return false;
				}
			},
			actualCostModify() {
				// 是否可以修改实际金额
				if (this.billDetailDialog.form.status == 55) {
					return false;
				} else {
					return true;
				}
			},
			canSelectedStatus() {
				// 修改审批状态时，可选择的状态
				var statusList = [];
				var _this = this;
				this.billStateInfo.list.forEach(function(item) {
					if(item.id > _this.billDetailDialog.form.status) {
						statusList.push(item);
					}
				});
				return statusList;
			}
		},
		methods: {
			toggleCurrentMenu(page) {
				// 切换页面
				if (page < this.currentPosition.page) {
					this.currentPosition.page = page;
				}
			},
			closePage2Fun() {
				// 关闭账单详情页面
				this.currentPosition.page = 1;
				// 清空数据
				this.billDetailInfo.proxyInfo = {};
				this.billDetailInfo.billInfo = {};
				this.billDetailInfo.balanceAccountList = {};
				this.billDetailInfo.billDetails = [];
			},
			rangeDateFormatter(row, column, cellValue) {
				console.log(row);
				if(row.maxdate === undefined){
					return this.chineseDate(cellValue);
				}else {
					return this.chineseDate(row.mindate) + ' - ' + this.chineseDate(row.maxdate);
				}
			},
			/***** 搜索操作：开始 *****/
			getSearchParams() {
				// 获取搜索参数
				var parameter = {sessionid: this.userperadmin.sessionid};
				this.pageLoading = true;
				getPartnerBillParaReg(parameter).then((res) => {
					console.log('getSearchParams', res);
					this.pageLoading = false;
					if(res.data.status === 0){
						this.appInfo.list = res.data.appList;
						this.partnerInfo.list = res.data.partnerList;
						this.companyInfo.list = res.data.balanceAccountList;
						this.cooperationInfo.list = res.data.payList;
						this.billStateInfo.list = res.data.billStatusList;
						AppList = res.data.appList;
						BillState = res.data.billStatusList;
						console.log(BillState);
					}else if(res.data.status === 1){
						this.$router.replace("/login");
					}else{
						this.toast(res.data.statusMsg, 'error');
					}
				})
				.catch((error) => {
					this.pageLoading = false;
					console.log(error);
				});
			},
			searchAction() {
				/* 搜索操作 */
				this.getBillList();
			},
			/***** 搜索操作：结束 *****/
			batchDownload() {
				/* 批量下载 */
				if( this.partnerInfo.selected == '' && this.companyInfo.selected == '' && this.appInfo.selected == '' && 
					this.cooperationInfo.selected == '' && this.billStateInfo.selected == '' && this.rangeDate.minDate == '' && this.rangeDate.maxDate == ''){
					this.toast('请至少选择一个搜索条件', 'warning');
					return;
				}
				if (this.rangeDate.minDate != '' && this.rangeDate.maxDate == '') {
					this.toast('请选择终止月份', 'error');
					return;
				}
				if (this.rangeDate.minDate == '' && this.rangeDate.maxDate != '') {
					this.toast('请选择起始月份', 'error');
					return;
				}
				if (this.rangeDate.minDate > this.rangeDate.maxDate) {
					this.toast('起始月份不能大于终止月份', 'error');
					return;
				}
				this.billTableInfo.loading = true;
				this.getExportInfo();
			},
			downloadBillExcel(batch){
				console.log('batch',batch);
				this.billExcel = [];
				if(batch === true){
					// 批量下载
					this.billExcel = this.billLists;
					this.billTableInfo.loading = false;
					console.log('before1', this.billExcel);
					this.currentPosition.page = 3;
				}else{
					// 下载一个账单
					this.billExcel = this.singleBill;
					this.batchLoading = false;
					console.log('before2', this.billExcel);
					this.currentPosition.page = 3;
				}
			},
			/***** 表格方法：开始 *****/
			getBillList() {
				/* 获取账单列表 */
				if(!this.rangeDate.minDate) this.rangeDate.minDate = '';
				if(!this.rangeDate.maxDate) this.rangeDate.maxDate = '';
				if (this.rangeDate.minDate != '' && this.rangeDate.maxDate == '') {
					this.toast('请选择终止月份', 'error');
					return;
				}
				if (this.rangeDate.minDate == '' && this.rangeDate.maxDate != '') {
					this.toast('请选择起始月份', 'error');
					return;
				}
				if (this.rangeDate.minDate > this.rangeDate.maxDate) {
					this.toast('起始月份不能大于终止月份', 'error');
					return;
				}
				var parameter = {
					sessionid: this.userperadmin.sessionid,
					cooperationCompany: this.partnerInfo.selected,
					payCompany: this.companyInfo.selected,
					appId: this.appInfo.selected,
					cooperationType: this.cooperationInfo.selected,
					status: this.billStateInfo.selected,
					minDate: dateFormatter(this.rangeDate.minDate).substr(0, 7),
					maxDate: dateFormatter(this.rangeDate.maxDate).substr(0, 7)
				};
				console.log(parameter);
				this.billTableInfo.loading = true;
				getPartnerBillReg(parameter).then((res) => {
					console.log('getBillList', res);
					this.billTableInfo.loading = false;
					if(res.data.status === 0){
						this.billTableInfo.table = res.data.reportforms_admin;
					}else if(res.data.status === 1){
						this.$router.replace("/login");
					}else{
						this.toast(res.data.statusMsg, 'error');
					}
				})
				.catch((error) => {
					this.billTableInfo.loading = false;
					console.log(error);
				});
			},
			openStateDialog(row) {
				/**
				 * 打开状态对话框
				 */
				console.log(row);
				if(row.isUpdate == 0){
					this.toast('您目前不能审核该账单', 'warning');
					// return;
				}
				this.billDetailDialog.form = {};
				this.billDetailDialog.visible = true;
				this.billDetailDialog.form.billId = row.id;
				this.billDetailDialog.form.company = row.company;
				this.billDetailDialog.form.cooperationType = row.cooperationType;
				this.billDetailDialog.form.ourCompany = row.ourCompany;
				this.billDetailDialog.form.month = row.month;
				this.billDetailDialog.form.cost = row.cost;
				this.billDetailDialog.form.actualCost = row.actualCost;
				this.billDetailDialog.form.status = parseInt(row.status);
			},
			viewDetailFun(row) {
				this.currentPosition.page = 2;
				this.billDetailInfo.curApp = row.app;
				this.billDetailInfo.curPartner = row.company;
				this.billDetailInfo.cooperationType = row.cooperationType;
				var idArr = [row.id];
				this.getExportInfo(idArr);
			},
			previewBill(row) {
				// 预览账单
				this.batchLoading = true;
				var idArr = [row.id];
				this.getExportInfo(idArr, 1);
			},
			updateBillFun() {

			},
			indexFormatter(index) {
				return index + 1;
			},
			tableAppFormatter(row, column, cellValue){
				// 表格appid转名称
				var cellValueStr = cellValue;
				var appList = this.appInfo.list;
				for(var i=0; i<appList.length; i++){
					if(appList[i].id == cellValue){
						cellValueStr = appList[i].name;
						break;
					}
				}
				return cellValueStr;
			},
			getExportInfo(idArr, single) {
				console.log('idArr',idArr);
				var billIdArray = [];
				if(idArr != undefined) {
					// 获取单个账单的明细
					billIdArray = idArr;
				} else {
					// 获取多个账单的明细
					var table = this.billTableInfo.table;
					for(var i=0; i<table.length; i++) {
						if (table[i].cooperationType !== '客户') {
							billIdArray.push(table[i].id);
						} 
					}
				}
				// 下载时客户合作方式
				if (billIdArray.length === 0 && single == 1) {
					this.toast('您未选择账单或您选择的账单是“客户的合作方式”','warning');
					this.billTableInfo.loading = false;
					return;
				}
				var parameter = {
					sessionid: this.userperadmin.sessionid,
					ids: billIdArray
				};
				console.log('getExportInfo', parameter);
				exportPartnerBillReg(parameter).then((res) => {
					console.log('getExportInfo', res);
					if(res.data.status === 0){
						if (idArr != undefined) {
							var data = res.data.reportforms_admin[0];
							this.billDetailInfo.partner = JSON.parse(data.partner);               // 合作方信息
							this.billDetailInfo.billInfo = {                          // 账单信息
								appName: data.app,
								companyId: data.companyId,
								cost: data.cost,
								createTime: data.createTime,
								month: data.month,
								ourCompanyId: data.ourCompanyId,
								invoiceContent: data.invoiceContent,
								remark: data.remark
							};
							this.billDetailInfo.balanceAccount = JSON.parse(data.balanceAccount); // 我方公司信息
							this.billDetailInfo.billDetail = JSON.parse(data.billDetail);         // 账单详细信息
							this.singleBill = [];
							var chineseMonth = data.month.split('-');
							chineseMonth = chineseMonth[0] + '年' + chineseMonth[1] + '月';
							var billName = data.app+'_'+this.billDetailInfo.partner.company+chineseMonth;
							this.singleBill.push({
								id: 'bill1',
								billName: billName,
								partnerName: this.billDetailInfo.partner.company,
								payDate: data.createTime,
								ticketType: '增值税专用发票',
								demo: this.billDetailInfo.billInfo.appName,
								payMonth: chineseMonth,
								payMount: data.cost,
								fromCompany: this.billDetailInfo.balanceAccount.company,
								fromTaxNo: this.billDetailInfo.balanceAccount.taxpayerNo,
								fromBank: this.billDetailInfo.balanceAccount.bank,
								fromBankNo: this.billDetailInfo.balanceAccount.accountNo,
								fromAdress: this.billDetailInfo.balanceAccount.address,
								fromTel: this.billDetailInfo.balanceAccount.phone,
								fromTicketContent: this.billDetailInfo.billInfo.invoiceContent,
								toCompany: this.billDetailInfo.partner.company,
								toTaxNo: this.billDetailInfo.partner.dutyParagraph,
								toBank: this.billDetailInfo.partner.bank,
								toBankNo: this.billDetailInfo.partner.bankAccount,
								toTaxNumber: this.billDetailInfo.partner.taxcode !== undefined ? this.billDetailInfo.partner.taxcode: '',
								downloadState: 0
							});
							// 下载单个账单
							if(single == 1) {
								this.downloadBillExcel();
							}
						} else {
							var data = res.data.reportforms_admin;
							this.billLists = [];
							for (var i=0; i<data.length; i++) {
								var partner = JSON.parse(data[i].partner);
								var billInfo = {
									appName: data[i].app,
									companyId: data[i].companyId,
									cost: data[i].cost,
									createTime: data[i].createTime,
									month: data[i].month,
									ourCompanyId: data[i].ourCompanyId,
									remark: data[i].remark
								}
								var us = JSON.parse(data[i].balanceAccount);
								var billDetail = JSON.parse(data[i].billDetail); 
								var chineseMonth = data[i].month.split('-');
								chineseMonth = chineseMonth[0] + '年' + chineseMonth[1] + '月';
								var billName = billInfo.appName+'_' + partner.company + chineseMonth;
								this.billLists.push({
									id: 'bill' + (i+1),
									billName: billName,
									partnerName: partner.company,
									payDate: billInfo.createTime,
									ticketType: '增值税专用发票',
									demo: billInfo.appName,
									payMonth: chineseMonth,
									payMount: billInfo.cost,
									fromCompany: us.company,
									fromTaxNo: us.taxpayerNo,
									fromBank: us.bank,
									fromBankNo: us.accountNo,
									fromAdress: us.address,
									fromTel: us.phone,
									fromTicketContent: '',
									toCompany: partner.company,
									toTaxNo: partner.dutyParagraph,
									toBank: partner.bank,
									toBankNo: partner.bankAccount,
									toTaxNumber: partner.taxcode !== undefined ? partner.taxcode: '',
									downloadState: 0
								});
							}
							this.downloadBillExcel(true);
						}   
					}else if(res.data.status === 1){
						this.$router.replace("/login");
					}else{
						this.toast(res.data.statusMsg, 'error');
					}
				})
				.catch((error) => {
					this.billTableInfo.loading = false;
					console.log(error);
				});
			},
			pushExcelData(bill) {
				// 把账单Excel数据装载进去

			},
			closePreview() {
				// 关闭账单预览
				this.currentPosition.page = 1;
			},
			/*****表格方法：结束 *****/
			updateBillState() {
				// 修改审批状态
				if (this.billDetailDialog.form.action == '' || this.billDetailDialog.form.action == undefined) {
					this.toast('请选择审批状态', 'warning');
					return;
				}
				var parameter = {
					sessionid: this.userperadmin.sessionid,
					id: this.billDetailDialog.form.billId,
					status: this.billDetailDialog.form.action,
					actualCost: this.billDetailDialog.form.actualCost
				};
				console.log(parameter);
				this.billDetailDialog.loading = true;
				updatePartnerBillReg(parameter).then((res) => {
					console.log('updatePartnerBillReg', res);
					this.billDetailDialog.loading = false;
					if(res.data.status === 0) {
						// 审批成功，刷新表格
						this.billDetailDialog.visible = false;
						this.getBillList();
					}else if(res.data.status === 1){
						this.$router.replace("/login");
					}else{
						this.toast(res.data.statusMsg, 'error');
					}
				})
				.catch((error) => {
					this.billDetailDialog.loading = false;
					console.log(error);
				});
			},
			toast(msg, type) {
				this.$message({
		            type: type,
		            message: msg
		        });
			}
		}
	}
</script>
<style lang="scss" scoped>
	@import url(../../assets/style/common.css);
	.myRangeMonth{
		display: inline-block;
		label{
			font-size: 12px;
		}
	}
	.page1{
		height: 100%;
	}
	.page2, .page3{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		background: #fff;
	}
	.px360W{
		width: 360px;
	}
	.px200W{
		width: 200px;
	}
	.btn{
		height: 35px;
		padding: 0 15px;
		border: none;
		border-radius: 2px;
		cursor: pointer;
		i{
			margin-right: 4px;
		}
		&.primary{
			color: #fff;
			background: #409EFF;
		}
		&.disabled{
			background: #909399;
			color: #fff;
		}
		&.download{
			font-size: 20px;
			i{
				margin-right: 0;
			}
		}
	}
	.mrgB{
		margin-bottom: 10px;
	}
	.page-title{
		text-align: center;
		font-size: 20px;
		width: 100%;
		height: 50px;
		margin-bottom: 10px;
		font-weight: 700;
		position: relative;
		.btn{
			position: absolute;
			right: 10px;
			top: 0;
		}
		.back{
			display: inline-block;
			position: absolute;
			top: 10px;
			left: 10px;
			color: #409EFF;
			cursor: pointer;
		}
		.small{
			font-size: 14px;
			font-weight: 500;
		}
	}
	.block{
		margin-bottom: 14px;
		.block-title{
			height: 40px;
			line-height: 40px;
			background: #E5E9F2;
			font-size: 14px;
			font-weight: 700;
			border: 1px solid #EBEEF5;
			span{
				border-left: 4px solid #409EFF;
				margin-left: 20px;
				padding-left: 10px;
			}
		}
		.content{
			border: 1px solid #EBEEF5;
			background: #fcfcfc;
			margin-top: -1px;
			padding: 10px 14px;
			font-size: 16px;
			&.table-wrapper{
				padding: 0;
			}
		}
	}
	.btn-group{
		margin-top: 10px;
		text-align: center;
	}
	.table-wrapper{
		margin-top: 10px;
		height: 500px;
		height: -webkit-calc(100% - 200px);
		height: -moz-calc(100% - 200px);
		height: calc(100% - 200px);
		thead{
			th{
				background: #F5F7FA;
			}
		}
		.tableA{
			color: #409EFF;
			font-weight: 700;
			text-decoration: none;
			cursor: default;
			&.unChenck{
				color: #666;
			}
			&.checking{
				color: #409EFF;
			}
			&.denail{
				color: #D54F42;
				text-decoration: none;
				cursor: default;
			}
			&.getMoney{
				color: #67C23A;
			}
			&.pay{
				color: #67C23A;
			}
			&.isUpdate{
				color: #409EFF;
				cursor: pointer;
				text-decoration: underline;
			}
			&.denail{
				color: #D54F42;
			}
		}
	}
	.form-wrapper{
		.input-group{
			height: 30px;
			font-size: 0;
			margin-bottom: 10px;
			label{
				display: inline-block;
				height: 100%;
				width: 28%;
				text-align: right;
				// margin-right: 10px;
				font-size: 14px;
				color: #909AB3;
			}
			input{
				height: 100%;
				width: 60%;
				border-radius: 2px;
				border: 1px solid #409EFF;
				padding-left: 10px;
				&[readonly=readonly]{
					color: #666;
					border: 1px solid #DCDFE6;
				}
			}
			textarea{
				width: 60%;
				resize: none;
				color: #666;
				border: 1px solid #DCDFE6;
				border-radius: 2px;
				line-height: 1.6;
				padding: 4px 0;
				padding-left: 10px;
				&[disabled=disabled]{
					background: #fff;
				}
			}
		}
		.textarea-group{
			height: 50px;
			label{
				vertical-align: top;
			}
		}
	}

</style>
<style>
	.grid-content, .content-wrapper{
		height: 100%;
	}
	.el-table--border th{
		background: #F5F7FA !important;
	}
	.el-dialog__header{
		background: #F5F7FA;
		padding-top: 10px;
		font-weight: 700;
	}
	.el-dialog__headerbtn{
		top: 12px;
	}
	.el-dialog__footer{
		/*background: #F5F7FA;*/
	}
	.billState{
		height: 30px;
		border-radius: 2px !important;
	}
	.el-input::-webkit-scrollbar-thumb{
		border-radius: 2px !important;
	}
	.handler-panel .el-date-editor.el-input{
		width: 120px;
	}
</style>


