    // 表格序号格式化
    indexFormatter(index) {
      return index + 1;
    },
    tableColumnsChange(tableInfo) {
      // 表格列选择变化
      tableInfo.reInitTable = false;
      tableInfo.columnsSelected = [];
      var _this = this;
      tableInfo.columnsIndexSelected.sort();
      tableInfo.columnsIndexSelected.forEach(function(item) {
        tableInfo.columnsSelected.push(tableInfo.columns[item]);
      });
      setTimeout(() => {
        tableInfo.reInitTable = true;
      }, 50);
    },
    // 点击查询：查询表格
    doSearch(tableInfo, index) {
      var minDate = tableInfo.rangeDate.minDate;
      var maxDate = tableInfo.rangeDate.maxDate;
      if (!minDate) minDate = "";
      if (!maxDate) maxDate = "";
      if (minDate != "" && maxDate == "") {
        this.toast("请选择结束日期", "error");
        return;
      }
      if (minDate == "" && maxDate != "") {
        this.toast("请选择开始日期", "error");
        return;
      }
      if (minDate > maxDate) {
        this.toast("开始日期不能大于结束日期", "error");
        return;
      }
      if (index === 1) {
        this.getTableDataFun1();
      } else if (index === 2) {
        this.getTableDataFun2();
      } else if (index === 3) {
        this.getTableDataFun3();
      } else if (index === 4) {
        this.getTableDataFun4();
      } else if (index === 5) {
        this.getTableDataFun5();
      } else if (index === 6) {
        this.getTableDataFun6();
      }
    },
    // 获取整体信息数据
    getTableDataFun1() {
      var parameter = {
        sessionId: this.userperadmin.sessionid,
        minDate: this.tableInfo1.rangeDate.minDate,
        maxDate: this.tableInfo1.rangeDate.maxDate,
        appId: this.appInfo.selected
      };
      console.log("getTotalInfoReg", parameter);
      this.tableInfo1.loading = true;
      getTotalInfoReg(parameter)
        .then(res => {
          this.tableInfo1.loading = false;
          console.log("getTotalInfoReg", res);
          if (res.data.status == 0) {
            var list = res.data.data.list;
            var tableData = [];
            list.forEach(function(item) {
              var temp1 = {
                date: item.date,
                flow: "注册", // 流程
                registOfTheDay: item.todayRegister, // 当天注册
                rateOfTotal:
                  (item.registerWholeCoversion * 100 + "").substr(0, 3) + "%", // 整体转化
                rateOfStep:
                  (item.registerStepCoversion * 100 + "").substr(0, 3) + "%", // 分步转化
                registOfOtherDay: item.otherRegister, // 非当天注册
                total: item.register // 合计
              };
              tableData.push(temp1);
              var temp2 = {
                date: "",
                flow: "身份认证", // 流程
                registOfTheDay: item.todayIdCard, // 当天注册
                rateOfTotal:
                  (item.idCardWholeCoversion * 100 + "").substr(0, 3) + "%", // 整体转化
                rateOfStep:
                  (item.idCardStepCoversion * 100 + "").substr(0, 3) + "%", // 分步转化
                registOfOtherDay: item.otherIdCard, // 非当天注册
                total: item.idCard // 合计
              };
              tableData.push(temp2);
              var temp3 = {
                date: "",
                flow: "绑卡", // 流程
                registOfTheDay: item.todayDebitCard, // 当天注册
                rateOfTotal:
                  (item.debitCardWholeCoversion * 100 + "").substr(0, 3) + "%", // 整体转化
                rateOfStep:
                  (item.debitCardStepCoversion * 100 + "").substr(0, 3) + "%", // 分步转化
                registOfOtherDay: item.otherDebitCard, // 非当天注册
                total: item.debitCard // 合计
              };
              tableData.push(temp3);
              var temp4 = {
                date: "",
                flow: "基本信息", // 流程
                registOfTheDay: item.todayHomeJob, // 当天注册
                rateOfTotal:
                  (item.homeJobWholeCoversion * 100 + "").substr(0, 3) + "%", // 整体转化
                rateOfStep:
                  (item.homeJobStepCoversion * 100 + "").substr(0, 3) + "%", // 分步转化
                registOfOtherDay: item.otherHomeJob, // 非当天注册
                total: item.homeJob // 合计
              };
              tableData.push(temp4);
              var temp5 = {
                date: "",
                flow: "视频认证", // 流程
                registOfTheDay: item.todayVedio, // 当天注册
                rateOfTotal:
                  (item.vedioWholeCoversion * 100 + "").substr(0, 3) + "%", // 整体转化
                rateOfStep:
                  (item.vedioStepCoversion * 100 + "").substr(0, 3) + "%", // 分步转化
                registOfOtherDay: item.otherVedio, // 非当天注册
                total: item.vedio // 合计
              };
              tableData.push(temp5);
              var temp6 = {
                date: "",
                flow: "申请开户", // 流程
                registOfTheDay: item.todayUpload, // 当天注册
                rateOfTotal:
                  (item.uploadWholeCoversion * 100 + "").substr(0, 3) + "%", // 整体转化
                rateOfStep:
                  (item.uploadStepCoversion * 100 + "").substr(0, 3) + "%", // 分步转化
                registOfOtherDay: item.otherUpload, // 非当天注册
                total: item.upload // 合计
              };
              tableData.push(temp6);
              var temp7 = {
                date: "",
                flow: "开户", // 流程
                registOfTheDay: item.todayAccount, // 当天注册
                rateOfTotal:
                  (item.accountWholeCoversion * 100 + "").substr(0, 3) + "%", // 整体转化
                rateOfStep:
                  (item.accountStepCoversion * 100 + "").substr(0, 3) + "%", // 分步转化
                registOfOtherDay: item.otherAccount, // 非当天注册
                total: item.account // 合计
              };
              tableData.push(temp7);
              var temp8 = {
                date: "",
                flow: "提现", // 流程
                registOfTheDay: item.todayLoan, // 当天注册
                rateOfTotal:
                  (item.loanWholeCoversion * 100 + "").substr(0, 3) + "%", // 整体转化
                rateOfStep:
                  (item.loanStepCoversion * 100 + "").substr(0, 3) + "%", // 分步转化
                registOfOtherDay: item.otherLoan, // 非当天注册
                total: item.loan // 合计
              };
              tableData.push(temp8);
              var temp9 = {
                date: "",
                flow: "补件", // 流程
                registOfTheDay: item.todayUnAccount, // 当天注册
                rateOfTotal:
                  (item.unAccountWholeCoversion * 100 + "").substr(0, 3) + "%", // 整体转化
                rateOfStep: "", // 分步转化
                registOfOtherDay: item.otherUnAccount, // 非当天注册
                total: item.unAccount // 合计
              };
              tableData.push(temp9);
            });
            this.tableInfo1.data = tableData;
          } else if (res.data.status === 1) {
            this.$router.replace("/login");
          } else if (res.data.status === 3) {
          } else {
            this.toast(res.data.statusMsg, "error");
          }
        })
        .catch(err => {
          this.tableInfo1.loading = false;
          this.toast("请求表1错误，请联系管理员", "error");
        });
    },
    // 注册-申请留存
    getTableDataFun2() {
      var parameter = {
        sessionId: this.userperadmin.sessionid,
        minDate: this.tableInfo2.rangeDate.minDate,
        maxDate: this.tableInfo2.rangeDate.maxDate,
        appId: this.appInfo.selected
      };
      this.tableInfo2.loading = true;
      console.log("getTableDataFun2 parameter", parameter);
      applyRetentionReg(parameter)
        .then(res => {
          this.tableInfo2.loading = false;
          console.log("getTableDataFun2", res);
          if (res.data.status == 0) {
            var data = [];
            var list = res.data.data.list;
            list.forEach(function(item) {
              if (item.type == 0) {
                var temp1 = {
                  date: "",
                  class: "当天完成身份认证",
                  numberOfperple: item.register,
                  firstDay: item.one,
                  secondDay: item.two,
                  thirdDay: item.three,
                  fourthDay: item.four,
                  fifthDay: item.fiv,
                  sixthDay: item.six,
                  seventhDay: item.seven,
                  otherDays: item.sevenLater
                };
                data.push(temp1);
              }
              if (item.type == 1) {
                var temp2 = {
                  date: item.date,
                  class: "当天未完成身份认证",
                  numberOfperple: item.register,
                  firstDay: item.one,
                  secondDay: item.two,
                  thirdDay: item.three,
                  fourthDay: item.four,
                  fifthDay: item.fiv,
                  sixthDay: item.six,
                  seventhDay: item.seven,
                  otherDays: item.sevenLater
                };
                data.push(temp2);
              }
            });
            console.log("--------------");
            console.log(list);
            this.tableInfo2.data = data;
          } else if (res.data.status === 1) {
            this.$router.replace("/login");
          } else if (res.data.status === 3) {
          } else {
            this.toast(res.data.statusMsg, "error");
          }
        })
        .catch(err => {
          this.tableInfo2.loading = false;
          this.toast("请求表2错误，请联系管理员", "error");
        });
    },
    // 查询表格3数据
    getTableDataFun3() {
      var parameter = {
        sessionId: this.userperadmin.sessionid,
        minDate: this.tableInfo3.rangeDate.minDate,
        maxDate: this.tableInfo3.rangeDate.maxDate,
        appId: this.appInfo.selected
      };
      console.log("openDistributionReg parameter", parameter);
      this.tableInfo3.loading = true;
      openDistributionReg(parameter)
        .then(res => {
          console.log("openDistributionReg", res);
          this.tableInfo3.loading = false;
          if (res.data.status === 0) {
            var data = [];
            var list = res.data.data.list;
            list.forEach(item => {
              var temp1 = {
                date: item.date,
                class: "申请开户",
                registerOfTheDay: this.formatter(item.todayApply),
                registerOfOtherDay: this.formatter(item.otherApply),
                submited: this.formatter(item.submit),
                total: this.formatter(item.todayApply + item.otherApply)
              };
              var temp2 = {
                date: "",
                class: "开户",
                registerOfTheDay: this.formatter(item.todayOpen),
                registerOfOtherDay: this.formatter(item.otherOpen),
                submited: this.formatter(item.submit),
                total: this.formatter(item.todayOpen + item.otherOpen)
              };
              data.push(temp1);
              data.push(temp2);
            });
            this.tableInfo3.data = data;
          } else if (res.data.status === 1) {
            this.$router.replace("/login");
          } else if (res.data.status === 3) {
          } else {
            this.toast(res.data.statusMsg, "error");
          }
        })
        .catch(err => {
          this.tableInfo3.loading = false;
          this.toast("请求表3错误，请联系管理员", "error");
        });
    },
    getTableDataFun4() {
      var parameter = {
        sessionId: this.userperadmin.sessionid,
        minDate: this.tableInfo4.rangeDate.minDate,
        maxDate: this.tableInfo4.rangeDate.maxDate,
        appId: this.appInfo.selected
      };
      console.log("openRetentionReg parameter", parameter);
      this.tableInfo4.loading = true;
      openRetentionReg(parameter)
        .then(res => {
          console.log("openRetentionReg", res);
          this.tableInfo4.loading = false;
          if (res.data.status === 0) {
            var data = [];
            var list = res.data.data.list;
            list.forEach(function(item) {
              var temp = {
                date: item.date,
                numberOfperple: item.today,
                firstDay: item.one,
                secondDay: item.two,
                thirdDay: item.three,
                fourthDay: item.four,
                fifthDay: item.fiv,
                sixthDay: item.six,
                seventhDay: item.seven,
                otherDays: item.sevenLater
              };
              data.push(temp);
            });
            this.tableInfo4.data = data;
          } else if (res.data.status === 1) {
            this.$router.replace("/login");
          } else if (res.data.status === 3) {
          } else {
            this.toast(res.data.statusMsg, "error");
          }
        })
        .catch(err => {
          this.tableInfo4.loading = false;
          this.toast("请求表4错误，请联系管理员", "error");
        });
    },
    getTableDataFun5() {
      var parameter = {
        sessionId: this.userperadmin.sessionid,
        minDate: this.tableInfo5.rangeDate.minDate,
        maxDate: this.tableInfo5.rangeDate.maxDate,
        appId: this.appInfo.selected
      };
      console.log("getTableDataFun5 parameter", parameter);
      this.tableInfo5.loading = true;
      loanDistributionReg(parameter)
        .then(res => {
          console.log("getTableDataFun5", res);
          this.tableInfo5.loading = false;
          if (res.data.status === 0) {
            var data = [];
            var list = res.data.data.list;
            list.forEach(item => {
              var temp1 = {
                date: item.date,
                class: "开户",
                registerOfTheDay: item.todayOpen,
                registerOfOtherDay: item.otherOpen,
                submited: this.formatter(item.submit),
                total: item.todayOpen + item.otherOpen
              };
              var temp2 = {
                date: "",
                class: "提现",
                registerOfTheDay: item.todayLoan,
                registerOfOtherDay: item.otherLoan,
                submited: this.formatter(item.submit),
                total: item.todayLoan + item.otherLoan
              };
              data.push(temp1);
              data.push(temp2);
            });
            this.tableInfo5.data = data;
          } else if (res.data.status === 1) {
            this.$router.replace("/login");
          } else if (res.data.status === 3) {
          } else {
            this.toast(res.data.statusMsg, "error");
          }
        })
        .catch(err => {
          this.tableInfo5.loading = false;
          this.toast("请求表5错误，请联系管理员", "error");
        });
    },
    getTableDataFun6() {
      var parameter = {
        sessionId: this.userperadmin.sessionid,
        minDate: this.tableInfo6.rangeDate.minDate,
        maxDate: this.tableInfo6.rangeDate.maxDate,
        appId: this.appInfo.selected
      };
      console.log("getTableDataFun6 parameter", parameter);
      this.tableInfo6.loading = true;
      loanRetentionReg(parameter)
        .then(res => {
          console.log("getTableDataFun6", res);
          this.tableInfo6.loading = false;
          if (res.data.status === 0) {
            var data = [];
            var list = res.data.data.list;
            list.forEach(function(item) {
              var temp = {
                date: item.date,
                numberOfperple: item.today,
                firstDay: item.one,
                secondDay: item.two,
                thirdDay: item.three,
                fourthDay: item.four,
                fifthDay: item.fiv,
                sixthDay: item.six,
                seventhDay: item.seven,
                otherDays: item.sevenLater
              };
              data.push(temp);
            });
            this.tableInfo6.data = data;
          } else if (res.data.status === 1) {
            this.$router.replace("/login");
          } else if (res.data.status === 3) {
          } else {
            this.toast(res.data.statusMsg, "error");
          }
        })
        .catch(err => {
          this.tableInfo6.loading = false;
          this.toast("请求表6错误，请联系管理员", "error");
        });
    },
    getAppListFun() {
      var parameter = {
        sessionid: this.userperadmin.sessionid
      };
      getAppListReg(parameter)
        .then(res => {
          console.log("getAppListFun", res);
          if (res.data.status === 0) {
            this.appInfo.list = res.data.appList;
            // 初始化产品默认选择
            this.appInfo.selected = this.appInfo.list[0].id;
          } else if (res.data.status === 1) {
            this.$router.replace("/login");
          } else {
            this.toast(res.data.statusMsg, "error");
          }
        })
        .catch(err => {
          this.toast("请求APP列表错误，请联系管理员", "error");
        });
    },
    appChange() {
      // app选择变化
      console.log("appChange");
      this.tableInfo1.data = [];
      this.tableInfo2.data = [];
      this.tableInfo3.data = [];
      this.tableInfo4.data = [];
      this.tableInfo5.data = [];
      this.tableInfo6.data = [];
      this.getTableDataFun1();
      this.getTableDataFun2();
      this.getTableDataFun3();
      this.getTableDataFun4();
      this.getTableDataFun5();
      this.getTableDataFun6();
    },
    // 下载表格1
    downloadExcelFun(tableId, filename) {
      downloadExcel(tableId, filename);
    },
    formatter(val) {
      if (val) {
        return val;
      } else {
        return 0;
      }
    },
    toast(msg, type) {
      this.$message({
        type: type,
        message: msg
      });
    }
  }
};
</script>
<style scoped>
.page {
  text-align: left;
}
.table-wrapper {
  margin-bottom: 25px;
  border: 1px solid #dee5e7;
  box-shadow: 0 0 6px 0 #dcdcdc;
}
.table-header {
  padding: 10px 15px;
  font-size: 16px;
  border-bottom: 1px solid #dee5e7;
  background: #e5e9f2;
  position: relative;
}
.table-action {
  min-height: 82px;
  padding: 10px;
  font-size: 12px;
  box-sizing: border-box;
}
.toolbar {
  overflow: hidden;
  margin-bottom: 15px;
}
.myRangeMonth {
  float: right;
}
.table-header > .title {
  font-weight: 700;
}
.table-header > .btn {
  float: right;
  cursor: pointer;
}
.table-header > .btn:hover {
  color: #409eff;
}
@media only screen and (min-width: 1200px) {
  .marginR {
    margin-left: 20px;
  }
}
</style>
<style>
.userAction .el-table--small td,
.el-table--small th {
  padding: 4px 0;
}
.userAction .el-table .cell,
.el-table--border td:first-child .cell,
.el-table th div,
.el-table--border th:first-child .cell {
  padding: 0 4px;
}
.userAction .el-checkbox__label {
  padding-left: 5px;
}
.userAction .el-checkbox + .el-checkbox {
  margin-left: 10px;
}
.userAction .el-date-editor.el-input {
  width: 100px;
}
.userAction .el-select .el-input--suffix .el-input__inner {
  padding-right: 6px;
  padding-left: 6px;
}
.userAction .el-input--suffix .el-input__inner {
  padding-right: 6px;
}
.userAction .el-select > .el-input {
  width: 100px;
}
</style>


