<template>
	<div class="page record-history">
		<div class="bread-nav">
			<p>当前位置：数据中心 > 成本录入历史</p>
		</div>
		<section class="action-wrapper clearfix">
			<div class="fleft">
				<el-select
				    v-model="searchInfo.channelNameSelected"
				    clearable
				    filterable
				    allow-create
				    default-first-option
				    placeholder="选择/输入渠道">
				    <el-option
				      v-for="(channelName,index) in searchInfo.channelNameList"
				      :key="index"
				      :label="channelName"
				      :value="channelName">
				    </el-option>
				</el-select>
				<el-select
				    v-model="searchInfo.channelSelected"
				    clearable
				    filterable
				    allow-create
				    default-first-option
				    placeholder="选择/输入渠道号">
				    <el-option
				      v-for="(channel,index) in searchInfo.channelList"
				      :key="index"
				      :label="channel"
				      :value="channel">
				    </el-option>
				</el-select>
			</div>
			<div class="fleft" id="dateRangeInput">
				<span class="label">选择日期:</span>
				<el-date-picker v-model="searchInfo.dateRange" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
			</div>
			<div class="fleft">
				<button class="btn primary" @click="searchForHistoryFun"><i class="fa fa-search"></i>查询</button>
			</div>
			<div class="fright">
				<button class="btn error" @click="deleteMuchFun" v-show="deleteHistory"><i class="fa fa-trash"></i>一键删除</button>
			</div>
		</section>
		<section class="table-wrapper" v-loading="loading" element-loading-spinner="el-icon-loading" element-loading-text="正在加载">
			<el-table :data="tableInfo.tableData" border style="width: 100%" @select="selectRow" @select-all="selectAll">
			    <el-table-column type="selection" width="50"></el-table-column>
			    <el-table-column type="index" :index="indexFormatter" label="序号" width="70"></el-table-column>
			    <el-table-column prop="date" label="日期"></el-table-column>
			    <el-table-column prop="channelName" label="渠道"></el-table-column>
			    <el-table-column prop="channel" label="渠道号"></el-table-column>
			    <el-table-column prop="cost" label="成本金额"></el-table-column>
			    <el-table-column prop="action" label="删除">
			    	<template slot-scope="scope">
			    		<a href="javascript:;"><i class="fa fa-trash" aria-hidden="true" @click="deleteDialog(scope.row,scope.$index)"></i></a>
			    	</template>
			    </el-table-column>
		  	</el-table>
		  	<el-pagination
                background
                @size-change="pageSizeChange"
                @current-change="currentPageChange"
                :current-page="tableInfo.pageId"
                :page-sizes="tableInfo.pageSizes"
                :page-size="tableInfo.pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="tableInfo.dataSize" class="paginationWrapper">
            </el-pagination>
		</section>
		<el-dialog
		  title="删除记录"
		  :visible.sync="dialogInfo.visible"
		  width="600px"
		  v-loading="dialogInfo.loading" element-loading-spinner="el-icon-loading" element-loading-text="正在加载">
		  <div class="item" v-if="dialogInfo.flag">
		  		<span style="color: #CD270D;">是否删除</span>
			  <el-table :data="dialogInfo.data" border>
			    <el-table-column property="date" label="日期"></el-table-column>
			    <el-table-column property="channelName" label="渠道"></el-table-column>
			    <el-table-column property="channel" label="渠道号"></el-table-column>
			    <el-table-column property="cost" label="成本金额"></el-table-column>
			  </el-table>
			  <p>删除后不可恢复，点击后“确认”删除，点击“取消”返回录入历史页面</p>
		  </div>
		  <p v-if="!dialogInfo.flag">是否<span style="color: #CD270D;">批量删除</span>选中数据，删除后不可恢复。点击“确认”删除，点击“取消”关闭窗口，返回成本录入历史页面。</p>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="dialogInfo.visible = false">取 消</el-button>
		    <el-button type="primary" @click="deleteItemFun">确 定</el-button>
		  </span>
		</el-dialog>
	</div>
</template>
<script>
import {getCostRecordReg,getChannelParasReg,deleteCostRecordReg} from '../../api/api.js';
import {timeStempToDatetime,dateFormatter} from '../../api/formatter.js';
	export default {
		data() {
			return {
				userperadmin: {},
				first: 0,
				searchInfo: {
					channelNameSelected: '',
					channelNameList: [],
					channelSelected: '',
					channelList: [],
					dateRange: [],
					start: '',
					end: '',
					loading: false,
					first: 0,
				},
				tableInfo: {
					tableData: [],
					pageId: 1,
					pageSize: 10,
					pageSizes: [10, 20, 50, 100, 500, 1000],
					dataSize: 0,
					first: 1,
				},
				deleteInfo: {
					data: []
				},
				loading: false,
				dialogInfo: {
					flag: true,
					visible: false,
					data: []
				},
				deleteHistory:false//删除成本录入历史
			}
		},
		mounted() {
			 // 获取对操作是否限权
			 let userPermission = JSON.parse(unescape(sessionStorage.getItem('userPermiss')));
             console.log(userPermission);
             let _this = this;
             // 对渠道商管理中操作限权进行循环读取状态
             for (var i = 0; i <userPermission.data.length; i++) {
             	if (userPermission.data[i].meta == '04') {
             		for (var j = 0; j <userPermission.data[i].children.length; j++) {
             			if (userPermission.data[i].children[j].meta == '0402') {
             				for (var m = 0; m < userPermission.data[i].children[j].children.length; m++) {
             					console.log(userPermission.data[i].children[j].children[m].meta)
             					if (userPermission.data[i].children[j].children[m].meta == '040201') {
             						// 获取删除成本录入历史权限值
             						_this.deleteHistory = userPermission.data[i].children[j].children[m].show == '0'?false:true;
             					};
             				};
             				
             			};
             		};
             	};
             	
             };
			this.userperadmin = JSON.parse(localStorage.getItem('userperadmin'));
			this.getSearchParamsFun();
		},
		methods: {
			deleteMuchFun() {
				this.dialogInfo.flag = false;
				this.dialogInfo.visible = true;
			},
			deleteDialog(row,index){
				console.log(row);
				this.dialogInfo.flag = true;
				this.dialogInfo.visible = true;
				this.dialogInfo.data = [row];
				this.deleteInfo.data = [{
					date: row.date,
					channel: row.channel
				}]
			},
			deleteItemFun() {
				var parameter = {
					sessionid: this.userperadmin.sessionid,
					deletions: this.deleteInfo.data
				};
				console.log('deleteItemFun',parameter);
				this.dialogInfo.loading = true;
				deleteCostRecordReg(parameter).then((res)=>{
			  		console.log('deleteCostRecordReg',res);
			  		this.dialogInfo.loading = false;
			  		this.dialogInfo.visible = false;
			  		if(res.data.status === 0) {
			  			this.first = 0;
			  			this.toast('删除成功', 'success');
			  			this.getCostRecordFun();
			  		}else if(res.data.status === 1){
			  			this.$router.replace("/login");
			  		}else {
			  			this.toast(res.data.statusMsg, 'error');
			  		}
			  	}).catch((error)=>{
			  		this.dialogInfo.loading = false;
			  		this.toast('服务器错误，请联系管理员','error');
				});
			},
			selectRow(selection, row) {
				//console.log(selection);
				this.deleteInfo.data = [];
				for(var i=0; i<selection.length; i++){
					var aSelection = {
						date: selection[i].date,
						channel: selection[i].channel
					}
					this.deleteInfo.data.push(aSelection);
				}
			},
			selectAll(selection) {
				//console.log(selection);
				this.deleteInfo.data = [];
				for(var i=0; i<selection.length; i++){
					var aSelection = {
						date: selection[i].date,
						channel: selection[i].channel
					}
					this.deleteInfo.data.push(aSelection);
				}
				console.log(this.deleteInfo.data);
			},
			indexFormatter(index) {
				var newIndex;
				newIndex = this.tableInfo.pageSize * (this.tableInfo.pageId - 1) + 1 + index;
				return newIndex;
			},
			pageSizeChange(pageSize) {
				this.tableInfo.pageId = 1;
				this.tableInfo.pageSize = pageSize;
				this.first = this.tableInfo.first;
				this.getCostRecordFun();
			},
			currentPageChange(currentPage) {
				this.tableInfo.pageId = currentPage;
				this.first = this.tableInfo.first;
				this.getCostRecordFun();
			},
			searchForHistoryFun() {
				/**
				 * 获取查询参数列表
				 */
				//if(this.searchInfo.channelNameSelected === '' && this.searchInfo.channelSelected === '')
				if(this.searchInfo.dateRange.length !== 0){
					this.searchInfo.dateRange[0] = dateFormatter(this.searchInfo.dateRange[0]);
					this.searchInfo.dateRange[1] = dateFormatter(this.searchInfo.dateRange[1]);
				}
				this.getCostRecordFun();
			},
			getSearchParamsFun() {
				/**
				 * 获取查询参数列表
				 */
				var parameter = {
					sessionid: this.userperadmin.sessionid
				};
				this.searchInfo.loading = true;
				getChannelParasReg(parameter).then((res)=>{
			  		console.log('getChannelParasReg',res);
			  		this.searchInfo.loading = false;
			  		if(res.data.status === 0) {
			  			this.searchInfo.channelNameList = res.data.channelNameList;
			  			this.searchInfo.channelList = res.data.channelNoList;
			  		}else if(res.data.status === 1){
			  			this.$router.replace("/login");
			  		}else {
			  			this.toast(res.data.statusMsg, 'error');
			  		}
			  	}).catch((error)=>{
			  		this.searchInfo.loading = false;
			  		this.toast('服务器错误，请联系管理员','error');
				});
			},
			getCostRecordFun() {
				/**
				 * 获取记录
				 */
				var parameter = {
					sessionid: this.userperadmin.sessionid,
					pageId: this.tableInfo.pageId - 1,
					pageSize: this.tableInfo.pageSize,
					first: this.first
				};
				if(this.searchInfo.channelNameSelected.replace(/[-]/g,'') !== ''){
					parameter['channelName'] = this.searchInfo.channelNameSelected.replace(/[-]/g,'');
				}
				if(this.searchInfo.channelSelected.replace(/[-]/g,'') !== ''){
					parameter['channel'] = this.searchInfo.channelSelected.replace(/[-]/g,'');
				}
				if(this.searchInfo.channelSelected.replace(/[-]/g,'') !== ''){
					parameter['channel'] = this.searchInfo.channelSelected.replace(/[-]/g,'');
				}
				if(this.searchInfo.dateRange[0] !== undefined){
					parameter['minDate'] = this.searchInfo.dateRange[0];
				}
				if(this.searchInfo.dateRange[1] !== undefined){
					parameter['maxDate'] = this.searchInfo.dateRange[1];
				}
				console.log('getCostRecordReg parameter',parameter);
				this.loading = true;
				getCostRecordReg(parameter).then((res)=>{
			  		console.log('getCostRecordReg',res);
			  		this.loading = false;
			  		if(res.data.status === 0) {
			  			this.tableInfo.tableData = res.data.costList;
			  			if(res.data.listSize){
			  				this.tableInfo.dataSize = parseInt(res.data.listSize);
			  			}
			  		}else if(res.data.status === 1){
			  			this.$router.replace("/login");
			  		}else {
			  			this.toast(res.data.statusMsg, 'error');
			  		}
			  	}).catch((error)=>{
			  		this.loading = false;
			  		this.toast('服务器错误，请联系管理员','error');
				});
			},
			toast(msg, type) {
				this.$message({
		            type: type,
		            message: msg
		        });
			}
		}
	}
</script>
<style lang="scss" scoped>
.clearfix{
	zoom: 1;
	&:after{
		display: block;
		height: 0;
		content: "";
		overflow: hidden;
		clear: both;
	}
}
.page{
	text-align: left;
	.bread-nav{
		font-size: .6rem;
		color: #999;
		border-left: 4px solid #324057;
		padding-left: .5rem;
		font-weight: normal;
		margin-bottom: 10px;
		.link{
			color: #409EFF;
			cursor: pointer;
		}
		.item:before{
			content: ">";
			display: inline-block;
			padding-left: 2px;
			padding-right: 2px;
		}
	}
	.table-wrapper{
		margin-top: 20px;
	}
	.success{
		color: #39B64D;
	}
	.fileUploadBtn{
		display: inline-block;
		position: relative;
		overflow: hidden;
		vertical-align: middle;
		.text{
			display: inline-block;
			width: 100%;
			height: 100%;
			line-height: 32px;
		}
		input.upload {
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			right: 0;
			margin: 0;
			padding: 0;
			font-size: 0;
			cursor: pointer;
			opacity: 0;
			filter: alpha(opacity=0);
		}
	}
	.btn{
		height: 32px;
		padding-left: 6px;
		padding-right: 6px;
		border: none;
		border-radius: 2px;
		font-size: 12px;
		outline: none;
		cursor: pointer;
		i{
			padding-right: 4px;
		}
		&.primary{
			background: #48A2FF;
			color: #fff;
		}
		&.error{
			background: #CD270D;
			color: #fff;
		}
		&.success{
			background: #39B64D;
			color: #fff;
		}
		&.info{
			background: #C3C4C3;
			color: #fff;
		}
	}
	.tooltip{
		font-size: 12px;
		margin-top: 10px;
		margin-bottom: 10px;
		color: #999;
		.start{
			color: #CD270D;
		}
	}
	.label{
		font-size: 12px;
		color: #666;
	}
	.fleft{
		float: left;
		margin-right: 10px;
	}
	.fright{
		float: right;
		margin-left: 10px;
	}
	.el-pagination{
		margin: 10px 0;
		text-align: center;
	}
}
</style>


