			    <el-table-column property="typeId" label="分成方式" :formatter="rewardTypeIdFormatter"></el-table-column>
			    <!-- <el-table-column property="price" label="分成比例|单价"></el-table-column> -->
			    <el-table-column prop="rewardPrice" label="分成比例 | 单价" width="200">
                	<template slot-scope="scope">
                		<p v-for="item in scope.row.rewardPrice" close-transition>{{item}}</p>
                    </template>
                </el-table-column>
			    <el-table-column property="date" label="分成起始时间" sortable :formatter="timeStempFormatter"></el-table-column>
			    <el-table-column property="adminName" label="负责人"></el-table-column>
			    <el-table-column property="date" label="任期起始时间" sortable :formatter="timeStempFormatter"></el-table-column>
			    <el-table-column property="updateAdminName" label="操作人"></el-table-column>
		  	</el-table>
            <p class="table-title mrgt">优化历史记录</span></p>
            <el-table :data="channelHistoryInfo.optiInfo.tableData" height="300" border style="width: 100%">
			    <el-table-column type="index" label="序号" width="50"></el-table-column>
			    <el-table-column property="optimization" label="优化比例" :formatter="rewardTypeIdFormatter"></el-table-column>
			    <el-table-column property="startDate" label="优化起始时间" sortable :formatter="timeStempFormatter1"></el-table-column>
			    <el-table-column property="updateDate" label="操作时间" sortable :formatter="timeStempFormatter2"></el-table-column>
			    <el-table-column property="adminName" label="操作人"></el-table-column>
		  	</el-table>
		</section>
		<el-dialog
		  title="切换渠道状态"
		  :visible.sync="channelStatusInfo.visible"
		  width="370px"
		  center>
		  <span>是否调整 <span style="color: #409EFF">{{searchChannelParams.parameter.proxyName}}（公司名称），{{channelStatusInfo.channelName}}（渠道名称），{{channelStatusInfo.channel}}（渠道号）</span>状态为<span style="color: red">{{channelStatusInfo.statusText}}</span>？点击确定<span style="color: red">“{{channelStatusInfo.statusText}}”</span>该渠道，点击取消返回渠道列表”</span>
		  <span slot="footer" class="dialog-footer">
		    <el-button type="primary" @click="saveChannelStatusFun">确 定</el-button>
		    <el-button class="info" @click="toggleStatusDialogFun">取 消</el-button>
		  </span>
		</el-dialog>
		<el-dialog title="切换渠道状态" :visible.sync="optiInfo.visible" width="400px" center v-loading="optiInfo.loading" element-loading-spinner="el-icon-loading" element-loading-text="拼命加载中">
		  <div>
		  	<div class="input-group required">
				<label for="channel">优化比例(%)</label>
				<input type="text" id="channel" v-model="actionChannelInfo.info.optiRate" maxLength="5" oninput="value=value.replace(/^[^0-9][^0-9\.]{0,}/g,'')">
			</div>
			<div class="input-group required">
				<label for="channel">优化起始时间</label>
				<el-date-picker v-model="actionChannelInfo.info.optiTime" type="date" placeholder="选择优化起始时间"></el-date-picker>
			</div>
			<div class="input-group required">
				<label>是否联动</label>
				<el-radio-group v-model="actionChannelInfo.info.linkage">
				    <el-radio :label="1">是</el-radio>
				    <el-radio :label="0">否</el-radio>
				</el-radio-group>
			</div>
			<div class="input-group required">
				<label>优化内容</label>
				<el-checkbox-group v-model="actionChannelInfo.info.optiContent">
				    <el-checkbox label="优化比例"></el-checkbox>
				    <el-checkbox label="优化起始时间"></el-checkbox>
				</el-checkbox-group>
			</div>
		  </div>
		  <span slot="footer" class="dialog-footer">
		    <el-button type="primary" @click="saveChannelOptiFun">确 定</el-button>
		    <el-button class="info" @click="toggleOptiDialogFun">取 消</el-button>
		  </span>
		</el-dialog>
		<el-dialog title="分成详情" center width="400" :visible.sync="firstLevelChannelRewardInfo.visible">
  			<el-table :data="firstLevelChannelRewardInfo.tableData" height="300" border style="width: 100%"  v-loading="firstLevelChannelRewardInfo.loading" element-loading-spinner="el-icon-loading" element-loading-text="拼命加载中">
			    <el-table-column property="appid" label="产品" :formatter="appFormatter"></el-table-column>
			    <el-table-column property="typeId" label="分成方式" :formatter="rewardTypeIdFormatter"></el-table-column>
			    <el-table-column prop="rewardPrice" label="分成比例 | 单价" width="200">
                	<template slot-scope="scope">
                		<p v-for="(item,index) in scope.row.rewardPrice" close-transition :key="index">{{item}}</p>
                    </template>
                </el-table-column>
		  	</el-table>
		</el-dialog>
		<el-dialog title="渠道状态历史记录" center width="400" :visible.sync="channelStatusHistory.visible">
  			<el-table :data="channelStatusHistory.list" height="300" border style="width: 100%"  v-loading="channelStatusHistory.loading" element-loading-spinner="el-icon-loading" element-loading-text="拼命加载中">
			    <el-table-column type="index" label="序号" :index="indexFormatter2"></el-table-column>
			    <el-table-column prop="log" label="备注"></el-table-column>
			    <el-table-column prop="updateDate" label="日期" :formatter="timeStempFormatter3"></el-table-column>
		  	</el-table>
		</el-dialog>
	</div>
</template>
<script>
import {getProxyNameListReg,getProxyInfoListReg,getChannelPermissionReg,getChannelParasReg,getChannelListReg,getEditChannelSelectListReg,getRewardListReg,getOptimizationInfoReg,saveOptimizationInfoReg,getfcHistoryReg,toggleChannelStatusReg, getPayCompanyReg, getRewardMainReg, getChannelHistoryReg} from '../../api/api.js';
import {dateFormatter,timeStempToDate,timeStempToDatetime} from '../../api/formatter.js'
	var RewardTypeInfo = {};
	export default {
		data() {
			return {
				userperadmin: {},
				breadNavInfo: {
					current1: 1,
					level1Items: ['查看渠道商信息','渠道管理'],
					current2: '',
					level2Items: ['编辑渠道信息','渠道历史记录']
				},
				searchProxyParams: {
					loading: false,
					selected: '',
					list: []
				},
				proxyInfo: {
					loading: false,
					first: 0,
					tableData: [],
					pagination: {
						currentPage: 1,
						pageSize: 10,
						pageSizes: [10,20,50,100,200,500,1000],
						dataSize: 0
					},
					actionInfo: {
						loading: false,
						proxyName: '',
						bankAccount: '',
						bank: '',
						dutyParagraph: '',
						phone: '',
						address: '',
						contacts: '',
						mobileno: '',
						email: ''
					},
					chartPermissionInfo: {
						registerChartPermission: false,
						loginChartPermission: false,
						applyChartPermission: false,
						loanAcctChartPermission: false,
						cashNumCharPermission: false,
						firstCashAmtChartPermission: false,
						totalCashAmtChartPermission: false
					}
				},
				// 一级渠道的分成信息
				firstLevelChannelRewardInfo: {
					visible: false,
					loading: false,
					tableData: [],
					editData: []
				},
				searchChannelParams: {
					loading: false,
					parameter: {
						proxyName: '',
						proxyId: '',
						channelName: '',
						channel: '',
						level: '',
						admin: '',
						status: '',
						appSelected: ''
					},
					channelNameList: [],
					channelList: [],
					levelList: [
						{name: '全部渠道', value: ''},
						{name: '一级渠道', value: 1},
						{name: '二级渠道', value: 2}
					],
					adminList: [],
					stateList: [
						{name: '全部状态', value: ''},
						{name: '启用', value: 1},
						{name: '禁用', value: 0}
					]
				},
				channelInfo: {
					loading: false,
					first: 0,
					tableData: [],
					pagination: {
						currentPage: 1,
						pageSize: 10,
						pageSizes: [10,20,50,100,500,1000],
						dataSize: 0
					}
				},
				actionChannelInfo: {
					loading: false,
					info: {
						channelId: '',         // 渠道ID
						optimizationId: '',    // 优化ID
						proxyName: '',         // 代理商名称（渠道商名称）
						level: '',             // 渠道级别
						channelName: '',       // 渠道名称
						channelNo: '',         // 渠道编码
						channel: '',           // 渠道号
						isCreate: '',          // 是否可创建子渠道
						appSelected: '',       // 所属APP
						attributeSelected: '', // 渠道属性
						typeSelected: '',      // 渠道类型
						divisionSelected: '',  // 渠道细分
						url: '',               // 推广链接
						fcTypeSelected: '',    // 分成方式
						isStep: '',            // 是否阶梯
						stepData: '',          // isStep = 0：不是阶梯时的分成单价（比例）
						rewards: '',           // isStep = 1：阶梯时，分成阶梯数据
						cycleSelected: '',     // 结算周期
						costSelected: '',      // 成本计算方式
						gitTimestartAndEnd:'', //起始时间
						adminSelected: '',     // 负责人
						synchronous: 0,        // 是否实时同步
						linkage: '',            // 是否联动
						optiContent: [],           // 优化比例
						optiRate: '',          // 优化比例
						optiRateOld: '',       // 优化比例
						optiTime: '',          // 优化起始时间
						optiTimeOld: ''        // 优化起始时间
					},
					selectListInfo: {
						appList: [],
						attributeList: [],     // 渠道属性选择列表
						typeList: [],          // 渠道类型选择列表
						divisionList: [],      // 渠道细分选择列表
						fcTypeList: [],        // 分成方式选择列表
						cycleList: [],         // 结算周期选择列表
						costList: [],          // 成本计算方式选择列表
						adminList: []          // 负责人选择列表
					},
					rewardInfo: {
						list: [],              // 阶梯数据列表
						temp: [],
						editList: [],           // 保存编辑时获取到的分成列表,
						fcUnitText: ''
					}
				},
				// 一级渠道的分成数据(显示)
				firstLevelFcDataList: [],
				// 一级渠道的分成数据(提交)
				mainReward: {
					channelId: '',
					data: {}
				},
				channelHistoryInfo: {
					channelName: '',
					fcInfo: {
						loading: false,
						tableData: []
					},
					optiInfo: {
						loading: false,
						tableData: []
					}
				},
				channelStatusInfo: {
					visible: false,
					statusText: '',
					channelName: '',
					channelId: '',
					channel: '',
					status: ''
				},
				optiInfo: {
					loading: false,
					visible: false
				},
				channelStatusHistory: {
					visible: false,
					loading: false,
					list: []
				},
				qudaoAdminStart:false,//渠道管理->启用
				qudaoAdminEdit:false,//渠道管理->编辑
				qudaoAdminHistory:false,//渠道管理->历史
				qudaoAdminProOpt:true//渠道管理->优化比例

			}
		},
		mounted() {
			setTimeout(()=>{
				try {
				// 获取对操作是否限权
				 let userPermission = JSON.parse(unescape(sessionStorage.getItem('userPermiss')));
				 console.log('------------------')
	             console.log(userPermission);
	             let _this = this;
	             // 对渠道商管理中操作限权进行循环读取状态
	             // 注：有的权限对象没有子对象所以要首先判断子对象是否存
	             for (var i = 0; i <userPermission.data.length; i++) {
	             	if (userPermission.data[i].meta == '01') {
	             		if (userPermission.data[i].children && userPermission.data[i].children.length>0) {
                            for (var j = 0; j <userPermission.data[i].children.length; j++) {
		             			if (userPermission.data[i].children[j].meta == '0102') {
		             				if (userPermission.data[i].children[j].children && userPermission.data[i].children[j].children.length>0) {
		             					for (var m = 0; m < userPermission.data[i].children[j].children.length; m++) {
			             					console.log(userPermission.data[i].children[j].children[m].meta)
			             					if (userPermission.data[i].children[j].children[m].meta == '010201') {
			             						// 获取渠道管理->启用权限值
			             						_this.qudaoAdminStart = userPermission.data[i].children[j].children[m].show == '0'?false:true;
			             					} else if (userPermission.data[i].children[j].children[m].meta == '010202') {
			             						// 获取渠道管理->编辑权限值
			             						_this.qudaoAdminEdit = userPermission.data[i].children[j].children[m].show == '0'?false:true;
			             					} else if (userPermission.data[i].children[j].children[m].meta == '010203') {
			             						// //渠道管理->历史权限值
			             						_this.qudaoAdminHistory = userPermission.data[i].children[j].children[m].show == '0'?false:true;
			             					} else if (userPermission.data[i].children[j].children[m].meta == '010204') {
			             						// //渠道管理->优化比例
			             						_this.qudaoAdminProOpt = userPermission.data[i].children[j].children[m].show == '0'?false:true;
			             					}
			             				}
		             				}
		             				
		             				
		             			}
		             		}
	             		}
	             	};
	             	
	             };
			} catch(e) {
				// statements
				console.log(e);
			}
			this.userperadmin = JSON.parse(localStorage.getItem('userperadmin'));
			this.getProxyNameListFun(); //初始化渠道商下拉列表数据
			//this.getProxyInfoListFun(); //初始化渠道商表格数据
			this.getChannelParamsFun();
			this.getChannelListFun();
			this.getEditChannelSelectListFun();
			},300)
		},
		computed: {
			fcText() {
				var fcType = this.actionChannelInfo.info.fcTypeSelected;
				switch(fcType){
					case 26 :
						this.actionChannelInfo.rewardInfo.fcUnitText = '元';
						return '注册用户';
					case 27 :
						this.actionChannelInfo.rewardInfo.fcUnitText = '元';
						return '首提用户';
					case 28 :
						this.actionChannelInfo.rewardInfo.fcUnitText = '%';
						return '分成比例';
					case 29 :
						this.actionChannelInfo.rewardInfo.fcUnitText = '元';
						return '开户成功用户数';
				}
			}
		},
		filters: {
			fcNumberFilter(val){
				/**
				 * 转换分成数量
				 */
				for(var i=0; i<RewardTypeInfo.length; i++){
					if(val === RewardTypeInfo[i].id){
						var options = RewardTypeInfo[i].other.split(',');
						return options[0];
					}
				}
			},
			fcNumber1Filter(val){
				/**
				 * 转换分成数量
				 */
				for(var i=0; i<RewardTypeInfo.length; i++){
					if(val === RewardTypeInfo[i].id){
						var options = RewardTypeInfo[i].other.split(',');
						return options[1];
					}
				}
			},
			fcNumber2Filter(val){
				/**
				 * 转换分成数量
				 */
				for(var i=0; i<RewardTypeInfo.length; i++){
					if(val === RewardTypeInfo[i].id){
						var options = RewardTypeInfo[i].other.split(',');
						return options[2];
					}
				}
			},
			rewardTypeIdFilter(val) {
				/**
				 * 分成方式格式化
				 */
				for(var i=0; i<RewardTypeInfo.length; i++){
					if(val === RewardTypeInfo[i].id){
						return RewardTypeInfo[i].name;
					}
				}
			},
			toggleStatus(val) {
				console.log(val);
				if(val === 1){
					return '启用';
				}else{
					return '禁用';
				}
			},
			fcFormatter(val) {
				if(val){
					return parseInt(val.max) + 1;
				}else{
					return 0;
				}
			},
			channelStatusFormatterSet(val) {
				if(val === 1){
					return '已启用';
				}else{
					return '已禁用';
				}
			},
			optiTimeFormmater(val) {
				if(val !== 0){
					return timeStempToDate(val);
				}else{
					return '';
				}
			}
		},
		methods: {
			getChannelStatusHistory(row, index) {
				console.log(row);
				// 获取渠道状态历史记录
				var parameter = {
					sessionid: this.userperadmin.sessionid,
					channelId: row.id
				};
				this.channelStatusHistory.visible = true;
				this.channelStatusHistory.loading = true;
				this.channelStatusHistory.list = [];
				getChannelHistoryReg(parameter).then((res)=>{
					console.log('getChannelStatusHistory', res)
					if(res.data.status == 0) {
						this.channelStatusHistory.loading = false;
						this.channelStatusHistory.list = res.data.channelHistories;
						this.channelStatusHistory.list.reverse();
					}else if(res.data.status == 1){
						this.$router.replace("/login");
					}else{
						this.toast(res.data.statusMsg,'error');
					}
				}).catch((err) => {
					this.channelStatusHistory.loading = false;
					console.log(err);
			  		this.toast('服务器错误，请联系管理员','error');
				});
			},
			getPayCompanyFun(proxyid, from, row) {
				/*
				 * 获取付款公司列表
				 * 调用来源
				 * （2）渠道管理页面：点击一级渠道的分成【详情】按钮，from = 'fromChannel'
				 * （3）编辑|新增一级渠道时，查询付款公司对应的产品
				 */
				var parameter = {
					sessionid: this.userperadmin.sessionid,
					proxyid: proxyid
				};
				console.log('getPayCompanyReg', parameter);
				getPayCompanyReg(parameter).then((res)=>{
					console.log('getPayCompanyReg', res);
					var payCompanyList = res.data.payCompanyList;
		  			var appList = this.actionChannelInfo.selectListInfo.appList;
		  			var payForAppList = [];
		  			var idArray = [];
		  			for(var i=0; i<payCompanyList.length; i++){
		  				for(var j=0; j<appList.length; j++){
		  					if(payCompanyList[i].appid === appList[j].id){
		  						if(idArray.includes(payCompanyList[i].appid)){
		  							continue;
		  						}
		  						idArray.push(payCompanyList[i].appid);
		  						payForAppList.push({
		  							id: appList[j].id,
		  							name: appList[j].name
		  						});
		  					}
		  				}
		  			}
		  			// console.log( this.appInfo.payForAppList);
		  			if(from === 'fromActionChannel') {
						// 编辑渠道时一级渠道的分成详情
		  				this.getRewardMainFun(row, payForAppList);
		  			}else{
						// 表格中一级渠道的分成详情
		  				this.getFirstLevelRewardListFun(row, payForAppList);
		  			}
			  	}).catch((error)=>{
			  		console.log(error);
			  		this.toast('服务器错误，请联系管理员','error');
				});
			},
			getFirstLevelRewardListFun(row, selectedAppList) {
				/*
				 * 获取一级渠道的分成数据
				 */
				var appListArr = [];
				for(var i=0; i<selectedAppList.length; i++){
					appListArr.push(selectedAppList[i].id);
				}
				var parameter = {
					sessionid: this.userperadmin.sessionid,
					channelId: row.id,
					appid: appListArr.toString()
				};
				this.firstLevelChannelRewardInfo.visible = true;
				console.log('getFirstLevelRewardListFun parameter', parameter);
				getRewardListReg(parameter).then((res)=>{
					console.log('getFirstLevelRewardListFun', res);
					if(res.data.status === 0){
						var _this = this;
						this.firstLevelChannelRewardInfo.tableData = [];
						res.data.rewardList.forEach(function(item){
							item.rewardPrice = _this.rewardPriceFormatter(item.rewardPrice);
							_this.firstLevelChannelRewardInfo.tableData.push(item);
						});
					}else if(res.data.status === 1){
			  			this.$router.replace("/login");
			  		}else {
			  			this.toast(res.data.statusMsg, 'error');
			  		}
			  		this.firstLevelChannelRewardInfo.loading = false;
				}).catch((error)=>{
			  		console.log('error',error);
			  		this.firstLevelChannelRewardInfo.loading = false;
			  		this.toast('服务器错误，请联系管理员','error');
				});
			},
			getRewardMainFun(row, selectedAppList) {
				/**
				 * 编辑一级渠道信息时：获取一级渠道的分成信息（编辑时）
				 */
				var appidStr = [];
				console.log(selectedAppList);
				for(var i=0; i<selectedAppList.length; i++){
					appidStr.push(selectedAppList[i].id);
				}
				appidStr = appidStr.toString();
				var parameter = {
					sessionid: this.userperadmin.sessionid,
					channelId: row.id,
					appid: appidStr
				};
				console.log('getRewardMainFun',parameter);
				getRewardMainReg(parameter).then((res)=>{
					console.log('getRewardMainFun',res);
					if(res.data.status === 0) {
						// 显示获取分成信息时间zhou
						for (var key in res.data.mainReward) {
							if (res.data.mainReward[key][0].date) {
						       this.actionChannelInfo.info.gitTimestartAndEnd = res.data.mainReward[key][0].date;
							}else {
								this.actionChannelInfo.info.gitTimestartAndEnd = '';
							}
						}
						var fcDataList = res.data.mainReward;
						// 清空数据
						this.firstLevelFcDataList.splice(0);
						// 获取appid
						var appidList = Object.keys(fcDataList);
						// 保存一级渠道id：提交信息时用
						this.mainReward.channelId = row.id;
						console.log(appidList);
						for(var i=0; i<appidList.length; i++){
							var mainTemp;
							if(fcDataList[appidList[i]].length !== 0) {
