			rewardTypeIdFormatter(row, column, cellValue) {
				/**
				 * 分成方式格式化
				 */
				if(cellValue === 26){
					return 'CPA单价';
				}else if(cellValue === 27){
					return 'CPS单价';
				}else if(cellValue === 28){
					return 'CPS比例';
				}else{
					return cellValue;
				}
			},
			rewardPriceFormatter(cellValue,flag) {
				/**
				 * 分成比例格式化
				 */
				if(cellValue){
					var temp = cellValue.split(',');
					var tempArray = [];
					var tempArray1 = [];
					if(temp.length <= 1){
						if(flag === 1){
							var notFc = cellValue.split('/');
							tempArray.push(notFc[1]);
						}else{
							tempArray.push(temp[0]);
						}
					}else {
						this.actionChannelInfo.rewardInfo.temp = temp;
						temp.forEach(function (item,index) {
							var temp1 = item.split('/');
							tempArray1.push({
								max: temp1[0],
								price: temp1[1]
							});
							if(index === 0){
								tempArray[index] = '0 - ' + temp1[0] + ' : ' + temp1[1];
							}else {
								tempArray[index] = (parseInt(tempArray1[index-1].max) + 1) + ' - ' + temp1[0] + ' : ' + temp1[1];
							}
						});
					}
					return tempArray;
				}
			},
			selectedFormatter(index) {
				/**
				 * 格式化渠道选择列表
				 */
				if(index === 1){
					this.searchChannelParams.parameter.channelName = this.searchChannelParams.parameter.channelName.replace(/[-]/g,'');
				}else{
					this.searchChannelParams.parameter.channel = this.searchChannelParams.parameter.channel.replace(/[-]/g,'');
				}
			},
			getChannelParamsFun() {
				/**
				 * 获取渠道查询条件列表
				 */
				var parameter = {
					sessionid: this.userperadmin.sessionid
					//proxyId: this.searchChannelParams.parameter.proxyId
				};
				console.log('getChannelParamsFun',parameter);
				this.searchChannelParams.loading = true;
				getChannelParasReg(parameter).then((res)=>{
			  		console.log('getChannelParamsFun',res);
			  		this.searchChannelParams.loading = false;
			  		if(res.data.status === 0) {
			  			this.searchChannelParams.channelNameList = res.data.channelNameList;
			  			this.searchChannelParams.channelList = res.data.channelNoList;
			  			if(res.data.adminIdList && res.data.adminIdList.length !== 0){
			  				var _this = this;
			  				_this.searchChannelParams.adminList = [];
			  				try{
			  					res.data.adminIdList.forEach(function(item,index){
				  					var temp1 = item.split(',');
				  					var temp2 = {
				  						id: temp1[0],
				  						name: temp1[1]
				  					};
				  					_this.searchChannelParams.adminList.push(temp2);
				  				});
			  				}catch(err){
			  					console.log(err);
			  				}
			  			}
			  		}else if(res.data.status === 1){
			  			this.$router.replace("/login");
			  		}else {
			  			this.toast(res.data.statusMsg, 'error');
			  		}
			  	}).catch((error)=>{
			  		this.searchChannelParams.loading = false;
			  		this.toast('服务器错误，请联系管理员','error');
				});
			},
			searchChannelListFun() {
				this.channelInfo.first = 0;
				this.getChannelListFun();
			},
			getChannelListFun(isInitial) {
				/**
				 * 获取对应渠道商下的渠道列表
				 */
				var parameter = {
					sessionid : this.userperadmin.sessionid,
					//proxyId: this.searchChannelParams.parameter.proxyId,
					first: this.channelInfo.first,
					pageId: this.channelInfo.pagination.currentPage - 1,
					pageSize: this.channelInfo.pagination.pageSize
				};
				if(this.searchChannelParams.parameter.channelName !== undefined && this.searchChannelParams.parameter.channelName !== ''){
					parameter['channelName'] = this.searchChannelParams.parameter.channelName.replace(/[-]/g,'');
				}
				if(this.searchChannelParams.parameter.channel !== undefined && this.searchChannelParams.parameter.channel !== ''){
					parameter['channel'] = this.searchChannelParams.parameter.channel.replace(/[-]/g,'');
				}
				if(this.searchChannelParams.parameter.level !== undefined && this.searchChannelParams.parameter.level !== ''){
					parameter['level'] = this.searchChannelParams.parameter.level;
				}
				if(this.searchChannelParams.parameter.admin !== undefined && this.searchChannelParams.parameter.admin !== ''){
					parameter['adminId'] = this.searchChannelParams.parameter.admin;
				}
				if(this.searchChannelParams.parameter.status !== undefined && this.searchChannelParams.parameter.status !== ''){
					parameter['status'] = this.searchChannelParams.parameter.status;
				}
				if(this.searchChannelParams.parameter.appSelected !== undefined && this.searchChannelParams.parameter.appSelected !== ''){
					parameter['appid'] = this.searchChannelParams.parameter.appSelected;
				}
				console.log('getChannelListFun parameter',parameter);
				this.channelInfo.loading = true;
				//this.channelInfo.tableData = [];
				getChannelListReg(parameter).then((res)=>{
			  		console.log('getChannelListFun',res);
			  		this.channelInfo.loading = false;
			  		if(res.data.status === 0) {
			  			if(res.data.channelList) {
			  				var _this = this;
			  				this.channelInfo.tableData = res.data.channelList;
			  				this.channelInfo.tableData.forEach(function(item,index,arr) {
			  					//_this.channelInfo.tableData.push(item);
			  					//_this.channelInfo.tableData[index].rewardPrice = _this.rewardPriceFormatter(item.rewardPrice);
			  					arr[index].rewardPrice = _this.rewardPriceFormatter(item.rewardPrice);
			  				});
			  				if(isInitial === 1){
			  					this.channelInfo.pagination.dataSize = parseInt(res.data.listSize);
			  				}
			  			}
			  			if(res.data.listSize && this.channelInfo.first !== 1){
			  				this.channelInfo.pagination.dataSize = parseInt(res.data.listSize);
			  			}
			  		}else if(res.data.status === 1){
			  			this.$router.replace("/login");
			  		}else {
			  			this.toast(res.data.statusMsg, 'error');
			  		}
			  	}).catch((error)=>{
			  		//console.log('error',error);
			  		this.channelInfo.loading = false;
			  		this.toast('服务器错误，请联系管理员','error');
				});
			},
			channelTablePageSizeChange(val) {
				/**
				 * 渠道变革分页查询
				 */
				console.log('channelTablePageSizeChange');
				this.channelInfo.first = 1;
				this.channelInfo.pagination.pageSize = val;
				this.channelInfo.pagination.currentPage = 1;
				this.getChannelListFun();
			},
			channelTableCurrentPageChange(val) {
				console.log('channelTableCurrentPageChange');
				this.channelInfo.first = 1;
				this.channelInfo.pagination.currentPage = val;
				this.getChannelListFun();
			},
			toggleBreadNav(level,current) {
				/**
				 * 切换面包屑导航
				 */
				console.log(level,current);
				this.breadNavInfo[level] = current;
				if(level === 'current1'){
					this.breadNavInfo.current2 = '';
					if(current === ''){
						this.searchChannelParams.parameter.channelName = '';
						this.searchChannelParams.parameter.channel = '';
						this.searchChannelParams.parameter.level = '';
						this.searchChannelParams.parameter.admin = '';
						this.searchChannelParams.parameter.status = '';
					}
				}
				this.resetChannelInfoFun();
			},
			backToProxyListFun() {
				/**
				 * 返回渠道商列表
				 */
				this.toggleBreadNav('current1','');
				this.resetProxyInfoFun();
			},
			proxyPageSizeChange(val) {
				/**
				 * 渠道商表格显示记录数变化
				 */
				this.proxyInfo.first = 1;
				this.proxyInfo.pagination.currentPage = 1;
				this.proxyInfo.pagination.pageSize = val;
				this.getProxyInfoListFun();
			},
			proxyCurrentPageChange(val) {
				/**
				 * 渠道商当前页码变化
				 */
				this.proxyInfo.first = 1;
				this.proxyInfo.pagination.currentPage = val;
				this.getProxyInfoListFun();
			},
			getProxyNameListFun() {
				/**
				 * 获取渠道商名称列表
				 */
				var parameter = {
					sessionid: this.userperadmin.sessionid
				};
				// console.log('getProxyNameListFun parameter',parameter);
				this.searchProxyParams.loading = true;
				getProxyNameListReg(parameter).then((res)=>{
			  		// console.log('getProxyNameListFun',res);
			  		this.searchProxyParams.loading = false;
			  		if(res.data.status === 0) {
			  			this.searchProxyParams.list = res.data.proxyList;
			  		}else if(res.data.status === 1){
			  			this.$router.replace("/login");
			  		}else {
			  			this.toast(res.data.statusMsg, 'error');
			  		}
			  	}).catch((error)=>{
			  		//console.log('getProxyNameListFun parameter',parameter);
			  		this.searchProxyParams.loading = false;
			  		this.toast('服务器错误，请联系管理员','error');
				});
			},
			getProxyInfoListFun(parameter) {
				/**
				 * 获取渠道商名称列表
				 */
				if(this.proxyInfo.loading){
					return;
				}
				if(!parameter){
					// 初始化请求
					var parameter = {
						sessionid : this.userperadmin.sessionid,
						first: this.proxyInfo.first,
						pageId: this.proxyInfo.pagination.currentPage - 1,
						pageSize: this.proxyInfo.pagination.pageSize
					};
					if(this.searchProxyParams.selected !== ''){
						var valueType = parseInt(this.searchProxyParams.selected);
						if(isNaN(valueType)){
							parameter.company = this.searchProxyParams.selected;
						}else{
							parameter.id = this.searchProxyParams.selected;
						}
					}
				}else{
					// 表格操作请求
					parameter.pageId = this.proxyInfo.pagination.currentPage - 1;
					parameter.pageSize = this.proxyInfo.pagination.pageSize
				}
				console.log('getProxyInfoListReg parameter',parameter);
				this.proxyInfo.loading = true;
				getProxyInfoListReg(parameter).then((res)=>{
			  		console.log('getProxyInfoListReg',res);
			  		this.proxyInfo.loading = false;
			  		if(res.data.status === 0) {
			  			this.proxyInfo.tableData = res.data.proxyList;
			  			if(res.data.listSize){
			  				this.proxyInfo.pagination.dataSize = parseInt(res.data.listSize);
			  			}
			  		}else if(res.data.status === 1){
			  			this.$router.replace("/login");
			  		}else {
			  			this.toast(res.data.statusMsg, 'error');
			  		}
			  	}).catch((error)=>{
			  		console.log('getProxyInfoListReg parameter',parameter);
			  		this.proxyInfo.loading = false;
			  		this.toast('服务器错误，请联系管理员','error');
				});
			},
			searchProxyInfoListFun() {
				/**
				 * 根据公司名称搜索渠道商表格数据
				 */
				var valueType = parseInt(this.searchProxyParams.selected);
				var parameter;
				if(this.searchProxyParams.selected === ''){
					// 查询全部
					this.proxyInfo.first = 0;
					this.getProxyInfoListFun();
				}else if(isNaN(valueType)){
					// 模糊查询
					parameter = {
						sessionid : this.userperadmin.sessionid,
						company: this.searchProxyParams.selected,
						first: 0
					};
					console.log('parameter',parameter);
					this.getProxyInfoListFun(parameter);
				}else {
					// 精确查询
					parameter = {
						sessionid : this.userperadmin.sessionid,
						id: this.searchProxyParams.selected,
						first: 0
					};
					console.log('parameter',parameter);
					this.getProxyInfoListFun(parameter);
				}
			},
			viewChannelListFun(row, index) {
				/**
				 * 查看渠道列表
				 */
				console.log('viewChannelListFun',row);
				this.toggleBreadNav('current1',1);
				this.searchChannelParams.parameter.proxyId = row.id;
				this.searchChannelParams.parameter.proxyName = row.company;
				this.getChannelParamsFun();
				this.getChannelListFun();
			},
			resetProxyInfoFun() {
				this.proxyInfo.actionInfo.proxyName = '';
				this.proxyInfo.actionInfo.bankAccount = '';
				this.proxyInfo.actionInfo.bank = '';
				this.proxyInfo.actionInfo.dutyParagraph = '';
				this.proxyInfo.actionInfo.phone = '';
				this.proxyInfo.actionInfo.address = '';
				this.proxyInfo.actionInfo.contacts = '';
				this.proxyInfo.actionInfo.mobileno = '';
				this.proxyInfo.actionInfo.email = '';
				this.proxyInfo.chartPermissionInfo = {
					registerChartPermission: false,
					loginChartPermission: false,
					applyChartPermission: false,
					loanAcctChartPermission: false,
					cashNumCharPermission: false,
					firstCashAmtChartPermission: false,
					totalCashAmtChartPermission: false
				}
			},
			viewProxyInfoFun(row, index) {
				/**
				 * 编辑渠道列表
				 */
				this.proxyInfo.actionInfo.loading = true;
				// console.log('渠道商信息',row);
				this.toggleBreadNav('current1',0);
				this.proxyInfo.actionInfo.proxyName = row.company;
				this.proxyInfo.actionInfo.bankAccount = row.bankAccount;
				this.proxyInfo.actionInfo.bank = row.bank;
				this.proxyInfo.actionInfo.dutyParagraph = row.dutyParagraph;
				this.proxyInfo.actionInfo.phone = row.phone;
				this.proxyInfo.actionInfo.address = row.address;
				this.proxyInfo.actionInfo.contacts = row.contacts;
				this.proxyInfo.actionInfo.mobileno = row.mobileno;
				this.proxyInfo.actionInfo.email = row.email;
				// 获取渠道商权限列表
				var parameter = {
					sessionid: this.userperadmin.sessionid,
					permissionId: row.permissionId
				};
				// console.log('getChannelPermissionReg parameter',parameter);
				getChannelPermissionReg(parameter).then((res)=>{
			  		// console.log('getChannelPermissionReg',res);
			  		this.proxyInfo.actionInfo.loading = false;
			  		if(res.data.status === 0) {
			  			this.proxyInfo.chartPermissionInfo = res.data.channelPermission;
			  			this.proxyInfo.chartPermissionInfo = this.permissionFormatter(this.proxyInfo.chartPermissionInfo, 1);
			  		}else if(res.data.status === 1){
			  			this.$router.replace("/login");
			  		}else {
			  			this.toast(res.data.statusMsg, 'error');
			  		}
			  	}).catch((error)=>{
			  		console.log(error);
			  		this.proxyInfo.actionInfo.loading = false;
			  		this.toast('服务器错误，请联系管理员','error');
				});
			},
			permissionFormatter(permissionList, type) {
				/**
				 * 权限转换
				 * type = 1 表示数值转布尔， = 2表示布尔转数值, 3表示计算true值的个数
				 */
				var keys = Object.keys(permissionList);
				var values = Object.values(permissionList);
				if(type === 1){
					values.forEach(function(item,index){
						if(keys[index] !== 'id'){
							if(item === 1){
								permissionList[keys[index]] = true;
							}else{
								permissionList[keys[index]] = false;
							}
						}
					});
				}else if(type === 2){
					values.forEach(function(item,index){
						if(keys[index] !== 'id'){
							if(item === true){
								permissionList[keys[index]] = 1;
							}else{
								permissionList[keys[index]] = 0;
							}
						}
					});
				}else if(type === 3){
					return values.includes(true);
				}else {
					// 重置
					var _this = this;
					values.forEach(function(item,index){
						if(keys[index] !== 'id'){
							_this.chartPermissionInfo[keys[index]] = false;
						}else {
							_this.chartPermissionInfo[keys[index]] = '';
						}
					});
				}
				return permissionList;
			},
			toast(msg, type) {
				this.$message({
		            type: type,
		            message: msg
		        });
			}
		}
	}
</script>
