						width: 100%;
						padding: .2rem 0;
						div:first-child{
							margin-bottom: .2rem;
						}
						span{
							display: inline-block;
							font-size: .5rem;
							color: #666;
							&.seperate{
								padding: 0 .2rem;
							}
							&.unit{
								color: red;
								padding-left: .2rem;
							}
						}
						input{
							width: 24%;
							padding: .4rem;
							border: 1px solid #DCDFE6;
							border-radius: .1rem;
							&[readonly=readonly]{
								background: #EDEFF2;
							}
						}
						
					}
				}
				button{
					padding: .3rem 1rem;
					border: none;
					background: #409EFF;
					color: #fff;
					border-radius: .1rem;
					&:last-child{
						margin-left: .4rem;
					}
				}
			}
		}
		.textAreaInput{
			font-size: 0;
			height: 3rem;
			line-height: 1.6rem;
			padding-right: 10%;
			label{
				display: inline-block;
				width: 30%;
				height: auto;
				text-align: right;
				font-size: .6rem;
				padding-right: 2%;
				box-sizing: border-box;
				white-spacing: nowrap;
				vertical-align: top;
			}
			textarea{
				height: 100%;
				width: 60%;
				box-sizing: border-box;
				border-radius: .1rem;
				border: 1px solid #DCDFE6;
				padding-left: 2%;
				resize: none;
				outline: none;
				&[readonly=readonly]{
					background: #f5f5f5;
				}
			}
		}
		.required{
			label{
				position: relative;
			}
			&>label:first-child{
				&:after{
					content: "*";
					display: inline-block;
					color: red;
					font-weight: bold;
				}
			}
		}
		.btns{
			margin-top: 20px;
			margin-bottom: 10px;
			text-align: center;
		}
	}
}

.landscape{
	.page{
		.breadnav{
			font-size: .6rem;
			color: #999;
			border-left: 4px solid #324057;
			padding-left: .5rem;
			font-weight: normal;
			.link{
				color: #409EFF;
				cursor: pointer;
			}
		}
		.action-container{
			display: flex;
			flex-wrap: wrap;
			.flex-item{
				padding-top: .6rem;
				padding-bottom: .6rem;
			}
			.search-form{
				width: 100%;
			}
			.add-form{
				width: 100%;
				button{
					width: 100%;
				}
			}
		}
		
	}
}
.el-button+.el-button{
	margin-left: 2px;
}
.dataStyle{width:220px;}
</style>


