<template>
	<div class="page userAction">
		<el-row>
			<el-col :xs="24" :lg="24">
				<div class="table-wrapper">
					<div class="table-header">
						<span class="title">用户分析</span>
						<span class="btn" @click="downloadExcelFun"><i class="fa fa-download"></i></span>
					</div>
					<div class="table-action clearfix">
						<div class="toolbar">
							<!-- 选择APP -->
								<el-select size="mini" v-model="tableInfo1.selected"  placeholder="选择产品">
							    	<el-option v-for="(item,index) in appInfo.list" :key="index" :label="item.name" :value="item.id"></el-option>
								</el-select>
							<!-- 类型 -->
							    <el-select size="mini" v-model="tableInfo1.type"  placeholder="选择类型" class="marginLeft" @change="changeList(tableInfo1.type)">
							    	<el-option v-for="(item,index) in opOrareatypes.list" :key="index" :label="item.name" :value="item.id" ></el-option>
								</el-select>
							<!-- 对应筛选 -->
							     <el-select size="mini" v-model="tableInfo1.iphoneopareatype"  filterable  clearable default-first-option placeholder="类别信息搜索" class="marginLeft">
							    	<el-option v-for="(item,index) in iphoneOropOrareatypes.list" :key="index" :label="item.name" :value="item.name+','+item.type" ></el-option>
								</el-select>
							<span class="myRangeMonth marginLeft">
								<label for="">开始</label>
								<el-date-picker size="mini" v-model="tableInfo1.rangeDate.minDate" type="date" placeholder="开始日期" value-format="yyyy-MM-dd"></el-date-picker>
								<label for="">结束</label>
								<el-date-picker size="mini" v-model="tableInfo1.rangeDate.maxDate" type="date" placeholder="结束日期" value-format="yyyy-MM-dd"></el-date-picker>
								<el-button size="mini" type="primary" @click="doSearch(tableInfo1, 1)"><i class="fa fa-search"></i></el-button>
							</span>
						</div>
					</div>
					<div class="table-main">
						<el-table id="table1" :data="tableInfo1.data" size="small" :height="tableInfo1.data.length>0?clientHeight:100" border style="width: 100%" v-loading="tableInfo1.loading">
							<el-table-column align="center" type="index" :index="indexFormatter1" label="序号"></el-table-column>
						    <el-table-column prop="date" label="日期" sortable align="center">
						    	<template slot-scope="scope">
			                         <span>{{scope.row.date}}</span>
			                    </template>
						     </el-table-column>
						    <el-table-column prop="cou" label="新注册" class-name="myClass" align="center"></el-table-column>
					    	<el-table-column align="center" prop="name" label="类别"></el-table-column>
					  	</el-table>
					  	<br>
					  	<el-pagination
			                background
			                @size-change="pageSizeChange"
			                @current-change="currentPageChange"
			                :current-page="currentPage"
			                :page-sizes="pageSizes"
			                :page-size="pagesize"
			                layout="total, sizes, prev, pager, next, jumper"
			                :total="dataSize" class="paginationWrapper">
			            </el-pagination>
					</div>
				</div>
			</el-col>
		</el-row>
	</div>
</template>
<script>
    import {getAppListReg,actionUserParamReg,actionUserReg,exportUserActionReg} from '../../api/api.js';
    import {yesterDay,toDay} from '../../api/formatter';
	export default {
		data() {
			return {
				// 登陆信息
			    userperadmin: '',
			    clientHeight:'500',//屏幕可视区域高度
			    // App信息
			    appInfo:{
			    	list:[]
			    },
			    opOrareatypes:{
			    	list:[{name:"机型",id:'1'},{name:"地区",id:'2'},{name:"供应商",id:'3'}]
			    },
			     // 信息类别数据信息
			    iphoneOropOrareatypes:{
			    	list:[]
			    },
			    getJsonData:[],//保存进入页面得到的运营商机型地区数据搜索信息
			    // 验证码数据
				tableInfo1: {
					loading: false,
					selected:'',//选择APP值
					type:'',//选择搜索类型
					iphoneopareatype:'',//选择机型/区域/运营商
					// 表格数据
					data: [],
					// 日期选择
					rangeDate: {
						minDate: '',
						maxDate: ''
					}
				},
				//分页
				 // 分页信息
		        currentPage: 1,
		        pageSizes: [100,300,500,1000],
		        dataSize: 0,
		        pagesize: 100
			}
		},
		mounted() {
			setTimeout(()=>{
				this.userperadmin = JSON.parse(localStorage.getItem('userperadmin'));
				this.screenclientHeight();
				// 获取所有APP产品
				this.getAppListFun();
				// 验证码时间默认选择昨天和今天
				this.tableInfo1.rangeDate.minDate = yesterDay()[0];
			    this.tableInfo1.rangeDate.maxDate = toDay()[1];
			},300)
		},
		methods:{
			// 获取可视区域高度
			screenclientHeight() {
				var clientHeight = document.documentElement.clientHeight;
				this.clientHeight = clientHeight-280;
			},
			// 点击查询：查询表格
			doSearch(tableInfo, index) {
				var minDate = tableInfo.rangeDate.minDate;
				var maxDate = tableInfo.rangeDate.maxDate;
				if(!minDate) minDate = '';
				if(!maxDate) maxDate = '';
				if (minDate != '' && maxDate == '') {
					this.toast('请选择结束日期', 'error');
					return;
				}
				if (minDate == '' && maxDate != '') {
					this.toast('请选择开始日期', 'error');
					return;
				}
				if (minDate > maxDate) {
					this.toast('开始日期不能大于结束日期', 'error');
					return;
				}
				this.getTableDataFun(tableInfo, index);
			},
			// 搜索请求数据
			getTableDataFun(tableData,index) {
				tableData.data = [];
				tableData.loading = true;
				var requireInterface = null;
				var parameter = {
					sessionid: this.userperadmin.sessionid,
					minDate: tableData.rangeDate.minDate,
					maxDate: tableData.rangeDate.maxDate,
					appId: tableData.selected,
					pageId:this.currentPage-1,
					pageSize: this.pagesize,
					name:tableData.iphoneopareatype?tableData.iphoneopareatype.split(",")[0]:'',
					type:tableData.type
				};
				console.log(parameter)
				let _this = this;
				actionUserReg(parameter).then(res=>{
					console.log(res.data)
					if (res.data.status === 0) {
						_this.tableInfo1.data = res.data.basicPage.list;
						_this.dataSize = res.data.basicPage.totalRecords;
						tableData.loading = false;
					};
					if (res.data.status === 1) {
						_this.$router.replace("/login");
					};
				})
			},
			// 改变显示条数
			pageSizeChange(res){
				this.pagesize = res;
				this.getTableDataFun(this.tableInfo1,1);
			},
			// 点击当前页
			currentPageChange(res){
				this.currentPage = res;
				this.getTableDataFun(this.tableInfo1,1);
			},
			// 获取App信息
			getAppListFun() {
				// 获取app列表
				var parameter = {
					sessionid: this.userperadmin.sessionid
				};
				let _this = this;
				actionUserParamReg(parameter).then((res) => {
					console.log('获取搜索条件', res);
					if(res.data.status === 0){
						// app数据
						for (var key in res.data.basicPage.appid) {
							console.log(key)
							var objName = key;
							var objId = res.data.basicPage.appid[objName];
							console.log(objId)
							_this.appInfo.list.push({name:objName,id:objId})
						}
						// 搜索地区机型运营商数据
						_this.getJsonData = res.data.basicPage.list;
						// app默认选择乐贷款
						_this.tableInfo1.selected = '1';
						_this.getTableDataFun(this.tableInfo1, 1);
						// 默认选择地区
						_this.tableInfo1.type = '2';
						_this.changeList('2')
					}
					if(res.data.status === 1){
						_this.$router.replace("/login");
					}
				})
			},
			// 序号
			indexFormatter1(index) {
				var newIndex;
				newIndex = this.pagesize * (this.currentPage - 1) + 1 + index;
				return newIndex;
			},
			toast(msg, type) {
				this.$message({
		            type: type,
		            message: msg
		        });
			},
			// 选择类型改变事件
			changeList(type) {
				console.log(type)
				this.iphoneOropOrareatypes.list = [];
				this.tableInfo1.iphoneopareatype = '';
				for (var i = 0; i < this.getJsonData.length; i++) {
					if (this.getJsonData[i].type == type) {
						this.iphoneOropOrareatypes.list.push({appid:this.getJsonData[i].appid,name:this.getJsonData[i].name,type:this.getJsonData[i].type})
					}
				}
			},
			// 下载
			downloadExcelFun() {
				let sendDownData = {
				    sessionid: this.userperadmin.sessionid,
					minDate: this.tableInfo1.rangeDate.minDate,
					maxDate: this.tableInfo1.rangeDate.maxDate,
					appId: this.tableInfo1.selected,
					name:this.tableInfo1.iphoneopareatype?this.tableInfo1.iphoneopareatype.split(",")[0]:'',
					type:this.tableInfo1.type
				}
				console.log(sendDownData)
				let _this = this;
				exportUserActionReg(sendDownData).then(res=>{
					console.log(res.data)
					  const content = res.data;
			          const blob = new Blob([content])
			          const fileName = '用户分析报表.xls'
			          if ('download' in document.createElement('a')) { // 非IE下载
			            const elink = document.createElement('a')
			            elink.download = fileName
			            elink.style.display = 'none'
			            elink.href = URL.createObjectURL(blob)
			            document.body.appendChild(elink)
			            elink.click()
			            URL.revokeObjectURL(elink.href) // 释放URL 对象
			            document.body.removeChild(elink)
			          } else { // IE10+下载
			            navigator.msSaveBlob(blob, fileName)
			          }
				})
			},
		}
	}
</script>
<style scoped>
.page{
	text-align: left;
}
.table-wrapper{
	margin-bottom: 25px;
	border: 1px solid #DEE5E7;
	box-shadow: 0 0 6px 0 #DCDCDC;
}
.table-header{
	padding: 10px 15px;
	font-size: 16px;
	border-bottom: 1px solid #DEE5E7;
	background: #E5E9F2;
	position: relative;
}
.table-action{
	padding: 10px;
	font-size: 12px;
	box-sizing: border-box;
}
.toolbar{
	overflow: hidden;
	margin-bottom: 15px;
}
.table-header>.title{
	font-weight: 700;
}
.table-header>.btn{
	float: right;
	cursor: pointer;
}
.table-header>.btn:hover{
	color: #409EFF;
}
@media only screen and (min-width: 1200px){
	.marginR{
		margin-left: 20px;
	}
}
</style>
<style>
.userAction .el-table--small td,.el-table--small th{
	padding: 4px 0;
}
.userAction .el-table .cell,.el-table--border td:first-child .cell,.el-table th div,.el-table--border th:first-child .cell{
	padding: 0 4px;
}
.userAction .el-checkbox__label{
	padding-left: 5px;
}
.userAction .el-checkbox+.el-checkbox{
	margin-left: 10px;
}
.userAction .el-date-editor.el-input{
	width: 120px;
}
.userAction .el-select .el-input--suffix .el-input__inner{
	padding-right: 6px;
	padding-left: 6px;
}
.userAction .el-input--suffix .el-input__inner{
	padding-right: 6px;
}
.userAction .el-select>.el-input{
	width: 100px;
}
.marginLeft{margin-left: 10px;}
.userAction .el-select>.el-input {width: 150px;}
</style>



