<template>
<div class="page downloadExcel-page">
	<p class="title">
		<a class="btn close" @click="closeBillPreview"><i class="fa fa-close"></i>关闭</a>
		<span>账单预览</span>
		<a class="btn" @click="exportExcel" v-if="billList.length === 1"><i class="fa fa-download"></i>下载</a>
		<a v-show="false" :id="'doExport' + index" class="btn" v-for="(bill,index) in billList" :key="index"></a>
	</p>
	<table :id="bill.id" v-show="billList.length === 1" v-for="(bill,index) in billList" :key="index">
		<tr rowspan="3"><td colspan="14" class="background" style="background: #E8EBF4; text-align: center; font-size: 22px; height: 40px;">上海麦广互娱文化传媒股份有限公司结算单</td></tr>
		<tr>
			<td colspan="2" class="background item" style="background: #E8EBF4; padding-left: 30px; text-align: left;">合作方名称</td>
			<td colspan="5" style="padding-left: 30px;">{{bill.channelName}}</td>
			<td colspan="2" class="background item" style="background: #E8EBF4; padding-left: 30px; text-align: left;">结算日期</td>
			<td colspan="5" style="padding-left: 30px;">{{bill.payDate}}</td>
		</tr>
		<tr>
			<td colspan="2" class="background item" style="background: #E8EBF4; padding-left: 30px; text-align: left;">发票类型</td>
			<td colspan="5" style="padding-left: 30px;">增值税专用发票</td>
			<td colspan="2" class="background item" style="background: #E8EBF4; padding-left: 30px; text-align: left;">备注</td>
			<td colspan="5" style="padding-left: 30px;">{{bill.demo}}</td>
		</tr>
		<tr>
			<td colspan="2" class="background item" style="background: #E8EBF4; padding-left: 30px; text-align: left;">结算月份</td>
			<td colspan="5" class="background" style="background: #E8EBF4;text-align: center;">结算比例</td>
			<td colspan="2" class="background item" style="background: #E8EBF4; padding-left: 30px; text-align: left;">结算数据</td>
			<td colspan="5" class="background" style="background: #E8EBF4;text-align: center;">结算金额</td>
		</tr>
		<tr>
			<td colspan="2" style="padding-left: 30px;">{{bill.payMonth}}</td>
			<td colspan="5" style="text-align: center;">{{bill.payRatio | rewardPriceFormatter}}</td>
			<td colspan="2" style="padding-left: 30px;">{{bill.payData}}</td>
			<td colspan="5" style="text-align: center;">{{bill.payMount}}元</td>
		</tr>
		<tr>
			<td colspan="7" class="background" style="background: #E8EBF4; text-align: center;">开票信息</td>
			<td colspan="7" class="background" style="background: #E8EBF4; text-align: center;">收款方信息</td>
		</tr>
		<tr>
			<td colspan="2" class="item">公司名称</td>
			<td colspan="5" style="padding-left: 30px;">{{bill.fromCompany}}</td>
			<td colspan="2" class="item">公司名称</td>
			<td colspan="5" style="padding-left: 30px;">{{bill.toCompany}}</td>
		</tr>
		<tr>
			<td colspan="2" class="item">纳税识别号</td>
			<td colspan="5" style="padding-left: 30px;">{{bill.fromTaxNo}}&nbsp;</td>
			<td colspan="2" class="item">纳税识别号</td>
			<td colspan="5" style="padding-left: 30px;">{{bill.toTaxNo}}&nbsp;</td>
		</tr>
		<tr>
			<td colspan="2" class="item">开户银行</td>
			<td colspan="5" style="padding-left: 30px;">{{bill.fromBank}}</td>
			<td colspan="2" class="item">银行名称</td>
			<td colspan="5" style="padding-left: 30px;">{{bill.toBank}}</td>
		</tr>
		<tr>
			<td colspan="2" class="item">开户账号</td>
			<td colspan="5" style="padding-left: 30px;">{{bill.fromBankNo}}&nbsp;</td>
			<td colspan="2" class="item">收款账户</td>
			<td colspan="5" style="padding-left: 30px;">{{bill.toBankNo}}&nbsp;</td>
		</tr>
		<tr>
			<td colspan="2" class="item">注册地址</td>
			<td colspan="5" style="padding-left: 30px;">{{bill.fromAdress}}</td>
			<td colspan="2" class="item">税收编码</td>
			<td colspan="5" style="padding-left: 30px;">{{bill.toTaxNumber}}&nbsp;</td>
		</tr>
		<tr>
			<td colspan="2" class="item">电话</td>
			<td colspan="5" style="padding-left: 30px;">{{bill.fromTel}}</td>
			<td colspan="2" rowspan="8" class="item">公司盖章</td>
			<td colspan="5" rowspan="8"></td>
		</tr>
		<tr>
			<td colspan="2" class="item">开票内容</td>
			<td colspan="5" style="padding-left: 30px;">{{bill.fromTicketContent}}</td>
		</tr>
		<tr>
			<td colspan="7" class="background" style="background: #E8EBF4;text-align: center;">邮寄地址（账单请打印同发票一起寄出）</td>
		</tr>
		<tr>
			<td colspan="7" rowspan="5"></td>
		</tr>
		<tr>
		</tr>
	</table>
	<el-table :data="billList" size="small" border style="width: 100%" v-if="billList.length > 1">
		 <el-table-column type="index" label="序号" width="80" align="center" :index="indexFormatter"></el-table-column>
	    <el-table-column prop="billName" label="账单名称" align="center"></el-table-column>
	    <el-table-column label="下载状态" align="center">
            <template slot-scope="scope">
            	<i class="fa fa-check" :class="{success: scope.row.downloadState == 1}"></i>
            </template>
        </el-table-column>
  	</el-table>
</div>
</template>
<script>
var idTmr;
export default {
	props: ['billList', 'show'],
	data() {
		return {
			showTableIndex: 0
		}
	},
	mounted() {
		if(this.billList.length > 1){
			this.exportExcel();
		}
	},
	filters: {
		rewardPriceFormatter(cellValue,flag) {
			console.log('rewardPriceFormatter',cellValue)
			/**
			 * 分成比例格式化
			 */
			return cellValue.toString();
			// if(cellValue.length > 0){
			// 	var temp = cellValue.split(',');
			// 	var tempArray = [];
			// 	var tempArray1 = [];
			// 	console.log(temp);
			// 	if(temp.length <= 1){
			// 		var notFc = cellValue.split('/');
			// 		if(notFc.length > 1){
			// 			tempArray.push(notFc[1]);
			// 		}else{
			// 			tempArray.push(temp[0]);
			// 		}
			// 	}else {
			// 		this.editFcTypeData = temp;
			// 		temp.forEach(function (item,index) {
			// 			var temp1 = item.split('/');
			// 			tempArray1.push({
			// 				max: temp1[0],
			// 				price: temp1[1]
			// 			});
			// 			if(index === 0){
			// 				tempArray[index] = '0 - ' + temp1[0] + ' : ' + temp1[1];
			// 			}else {
			// 				tempArray[index] = (parseInt(tempArray1[index-1].max) + 1) + ' - ' + temp1[0] + ' : ' + temp1[1];
			// 			}
			// 		});
			// 	}
			// 	return tempArray.toString();
			// }
		}
	},
	methods: {
		indexFormatter(index) {
			return index + 1;
		},
		closeBillPreview() {
			this.$emit('close');
		},
		getExplorer() {
		    var explorer = window.navigator.userAgent ;
		    //ie 
		    if (explorer.indexOf("MSIE") >= 0) {
		        return 'ie';
		    }
		    //firefox 
		    else if (explorer.indexOf("Firefox") >= 0) {
		        return 'Firefox';
		    }
		    //Chrome
		    else if(explorer.indexOf("Chrome") >= 0){
		        return 'Chrome';
		    }
		    //Opera
		    else if(explorer.indexOf("Opera") >= 0){
		        return 'Opera';
		    }
		    //Safari
		    else if(explorer.indexOf("Safari") >= 0){
		        return 'Safari';
		    }
		},
		exportExcel() {
			//整个表格拷贝到EXCEL中
		    if(this.getExplorer()=='ie') {
		        var curTbl = document.getElementById(tableid);
		        var oXL = new ActiveXObject("Excel.Application");
		        //创建AX对象excel 
		        var oWB = oXL.Workbooks.Add();
		        //获取workbook对象 
		        var xlsheet = oWB.Worksheets(1);
		        //激活当前sheet 
		        var sel = document.body.createTextRange();
		        sel.moveToElementText(curTbl);
		        //把表格中的内容移到TextRange中 
		        sel.select();
		        //全选TextRange中内容 
		        sel.execCommand("Copy");
		        //复制TextRange中内容  
		        xlsheet.Paste();
		        //粘贴到活动的EXCEL中       
		        oXL.Visible = true;
		        //设置excel可见属性
		        try {
		            var fname = oXL.Application.GetSaveAsFilename("Excel.xls", "Excel Spreadsheets (*.xls), *.xls");
		        } catch (e) {
		            print("Nested catch caught " + e);
		        } finally {
		            oWB.SaveAs(fname);
		            oWB.Close(savechanges = false);
		            //xls.visible = false;
		            oXL.Quit();
		            oXL = null;
		            //结束excel进程，退出完成
		            //window.setInterval("Cleanup();",1);
		            idTmr = window.setInterval("Cleanup();", 1);
		        }
		    }else{
		        this.tableToExcel(this.billList);
		    }
		},
		Cleanup() {
		    window.clearInterval(idTmr);
		    CollectGarbage();
		},
		tableToExcel(billList) {
			var _this = this;
			var temp = function(){
				var tableStyle = '<style>table{margin: 0 auto;border-left: 1px solid #E8EBF4;border-top: 1px solid #E8EBF4;;font-size: 14px;border-collapse: collapse;}'+
									'tr{border-top: 1px solid #EBEEF5;height: 30px;}'+
									'td{border-bottom: 1px solid #CCD3E8;border-right: 1px solid #CCD3E8;text-align: left; padding-left: 20px;}'+
									'.background{background: #E8EBF4text-align: center;}'+
									'.item{padding-left: 30px;text-align: left;}</style>';
				var uri = 'data:application/vnd.ms-excel;base64,',
			    	template = '<html xmlns:o="urn:schemas-microsoft-com:office:office" xmlns:x="urn:schemas-microsoft-com:office:excel" xmlns="http://www.w3.org/TR/REC-html40"><head><!--[if gte mso 9]><xml><x:ExcelWorkbook><x:ExcelWorksheets><x:ExcelWorksheet><x:Name>{worksheet}</x:Name><x:WorksheetOptions><x:DisplayGridlines/></x:WorksheetOptions></x:ExcelWorksheet></x:ExcelWorksheets></x:ExcelWorkbook></xml><![endif]-->'+tableStyle+'</head><body><table>{table}</table></body></html>',
			    	base64 = function(s) {
			    		return window.btoa(unescape(encodeURIComponent(s))) 
			    	},
			    	format = function(s, c) {
			        	return s.replace(/{(\w+)}/g, function(m, p) { return c[p]; }) 
			        };
	            return function(billList) {
	            	for(var i=0; i<billList.length; i++){
	            		_this.showTableIndex = i + 1;
	            		console.log(i);
						console.log(billList[i].id);
						var table = document.getElementById(billList[i].id);
						var ctx = {worksheet: name || 'Worksheet', table: table.innerHTML};
						var aEl = document.getElementById('doExport' + i);
						aEl.href = uri + base64(format(template, ctx));
						aEl.download = billList[i].billName;
						aEl.click();
						_this.billList[i].downloadState = 1;
					}
	                // if (!table.nodeType) table = document.getElementById(table)
	                // var ctx = {worksheet: name || 'Worksheet', table: table.innerHTML}
	            	// window.location.href = uri + base64(format(template, ctx))
	            	// document.getElementById('doExport').href = uri + base64(format(template, ctx))
	            	// document.getElementById('doExport').download = name;
	            }
			}
			console.log(billList);
            var temp1 = temp();
			temp1(billList);
		}
	}
}
</script>
<style lang="scss" scoped>
.success{
	color: #67C23A;
}
.downloadExcel-page{
	width: 1000px;
	margin: 0 auto;
	&.hidePage{
		z-index: -1;
	}
}
.downloading{
	text-align: right;
	font-size: 14px;
	margin-bottom: 10px;
	.downloadingIndex{
		color: red;
		padding: 0 4px;
	
	}
}
.title{
	text-align: center;
	position: relative;
	margin-bottom: 20px;
	.btn{
		position: absolute;
		right: 10px;
		top: 0;
		display: inline-block;
		width: 60px;
		height: 30px;
		line-height: 30px;
		background: #409EFF;
		color: #fff;
		font-size: 12px;
		border-radius: 2px;
		cursor: pointer;
		text-decoration: none;
	}
	.close{
		left: 10px;
	}
}
table{
	width: 100%;
	border-left: 1px solid #E8EBF4;
	border-top: 1px solid #E8EBF4;
	font-size: 14px;
	border-collapse: collapse;
	tr{
		border-top: 1px solid #EBEEF5;
		height: 40px;
	}
	td{
		// border: 1px solid #CCD3E8;
		text-align: center;
		border-right: 1px solid #CCD3E8;
		border-bottom: 1px solid #CCD3E8;
	}
	.background{
		background: #E8EBF4;
	
	}
	.item{
		padding-left: 30px;
		text-align: left;
	}
}
</style>


