								            data:this.BDGetDataInfo[j].value.map(function(index, elem) {return index.grossProfitRate2;})
								        }
	        						)
	        				}
	        			}
	        		}

	        		 BDLineObj = {
						name:name,
						tooltipTrigger:tooltipTrigger,
						tooltipFormatter:tooltipFormatter,
						legendData:legendData,
						xAxisData:xAxisData,
						yAxisFormatter:yAxisFormatter,
						seriesData:seriesData
					};
					this.BDLineData = BDLineObj;
					this.$set(this.BDLineData,name,BDLineObj.seriesData)
		       }
		       this.loading = false;
		    },
		     // 市场折线切换
		    handleClickLineFour(tab, event) {
		        console.log(tab, event);
		        console.log(this.activeName6)
		        // 进行折线曲线和转化率切换时候App恢复全部勾选，要不会存在点击搜索时候没有勾选的也显示
		    	this.bazaarcheckList = this.bazaarQudao;
		        this.BDLineShowTable(this.activeName6)
		    },
		     // 市场点击自定义查询
		    //市场点击近七天近五周近四个月时候自定义查询时间重新置空为解决点击搜索时候时间存在造成chooseBD不再会出现00的情况
            togglerebazaarSelectorFun() {
				this.togglerebazaar = !this.togglerebazaar;
				console.log(this.togglerebazaar)
				this.rangeDatebazaar = [];
				this.statusbazaarDate = this.togglerebazaar;
			},
		    // 市场table切换
		    getTableBazaar(channelType,dataType,tableIndex) {
		    	this.loading = true;
		    	this.bazaarQudaoOptions = [];//市场获取所有渠道进行初始化
		    	this.bazaarQudao = [];//市场选择的渠道进行初始化
		    	let setDataInfo = {
		    		sessionid:this.userInfo.sessionid,
                    appId: this.appValue,
                    date: this.chooseDate,
                    dateType:dataType,
                    channelType:channelType
		    	};
		    	let _this = this;
		    	console.log('市场表格发送',setDataInfo)
		    	getConclusionDataReg(setDataInfo).then(res=>{
		    		console.log(res.data)
		    		if (res.data.status === 0) {
						//市场表格，市场渠道搜索数据
		    			_this.bazaarData = res.data.firstPage;
		    			for (var i = 0; i < res.data.reportforms_admin.length; i++) {
		    				_this.bazaarQudaoOptions.push(res.data.reportforms_admin[i].channelName);
		    			}
		    			_this.loading = false;
		    		};
		    		if (res.data.status === 1) {
		    			_this.$router.replace("/login");
		    		};
		    	})

		    },
		    // 市场渠道选择改变事件
		    bazaarQudaochange(res) {
		    	// console.log(res)
		    	this.bazaarcheckList = res;
		    },
		    // 市场点击搜索事件
		    bazaarSearch(channelNmae) {
		    	if (this.rangeDatebazaar && this.rangeDatebazaar.length>0) {
		    		this.bazaar = 0;
		    	};
		    	if (this.statusbazaarDate == false && this.rangeDatebazaar.length<=0) {
		    		this.$message.error('请选择自定义的时间');
		    		return;
		    	}
		    	 this.loading = true;
		    	this.bazaarDataInfo =[];
		    	if (this.bazaarQudao.length>0) {
		    		let setDataInfo = {
			    		sessionid:this.userInfo.sessionid,
	                    appId: this.appValue,
	                    date: this.chooseDate,
	                    dateType:this.bazaar,
	                    channelType:this.activeName2,
	                    channelName:channelNmae,
	                    minDate:this.rangeDatebazaar&&this.rangeDatebazaar.length>0?this.rangeDatebazaar[0]:'',
                        maxDate:this.rangeDatebazaar&&this.rangeDatebazaar.length>0?this.rangeDatebazaar[1]:''
			    	};
			    	console.log('市场发送',setDataInfo)
			    	let _this = this;
			    	getMarketCurveDataReg(setDataInfo).then(res=>{
			    		console.log(res.data)
			    		if (res.data.status === 0) {
			    			// 把市场折线返回数据放在一个数组中bazaarDataInfo
			    			var resData = res.data.reportforms_admin;
			    			for (var i = 0; i <resData.length; i++) {
			    				_this.bazaarDataInfo.push({name:resData[i].name,value:JSON.parse(resData[i].value)})
			    			}
			    			console.log('搜索获取折线需要的数据')
			    			console.log(_this.bazaarDataInfo)
			    			_this.bazaarLineShowTable(_this.activeName6)
			    		};
			    		if (res.data.status === 1) {
			    			_this.$router.replace("/login");
			    		};
			    	})

		    	} else {
		    		this.$message.error('市场推广渠道为必选项');
		    		 this.loading = false;
		    		return;
		    	}
		    },
		     // 市场渠道选择的数据勾选状态事件
		    bazaarCheckChange(item,index) {
		    	this.bazaarSearch(this.bazaarcheckList)
		    },
		    // 不出市场折线
		    bazaarLineShowTable(tableindex){
		    	var  bazaarLineObj = {};
		    	 if (this.bazaarDataInfo.length<=0) {
		    	 	 bazaarLineObj = {
						name:tableindex==='1'?'市场数据曲线':'市场转化率曲线',
						tooltipTrigger:'axis',
						tooltipFormatter:'',
						legendData:[],
						xAxisData:[],
						yAxisFormatter:[],
						seriesData:[]
					};
					this.bazaarLineData = bazaarLineObj;
					this.loading = false;
					return;
		       };
		        if (tableindex === '1') {
		        	 // 颜色值
		       	    let colorList = this.color;
		       	    let name = '市场数据曲线';
		       	    let tooltipTrigger = 'axis';
		       	    let legendFont = ['注册数据','进件数据','开户数据','放款总笔数','提现总金额','提现笔均','收入','成本','毛利','注册CPA','开户CPA'];
		       	    let legendData = [
	        			{
		        			y: 'center',    //延Y轴居中
        					x: 'right', //居右显示
		        			orient: 'vertical',  //垂直显示
		        			icon:'rect',
		        			data:legendFont,
		        			selected:{
		        				'进件数据':false,
		        				'开户数据':false,
		        				'放款总笔数':false,
		        				'提现总金额':false,
		        				'提现笔均':false,
		        				'收入':false,
		        				'成本':false,
		        				'毛利':false,
		        				'注册CPA':false,
		        				'开户CPA':false
		        			}
	        		    }
	        		];
	        		// 坐标轴时间
	        		let xAxisData = this.bazaarDataInfo[0].value.map(function(index, elem) {
	        			return index.date;
	        		});
	        		let yAxisFormatter = '{value}';
	        		// 进行name和data匹配
	        		let tooltipFormatter ='';
	        		var seriesData = [];
	        		let bazaarlength = this.bazaarDataInfo.length;
	        		for (var i = 0; i <this.bazaarQudao.length; i++) {
	        			for (var j = 0; j < bazaarlength; j++) {
	        				if (this.bazaarQudao[i] === this.bazaarDataInfo[j].name) {
	        					console.log(this.bazaarQudao[i])
	        					seriesData.push(
	        						{
							            name:'注册数据',
							            type:'line',
							            label:{
							            	positiong: 'top',
							            	formatter:this.bazaarDataInfo[j].name+':\n'+'{a}{c}',
							                show: true
							            },
							            color:colorList[j],
							            data:this.bazaarDataInfo[j].value.map(function(index, elem) {return index.register;})
							        },
							        {
							            name:'进件数据',
							            type:'line',
							            label:{
							            	positiong: 'top',
							            	formatter:this.bazaarDataInfo[j].name+':\n'+'{a}{c}',
							                show: true
							            },
							            color:colorList[j],
							            data:this.bazaarDataInfo[j].value.map(function(index, elem) {return index.upload;})
							        },{
							            name:'开户数据',
							            type:'line',
							            label:{
							            	positiong: 'top',
							            	formatter:this.bazaarDataInfo[j].name+':\n'+'{a}{c}',
							                show: true
							            },
							            color:colorList[j],
							            data:this.bazaarDataInfo[j].value.map(function(index, elem) {return index.account;})
							        },{
							            name:'放款总笔数',
							            type:'line',
							            label:{
							            	positiong: 'top',
							            	formatter:this.bazaarDataInfo[j].name+':\n'+'{a}{c}',
							                show: true
							            },
							            color:colorList[j],
							            data:this.bazaarDataInfo[j].value.map(function(index, elem) {return index.loaner;})
							        },{
							            name:'提现总金额',
							            type:'line',
							            label:{
							            	positiong: 'top',
							            	formatter:this.bazaarDataInfo[j].name+':\n'+'{a}{c}',
							                show: true
							            },
							            color:colorList[j],
							            data:this.bazaarDataInfo[j].value.map(function(index, elem) {return index.channelSum;})
							        },{
							            name:'提现笔均',
							            type:'line',
							            label:{
							            	positiong: 'top',
							            	formatter:this.bazaarDataInfo[j].name+':\n'+'{a}{c}',
							                show: true
							            },
							            color:colorList[j],
							            data:this.bazaarDataInfo[j].value.map(function(index, elem) {return index.perCapital;})
							        },{
							            name:'收入',
							            type:'line',
							            label:{
							            	positiong: 'top',
							            	formatter:this.bazaarDataInfo[j].name+':\n'+'{a}{c}',
							                show: true
							            },
							            color:colorList[j],
							            data:this.bazaarDataInfo[j].value.map(function(index, elem) {return index.income;})
							        },{
							            name:'成本',
							            type:'line',
							            label:{
							            	positiong: 'top',
							            	formatter:this.bazaarDataInfo[j].name+':\n'+'{a}{c}',
							                show: true
							            },
							            color:colorList[j],
							            data:this.bazaarDataInfo[j].value.map(function(index, elem) {return index.cost;})
							        },{
							            name:'毛利',
							            type:'line',
							            label:{
							            	positiong: 'top',
							            	formatter:this.bazaarDataInfo[j].name+':\n'+'{a}{c}',
							                show: true
							            },
							            color:colorList[j],
							            data:this.bazaarDataInfo[j].value.map(function(index, elem) {return index.grossProfit;})
							        },{
							            name:'注册CPA',
							            type:'line',
							            label:{
							            	positiong: 'top',
							            	formatter:this.bazaarDataInfo[j].name+':\n'+'{a}{c}',
							                show: true
							            },
							            color:colorList[j],
							            data:this.bazaarDataInfo[j].value.map(function(index, elem) {return index.registerCpa;})
							        },{
							            name:'开户CPA',
							            type:'line',
							            label:{
							            	positiong: 'top',
							            	formatter:this.bazaarDataInfo[j].name+':\n'+'{a}{c}',
							                show: true
							            },
							            color:colorList[j],
							            data:this.bazaarDataInfo[j].value.map(function(index, elem) {return index.accountCpa;})
							        }
	        					)
	        				}
	        			}
	        		}
	        		 bazaarLineObj = {
						name:name,
						tooltipTrigger:tooltipTrigger,
						tooltipFormatter:tooltipFormatter,
						legendData:legendData,
						xAxisData:xAxisData,
						yAxisFormatter:yAxisFormatter,
						seriesData:seriesData
					};
					this.bazaarLineData = bazaarLineObj;
		        };
		        if (tableindex === '2') {
		        	 // 颜色值
		       	    let colorList = this.color;
		       	    let name = '市场转化率曲线';
		       	    let tooltipTrigger = 'axis';
		       	    let legendFont = ['进件转化率','开户转化率','毛利率'];
		       	    let legendData = [
	        			{
        					x: 'center', //居右显示
		        			icon:'rect',
		        			data:legendFont,
		        			selected:{
		        				'开户转化率':false,
		        				'毛利率':false
		        			}
	        		    }
	        		];
	        		// 坐标轴时间
	        		let xAxisData = this.bazaarDataInfo[0].value.map(function(index, elem) {
	        			return index.date;
	        		});
	        		let yAxisFormatter = '{value}%';
	        		// 进行name和data匹配
	        		let tooltipFormatter ='';
	        		var seriesData = [];
	        		let bazaarlength = this.bazaarDataInfo.length;
	        		for (var i = 0; i <this.bazaarQudao.length; i++) {
	        			for (var j = 0; j < bazaarlength; j++) {
	        				if (this.bazaarQudao[i] === this.bazaarDataInfo[j].name) {
	        					console.log(this.bazaarQudao[i])
	        					seriesData.push(
	        						{
							            name:'进件转化率',
							            type:'line',
							            label:{
							            	positiong: 'top',
							            	top:'2%',
							            	formatter: this.bazaarDataInfo[j].name+':\n'+'{a}{c}%',
							                show: true
							            },
							            color:colorList[j],
							            data:this.bazaarDataInfo[j].value.map(function(index, elem) {return index.uploadConversion;})
							        },
							        {
							            name:'开户转化率',
							            type:'line',
							            label:{
							            	positiong: 'top',
							            	formatter: this.bazaarDataInfo[j].name+':\n'+'{a}{c}%',
							                show: true
							            },
							            color:colorList[j],
							            data:this.bazaarDataInfo[j].value.map(function(index, elem) {return index.accountConversion;})
							        },{
							            name:'毛利率',
							            type:'line',
							            label:{
							            	positiong: 'top',
							            	formatter: this.bazaarDataInfo[j].name+':\n'+'{a}{c}%',
							                show: true
							            },
							            color:colorList[j],
							            data:this.bazaarDataInfo[j].value.map(function(index, elem) {return index.grossProfitRate;})
							        }
	        					)
	        				}
	        			}
	        		}
	        		 bazaarLineObj = {
						name:name,
						tooltipTrigger:tooltipTrigger,
						tooltipFormatter:tooltipFormatter,
						legendData:legendData,
						xAxisData:xAxisData,
						yAxisFormatter:yAxisFormatter,
						seriesData:seriesData
					};
					this.bazaarLineData = bazaarLineObj;
		        }
		         this.loading = false;
		    },
		    // 评论子组件传参事件
		    clickRowComment(msg) {
		    	console.log('打印出子传父过来的某行数据')
		    	console.log(msg)
		    	// 把点击评论获取的点击此行信息保存用与读取
		    	this.componentGetInfo = msg;
		    	// 初始话输入的评论内容和获取评论的信息
		    	this.textareaComment = '';
		    	this.getCommentInfo = [];
		    	let getComment = {
		    		sessionid:this.userInfo.sessionid,
                    appId: this.appValue,
                    date: this.chooseDate,
                    type:this.activeName2,
                    channelName: msg.channelName?msg.channelName:'',
                    channel:msg.channel?msg.channel:''
		    	}
		    	console.log('评论发送的参数',getComment)
		    	let _this = this;
		    	getCommentListReg(getComment).then(res=>{
		    		console.log('评论得到的数据',res.data)
		    		if (res.data.status === 0) {
		    			if (res.data.reportforms_admin && res.data.reportforms_admin.length>0) {
		    				_this.commentInfoLength = true;
		    				_this.getCommentInfo = res.data.reportforms_admin;

		    			}else {
		    				_this.commentInfoLength = false;
		    			}
		    			_this.commentVisible = true;
		    		};
		    		if (res.data.status === 1) {
		    			_this.$router.replace("/login");
		    		};
		    	})
		    },
		    // 保存评论信息事件
		    saveComment(){	
		    	let sendSaveComment = {
		    		sessionid:this.userInfo.sessionid,
                    appId: this.appValue,
                    date: this.chooseDate,
                    type:this.activeName2,
                    channelName: this.componentGetInfo.channelName?this.componentGetInfo.channelName:'',
                    channel:this.componentGetInfo.channel?this.componentGetInfo.channel:'',
                    comment:this.textareaComment
		    	};
		    	console.log('保存评论时发送数据',sendSaveComment)
		    	let _this = this;
		    	addCommentReg(sendSaveComment).then(res=>{
		    		console.log(res.data)
		    		if (res.data.status === 0) {
		    			 _this.$message({
				          message: '评论保存成功',
				          type: 'success'
				        });
		    			_this.commentVisible = false;
		    		};
		    		if (res.data.status === 1) {
		    			_this.$router.replace("/login");
		    		};
		    	})
		    },
		    // 点击下载事件
		    dailyDown() {
		    	console.log('点击下载')
		    	console.log(this.activeName2)
		    	var downFunction;
		    	let downName;
		    	let sendDown = {
		    		sessionid:this.userInfo.sessionid,
                    appId: this.appValue,
                    date: this.chooseDate,
                    channelType:this.activeName2
		    	};
		    	switch (this.activeName2) {
		    		case '6':
		    		    downName = '日报运营数据'
		    			downFunction = exportOperateDataReg
		    			break;
		    		case '7':
		    			downName = '日报续贷数据'
		    			downFunction = exportSecondDataReg
		    			break;
		    		case '2':
		    			downName = '日报BD数据'
		    			downFunction = exportBdDataReg
		    			break;
		    		case '1':
		    		    downName = '日报市场数据'
		    			downFunction = exportMarketDataReg
		    			break;
		    		default:
		    			// statements_def
		    			break;
		    	};
		    	console.log("发送参数",sendDown)
		    	let _this = this;
		    	downFunction(sendDown).then(res=>{
		    		console.log(res.data)
		    		// if (res.data.status === 0) {
		    			const content = res.data;
				        const blob = new Blob([content])
				        const fileName = downName+'统计表.xls'
				        if ('download' in document.createElement('a')) { // 非IE下载
				            const elink = document.createElement('a')
				            elink.download = fileName
				            elink.style.display = 'none'
				            elink.href = URL.createObjectURL(blob)
				            document.body.appendChild(elink)
				            elink.click()
				            URL.revokeObjectURL(elink.href) // 释放URL 对象
				            document.body.removeChild(elink)
				        } else { // IE10+下载
				            navigator.msSaveBlob(blob, fileName)
				        }
		    		// };
		    		// if (res.data.status === 1) {
		    		// 	_this.$router.replace("/login");
		    		// };
		    	})
		    }
		}
	}
</script>
<style lang="scss" scoped>
  *{text-align: left;}
  .demonstration{font-size:14px;}
  .dateStyle{width: 150px;}
  .marginLeft{margin-left: 20px;}
  .content{margin-top: 20px;}
  .lineStyle{margin-top: 40px;}
  .commentInfo{padding:0px 20px 10px 20px;max-height:200px;overflow-y: scroll}
  .boxcomment{padding-bottom:90px;}
  .clearfix:before{height:0px;display: none;clear:both;overflow:hidden;content:''}
  .commentUserNameDate,.commentline,.commentConter{float: left}
  .commentUserNameDate{width: 120px;height:60px;border:1px dotted #999;padding: 2px;text-align: center;line-height: 30px;}
  .uesrName{font-size: 16px;text-align: center;}
  .commentdate{font-size:12px;text-align: center;}
  .commentline{border-left: 1px dotted #999;margin-left:20px;height:64px;}
  .commentConter{margin-left: 15px;width:60%;height:60px;overflow-y: scroll;text-align: justify;    word-wrap: break-word;}
  .tltleComment{text-align:center;margin-top: 20px;}
  .inputInfoContent{margin-top: 15px;}
  .inputInfoContent input{text-align: justify}
  .searchOne,.searchTow,.searchThree{float: left}

</style>


