<template>
<div class="page distributor-bill" v-loading="loading">
	<div class="bread-nav">
		<span class="current-text">当前位置：</span>
		<span v-if="currentPosition.page >= item.id" :class="{menuActive: currentPosition.page > item.id}" v-for="(item, index) in currentPosition.menu" @click="toggleCurrentMenu(item.id)"><span v-if="index > 0">></span> {{item.name}}</span>
	</div>
	<transition name="slide-fade">
	<div class="page1" v-show="showPage1">
		<el-row class="search-wrapper">
			<el-col :xs="24" :sm="20" class="handler-panel">
				<el-col :xs="24" :sm="20">
					<el-select class="px360W mrgB" v-model="distributorInfo.selected" clearable filterable default-first-option placeholder="选择渠道商">
					    <el-option v-for="(item,index) in distributorInfo.list" :key="index" :label="item" :value="item"></el-option>
					</el-select>
					<el-select class="px360W mrgB" v-model="paymentCompanyInfo.selected" clearable filterable default-first-option placeholder="选择付款公司">
					    <el-option v-for="(item,index) in paymentCompanyInfo.list" :key="index" :label="item.company" :value="item.company"></el-option>
					</el-select>
					<el-select class="mrgB" v-model="firstChannelInfo.selected" clearable filterable default-first-option placeholder="选择一级渠道">
					    <el-option v-for="(item,index) in firstChannelInfo.list" :key="index" :label="item" :value="item"></el-option>
					</el-select>
					<el-select class="mrgB" v-model="appInfo.selected" clearable filterable default-first-option placeholder="选择产品">
					    <el-option v-for="(item,index) in appInfo.list" :key="index" :label="item.name" :value="item.id"></el-option>
					</el-select>
					<el-select class="mrgB" v-model="adminInfo.selected" clearable filterable default-first-option placeholder="选择负责人">
					    <el-option v-for="(item,index) in adminInfo.list" :key="index" :label="item.adminname" :value="item.adminId"></el-option>
					</el-select>
					<el-select class="mrgB" v-model="billStateInfo.selected" clearable filterable default-first-option placeholder="选择账单状态">
					    <el-option v-for="(item,index) in billStateInfo.list" :key="index" :label="item.name" :value="item.id"></el-option>
					</el-select>
					<div class="myRangeMonth">
						<label for="">起始月份：</label>
						<el-date-picker  v-model="rangeDate.minDate" type="month" placeholder="起始月份"></el-date-picker>
						<label for="">终止月份：</label>
						<el-date-picker  v-model="rangeDate.maxDate" type="month" placeholder="终止月份"></el-date-picker>
					</div>
				</el-col>
				<el-col :xs="24" :sm="4">
					<button class="btn primary" @click="searchAction"><i class="fa fa-search"></i>搜索</button>
				</el-col>
			</el-col>
			<el-col :xs="24" :sm="4" class="btn-panel">
				<button class="btn primary download" @click="batchDownload"><i class="fa fa-cloud-download" v-loading="batchLoading"></i></button>
				<button class="btn primary download" @click="batchShenPiFun"><i class="fa fa-check-square-o" aria-hidden="true"></i></button>
			</el-col>
		</el-row>
		<div class="table-wrapper tableMinHeight">
			<el-table ref="billTable" :data="billTableInfo.table" size="small" height="90%" border style="width: 100%" v-loading="billTableInfo.loading" @selection-change="selectChange" @select="selectedFun">
				<el-table-column type="selection" width="40"></el-table-column>
			    <el-table-column type="index" label="序号" width="80" :index="indexFormatter"></el-table-column>
			    <el-table-column prop="id" label="账单ID"></el-table-column>
			    <el-table-column prop="appId" label="产品" :formatter="tableAppFormatter"></el-table-column>
			    <el-table-column prop="adminname" label="负责人"></el-table-column>
			    <el-table-column prop="proxyName" label="渠道商" width="220"></el-table-column>
			    <el-table-column prop="mainChannelName" label="渠道名称"></el-table-column>
			    <el-table-column prop="payCompany" label="付款公司" width="220"></el-table-column>
			    <el-table-column prop="month" label="归属期间"></el-table-column>
			    <el-table-column prop="cost" label="含税成本金额(元)" width="120"></el-table-column>
			    <el-table-column prop="createTime" label="生成时间" width="90"></el-table-column>
			    <el-table-column label="状态">
	                <template slot-scope="scope">
	                	<a class="tableA" :class="scope.row | tableStatusStyleFilter" @click="openStateDialog(scope.row)">
	                	<i class="fa" :class="scope.row.status | tableStatusStyleFilter1"></i>
	                	{{scope.row.status | tableStatusFilter}}</a>
	                </template>
	            </el-table-column>
			    <el-table-column label="账单详情" width="180">
	                <template slot-scope="scope">
	                    <el-button type="text" size="small" @click="viewDetailFun(scope.row)">明细</el-button>
	                    <el-button type="text" size="small" @click="previewBill(scope.row)">预览</el-button>
	                    <el-button v-show="billTableInfo.isRefreshBill == 1 && scope.row.isUpdate == 1" type="text" size="small" @click="updateBillFun(scope.row)">重跑</el-button>
	                </template>
	            </el-table-column>
		  	</el-table>
		</div>
	</div>
	</transition>
	<transition name="slide-fade">
	<div class="page2 form-wrapper" v-show="showPage2" v-loading="billDetailInfo.loading">
		<div class="page-title">
			<p>{{billDetailInfoTitle}} 账单详情</p>
			<p class="small">（产品名称 - 渠道商名称 - 一级渠道名称）</p>
			<button class="btn primary download" @click="downloadBillExcel"><i class="fa fa-cloud-download"></i></button>
			<span class="back" @click="closePage2Fun"><i class="fa fa-chevron-left" aria-hidden="true"></i></i></span>
		</div>
		<el-row :gutter="20">
			<el-col :sx="24" :sm="12" class="block distributorInfo">
				<div class="block-title">
					<span>渠道商信息</span>
				</div>
				<div class="content">
					<div class="input-group">
						<label for="proxyName">渠道商：</label>
						<input type="text" id="proxyName" readonly="true" v-model="billDetailInfo.proxyInfo.company">
					</div>
					<div class="input-group">
						<label for="bankAccount">银行账户：</label>
						<input type="text" id="bankAccount" readonly="true" v-model="billDetailInfo.proxyInfo.bankAccount">
					</div>
					<div class="input-group">
						<label for="bank">开户行：</label>
						<input type="text" id="bank" readonly="true" v-model="billDetailInfo.proxyInfo.bank">
					</div>
					<div class="input-group">
						<label for="dutyParagraph">税号：</label>
						<input type="text" id="dutyParagraph" readonly="true" v-model="billDetailInfo.proxyInfo.dutyParagraph">
					</div>
					<div class="input-group">
						<label for="dutyParagraph">税收编码：</label>
						<input type="text" id="dutyParagraph" readonly="true" v-model="billDetailInfo.proxyInfo.taxNo">
					</div>
					<div class="input-group">
						<label for="phone">公司电话：</label>
						<input type="text" id="phone" readonly="true" v-model="billDetailInfo.proxyInfo.phone">
					</div>
					<div class="input-group textarea-group">
						<label for="address">公司地址：</label>
						<textarea rows="2" disabled="true">{{billDetailInfo.proxyInfo.address}}</textarea>
					</div>
				</div>
			</el-col>
			<el-col :sx="24" :sm="12" class="block companyInfo">
				<div class="block-title">
					<span>付款公司信息</span>
				</div>
				<div class="content">
					<div class="input-group">
						<label for="company">公司名称：</label>
						<input type="text" id="company" readonly="true" v-model="billDetailInfo.balanceAccountList.company">
					</div>
					<div class="input-group">
						<label for="accountNo">银行账号：</label>
						<input type="text" id="accountNo" readonly="true" v-model="billDetailInfo.balanceAccountList.accountNo">
					</div>
					<div class="input-group">
						<label for="bank">开户行：</label>
						<input type="text" id="bank" readonly="true" v-model="billDetailInfo.balanceAccountList.bank">
					</div>
					<div class="input-group">
						<label for="taxpayerNo">税号：</label>
						<input type="text" id="taxpayerNo" readonly="true" v-model="billDetailInfo.balanceAccountList.taxpayerNo">
					</div>
					<div class="input-group">
						<label for="phone">公司电话：</label>
						<input type="text" id="phone" readonly="true" v-model="billDetailInfo.balanceAccountList.phone">
					</div>
					<div class="input-group textarea-group">
						<label for="address">公司地址：</label>
						<textarea rows="2" disabled="true">{{billDetailInfo.balanceAccountList.address}}</textarea>
					</div>
				</div>
			</el-col>
		</el-row>
		<el-row :gutter="20" class="block">
			<el-col :sx="24" class="distributorInfo">
				<div class="block-title">
					<span>账单信息</span>
				</div>
				<el-row class="content">
					<el-col :sx="24" :sm="12" class="block">
						<div class="input-group">
							<label for="appId">产品名称：</label>
							<input type="text" id="appId" readonly="true" :value="billDetailInfo.billInfo.appId | getAppNameFilter">
						</div>
						<div class="input-group">
							<label for="adminname">负责人：</label>
							<input type="text" id="adminname" readonly="true" v-model="billDetailInfo.billInfo.adminname">
						</div>
					</el-col>
					<el-col :sx="24" :sm="12" class="block">
						<div class="input-group">
							<label for="cost">申请金额(元)：</label>
							<input type="text" id="cost" readonly="true" v-model="billDetailInfo.billInfo.cost">
						</div>
						<div class="input-group">
							<label for="month">归属期间：</label>
							<input type="text" id="month" readonly="true" v-model="billDetailInfo.billInfo.month">
						</div>
					</el-col>
				</el-row>
			</el-col>
		</el-row>
		<el-row :gutter="20" class="block">
			<el-col :sx="24" class="distributorInfo">
				<div class="block-title">
					<span>详细信息</span>
				</div>
				<div class="content table-wrapper">
					<el-table :data="billDetailInfo.billDetails" size="small" height="300" border style="width: 100%">
					    <el-table-column type="index" label="序号" width="80" :index="indexFormatter"></el-table-column>
					    <el-table-column prop="channelName" label="渠道名称"></el-table-column>
					    <el-table-column prop="channel" label="渠道号" ></el-table-column>
					    <el-table-column prop="rewardType" label="分成方式"></el-table-column>
					    <!-- <el-table-column prop="price" label="分成价格"></el-table-column> -->
					    <el-table-column prop="price" label="分成价格" width="220">
		                	<template slot-scope="scope">
		                		<p v-for="item in scope.row.price">{{item}}</p>
		                    </template>
		                </el-table-column>
					    <el-table-column prop="cost2" label="含税成本金额(元)"></el-table-column>
					    <el-table-column prop="rewardSum" label="推广数据"></el-table-column>
					    <el-table-column prop="month" label="归属期间(账期)" :formatter="rangeDateFormatter" width="220"></el-table-column>
				  	</el-table>
				</div>
			</el-col>
		</el-row>
		<el-row :gutter="20" class="block">
			<el-col :sx="24" class="distributorInfo">
				<div class="block-title">
					<span>审批路径</span>
				</div>
				<div class="content table-wrapper">
					<el-table :data="billDetailInfo.billStepList" size="small" border style="width: 100%">
						 <el-table-column type="index" label="流程" width="80" align="center" :index="indexFormatter"></el-table-column>
					    <el-table-column prop="adminName" label="审核人" align="center"></el-table-column>
					    <el-table-column prop="date" label="审核时间" align="center"></el-table-column>
					    <el-table-column prop="status" label="审核状态" align="center"></el-table-column>
				  	</el-table>
				</div>
			</el-col>
		</el-row>
		<div class="btn-group">
			<button class="btn primary" @click="closePage2Fun">关闭</button>
		</div>
	</div>
	</transition>
	<transition name="slide-fade">
		<div class="page3 previewBill" v-if="showPage3">
			<download-excel v-on:close="closePreview" :billList="billExcel"></download-excel>
		</div>
	</transition>
	<el-dialog title="修改渠道账单状态" :visible.sync="billDetailDialog.visible" width="550px" center v-loading="billDetailDialog.loading">
		<form class="form-wrapper">
			<div class="input-group">
				<label for="proxyName">渠道商：</label>
				<input type="text" id="proxyName" readonly="true" v-model="billDetailDialog.form.proxyName">
			</div>
			<div class="input-group">
				<label for="channelName">渠道名称：</label>
				<input type="text" id="channelName" readonly="true" v-model="billDetailDialog.form.mainChannelName">
			</div>
			<div class="input-group">
				<label for="admin">负责人：</label>
				<input type="text" id="admin" readonly="true" v-model="billDetailDialog.form.adminname">
			</div>
			<div class="input-group">
				<label for="payCompany">付款公司：</label>
				<input type="text" id="payCompany" readonly="true" v-model="billDetailDialog.form.payCompany">
			</div>
			<div class="input-group">
				<label for="month">归属期间：</label>
				<input type="text" id="month" readonly="true" v-model="billDetailDialog.form.month">
			</div>
			<div class="input-group">
				<label for="cost">含税成本金额(元)：</label>
				<input type="text" id="cost" readonly="true" v-model="billDetailDialog.form.cost">
			</div>
			<div class="input-group">
				<label for="status">账单当前状态：</label>
				<input type="text" id="status" readonly="true" :value="billDetailDialog.form.status | tableStatusFilter">
			</div>
			<div class="input-group">
				<label for="billState">审批操作：</label>
				<el-select class="mrgB billState" size="small" v-model="billDetailDialog.form.action" filterable default-first-option placeholder="选择账单状态">
				    <el-option v-for="item in billAdminStatusList" :key="item.id" :label="item.name" :value="item.id"></el-option>
				</el-select>
			</div>
		</form>
		<span slot="footer" class="dialog-footer">
		    <el-button @click="billDetailDialog.visible = false">取 消</el-button>
		    <el-button type="primary" @click="updateBillState">确 定</el-button>
	  	</span>
	</el-dialog>
	<el-dialog title="批量审批" center width="400" :visible.sync="batchShenPiInfo.visible">
			<el-table :data="batchShenPiInfo.data" height="300" border style="width: 100%">
		    <el-table-column type="index" label="序号" :index="indexFormatter"></el-table-column>
		    <el-table-column prop="appId" label="产品" :formatter="tableAppFormatter"></el-table-column>
		    <el-table-column prop="adminname" label="负责人"></el-table-column>
		    <el-table-column prop="proxyName" label="渠道商" width="220"></el-table-column>
		    <el-table-column prop="mainChannelName" label="渠道名称"></el-table-column>
		    <el-table-column prop="payCompany" label="付款公司" width="220"></el-table-column>
		    <el-table-column prop="month" label="归属期间"></el-table-column>
		    <el-table-column prop="cost" label="含税成本金额(元)" width="120"></el-table-column>
		    <el-table-column prop="createTime" label="生成时间" width="90"></el-table-column>
	  	</el-table>
	  	<div class="input-group" style="text-align: center; margin-top: 10px;">
			<label for="billState">审批操作：</label>
			<el-select class="mrgB billState" size="small" v-model="batchShenPiInfo.action" filterable default-first-option placeholder="选择账单状态">
			    <el-option v-for="item in billAdminStatusList" :key="item.id" :label="item.name" :value="item.id"></el-option>
			</el-select>
		</div>
	  	<span slot="footer" class="dialog-footer">
		    <el-button @click="batchShenPiInfo.visible = false">取 消</el-button>
		    <el-button type="primary" @click="doBatchShenPiFun">提交</el-button>
	  	</span>
	</el-dialog>
</div>
</template>

<script>
import {getBillParameterReg, getBillReg, getBillDetailReg, updateBillStatusReg, updateBillReg, batchUpdateBillReg} from '../../api/api';
import DownloadExcel from './DownloadExcel';
import {dateFormatter} from '../../api/formatter';
var AppList = [];
var BillState = [];
export default {
	components: {
		'download-excel': DownloadExcel
	},	
	data() {
		return {
			// 登陆信息
			userperadmin: {},
			loading: false,
			// 当前显示的页面
			currentPosition: {
				page: 1,
				menu: [
					{id: 1 ,name: '渠道商账单'},
					{id: 2 ,name: '账单详情'},
					{id: 3 ,name: '账单预览'}
				]
			},
			/* 搜索条件信息 start */
			// 渠道商列表
			distributorInfo: {
				selected: '',
				list: []
			},
			// 一级渠道列表
			firstChannelInfo: {
				selected: '',
				list: []
			},
			// 付款公司列表
			paymentCompanyInfo: {
				selected: '',
				list: []
			},
			// 产品列表
			appInfo: {
				selected: '',
				list: []
			},
			// 负责人列表
			adminInfo: {
				selected: '',
				list: []
			},
			// 账单状态列表
			billStateInfo: {
				selected: '',
				list: []
			},
			// 可审核状态列表
			billAdminStatusList: [],
			// 日期
			rangeDate: {
				minDate: '',
				maxDate: ''
			},
			// 当前用户是否可审批
			isUpdateSelected: '',
			/* 搜索条件信息 end */
			billTableInfo: {
				table: [],
				loading: false,
				isRefreshBill: 0
			},
			billDetailDialog: {
				visible: false,
				loading: false,
				form: {
					billId: '',
					proxyName: '',
					mainChannelName: '',
					adminname: '',
					payCompany: '',
					month: '',
					cost: '',
					status: '',
					action: ''
				}
			},
			billDetailInfo: {
				loading: false,
				curApp: '',
				curDistributor: '',
				curFirstChannel: '',
				proxyInfo: {},
				billInfo: {},
				balanceAccountList: {},
				billDetails: [],
				billStepList: []
			},
			batchLoading: false,
			// 存放要下载的账单id列表
			billIdList: [],
			// 批量下载账单
			billLists: [],
			// 单个下载账单
			singleBill: [],
			// 批量下载时：查询明细报错的账单数
			errorBillList: [],
			// 准备下载账单
			billExcel: [],
			// 批量审核
			batchShenPiInfo: {
				visible: false,
				action: '',
				data: []
			}
		}
	},
	mounted() {
		this.userperadmin = JSON.parse(localStorage.getItem('userperadmin'));
		this.getBillParameterFun();
	},
	filters: {
		getAppNameFilter(val){
			var appName = '';
			for(var i=0; i<AppList.length; i++){
				if(val == AppList[i].id){
					appName = AppList[i].name;
					break;
				}
			}
			return appName;
		},
		tableStatusFilter(val) {
			var cellValueStr = val;
			for(var i=0; i<BillState.length; i++){
				if(BillState[i].id == val){
					cellValueStr = BillState[i].name;
					break;
				}
			}
			return cellValueStr;
		},
		tableStatusStyleFilter(row) {
			var className = '';
			if(row.status == 43) {
				className = 'unChenck';
			}
			if(row.status == 44) {
				className = 'checking';
			}
			if(row.status == 45) {
				className = 'denail';
			}
			if(row.status == 46) {
				className = 'noPay';
			}
			if(row.status == 47) {
				className = 'pay';
			}
			if(row.isUpdate == 1){
				className += ' isUpdate';
			}
			return className;
		},
		tableStatusStyleFilter1(val) {
			if(val == 43) {
				return '';
			}
			if(val == 44){
				return 'fa-spinner';
			}
			if(val == 45) {
				return 'fa-close';
			}
			if(val == 46) {
				return 'fa-check';
			}
			if(val == 47) {
				return 'fa-check';
			}
		}
	},
	computed: {
		showPage1() {
			if(this.currentPosition.page === 1){
				return true;
			}else{
				return false;
			}
		},
		showPage2() {
			if(this.currentPosition.page === 2){
				return true;
			}else{
				return false;
			}
		},
		showPage3() {
			if(this.currentPosition.page === 3){
				return true;
			}else{
				return false;
			}
		},
		billDetailInfoTitle() {
			// 账单详情标题
			return this.billDetailInfo.curApp + '-' + this.billDetailInfo.curDistributor + '-' + this.billDetailInfo.curFirstChannel;
		}
	},	
	methods: {
		indexFormatter(index){
			return index + 1;
		},
		batchShenPiFun() {
			// 弹出批量审批的对话框
			if(this.batchShenPiInfo.data.length === 0){
				this.toast('您未选择账单', 'warning');
			}else{
				this.batchShenPiInfo.visible = true;
			}
		},
		doBatchShenPiFun() {
			// 提交批量审批
			if(this.batchShenPiInfo.action == '' || this.batchShenPiInfo.action == undefined) {
				this.toast('请选择审批操作', 'error');
				return;
			}
			var ids = '';
			var _this = this;
			this.batchShenPiInfo.data.forEach(function(item, index){
				if(index === _this.batchShenPiInfo.data.length-1){
					ids += item.id;
				}else{
					ids += item.id + ',';
				}
			});
			var parameter = {
				sessionid: this.userperadmin.sessionid,
				status: this.batchShenPiInfo.action,
				ids: ids
			};
			console.log(parameter);
			batchUpdateBillReg(parameter).then((res) => {
				console.log(res);
				if(res.data.status == 0) {
					this.batchShenPiInfo.visible = false;
					this.getBillFun();
				}else if(res.data.status === 1){
					this.$router.replace("/login");
				}else{
					this.toast(res.data.statusMsg, 'error');
				}
			}).catch((err) => {
				console.error(err);
				// this.toast('', 'warning');
			});
		},
		selectChange(selection) {
			// 选择账单
			console.log('selectChange', selection);
			this.batchShenPiInfo.data = selection;
		},
		selectedFun(selection, row){
			console.log('selectedFun', row);
			if(row.isUpdate == 0){
				this.$refs.billTable.toggleRowSelection(row);
				this.toast('您目前不能审核该账单', 'warning');
				return;
			}
		},
		toggleCurrentMenu(page) {
			// 切换页面
			if (page < this.currentPosition.page) {
				this.currentPosition.page = page;
			}
		},
		isUpdateSelectedChange() {
			// 查看当前用户对账单操作的数据
			this.getBillFun();
		},
		updateBillFun(row) {
			// 刷新账单
			var parameter = {
				sessionid: this.userperadmin.sessionid,
				id: row.id
			};
			this.billTableInfo.loading = true;
			updateBillReg(parameter).then((res) => {
				this.billTableInfo.loading = false;
				console.log('updateBillReg', res);
				if(res.data.status === 0) {
				}else if(res.data.status === 1){
					this.$router.replace("/login");
				}else{
					this.toast(res.data.statusMsg, 'error');
				}
			})
			.catch((error) => {
				this.billTableInfo.loading = false;
				console.log(error);
			});
		},
		updateBillState() {
			// 修改审批状态
			var parameter = {
				sessionid: this.userperadmin.sessionid,
				id: this.billDetailDialog.form.billId,
				status: this.billDetailDialog.form.action
			};
			console.log(parameter);
			this.billDetailDialog.loading = true;
			updateBillStatusReg(parameter).then((res) => {
				console.log('updateBillStatusReg', res);
				this.billDetailDialog.loading = false;
				if(res.data.status === 0) {
					// 审批成功，刷新表格
					this.billDetailDialog.visible = false;
					this.getBillFun();
				}else if(res.data.status === 1){
					this.$router.replace("/login");
				}else{
					this.toast(res.data.statusMsg, 'error');
				}
			})
			.catch((error) => {
				this.billDetailDialog.loading = false;
				console.log(error);
			});
		},
		downloadBillExcel(batch){
			console.log('batch',batch);
			this.billExcel = [];
			if(batch === true){
				// 批量下载
				this.billExcel = this.billLists;
				console.log('before1', this.billExcel);
				this.currentPosition.page = 3;
			}else{
				// 下载一个账单
				this.billExcel = this.singleBill;
				console.log('before2', this.billExcel);
				this.currentPosition.page = 3;
			}
		},
		closePreview() {
			// 关闭账单预览
			this.currentPosition.page = 1;
		},
		previewBill(row) {
			// 预览并下载单个账单
			this.viewDetailFun(row, 1);
		},
		indexFormatter(index) {
			return index + 1;
		},
		tableAppFormatter(row, column, cellValue){
			var cellValueStr = cellValue;
			var appList = this.appInfo.list;
			for(var i=0; i<appList.length; i++){
				if(appList[i].id == cellValue){
					cellValueStr = appList[i].name;
					break;
				}
			}
			return cellValueStr;
		},
		tableStatusFormatter(row, column, cellValue) {
			var cellValueStr = cellValue;
			var billStateList = this.billStateInfo.list;
			for(var i=0; i<billStateList.length; i++){
				if(billStateList[i].id == cellValue){
					cellValueStr = billStateList[i].name;
					break;
				}
			}
			return cellValueStr;
		},
		rewardPriceFormatter(cellValue,flag) {
			/**
			 * 分成比例格式化
			 */
			if(cellValue){
				var temp = cellValue.split(',');
				var tempArray = [];
				var tempArray1 = [];
				console.log(temp);
				if(temp.length <= 1){
					var notFc = cellValue.split('/');
					if(notFc.length > 1){
						tempArray.push(notFc[1]);
					}else{
						tempArray.push(temp[0]);
					}
				}else {
					this.editFcTypeData = temp;
					temp.forEach(function (item,index) {
						var temp1 = item.split('/');
						tempArray1.push({
							max: temp1[0],
							price: temp1[1]
						});
						if(index === 0){
							tempArray[index] = '0 - ' + temp1[0] + ' : ' + temp1[1];
						}else {
							tempArray[index] = (parseInt(tempArray1[index-1].max) + 1) + ' - ' + temp1[0] + ' : ' + temp1[1];
						}
					});
				}
				return tempArray;
			}
		},
		chineseDate(date) {
			// 加上年月日
			var dateArray = date.split('-');
			var year = dateArray[0] + '年';
			var month = dateArray[1] + '月';
			var day;
			if(dateArray.length > 2){
				day = dateArray[2] + '日'
				return year + month + day;
			}else{
				return year + month;
			}
		},
		rangeDateFormatter(row, column, cellValue) {
			console.log(row);
			if(row.maxdate === undefined){
				return this.chineseDate(cellValue);
			}else {
				return this.chineseDate(row.mindate) + ' - ' + this.chineseDate(row.maxdate);
			}
		},
		closePage2Fun() {
			// 关闭账单详情页面
			this.currentPosition.page = 1;
			// 清空数据
			this.billDetailInfo.proxyInfo = {};
			this.billDetailInfo.billInfo = {};
			this.billDetailInfo.balanceAccountList = {};
			this.billDetailInfo.billDetails = [];
		},
		openStateDialog(row) {
			/**
			 * 打开状态对话框
			 */
			console.log(row);
			if(row.isUpdate == 0){
				this.toast('您目前不能审核该账单', 'warning');
				return;
			}
			// 当审批状态处于已审批或驳回时，当前用户不能在操作
			// if(row.status == 39 || row.status == 40){
			// 	this.toast('当前状态不可更改。', 'success');
			// 	return;
			// }
			this.billDetailDialog.form = {};
			this.billDetailDialog.visible = true;
			this.billDetailDialog.form.billId = row.id;
			this.billDetailDialog.form.proxyName = row.proxyName;
			this.billDetailDialog.form.mainChannelName = row.mainChannelName;
			this.billDetailDialog.form.adminname = row.adminname;
			this.billDetailDialog.form.payCompany = row.payCompany;
			this.billDetailDialog.form.month = row.month;
			this.billDetailDialog.form.cost = row.cost;
			this.billDetailDialog.form.status = parseInt(row.status);
		},
		getBillParameterFun() {
			/**
			 * 获取渠道商账单的查询参数
			 */
			var parameter = {
				sessionid: this.userperadmin.sessionid
			};
			// console.log('getBillParameterReg parameter', parameter);
			this.loading = true;
			getBillParameterReg(parameter)
			.then((res) => {
				console.log('getBillParameterReg', res);
				this.loading = false;
				if(res.data.status === 0){
					this.distributorInfo.list = res.data.proxyNameList;
					this.firstChannelInfo.list = res.data.mainChannelNameList;
					this.paymentCompanyInfo.list = res.data.balanceAccountList;
					this.appInfo.list = res.data.appList;
					this.adminInfo.list = res.data.billAdminList;
					this.billStateInfo.list = res.data.billStatusList;
					this.billAdminStatusList = res.data.billAdminStatusList;
					AppList = res.data.appList;
					BillState = res.data.billStatusList;
				}else if(res.data.status === 1){
					this.$router.replace("/login");
				}else{
					this.toast(res.data.statusMsg, 'error');
				}
			})
			.catch((error) => {
				this.loading = false;
				console.log(error);
			});
		},
		searchAction() {
			this.getBillFun();
		},
		getBillFun(init) {
			/**
			 * 获取渠道商账单列表
			 */
			console.log(this.rangeDate);
			if(!this.rangeDate.minDate) this.rangeDate.minDate = '';
			if(!this.rangeDate.maxDate) this.rangeDate.maxDate = '';
			if (this.rangeDate.minDate != '' && this.rangeDate.maxDate == '') {
				this.toast('请选择终止月份', 'error');
				return;
			}
			if (this.rangeDate.minDate == '' && this.rangeDate.maxDate != '') {
				this.toast('请选择起始月份', 'error');
				return;
			}
			if (this.rangeDate.minDate > this.rangeDate.maxDate) {
				this.toast('起始月份不能大于终止月份', 'error');
				return;
			}
			var parameter = {
				sessionid: this.userperadmin.sessionid,
				proxyName: this.distributorInfo.selected,
				mainChannelName: this.firstChannelInfo.selected,
				payCompany: this.paymentCompanyInfo.selected,
				adminId: this.adminInfo.selected,
				appId: this.appInfo.selected,
				status: this.billStateInfo.selected,
				maxDate: dateFormatter(this.rangeDate.maxDate).substr(0, 7),
				minDate: dateFormatter(this.rangeDate.minDate).substr(0, 7)
			};
			console.log('getBillFun parameter', parameter);
			this.billTableInfo.loading = true;
			getBillReg(parameter).then((res) => {
				console.log('getBillFun', res);
				this.billTableInfo.loading = false;
				if(res.data.status === 0){
					this.billTableInfo.isRefreshBill = res.data.isRefreshBill;
					this.billTableInfo.table = res.data.billList;
					// 保存当前所有账单的id，一遍用于批量下载时查询账单
					var _this = this;
					this.billIdList = [];
					if(this.billTableInfo.table){
						this.billTableInfo.table.forEach(function(item){
							_this.billIdList.push({
								id: item.id,
								billName: item.product
							});
						});
					}
				}else if(res.data.status === 1){
					this.$router.replace("/login");
				}else{
					this.toast(res.data.statusMsg, 'error');
				}
				this.loading = false;
			})
			.catch((error) => {
				console.log(error);
				this.billTableInfo.loading = false;
			}); 
		},
		getAppName(appId){
			/**
			 * 获取App名称
			 */
			console.log(appId);
			var appName = '';
			var appList = this.appInfo.list;
			for(var i=0; i<appList.length; i++){
				if(appId == appList[i].id){
					appName = appList[i].name;
					break;
				}
			}
			return appName;
		},
		batchDownload() {
			// 批量下载
			if( this.distributorInfo.selected == '' && this.firstChannelInfo.selected == '' && this.paymentCompanyInfo.selected == '' && 
				this.adminInfo.selected == '' && this.appInfo.selected == '' && this.billStateInfo.selected == '' && this.rangeDate.minDate == '' && this.rangeDate.maxDate == ''){
				this.toast('请至少选择一个搜索条件', 'warning');
				return;
			}
			if (this.rangeDate.minDate != '' && this.rangeDate.maxDate == '') {
				this.toast('请选择终止月份', 'error');
				return;
			}
			if (this.rangeDate.minDate == '' && this.rangeDate.maxDate != '') {
				this.toast('请选择起始月份', 'error');
				return;
			}
			if (this.rangeDate.minDate > this.rangeDate.maxDate) {
				this.toast('起始月份不能大于终止月份', 'error');
				return;
			}
			this.loading = true;
			this.billLists = [];
			this.errorBillList = [];
			for(var i=0; i<this.billIdList.length; i++){
				console.log(new Date());
				if(i == this.billIdList.length-1){
					// 最后一个账单详情
					this.viewDetailFun(this.billIdList[i], 2, 1);
				}else{
					this.viewDetailFun(this.billIdList[i], 2);
				}
			}
		},
		viewDetailFun(row, batch, lastBatch) {
			/**
			 * 查询账单明细
			 */
			if(!batch){
				// 查询单个明细
				this.currentPosition.page = 2;
				this.billDetailInfo.curApp = this.getAppName(row.appId);
				this.billDetailInfo.curDistributor = row.proxyName;
				this.billDetailInfo.curFirstChannel = row.mainChannelName;
			}
			var parameter = {
				sessionid: this.userperadmin.sessionid,
				id: row.id
			}
			console.log('getBillDetailReg parameter', parameter);
			this.billDetailInfo.loading = true;
			getBillDetailReg(parameter)
			.then((res) => {
				console.log('getBillDetailReg', res);
				if(res.data.status === 0){
					if(batch === 2){
						// 批量查询明细
						console.log(2, row.id);
						var chineseMonth = '';
						var temp = res.data.bill.month.split('-');
						chineseMonth = temp[0] + '年' + temp[1] + '月';
						var appName = '';
						AppList.forEach(function(item){
							if(res.data.bill.appId == item.id) {
								appName = item.name;
							}
						});
						console.log(appName);
						try{
							this.billLists.push({
								id: 'bill' + row.id,
								billName: res.data.bill.product,
								partnerName: res.data.proxy.company,
								payDate: res.data.bill.createTime,
								ticketType: '增值税专用发票',
								demo: appName,
								payMonth: chineseMonth,
								payData: res.data.billDetails[0].rewardSum,
								payRatio: res.data.billDetails[0].price,
								payMount: res.data.billDetails[0].cost2,
								fromCompany: res.data.balanceAccountList[0].company,
								fromTaxNo: res.data.balanceAccountList[0].taxpayerNo,
								fromBank: res.data.balanceAccountList[0].bank,
								fromBankNo: res.data.balanceAccountList[0].accountNo,
								fromAdress: res.data.balanceAccountList[0].address,
								fromTel: res.data.balanceAccountList[0].phone,
								fromTicketContent: '',
								toCompany: res.data.proxy.company,
								toTaxNo: res.data.proxy.dutyParagraph,
								toBank: res.data.proxy.bank,
								toBankNo: res.data.proxy.bankAccount,
								toTaxNumber: res.data.proxy.taxNo !== undefined ? res.data.proxy.taxNo: '',
								downloadState: 0
							});
							if(this.billLists.length === (this.billIdList.length - this.errorBillList.length)){
								// 下载到最后一条明细
								console.log('last', this.billLists);
								this.downloadBillExcel(true);
							}
						}catch (error){
							this.errorBillList.push(row.id);
						}
					}else if(batch === 1){
						// 预览一个账单
						console.log(1, row.id);
						var chineseMonth = '';
						var temp = res.data.bill.month.split('-');
						chineseMonth = temp[0] + '年' + temp[1] + '月';
						this.singleBill = [];
						var appName = '';
						AppList.forEach(function(item){
							if(res.data.bill.appId == item.id) {
								appName = item.name;
							}
						});
						console.log(appName);
						this.singleBill.push({
							id: 'bill' + row.id,
							billName: res.data.bill.product,
							channelName: res.data.bill.mainChannelName,
							partnerName: res.data.proxy.company,
							payDate: res.data.bill.createTime,
							ticketType: '增值税专用发票',
							demo: appName,
							payMonth: chineseMonth,
							payData: res.data.billDetails[0].rewardSum,
							payRatio: res.data.billDetails[0].price,
							payMount: res.data.billDetails[0].cost2,
							fromCompany: res.data.balanceAccountList[0].company,
							fromTaxNo: res.data.balanceAccountList[0].taxpayerNo,
							fromBank: res.data.balanceAccountList[0].bank,
							fromBankNo: res.data.balanceAccountList[0].accountNo,
							fromAdress: res.data.balanceAccountList[0].address,
							fromTel: res.data.balanceAccountList[0].phone,
							fromTicketContent: '',
							toCompany: res.data.proxy.company,
							toTaxNo: res.data.proxy.dutyParagraph,
							toBank: res.data.proxy.bank,
							toBankNo: res.data.proxy.bankAccount,
							toTaxNumber: res.data.proxy.taxNo !== undefined ? res.data.proxy.taxNo: '',
							downloadState: 0
						});
						this.downloadBillExcel();
					}else{
						// 查询单个账单明细
						this.billDetailInfo.proxyInfo = res.data.proxy;
						this.billDetailInfo.billInfo = res.data.bill;
						this.billDetailInfo.balanceAccountList = res.data.balanceAccountList[0];
						this.billDetailInfo.billDetails = res.data.billDetails;
						var tempBillDetails = res.data.billDetails.slice(0);
						// console.log('tempBillDetails', tempBillDetails);
						// tempBillDetails = tempBillDetails.split(',');
						var _this = this;
						tempBillDetails.forEach(function(item,index,arr) {
		  					item.price = _this.rewardPriceFormatter(item.price);
		  				});
		  				this.billDetailInfo.billDetails = tempBillDetails;
		  				this.billDetailInfo.billStepList = res.data.billStepList;

		  				// 生成预览
		  				var chineseMonth = '';
						var temp = res.data.bill.month.split('-');
						chineseMonth = temp[0] + '年' + temp[1] + '月';
						this.singleBill = [];
						this.singleBill.push({
							id: 'bill' + row.id,
							billName: res.data.bill.product,
							channelName: res.data.bill.mainChannelName,
							partnerName: res.data.proxy.company,
							payDate: res.data.bill.createTime,
							ticketType: '增值税专用发票',
							demo: '',
							payMonth: chineseMonth,
							payData: res.data.billDetails[0].rewardSum,
							payRatio: res.data.billDetails[0].price,
							payMount: res.data.billDetails[0].cost2,
							fromCompany: res.data.balanceAccountList[0].company,
							fromTaxNo: res.data.balanceAccountList[0].taxpayerNo,
							fromBank: res.data.balanceAccountList[0].bank,
							fromBankNo: res.data.balanceAccountList[0].accountNo,
							fromAdress: res.data.balanceAccountList[0].address,
							fromTel: res.data.balanceAccountList[0].phone,
							fromTicketContent: '',
							toCompany: res.data.proxy.company,
							toTaxNo: res.data.proxy.dutyParagraph,
							toBank: res.data.proxy.bank,
							toBankNo: res.data.proxy.bankAccount,
							toTaxNumber: res.data.proxy.taxNo !== undefined ? res.data.proxy.taxNo: '',
							downloadState: 0
						});
						console.log('this.singleBill', this.singleBill);
					}

				}else if(res.data.status === 1){
					this.$router.replace("/login");
				}else{
					this.toast(res.data.statusMsg, 'error');
				}
				this.loading = false;
				this.billDetailInfo.loading = false;
			})
			.catch((error) => {
				this.loading = false;
				this.billDetailInfo.loading = false;
				console.log(error);
			});
		},
		toast(msg, type) {
			this.$message({
	            type: type,
	            message: msg
	        });
		}
	}
}
</script>

<style lang="scss" scoped>
