	}
	.page-title{
		text-align: center;
		font-size: 20px;
		width: 100%;
		height: 50px;
		margin-bottom: 10px;
		font-weight: 700;
		position: relative;
		.btn{
			position: absolute;
			right: 10px;
			top: 0;
		}
		.back{
			display: inline-block;
			position: absolute;
			top: 10px;
			left: 10px;
			color: #409EFF;
			cursor: pointer;
		}
		.small{
			font-size: 14px;
			font-weight: 500;
		}
	}
	.block{
		margin-bottom: 14px;
		.block-title{
			height: 40px;
			line-height: 40px;
			background: #E5E9F2;
			font-size: 14px;
			font-weight: 700;
			border: 1px solid #EBEEF5;
			span{
				border-left: 4px solid #409EFF;
				margin-left: 20px;
				padding-left: 10px;
			}
		}
		.content{
			border: 1px solid #EBEEF5;
			background: #fcfcfc;
			margin-top: -1px;
			padding: 10px 14px;
			font-size: 16px;
			&.table-wrapper{
				padding: 0;
			}
		}
	}
	.btn-group{
		margin-top: 10px;
		text-align: center;
	}
	.table-wrapper{
		margin-top: 10px;
		height: 500px;
		height: -webkit-calc(100% - 200px);
		height: -moz-calc(100% - 200px);
		height: calc(100% - 200px);
		thead{
			th{
				background: #F5F7FA;
			}
		}
		.tableA{
			color: #409EFF;
			font-weight: 700;
			text-decoration: none;
			cursor: default;
			&.unChenck{
				color: #666;
			}
			&.checking{
				color: #666;
			}
			&.denail{
				color: #D54F42;
				text-decoration: none;
				cursor: default;
			}
			&.noPay{
				color: #E6A23C;
			}
			&.pay{
				color: #666;
			}
			&.isUpdate{
				color: #409EFF;
				cursor: pointer;
				text-decoration: underline;
			}
			&.denail{
				color: #D54F42;
			}
		}
	}
	.tableMinHeight{
		height: 25rem;
	}
	.form-wrapper{
		.input-group{
			height: 30px;
			font-size: 0;
			margin-bottom: 10px;
			label{
				display: inline-block;
				height: 100%;
				width: 28%;
				text-align: right;
				// margin-right: 10px;
				font-size: 14px;
				color: #909AB3;
			}
			input{
				height: 100%;
				width: 60%;
				border-radius: 2px;
				border: 1px solid #DCDFE6;
				padding-left: 10px;
				&[readonly=readonly]{
					color: #666;
				}
			}
			textarea{
				width: 60%;
				resize: none;
				color: #666;
				border: 1px solid #DCDFE6;
				border-radius: 2px;
				line-height: 1.6;
				padding: 4px 0;
				padding-left: 10px;
				&[disabled=disabled]{
					background: #fff;
				}
			}
		}
		.textarea-group{
			height: 50px;
			label{
				vertical-align: top;
			}
		}
	}
}
</style>
<style>
	.grid-content, .content-wrapper{
		height: 100%;
	}
	.el-table--border th{
		background: #F5F7FA !important;
	}
	.el-dialog__header{
		background: #F5F7FA;
		padding-top: 10px;
		font-weight: 700;
	}
	.el-dialog__headerbtn{
		top: 12px;
	}
	.el-dialog__footer{
		/*background: #F5F7FA;*/
	}
	.billState{
		height: 30px;
		border-radius: 2px !important;
	}
	.el-input::-webkit-scrollbar-thumb{
		border-radius: 2px !important;
	}
	.handler-panel .el-date-editor.el-input{
		width: 120px;
	}
</style>


