          getTime: data[key]._1, //次数
          successRen: data[key]._4, //人数
          successTime: data[key]._3 //次数
        };
        table1data.push(temp1);
        // var temp2 = {
        // date:key,
        // 	flow:'点击密码登录',
        // 	getNumber:data[key].mimadenglu_6,//人数
        // 	getTime:data[key].mimadenglu_5,//次数
        // 	successRen:data[key].mimadenglu_8,//人数
        // 	successTime:data[key].mimadenglu_7,//次数
        // }
        // table1data.push(temp2)
        var temp3 = {
          date: key,
          flow: "输入手机号",
          getNumber: data[key].shurushoujihao_6, //人数
          getTime: data[key].shurushoujihao_5, //次数
          successRen: data[key].shurushoujihao_8, //人数
          successTime: data[key].shurushoujihao_7 //次数
        };
        table1data.push(temp3);
        var temp4 = {
          date: key,
          flow: "输入密码",
          getNumber: data[key].shurumima_6, //人数
          getTime: data[key].shurumima_5, //次数
          successRen: data[key].shurumima_8, //人数
          successTime: data[key].shurumima_7 //次数
        };
        table1data.push(temp4);
        var temp5 = {
          date: key,
