    getTableDataFun(tableInfo, index) {
      tableInfo.data = [];
      tableInfo.loading = true;
      var parameter = {
        sessionid: this.userperadmin.sessionid,
        minDate: tableInfo.rangeDate.minDate,
        maxDate: tableInfo.rangeDate.maxDate,
        page_name: tableInfo.pageName,
        channel: tableInfo.channel,
        appId: this.appInfo.selected
      };
      console.log("搜索数据", parameter);
      actionRetentionReg(parameter).then(res => {
        console.log("getTableDataFun", res);
        if (res.data.status === 0) {
          if (index === 1) {
            this.getTableDataFun1(res.data.actionList);
          } else if (index === 2) {
            this.getTableDataFun2(res.data.actionList);
          } else if (index === 3) {
            this.getTableDataFun3(res.data.actionList);
          } else if (index === 4) {
            this.getTableDataFun4(res.data.actionList);
          } else if (index === 5) {
            this.getTableDataFun5(res.data.actionList);
          } else if (index === 6) {
            this.getTableDataFun6(res.data.actionList);
          }
          tableInfo.loading = false;
        } else if (res.data.status === 1) {
          this.$router.replace("/login");
        } else {
          this.toast(res.data.statusMsg, "error");
        }
      });
      // .catch((err) => {
      // 	this.toast('请求错误，请联系管理员', 'error');
      // });
    },
    // getChannelListFun() {
    // 	// 获取渠道列表
    // },
    getAppListFun() {
      // 获取app列表
      var parameter = {
        sessionid: this.userperadmin.sessionid
      };
      getAppListReg(parameter)
        .then(res => {
          // console.log('getAppListFun', res);
          if (res.data.status === 0) {
            this.appInfo.list = res.data.appList;
            // 初始化产品默认选择
            this.appInfo.selected = this.appInfo.list[0].id;
          } else if (res.data.status === 1) {
            this.$router.replace("/login");
          } else {
            this.toast(res.data.statusMsg, "error");
          }
        })
        .catch(err => {
          this.toast("请求APP列表错误，请联系管理员", "error");
        });
    },
