  margin-bottom: 25px;
  border: 1px solid #dee5e7;
  box-shadow: 0 0 6px 0 #dcdcdc;
}
.table-header {
  padding: 10px 15px;
  font-size: 16px;
  border-bottom: 1px solid #dee5e7;
  background: #e5e9f2;
  position: relative;
}
.table-action {
  padding: 10px;
  font-size: 12px;
  box-sizing: border-box;
}
.toolbar {
  overflow: hidden;
  margin-bottom: 15px;
}
.myRangeMonth {
  float: right;
}
.table-header > .title {
  font-weight: 700;
}
.table-header > .btn {
  float: right;
  cursor: pointer;
}
.table-header > .btn:hover {
  color: #409eff;
}
@media only screen and (min-width: 1200px) {
  .marginR {
    margin-left: 20px;
  }
}
</style>
<style>
.userAction .el-table--small td,
.el-table--small th {
  padding: 4px 0;
}
.userAction .el-table .cell,
.el-table--border td:first-child .cell,
.el-table th div,
.el-table--border th:first-child .cell {
  padding: 0 4px;
}
.userAction .el-checkbox__label {
  padding-left: 5px;
}
.userAction .el-checkbox + .el-checkbox {
  margin-left: 10px;
}
.userAction .el-date-editor.el-input {
  width: 100px;
}
.userAction .el-select .el-input--suffix .el-input__inner {
  padding-right: 6px;
  padding-left: 6px;
}
.userAction .el-input--suffix .el-input__inner {
  padding-right: 6px;
}
.userAction .el-select > .el-input {
  width: 100px;
}
</style>


