<template>
	<div class="page">
		<div class="bread-nav">
			<p>当前位置：数据中心 > 重跑历史</p>
		</div>
		<section class="table-container">
			<div class="search-wrapper" v-loading="searchInfo.loading" element-loading-spinner="el-icon-loading" element-loading-text="拼命加载中">
				<div class="fleft">
					<el-select
					    v-model="searchInfo.reportSelected"
					    clearable
					    filterable
					    allow-create
					    default-first-option
					    placeholder="输入/选择报表">
					    <el-option
					      v-for="report in searchInfo.reportList"
					      :key="report.id"
					      :label="report.name"
					      :value="report.id">
					    </el-option>
					</el-select>
				</div>
				<div class="fleft">
					<span class="label">选择日期:</span>
					<el-date-picker v-model="searchInfo.dateRange" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
				</div>
				<button class="btn primary" @click="searchTableDataFun"><i class="fa fa-search"></i>查询</button>
			</div>
			<div class="table-wrapper" v-loading="tableInfo.loading" element-loading-spinner="el-icon-loading" element-loading-text="拼命加载中">
				<el-table :data="tableInfo.data" style="width: 100%" border size="small" >
	                <el-table-column type="index" label="序号"></el-table-column>
	                <el-table-column prop="tableId" label="重跑报表" :formatter="reportFormatter"></el-table-column>
	                <el-table-column prop="startDate" label="重跑数据日期" :formatter="dateRangeFormatter"></el-table-column>
	                <el-table-column prop="channelName" label="渠道名称"></el-table-column>
	                <el-table-column prop="channel" label="渠道号"></el-table-column>
	                <el-table-column prop="optimization" label="优化比例(%)" :formatter="optiFormatter"></el-table-column>
	                <el-table-column prop="id" label="操作时间" :formatter="actionDateFormatter"></el-table-column>
	                <el-table-column label="详情">
	                    <template slot-scope="scope">
		                    <el-tooltip placement="left" effect="light">
							  	<div slot="content">
							  		<p class="tooltip-title">详情</p>
							  		{{scope.row.comment}}
							  	</div>
							  	<el-button type="text" size="small"><i class="fa fa-file-text-o" aria-hidden="true"></i></el-button>
							</el-tooltip>
	                        <!-- <el-tooltip class="item" effect="dark" :content="scope.row.comment" placement="left">
						      	
						    </el-tooltip> -->
	                    </template>
	                </el-table-column>
	            </el-table>
	            <!-- <el-pagination
	                background
	                @size-change="pageSizeChange"
	                @current-change="currentPageChange"
	                :current-page="tableInfo.currentPage"
	                :page-sizes="tableInfo.pageSizes"
	                :page-size="tableInfo.pageSize"
	                layout="total, sizes, prev, pager, next, jumper"
	                :total="tableInfo.dataSize" class="paginationWrapper">
	            </el-pagination> -->
			</div>
		</section>
	</div>
</template>
<script>
import {getReportTypeReg,getRedoHistoryListReg} from '../../api/api.js';
import {timeStempToDatetime,dateFormatter} from '../../api/formatter.js';
export default {
	data() {
		return {
			userperadmin: {},
			searchInfo: {
				loading: false,
				reportSelected: '',
				reportList: [],
				dateRange: [],
				startDate: '',
				endDate: ''
			},
			tableInfo: {
				loading: false,
				data: [],
				currentPage: 1,
				pageSize: 10,
				pageSizes: [10,20,50,100,500,1000],
				dataSize: 0
			}
		}
	},
	mounted() {
		this.userperadmin = JSON.parse(localStorage.getItem('userperadmin'));
		this.getReportTypeFun();
		//this.getRedoHistoryListFun();
	},
	methods: {
		getReportTypeFun() {
			/**
			 * 获取报表类型
			 */
			var parameter = {
				sessionid: this.userperadmin.sessionid
			};
			//console.log('getReportTypeFun parameter',parameter);
			this.searchInfo.loading = true;
			getReportTypeReg(parameter).then((res)=>{
		  		// console.log('getReportTypeFun',res);
		  		this.searchInfo.loading = false;
		  		if(res.data.status === 0) {
		  			this.searchInfo.reportList = res.data.fromTypeList;
		  		}else if(res.data.status === 1){
		  			this.$router.replace("/login");
		  		}else {
		  			this.toast(res.data.statusMsg, 'error');
		  		}
		  	}).catch((error)=>{
		  		this.searchInfo.loading = false;
		  		console.log('getReportTypeFun error',error);
		  		this.toast('服务器错误，请联系管理员','error');
			});
		},
		searchTableDataFun() {
			//console.log(this.searchInfo.dateRange);
			if(!this.searchInfo.dateRange){
				this.searchInfo.dateRange = [];
			}
			if(this.searchInfo.dateRange.length === 0 && this.searchInfo.reportSelected === ''){
				this.toast('请至少选择一个查询条件','warning');
				return;
			}
			if(this.searchInfo.dateRange.length !== 0){
				this.searchInfo.dateRange[0] = dateFormatter(this.searchInfo.dateRange[0]);
				this.searchInfo.dateRange[1] = dateFormatter(this.searchInfo.dateRange[1]);
			}
			this.getRedoHistoryListFun();
		},
		getRedoHistoryListFun() {
			/**
			 * 获取历史记录
			 */
			var parameter = {
				sessionid: this.userperadmin.sessionid,
				queryType: 1,
				minDate: this.searchInfo.dateRange[0] === undefined ? '' : this.searchInfo.dateRange[0],
				maxDate: this.searchInfo.dateRange[1] === undefined ? '' : this.searchInfo.dateRange[1],
				tableId: this.searchInfo.reportSelected
			};
			//console.log('getRedoHistoryListReg parameter',parameter);
			this.tableInfo.loading = true;
			this.tableInfo.data = [];
			getRedoHistoryListReg(parameter).then((res)=>{
		  		console.log('getRedoHistoryListReg',res);
		  		this.tableInfo.loading = false;
		  		if(res.data.status === 0) {
		  			this.tableInfo.data = res.data.optimizationTaskList;
		  		}else if(res.data.status === 1){
		  			this.$router.replace("/login");
		  		}else {
		  			this.toast(res.data.statusMsg, 'error');
		  		}
		  	}).catch((error)=>{
		  		this.tableInfo.loading = false;
		  		console.log('getReportTypeFun error',error);
		  		this.toast('服务器错误，请联系管理员','error');
			});
		},
		getDetailFun(row) {
			/**
			 * 查看详细信息
			 */
		},
		actionDateFormatter(row) {
			return timeStempToDatetime(row.id);
		},
		optiFormatter(row) {
			return row.optimization + '%';
		},
		dateRangeFormatter(row) {
			var startDate = row.startDate;
			var endDate = row.endDate;
			var dateRange = startDate + ' 至 ' + endDate;
			return dateRange;
		},
		reportFormatter(row) {
			var reportList = this.searchInfo.reportList.copyWithin(0,0);
			var name = row.tableId;
			for (var i=0; i<reportList.length; i++){
				if(reportList[i].id === row.tableId){
					name = reportList[i].name.trim();
					break;
				}
			}
			return name;
		},
		pageSizeChange(pageSize) {
			/**
			 * 表格页数变化
			 */
		},
		currentPageChange(currentPage) {
			/**
			 * 表格页码变化
			 */
		},
		toast(msg, type) {
			this.$message({
	            type: type,
	            message: msg
	        });
		}
	}
}
</script>
<style lang="scss" scoped>
.page{
	text-align: left;
	zoom: 1;
	&:after{
		content: "";
		display: block;
		overflow: hidden;
		clear: both;
	}
	.bread-nav{
		margin-bottom: 0.5rem;
		font-size: .6rem;
		border-left: 4px solid #DCDFE6;
		padding-left: 10px;
		color: #999;
	}
	.fleft{
		float: left;
		margin-right: 10px;
	}
	.label{
		font-size: .6rem;
		color: #999;
	}
	.btn{
		height: 34px;
		padding-left: 0.5rem;
		padding-right: 0.5rem;
		border-radius: 2px;
		border: none;
		&>i{
			padding-right: 2px;
		}
	}
	.primary{
		background: #48A2FF;
		color: #fff;
	}
	.search-wrapper{
		padding-bottom: 1rem;
	}
	.paginationWrapper{
		text-align: center;
		padding-top: 1rem;
	}
	
}
</style>
<style lang="scss">
.el-tooltip__popper{
	min-width: 100px;
	max-width: 300px;
	padding-bottom: .5rem;
	line-height: 1.6;
	p{
		text-align: center;
		font-weight: bold;
		padding-bottom: .2rem;
	}
}
</style>


