<template>
	<div class="operDataEchart">
		<div id='main1' :style="{minWidth:clientWidth, height: '430px'}" ></div>
	</div>
</template>
<script>
	export default {
		props:['opDataAll','operIndex'],
		data() {
			return {
                clientWidth:'1000px'
			}
		},
		mounted() {
			console.log('11111')
			console.log(this.opDataAll.name)
			// console.log(this.operIndex)
			this.windosWidth()
		},
		watch:{
			//监听父组件传过来的数据
			opDataAll:{
				handler:function(old,newn){
					console.log('echart')
					console.log(old)
				    console.log(newn)
					this.$nextTick(function(){
				        this.showEarch()
					})
				},
				deep:true
			}
		},
		methods:{
			windosWidth() {
				var clientWidth = document.documentElement.clientWidth;
				 this.clientWidth = (clientWidth-300)+'px';
			},
			showEarch() {
				// var myChart = this.$echarts.init();
				 var myChart = this.$echarts.init(document.getElementById('main1'));
				    // 绘制图表
				 myChart.setOption({
				 	title: {
				        text: this.opDataAll.name
				    },
				    tooltip: {
				        trigger:this.opDataAll.tooltipTrigger,
				        formatter:this.opDataAll.tooltipFormatter //显示百分比 
				        // trigger: 'item'只显示一个折线数据
				    },
				    legend:this.opDataAll.legendData,
				    grid: {
				        left: '3%',
				        right: '20%',
				        bottom: '8%',
				        containLabel: true
				    },
				    toolbox: {
				    	top:-8,
				        feature: {
				            saveAsImage : {show: true}
				        }
				    },
				    xAxis: {
				        type: 'category',
				        boundaryGap: false,
				        data: this.opDataAll.xAxisData,
				        axisLabel:{  
	                         interval:0,//横轴信息全部显示  
	                         rotate:-30,//-30度角倾斜显示  
	                    } 
				    },
				    yAxis: {
				        type: 'value',
				        axisLabel: {  
		                  show: true,  
		                  interval: 'auto',  
		                  formatter:this.opDataAll.yAxisFormatter //显示百分比 
		                }
				    },
				    series:this.opDataAll.seriesData


				 },true)
			}
		}
	}
</script>
<style lang="scss" scoped>
	
</style>


