import Vue from 'vue'
import Router from 'vue-router'
import Home from '../views/Home'
import Login from '../views/Login'
/*
 总结中心
*/
import dailyInfo from '../views/ConclusionTheCenter/dailyInfo'
import dailyHistory from '../views/ConclusionTheCenter/dailyHistory'
import resultsTheQuery from '../views/ConclusionTheCenter/resultsTheQuery'
/*
 渠道中心
*/
import SharedPaes from '../views/navpage/sharedPaes'
import proxy from '../views/channels/Proxy'
import channel from '../views/channels/Channel'
/*
 报表中心
*/
// dataInfo ,dataTiem, ProcessConversTable
import dataInfo from '../views/ReportCenter/dataInfo';
import dataTiem from '../views/ReportCenter/dataTiem';
import ProcessConversTable from '../views/ReportCenter/ProcessConversTable';
import seoDataInfo from '../views/ReportCenter/seoDataInfo';
import UserAction from '../views/ReportCenter/UserAction';
import generationOfuser from '../views/ReportCenter/GenerationOfUserAction';
import analyzeOfuser from '../views/ReportCenter/AnalyzeOfUserAction';
import durationAnalysis from '../views/ReportCenter/durationAnalysis';
import returnAnalysis from '../views/ReportCenter/returnAnalysis';
import userAnalysis from '../views/ReportCenter/userAnalysis';
import ReportConfigurat from '../views/ReportCenter/ReportConfigurat';//报表配置
/*
  数据中心
*/
//  CostDataRecord,RecordHistory, DataBackRun , RedoHistory, OpCostDataManagement
import costDataRecord from '../views/dataControl/CostDataRecord';
import recordHistory from '../views/dataControl/RecordHistory';
import DataBackRun from '../views/dataControl/dataBackRun';
import RedoHistory from '../views/dataControl/RedoHistory';
import OpCostDataManagement from '../views/dataControl/OpCostDataManagement';
/*
  系统中心
*/
//  functionalGroup 
import functionalGroup from '../views/systemControl/functionalGroup'
import UserControl from '../views/systemControl/UserControl'
/*
  运营中心
*/
import PartnerManagement from '../views/operatingCenter/PartnerManagement'
/*
  财务中心
*/
import DistributorBill from '../views/financeCenter/DistributorBill'
import PartnerBill from '../views/financeCenter/PartnerBill'
import synchrFinanace from '../views/financeCenter/SynchrFinance'

Vue.use(Router)

export default new Router({
  routes: [
    {
        path: '/Login',
        component: Login,
        name: '',
        meta:'00',
        hidden: true
    },
    { path: "/", redirect: '/Login',meta:'00',hidden: true },
    {
       path: '/',
       component: Home,
       name: '总结中心',
       iconCls: 'el-icon-edit-outline',//图标样式class
       meta:'03',
       children: [
          { path: '/dailyInfo',component:dailyInfo, name: '日报',meta:'0301'},
          { path: '/dailyHistory',component:dailyHistory, name: '日报历史',meta:'0302'},
          { path: '/resultsTheQuery',component:resultsTheQuery, name: '业绩查询',meta:'0303'}
       ]
    },
    {
       path: '/',
       component: Home,
       name: '渠道中心',
       iconCls: 'el-icon-message',//图标样式class
       meta:'01',
       children: [
          { path: '/SharedPaes', component:SharedPaes, name: '',meta:'0100',hidden: true},
          { path: '/proxy', component:proxy, name: '渠道商管理',meta:'0101'},
          { path: '/channel', component:channel, name: '渠道管理',meta:'0102'},

       ]
    },
    {
       path: '/',
       component: Home,
       name: '报表中心',
       iconCls: 'el-icon-tickets',//图标样式class
       meta:'02',
       children: [
          { path: '/dataInfo', component:dataInfo, name: '数据详情',meta:'0201'},
          // { path: '/dataTiem', component: dataTiem, name: '数据时报',meta:'0202'},
          { path: '/ProcessConversTable', component:ProcessConversTable, name: '流程转化表',meta:'0203'},
          { path: '/seoDataInfo', component:seoDataInfo, name: 'SEO/SEM查询',meta:'0205'},
          { path: '/ReportConfigurat', component:ReportConfigurat, name: '报表配置',meta:'0206'},
          { path: '/userAction', component: UserAction, name: '用户行为',meta:'0204', children: [
            { path: '/generationOfuser', component: generationOfuser, name: '概况',meta:'020401'},
            { path: '/analyzeOfuser', component:analyzeOfuser, name: '转化分析',meta:'020402'},
            { path: '/durationAnalysis', component:durationAnalysis, name: '时长分析',meta:'020403'},
            { path: '/returnAnalysis', component:returnAnalysis, name: '返回分析',meta:'020404'},
            { path: '/userAnalysis', component:userAnalysis, name: '用户分析',meta:'020405'}
          ]}
       ]
    },
    {
        path: '/',
        component: Home,
        name: '数据中心',
        iconCls: 'el-icon-date',
        meta:'04',
        children: [
            { path: '/costDataRecord', component:costDataRecord, name: '成本数据录入',meta:'0401'},
            { path: '/recordHistory', component:recordHistory, name: '成本录入历史',meta:'0402'},
            { path: '/DataBackRun', component:DataBackRun, name: '数据重跑',meta:'0403'},
            { path: '/RedoHistory', component:RedoHistory, name: '重跑历史',meta:'0404' },
            { path: '/OpCostDataManagement', component:OpCostDataManagement, name: '运营成本数据管理',meta:'0405' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '运营中心',
        iconCls: 'el-icon-news',
        meta:'06',
        children: [
            { path: '/PartnerManagement', component:PartnerManagement, name: '合作方管理',meta:'0601'}
        ]
    },
    {
        path: '/',
        component: Home,
        name: '财务中心',
        iconCls: 'el-icon-news',
        meta:'07',
        children: [
            { path: '/distributorBill', component: DistributorBill, name: '渠道商账单',meta:'0701'},
            { path: '/partnerBill', component: PartnerBill, name: '合作方账单',meta:'0702'},
            { path: '/synchrFinanace', component:synchrFinanace, name: '财务同步',meta:'0703'}
        ]
    },
       {
        path: '/',
        component: Home,
        name: '系统中心',
        iconCls: 'el-icon-setting',
        meta:'05',
        //leaf: true,//只有一个节点
        children: [
            { path: '/functionalGroup', component:functionalGroup, name: '功能列表',meta:'0501'},
            { path: '/UserControl', component:UserControl, name: '账号管理',meta:'0502'}
        ]
    }
  ]
})



