<template>
	<div class="page cost-data-record-page">
		<div class="bread-nav">
			<p>当前位置：数据中心 > 成本数据录入</p>
		</div>
		<section class="action-wrapper">
			<div class="fileUploadBtn btn primary" v-show='costDataEntry'>
			    <span class="text"><i class="fa fa-upload"></i>上传预览</span>
			    <input id="fileHandle" type="file" class="upload" @change="getFileContent"/>
			</div>
			<button class="btn error" @click="clearFileContent" v-show='costDataEntry'><i class="fa fa-close"></i>删除预览</button>
			<button class="btn success" @click="uploadDataFun" v-show='costDataEntry'><i class="fa fa-check"></i>提交预览</button>
			<p class="tooltip"><span class="start">注：</span>灰色<i class="fa fa-check-circle" aria-hidden="true"></i>表示预览状态，绿色<i class="fa fa-check-circle success" aria-hidden="true"></i>表示已经提交预览。每次上传预览时，上一次未提交的预览会被覆盖。<span class="notice" @mouseover="toggleImg" @mouseout="toggleImg">查看数据格式</span></p>
		</section>
		<section class="zindextop" v-if="showImg">
			<img src="../../assets/images/cost_1.png" alt="提示">
		</section>
		<section class="table-wrapper"  v-loading="loading" element-loading-spinner="el-icon-loading" element-loading-text="正在提交">
			<div class="table-toolbar" v-if="fileArray.length >= 1">
				<label v-if="!showAllFilesFlag">Excel sheet：</label>
				<el-select
				    v-model="page"
				    clearable
				    filterable
				    allow-create
				    default-first-option
				    placeholder="选择数据页" v-if="!showAllFilesFlag">
				    <el-option
				      v-for="(page,index) in fileArray"
				      :key="page.id"
				      :label="page.id"
				      :value="index">
				    </el-option>
				</el-select>
				<button class="btn primary" @click="showAllFiles">{{toogleText}}</button>
			</div>
			<el-table id="fileTable" :data="fileContent" height="400px" border style="width: 100%">
			    <el-table-column type="index" label="序号" width="80"></el-table-column>
			    <el-table-column prop="date" label="日期"></el-table-column>
			    <el-table-column prop="appid" label="app"></el-table-column>
			    <el-table-column prop="channelName" label="渠道"></el-table-column>
			    <el-table-column prop="channel" label="渠道号"></el-table-column>
			    <el-table-column prop="cost" label="成本金额"></el-table-column>
			    <el-table-column prop="state" label="上传状态">
			    	<template slot-scope="scope">
			    		<i class="fa fa-check-circle" aria-hidden="true" :class="{success: uploading}"></i>
			    		<!-- <i class="fa fa-close error" v-if="uploading" aria-hidden="true"></i> -->
			    	</template>
			    </el-table-column>
		  	</el-table>
		</section>
	</div>
</template>
<script>
import {uploadCostRecordReg, getAppListReg} from '../../api/api.js';
import XLSX from 'xlsx';
	export default {
		name: 'costDataRecord',
		data() {
			return {
				userperadmin: {},
				appList: [],
				fileData: [],
				fileArray: [],
				loading: false,
				allFiles: [],
				page: 0,
				uploading: false,
				showImg: false,
				showAllFilesFlag: false,
				toogleText: '查看全部',
				costDataEntry:false//成本数据录入上传删除提交权限状态
			}
		},
		mounted() {
			 // 获取对操作是否限权
			 let userPermission = JSON.parse(unescape(sessionStorage.getItem('userPermiss')));
             console.log(userPermission);
             let _this = this;
             // 对渠道商管理中操作限权进行循环读取状态
             for (var i = 0; i <userPermission.data.length; i++) {
             	if (userPermission.data[i].meta == '04') {
             		for (var j = 0; j <userPermission.data[i].children.length; j++) {
             			if (userPermission.data[i].children[j].meta == '0401') {
             				for (var m = 0; m < userPermission.data[i].children[j].children.length; m++) {
             					console.log(userPermission.data[i].children[j].children[m].meta)
             					if (userPermission.data[i].children[j].children[m].meta == '040101') {
             						// 获取删除成本录入历史权限值
             						_this.costDataEntry = userPermission.data[i].children[j].children[m].show == '0'?false:true;
             					};
             				};
             				
             			};
             		};
             	};
             	
             };
			this.userperadmin = JSON.parse(localStorage.getItem('userperadmin'));
			this.getAppArrayFun();
		},
		computed: {
			fileContent() {
				var fileData = [];
				if(this.showAllFilesFlag){
					fileData = this.allFiles;
				}else{
					var page = this.page | 0;
					if(this.fileArray.length !== 0){
						var files = this.fileArray[page].list;
						fileData = this.fileDataFormatter(files);
					}
				}
				return fileData;
			}
		},
		methods: {
			getAppArrayFun() {
				/**
				 * 获取APP列表
				 */
				var parameter = {
					sessionid: this.userperadmin.sessionid
				};
				getAppListReg(parameter).then((res)=>{
			  		console.log('getAppListReg', res);
			  		if(res.data.status === 0) {
			  			this.appList = res.data.appList;
			  		}else if(res.data.status === 1){
			  			this.$router.replace("/login");
			  		}else {
			  			this.toast(res.data.statusMsg, 'error');
			  		}
			  	}).catch((error)=>{
			  		this.toast('服务器错误，请联系管理员','error');
				});
			},
			toggleImg() {
				this.showImg = !this.showImg;
			},
			dateFormatter(date) {
				var newDate = '';
				var temp = date.split('/');
				var year,
					month,
					day;
				if(temp[0].length === 4 || temp[2].length === 4){
					year = temp[0];
					month = ('0' + temp[1]).substr(-2,2);
					day = ('0' + temp[2]).substr(-2,2);
				}else{
					year = ('20' + temp[2]).substr(-4,4);
					month = ('0' + temp[0]).substr(-2,2);
					day = ('0' + temp[1]).substr(-2,2);
				}
				newDate = year + '-' + month + '-' + day;
				return newDate;
			},
			fileDataFormatter(files) {
				var fileData =[];
				for(var item in files){
					var itemStr = '渠道';
					if(files[item]['渠道'] === undefined) {
						itemStr = '渠道名';
						if(files[item]['渠道名'] === undefined){
							itemStr = '渠道名称';
						}
					}
					fileData.push({
						date: this.dateFormatter(files[item]['日期']),
						appid: files[item]['app'],
						channelName: files[item][itemStr],
						channel: files[item]['渠道号'],
						cost: files[item]['成本金额'] !== undefined ? files[item]['成本金额'] : files[item]['成本']
					});
				}
				return fileData;
			},
			appFormatter(appName) {
				var appid;
				this.appList.forEach(function(item){
					if(item.name === appName){
						appid = item.id;
					}
				});
				return appid;
			},
			clearFileContent() {
				/**
				 * 删除预览
				 */
				var fileHandle = document.getElementById('fileHandle');
				// this.fileData = [];
				this.fileArray = [];
				this.page = 0;
				fileHandle.setAttribute("type","text");
				fileHandle.setAttribute("type","file");
			},
			showAllFiles() {
				/**
				 * 查看全部数据
				 */
				console.log(this.showAllFilesFlag);
				if(!this.showAllFilesFlag){
					this.showAllFilesFlag = true;
					this.toogleText = '分页查看';
				}else{
					this.showAllFilesFlag = false;
					this.toogleText = '查看全部';
				}
			},
			getFileContent(e) {
				/**
				 * 上传预览
				 */
				if(this.loading) return;
				this.uploading = false;
	            var files = e.target.files;
	            var fileReader = new FileReader();
	            var fileArray = [];
	            var fileContent = [];
	            var allFiles = [];
	            var _this = this;
	            this.loading = true;
	            try{
	            	fileReader.onload = function(ev) {
		                try {
		                    var data = ev.target.result,
		                        workbook = XLSX.read(data, {
		                            type: 'binary'
		                        }); // 以二进制流方式读取得到整份excel表格对象
		                } catch (err) {
		                    console.log(err);
		                    _this.loading = false;
		                    _this.toast('文件类型不正确','error');
		                    return;
		                }
		                // 表格的表格范围，可用于判断表头是否数量是否正确
		                var fromTo = '';
		                // 遍历每张表读取
		                for (var sheet in workbook.Sheets) {
		                	fileContent = [];
		                    if (workbook.Sheets.hasOwnProperty(sheet)) {
		                        fromTo = workbook.Sheets[sheet]['!ref'];
		                        //console.log(fromTo);
		                        allFiles = allFiles.concat(XLSX.utils.sheet_to_json(workbook.Sheets[sheet]));
		                        fileContent = fileContent.concat(XLSX.utils.sheet_to_json(workbook.Sheets[sheet]));
		                        var temp = {
		                        	'id': sheet,
		                        	'list': fileContent
		                        };
		                        fileArray.push(temp);
		                        //break; // 如果只取第一张表，就取消注释这行
		                    }
		                }
		                // console.log(fileArray)
		                _this.fileData = fileContent;
		                _this.fileArray = fileArray;
		                _this.allFiles = _this.fileDataFormatter(allFiles);;
		                _this.loading = false;
		            };
		            // 以二进制方式打开文件
		            fileReader.readAsBinaryString(files[0]);
	            }catch(err){
	            	console.log(err);
	            	this.loading = false;
	            }
			},
			uploadDataFun() {
				if(this.loading) return;
				if(this.allFiles.length === 0) return;
				// 对app名称进行转化成appid
				var allFiles = this.allFiles.slice(0);
				var appList = this.appList;
				allFiles.forEach(function(item){
					for(var i=0; i<appList.length; i++){
						if(item.appid === appList[i].name){
							item.appid = appList[i].id;
						}else{
							continue;
						}
					}
				});
				var parameter = {
					sessionid: this.userperadmin.sessionid,
					fileData: allFiles
				};
				console.log('uploadDataFun', parameter);
				this.loading = true;
				uploadCostRecordReg(parameter).then((res)=>{
			  		console.log('uploadCostRecordReg',res);
			  		this.loading = false;
			  		if(res.data.status === 0) {
			  			this.uploading = true;
			  		}else if(res.data.status === 1){
			  			this.$router.replace("/login");
			  		}else {
			  			this.toast(res.data.statusMsg, 'error');
			  		}
			  	}).catch((error)=>{
			  		this.loading = false;
			  		this.toast('服务器错误，请联系管理员','error');
				});
			},
			toast(msg, type) {
				this.$message({
		            type: type,
		            message: msg
		        });
			}
	    }
	}
</script>
<style lang="scss" scoped>
.page{
	text-align: left;
	position: relative;
	.zindextop{
		position: absolute;
		top: 20%;
		left: 50%;
		transform: translateX(-50%);
		z-index: 100;
		width: 50%;
		background: #fff;
		text-align: center;
		padding: 10px;
		border: 1px solid #324057;
		img{
			width: 100%;
			max-width: 500px;
			height: auto;
		}
	}
	.bread-nav{
		font-size: .6rem;
		color: #999;
		border-left: 4px solid #324057;
		padding-left: .5rem;
		font-weight: normal;
		margin-bottom: 10px;
		.link{
			color: #409EFF;
			cursor: pointer;
		}
		.item:before{
			content: ">";
			display: inline-block;
			padding-left: 2px;
			padding-right: 2px;
		}
	}
	.success{
		color: #39B64D;
	}
	.error{
		color: #CD270D;
	}
	.fileUploadBtn{
		display: inline-block;
		position: relative;
		overflow: hidden;
		vertical-align: middle;
		.text{
			display: inline-block;
			width: 100%;
			height: 100%;
			line-height: 32px;
		}
		input.upload {
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			right: 0;
			margin: 0;
			padding: 0;
			font-size: 0;
			cursor: pointer;
			opacity: 0;
			filter: alpha(opacity=0);
		}
	}
	.btn{
		height: 32px;
		padding-left: 6px;
		padding-right: 6px;
		border: none;
		border-radius: 2px;
		font-size: 12px;
		outline: none;
		cursor: pointer;
		i{
			padding-right: 4px;
		}
		&.primary{
			background: #48A2FF;
			color: #fff;
		}
		&.error{
			background: #CD270D;
			color: #fff;
		}
		&.success{
			background: #39B64D;
			color: #fff;
		}
		&.info{
			background: #C3C4C3;
			color: #fff;
		}
	}
	.tooltip{
		font-size: 12px;
		margin-top: 10px;
		margin-bottom: 10px;
		color: #999;
		.start{
			color: #CD270D;
		}
		.notice{
			cursor: pointer;
			color: blue;
		}
	}
	.table-toolbar{
		background: #EDEEF0;
		padding: 10px;
		label{
			font-size: 12px;
		}
	}
}
</style>


