<template>
	<div class="UserControl">
	 <div class="header" v-show="!showSerch" style="text-align: left">
	    <el-button size="small" @click.native="showSerch=true" style="border:0px"><i class="el-icon-back"></i> &nbsp;返回账号信息</el-button>
	 </div>
	 <!-- 搜索显示功能 -->
	 <div class="seachInfoFun" v-show="showSerch">
	 	    <!-- 搜索信息 -->
		   <el-row :gutter="20">
			  <el-col :xs="3" :sm="3" :md="4"><el-input v-model="seach.name" placeholder="姓名搜索"></el-input></el-col>
			  <el-col :xs="3" :sm="3" :md="4"><el-input v-model="seach.email" placeholder="邮箱搜索"></el-input></el-col>
			  <el-col :xs="4" :sm="3" :md="4">
			    <el-select v-model="seach.departmentId" placeholder="选择职能部门">
			      <!-- <el-option label="选择职能部门" value="选择职能部门"></el-option> -->
			      <el-option label="全部" value="0"></el-option>
			      <el-option label="市场部" value="7"></el-option>
			      <el-option label="运营部" value="5"></el-option>
			      <el-option label="技术部" value="6"></el-option>
			    </el-select>
			  </el-col>
			  <el-col :xs="4" :sm="3" :md="4">
			  	<el-select v-model="seach.level" placeholder="选择账号等级">
			      <!-- <el-option label="选择账号等级" value="选择账号等级"></el-option> -->
			      <el-option label="全部" value="0"></el-option>
			      <el-option label="一级账号" value="1"></el-option>
			      <el-option label="二级账号" value="2"></el-option>
			      <el-option label="三级账号" value="3"></el-option>
			    </el-select>
			  </el-col>
			   <el-col :xs="10" :sm="8" :md="6" >
			   	 <el-button  type="primary" icon="el-icon-search" @click="secrch" size="small"></el-button>
			   	 <el-button  type="success" icon="el-icon-plus" @click="addUser" size="small" v-show="addUserId"></el-button>
			   </el-col>
			</el-row>
			<!-- 表格 -->
			<div class="table-wrapper">
				<el-table :data="fbsData" style="width: 90%" border size="small"  v-loading="loading" element-loading-text="拼命加载中..." :highlight-current-row="true">
	                <el-table-column prop="name" label="姓名" width='80'></el-table-column>
	                <el-table-column prop="email" label="邮箱" width="200"></el-table-column>
	                <el-table-column prop="departmentId" label="职能部门"  ></el-table-column>
	                <el-table-column prop="level" label="账号等级"></el-table-column>
	                <el-table-column prop="date" label="创建时间" width="150"></el-table-column>
	                <el-table-column prop="showTitle" label="权限管理">
			            <template slot-scope="scope">
			                <el-button close-transition @click="adminuser(scope.row,scope.$index)" type="text" size="small" v-show="manageUsers">管理</el-button>
			            </template>   
			      </el-table-column>
	              <!--   <el-table-column label="账号管理" width="100" v-if="isShow">
	                    <template slot-scope="scope">
	                        <el-button @click="readonlyuser(scope.row,scope.$index)" type="text" size="small" v-show="isShow && scope.row.state == 0"><i class="fa fa-ban"></i></el-button>
	                        <el-button @click="readonlyuser(scope.row,scope.$index)" type="text" size="small" v-show="isShow && scope.row.state != 0"><i class="fa fa-ban staylcolor"></i></el-button>
	                        <el-button @click="deleteuser(scope.row,scope.$index)" type="text" size="small" v-show="isShow"><i class="el-icon-delete"></i></el-button>
	                    </template>
	                </el-table-column> -->
	            </el-table>
	            <el-pagination
	                background
	                @size-change="pageSizeChange"
	                @current-change="currentPageChange"
	                :current-page="currentPage"
	                :page-sizes="pageSizes"
	                :page-size="pagesize"
	                layout="total, sizes, prev, pager, next, jumper"
	                :total="dataSize" class="paginationWrapper" v-show="tableshow">
	            </el-pagination>
			</div>
			<!-- 模态框 -->
			<el-dialog title="创建新用户" :visible.sync="dialogVisible" width="50%" >
	            <el-form :model="ruleForm"  ref="ruleForm" label-width="100px" class="demo-ruleForm">
				  <el-form-item label="邮箱(账号)" prop="email">
				    <el-input v-model="ruleForm.email"></el-input>
				  </el-form-item>
				  <el-form-item label="密码" prop="pwd">
				    <el-input v-model="ruleForm.pwd"></el-input>
				  </el-form-item>
				  <el-form-item label="姓名" prop="name">
				    <el-input v-model="ruleForm.name"></el-input>
				  </el-form-item>
				  <el-form-item label="职能部门" prop="departmentId">
				  	 <el-col :span="10">
					    <el-select v-model="ruleForm.departmentId" placeholder="选择职能部门" ali>
					      <div v-for="item in userdepartment">
					          <el-option :label="item.name" :value="item.id"></el-option>
					     </div>
					    </el-select>
					</el-col>
				  </el-form-item>
				  <el-form-item label="账号级别" prop="level">
				  	<el-col :span="10">
					    <el-select v-model="ruleForm.level" placeholder="选择账号级别">
					      <div v-for="item in userlevel">
					          <el-option :label="item.name" :value="item.id"></el-option>
					      </div>
					    </el-select>
					</el-col>
				  </el-form-item>
				  <el-form-item label="上司" prop="higherId">
				  	<el-col :span="10">
					    <el-select v-model="ruleForm.higherId" placeholder="默认创建者">
					    	<div v-for="item in superior">
					             <el-option :label="item.name" :value="item.id"></el-option>
					    	</div>
					    </el-select>
					</el-col>
				  </el-form-item>
				   <el-form-item>
				    <el-button type="primary" @click="submitForm('ruleForm')" :loading="addSaveLoading">保存</el-button>
				    <el-button @click="dialogVisible = false">取消</el-button>
				  </el-form-item>
				</el-form>
			</el-dialog>
	 </div>
	 <div class="fungroup" v-show="!showSerch">
		 <div class="havedGroup" v-for="(item,index) in havedFunGroup">
	 	      <el-button class='btnStyle' @click="clickFunGroup(item)">{{item.name}}</el-button>
		 </div>
	 </div>
	   <!-- 模态框 -->
	    <!-- useManagemodel -->
	     <el-dialog :title="manageTile" :visible.sync="useManageVisible" width="50%">
	            <el-table :data="setPermissData"  style="width: 100%;"  border  size="mini" v-loading="tableloading" element-loading-text="拼命加载中..." >
	                 <el-table-column  type="index" label="序号"></el-table-column>
	                 <el-table-column  label="功能名称"  >
	                 	<template slot-scope="scope">
	                 		 <span  class="clickSpanStyle">{{scope.row.name}}</span>
	                    </template>
	                 </el-table-column>
	                 <el-table-column prop="className" label="授权"  >
	                 	<template slot-scope="scope">
	                         <el-switch
	                          v-model="scope.row.show"
							  active-color="#13ce66"
							  inactive-color="#999999"
							  @change="clickShowuse(scope.row,scope.$index)"
							  v-show="(scope.row.name !='总结中心')&&(scope.row.name !='日报')"
							  >
							</el-switch>
	                    </template>
	                 </el-table-column>
	            </el-table>
	    </el-dialog>
	</div>
</template>
<script>
// 此模块现在在总结中心和日报的开关中进行了隐藏，默认两个都显示 使用语句v-show="(scope.row.name !='总结中心')&&(scope.row.name !='日报')"
import {getAdminList,addAdmin,AdminParameter,getPermissionTypeReg,getAdminPermissionReg,addAdminPermissionReg} from '../../api/api.js'
	export default{
		data(){
			return{
				// 本地保存的用户信息
				userLocalInfo:null,
				// 是否显示添加
				isShow: true,
				// 分页是否显示
				tableshow: false,
				loading: false,
				// 是否用户权限值
				userpermission:null,
				// 用户信息
				userperadmin:{},
				dialogVisible: false,
				// 账号级别
				userlevel:[],
				// 用户上司
				superior:[],
				// 职能部门
				userdepartment:[],
				// 搜索对象信息
				seach:{
				  sessionid:'',
                  name: '',
				  email: '',
				  departmentId:'',
				  level:'',
				  pageId:0,
				  pageSize:10,
				  first:0
				},
				// 分页信息
				currentPage: 1,
			    pageSizes: [10,20,50],
			    dataSize: 0,
			    pagesize: 10,
			    // 获取数据
				fbsData:[
				  
				],
				// 数据为空时验证信息
                 ruleForm: {
                  sessionid:'',
		          email:'',
		          pwd: '',
		          name:'',
		          departmentId:'',
		          level: '',
		          higherId:''

		        },
		        addSaveLoading:false,
		        // 用户管理--》获取点击的用户信息和选择的权限模块
		        clickGetUserInfo:null,
		        clickJurisdiction:null,
		        // 管理权限参数
		        showSerch: true,
		        // 获取已经存在功能组
		        havedFunGroup:null,
		        useManageVisible:false,//模态框是否显示
		        tableloading:false,//加载动画
		        manageTile:null,//table显示选择哪个功能组
		        setPermissData:null,//获取的权限信息
		        saveAddUserInfo:null,//保存修改权限信息中发送的type,adminid,sessionid
		        addUserId:false,//添加账号
		        manageUsers: false//管理用户
			}
		},
		mounted(){
			 try {
                // 获取对操作是否限权
				 let userPermission = JSON.parse(unescape(sessionStorage.getItem('userPermiss')));
	             console.log(userPermission);
	             let _this = this;
	             // 对渠道商管理中操作限权进行循环读取状态
	             for (var i = 0; i <userPermission.data.length; i++) {
	             	if (userPermission.data[i].meta == '05') {
	             		for (var j = 0; j <userPermission.data[i].children.length; j++) {
	             			if (userPermission.data[i].children[j].meta == '0502') {
	             				for (var m = 0; m < userPermission.data[i].children[j].children.length; m++) {
	             					console.log(userPermission.data[i].children[j].children[m].meta)
	             					if (userPermission.data[i].children[j].children[m].meta == '050201') {
	             						// 添加用户获取权限
	             						_this.addUserId = userPermission.data[i].children[j].children[m].show == '0'?false:true;
	             					} else if (userPermission.data[i].children[j].children[m].meta == '050202') {
	             						// 获取管理权限值
	             						_this.manageUsers = userPermission.data[i].children[j].children[m].show == '0'?false:true;
	             					};
	             				};
	             				
	             			};
	             		};
	             	};
	             	
	             };
			 }catch(e) {
             	// statements
             	console.log(e);
             }
			// 获取数据信息
			this.userLocalInfo = JSON.parse(localStorage.getItem("userperadmin"));
			var userperadmin = JSON.parse(localStorage.getItem("userperadmin"));
			console.log(userperadmin.admin.level)
			 if (userperadmin.admin){
			 	this.userperadmin = userperadmin;
			 	this.ruleForm.sessionid = this.userperadmin.sessionid;
			    this.seach.sessionid = this.userperadmin.sessionid;
			    this.userpermission = this.userperadmin.admin.level;
			 	if (this.userpermission >2) {
                this.isShow = false;
				} else {
					this.isShow = true;
				}
				this.secrch();
			 } else {
			 	this.userperadmin = {};
			 }
		},
		 methods: {
		 	// 搜索
		  secrch(){
		  	console.log(this.seach)
		  	if (this.seach.email) {
		  		var reg = /^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/; 
                if (!reg.test(this.seach.email)) {
                    this.$message.error('输入邮箱格式不正确');
                    return;
                }
		  	}
		  	if (this.seach.departmentId === "0") {
		  		this.seach.departmentId = "";
		  	} 
		  	 if (this.seach.level === "0") {
		  		this.seach.level = "";
		  	}
		  	this.loading = true;
		  	let seach = JSON.stringify(this.seach)
		  	console.log(seach)
		  	getAdminList(seach).then((res)=>{
		  		console.log(res.data)
		  		if (res.data.status === 0) {
		  			// 获取数据信息
		  			this.fbsData = res.data.adminList;
		  			if (this.fbsData.length<1) {
		  				this.loading = false;
                        // 显示分页
			  			this.tableshow = false;
		  			} else {
		  				for (var i = 0; i <this.fbsData.length; i++) {
			  				// 时间戳转时间
							this.fbsData[i].date = this.formatDate(this.fbsData[i].date);
			  				if (this.fbsData[i].departmentId === 5) {
			  					this.fbsData[i].departmentId = "运营部"
			  				} else if (this.fbsData[i].departmentId === 6) {
			  					this.fbsData[i].departmentId = "技术部"
			  				} else if (this.fbsData[i].departmentId === 7) {
			  					this.fbsData[i].departmentId = "市场部"
			  				};
			  				if (this.fbsData[i].level === 1) {
			  					this.fbsData[i].level = '一级账号'
			  				} else if (this.fbsData[i].level === 2) {
			  					this.fbsData[i].level = '二级账号'
			  				} else if (this.fbsData[i].level === 3) {
			  					this.fbsData[i].level = '三级账号'
			  				};
			  			}
			  			if (!res.data.listSize) {
			  				this.dataSize = this.dataSize;
			  			} else {
			  			   this.dataSize= parseFloat(res.data.listSize);
			  			}
			  			this.loading = false;
			  			// 显示分页
			  			this.tableshow = true;
			  		}	
		  		} else if (res.data.status === 1) {
		  		   this.tableshow = false;
		  		   this.$router.replace("/login");
		  		}
		  	})
		  },
		  // 添加用户
		  addUser(){
		  	// 点击添加时候情况输入框
		        this.ruleForm.email = '';
		        this.ruleForm.pwd = '';
		        this.ruleForm.name = '';
		        this.ruleForm.departmentId = '';
		        this.ruleForm.level = '';
		        this.ruleForm.higherId = '';
		    // 职能部门数据
		  	this.userdepartment = [];
		  	if (this.userperadmin.admin.departmentId === 5) {
				this.userdepartment.push({'name':'运营部','id':5});
			} else if (this.userperadmin.admin.departmentId === 6) {
				this.userdepartment.push({'name':'技术部','id':6});
			} else if (this.userperadmin.admin.departmentId === 7) {
				this.userdepartment.push({'name':'市场部','id':7});
			};
			// 账号级别
			this.userlevel = [];
		  	console.log(this.userperadmin.admin.level)
		  	if (this.userperadmin.admin.level === 1) {
		  		this.userlevel = [{'name':'二级账号','id':2},{'name':'三级账号','id':3}];
		  	} else  {
		  		this.userlevel = [{'name':'三级账号','id':3}];
		  	};
		  	var data = {
		  		sessionid : this.userperadmin.sessionid
		  	};
		  	data = JSON.stringify(data);
		  	AdminParameter(data).then(res=>{
                console.log(res.data)
                if (res.data.status === 0) {
                	this.superior = res.data.adminList;
                	console.log(this.superior)
                	this.dialogVisible = true;
                }
                if (res.data.status === 1) {
                  this.$router.replace("/login");
                }
		  	}).catch(res=>{
		  		this.$message.error('获取负责人信息失败');
		  	})
		  	// this.userperadmin;
		  	// this.dialogVisible = true;
		  },
		  pageSizeChange(res){
		  	this.seach.pageSize = res;
		  	this.seach.first = 1;
		  	this.secrch()
		  },
		  currentPageChange(res){
		  	this.seach.pageId = (res*1-1);
		  	this.seach.first = 1;
		  	this.secrch()
		  },
		  // 管理事件
		  adminuser(data,index){
		  	console.log(data)
		  	this.clickGetUserInfo = data;
		  	console.log(index)
		  	this.havedFunGroup = null;
		  	let useSendData = {
		  		sessionid:this.userLocalInfo.sessionid
		  	};
		  	let _this = this;
		  	getPermissionTypeReg(useSendData).then(res=>{
		  		console.log(res.data)
		  		if (res.data.status === 0) {
		  			_this.havedFunGroup = res.data.permissionTypeList;
		  			_this.showSerch = false;
		  		};
		  		if (res.data.status === 1) {
		  			_this.$router.replace("/login");
		  		}
		  	})
		  },
		  // 冻结用户
		  readonlyuser(data,index){
		  	console.log(data)
		  	console.log(index)
		  },
		  // 删除用户
		  deleteuser(data,index){},
		  // 保存
	      submitForm(formName) {
	        this.$refs[formName].validate((valid) => {
	          if (valid) {
	            console.log(this.ruleForm)
	            if (this.ruleForm.email === '') {
	            	this.$message.error('邮箱不能为空');
	            	
	            } else if (this.ruleForm.pwd === '') {
	            	this.$message.error('密码不能为空');
	            } else if (this.ruleForm.name === '') {
	            	this.$message.error('姓名不能为空');
	            } 
	            else if (this.ruleForm.departmentId === '') {
	            	this.$message.error('职能部门不能为空');
	            } else if (this.ruleForm.level === '') {
	            	this.$message.error('账号级别不能为空');
	            } 
	            else if (this.ruleForm.higherId === '') {
	            	this.$message.error('上司不能为空');
	            } else {
	            	if (this.ruleForm.email) {
	            	    var reg = /^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/; 
		                if (!reg.test(this.ruleForm.email)) {
		                    this.$message.error('输入邮箱格式不正确');
		                    return;
		                } else {
		                	this.addSaveLoading = true;
		                	var _this = this;
		                	let ruleForm = JSON.stringify(_this.ruleForm)
		                	addAdmin(ruleForm).then(res=>{
                               console.log(res.data)
                               if (res.data.status === 0) {
                               	   _this.$message({
							          message: res.data.statusMsg,
							          type: 'success'
							        });
                               	   _this.addSaveLoading = false;
                               	   _this.dialogVisible = false;
                               	   _this.secrch();
                               }
                               if (res.data.status === 1) {
                               	 _this.$router.replace("/login");
                               	 _this.addSaveLoading = false;
                               	 _this.dialogVisible = false;
                               }
		                	}).catch(res=>{
                               _this.addSaveLoading = false;
                               _this.dialogVisible = false;
                               _this.$message.error('保存失败');
		                	})
		                }
	            	}
	            }
	          } else {
	            console.log('error submit!!');
	            return false;
	          }
	        });
	      },
	      // 点击功能组
	      clickFunGroup(res) {
	      	 console.log(this.clickGetUserInfo);
	      	 console.log(res)
	      	 this.tableloading = true;
	      	 this.manageTile = res.name;
	      	 let lookUserInfo = {
                 sessionid:this.userLocalInfo.sessionid,
                 adminid:this.clickGetUserInfo.id,
                 type:res.id
	      	 };
	      	 this.saveAddUserInfo = lookUserInfo;
	      	 console.log(lookUserInfo)
	      	 let _this = this;
	      	 getAdminPermissionReg(lookUserInfo).then(res=>{
	      	 	console.log(res.data)
	      	 	if (res.data.status === 0) {
	      	 		_this.setPermissData = res.data.adminPermissionList;
	      	 		for (var i = 0; i < res.data.adminPermissionList.length; i++) {
	      	 			_this.setPermissData[i].show = res.data.adminPermissionList[i].show==='0'?false:true;
	      	 		}
	      	        _this.tableloading = false;
	      	 		_this.useManageVisible = true;
	      	 	};
	      	 	if (res.data.status === 1) {
	      	 		_this.$router.replace("/login");
	      	 	};
	      	 })
	      },
	      clickShowuse(row,index) {
	      	console.log(row)
	      	console.log(index)
	      	console.log(row.show)
	      	console.log(this.saveAddUserInfo)
	      	// let isshow =  row.show;
	      	let saveAdminPermiss = {
	      		sessionid:this.saveAddUserInfo.sessionid,
	      		type: this.saveAddUserInfo.type,
	      		adminid: this.saveAddUserInfo.adminid,
	      		id:row.id,
	      		name:row.name,
	      		show:row.show
	      	}
	      	console.log(saveAdminPermiss)
	      	let _this = this;
	      	addAdminPermissionReg(saveAdminPermiss).then(res=>{
	      		console.log(res)
	      		console.log(res.data)
	      		row.id = res.data.id
	      		if (res.data.status === 0) {
	      			_this.$message({
					    message: '修改'+row.name+'授权成功',
						type: 'success'
					});
	      		};
	      		if (res.data.status === 1) {
                   _this.$router.replace("/login");
	      		};
	      	})
	      }
	    }
	}
</script>
<style lang="scss" type="text/css" scoped>
