<template>
	<div class="config" v-loading="Statrloading" element-loading-text="拼命加载中...">
		<el-row :gutter="10">
			<!-- 数据详情表 -->
		   <el-col  :md="10" :lg="10">
		  	 <!-- 数据详情表 -->
			    <div class="dataInfo">
					<el-row :gutter="10">
						<el-col :sm="12" :md="10" :lg="8" :xl="5">
							<p class="titleDataInfo">数据详情表</p>
						</el-col>
						<el-col :sm="8" :md="10" :lg="12" :xl="15">&nbsp;</el-col>
						<el-col :sm="4" :md="2" :lg="2" :xl="2">
							<p class="addDataInfo">
								<el-button type="primary" icon="el-icon-menu" circle size="small" @click="clickDataInfoMenu(1)"></el-button>
							</p>
					    </el-col>
					</el-row>
					<!-- 显示存在的配置 -->
					<div>
						<el-collapse accordion v-model="activeNameData" v-for="(item,index) in Detail" :key="index">
						  <el-collapse-item :title="item.name" :name="index">
						    <ul :id="item.onlyType" class="touchStyle">
								<li v-for="(child1,index) in item.columns" :key="index">{{child1}}</li>
							</ul>
						  </el-collapse-item>
						</el-collapse>
					</div>
			    </div>
		   </el-col>
		   <!-- 空隙 -->
		   <el-col  :md="1" :lg="1">
		  	    <div>&nbsp;</div>
		   </el-col>
		   <!-- 流程转化表 -->
		   <el-col  :md="10" :lg="10">
			  	<!-- 流程转化表 -->
				<div class="dataInfo">
					<el-row :gutter="10">
						<el-col :sm="12" :md="10" :lg="8" :xl="5">
							<p class="titleDataInfo">流程转化表</p>
						</el-col>
						<el-col :sm="8" :md="10" :lg="12" :xl="15">&nbsp;</el-col>
						<el-col :sm="4" :md="2" :lg="2" :xl="2">
							<p class="addDataInfo">
								<el-button type="success" icon="el-icon-menu" circle size="small"  @click="clickDataInfoMenu(2)"></el-button>
							</p>
					    </el-col>
					</el-row>
					<!-- 显示存在的配置 -->
						<el-collapse  accordion v-model="activeNameTrans" v-for="(item,index) in Trans" :key="index">
						  <el-collapse-item :title="item.name" :name="index">
						    <ul :id="item.onlyType" class="touchStyle">
								<li v-for="(childData,index) in item.columns" :key="index">{{childData}}</li>
							</ul>
						  </el-collapse-item>
						</el-collapse>
				</div>
		  </el-col>
		</el-row>
		<!-- 数据详情表菜单模态框 -->
		<div class="menuDataInfo">
		     <el-dialog title="增加/修改" :visible.sync="dataInfoMenuVisible" width="60%">
		     	<!-- 新增产品暂时不使用进行隐藏 -->
	            <div class="addProduct clearfix" v-show="false">
	            	<el-button type="success" icon="el-icon-plus" size="small" class="btnAddStyle" @click="addDataInfo(1)">新增产品</el-button>
	            </div>
	            <!-- 结束隐藏 -->
		     	<!-- 已经存在的配置数据 -->
		     	<div class="modelDataHeight">
		     		<ul class="existMenu">
		            	<li v-for="(item,index) in Detail">
		            		<el-button type="success" size="small">{{item.name}}</el-button>
		            		<el-button type="primary" size="small" @click="modification(item,1)">修改</el-button>
		            		<!-- 配置的数据 -->
		            		<div class="havedDataInfo">
		            			<span class="havedSpanStyle" v-for="itemChild in item.columns">{{itemChild}}</span>
		            		</div>
		            	</li>
		            </ul>
		     	</div>
		    </el-dialog>
		</div>
		<!-- 修改新增的表头功能模态框 -->
		<div class="menuDataInfo">
		    <el-dialog title="表头增加修改" :visible.sync="dataInfoTableVisible" width="50%">
		    	<!-- 选择APP -->
            	<div class="addInfo">
            		<el-select v-model="addInfoData" placeholder="选择APP产品" size="small">
					    <el-option
					      v-for="item in AppName"
					      :key="item.value"
					      :label="item.label"
					      :value="item.label">
					    </el-option>
					</el-select>
            	</div>
            	<br>
            	<br>
            	<!-- 添加的穿梭框 -->
            	<div>
            		<el-transfer
					    filterable
					    :titles="['原始数据', '配置数据']"
					    @change="handleChange"
					    filter-placeholder="请输入搜索信息"
					    v-model="valueData"
					    :data="data2">
					</el-transfer>
            	</div>
            	<span slot="footer" class="dialog-footer">
				    <el-button type="primary" @click="saveTableName(1)" size="small">保存</el-button>
				 </span>
		    </el-dialog>
		</div>
		<!-- 流程转化表模态框 -->
		<div class="menuDataInfo">
		     <el-dialog title="增加/修改" :visible.sync="liuchenMenuVisible" width="60%">
		     	<!-- 新增产品暂时不使用进行隐藏 -->
	            <div class="addProduct clearfix" v-show="false">
	            	<el-button type="success" icon="el-icon-plus" size="small" class="btnAddStyle" @click="addDataInfo(2)">新增产品</el-button>
	            </div>
	            <!-- 结束隐藏 -->
		     	<!-- 已经存在的配置数据 -->
		     	<div class="modelDataHeight">
		     		<ul class="existMenu">
		            	<li  v-for="(item,index) in Trans">
		            		<el-button type="success" size="small">{{item.name}}</el-button>
		            		<el-button type="primary" size="small" @click="modification(item,2)">修改</el-button>
		            		<!-- 配置的数据 -->
		            		<div class="havedDataInfo">
		            			<span class="havedSpanStyle" v-for="itemChildTrans in item.columns">{{itemChildTrans}}</span>
		            		</div>
		            	</li>
		            </ul>
		     	</div>
		    </el-dialog>
		</div>
		<!-- 修改新增的表头功能模态框 -->
		<div class="menuDataInfo">
		    <el-dialog title="表头增加修改" :visible.sync="liuchenTableVisible" width="50%">
		    	<!-- 选择APP -->
            	<div class="addInfo">
            		<el-select v-model="addInfoData" placeholder="选择APP产品" size="small">
					    <el-option
					      v-for="item in AppName"
					      :key="item.value"
					      :label="item.label"
					      :value="item.label">
					    </el-option>
					</el-select>
            	</div>
            	<br>
            	<br>
            	<!-- 添加的穿梭框 -->
            	<div>
            		<el-transfer
					    filterable
					    :titles="['原始数据', '配置数据']"
					    @change="handleChange"
					    filter-placeholder="请输入搜索信息"
					    v-model="valueData"
					    :data="data2">
					</el-transfer>
            	</div>
            	<span slot="footer" class="dialog-footer">
				    <el-button type="primary" @click="saveTableName(2)" size="small">保存</el-button>
				 </span>
		    </el-dialog>
		</div>
	</div>
</template>
<script>
    import {getTableColumnsReg,updateTableColumnsReg} from "../../api/api.js"
	export default {
		 data() {
		 	  // 数据详情表数据穿梭框参数
		      return {
		      	userperadmin:'',// 用户信息
		      	Statrloading:true,//加载动画
		      	activeNameData: '',//默认折叠显示第几个
				activeNameTrans:'',
				dataInfoMenuVisible:false,
				dataInfoTableVisible:false,
				liuchenMenuVisible:false,
				liuchenTableVisible:false,
				// 首次进入获取配置的数据信息
				Detail:[],//数据详情表配置值
				Trans:[],//流程转化表配置值
				// 添加选择的APP产品所有信息名字
				AppName:[],
				// 数据详情中添加APP产品选择的app产品
				addInfoData:'',
				// 数据详情表数据穿梭框参数
				channelType:[],//原始数据数据详情表
				payList:[],//原始数据流程转化表
		        data2:[],//原始数据处理后赋值给data2
		        valueData: [],//最后用户自己配置的值
		        // 原始数据进行保存便于新增使用
		        dataInfoDataOld:[],//数据表
		        liuchengDataOld:[],//流程表
		      };
		    },
		activated() {
			// 获取用户后台信息
			this.userperadmin = JSON.parse(localStorage.getItem('userperadmin'));
			setTimeout(()=>{
				// 得到信息有：所有的APP产品，原始表格头名字，已经配置是产品表头数据（注：区分数据详情和流程转化表的）
				this.getNeedInfo(this.userperadmin)
			},500)
		},
		methods:{
			// 第一次进入得到有效信息
			getNeedInfo(res){
				console.log(1111)
				// 获取数据时候进行初始化用于保存调用时候防止没有清空报错
				this.Statrloading = true;
				this.Detail = [];
				this.Trans = [];
				this.AppName = [];
				this.channelType = [];
				this.payList = [];
				this.dataInfoDataOld = [];
				this.liuchengDataOld = [];
				var sessionid = {sessionid:res.sessionid,system:1}
				let _this = this;
				getTableColumnsReg(sessionid).then(res=>{
					console.log(res.data)
					console.log('11111111')
					if (res.data.status === 0) {
						// 产品信息
						res.data.appList.map((val,index)=>{
							_this.AppName.push({label:res.data.appList[index].name,value:res.data.appList[index].id})
						})
						// 数据详情表原始数据
						res.data.channelType.map((val,index)=>{
							_this.channelType.push(res.data.channelType[index].name)
						})
						// 流程转化表原始数据
						res.data.payList.map((val,index)=>{
							_this.payList.push(res.data.payList[index].name)
						})
						// 保存原始数据
						// 数据
						res.data.channelType.map((val,index)=>{
							_this.dataInfoDataOld.push(res.data.channelType[index].name)
						})
						// 流程
						res.data.payList.map((val,index)=>{
							_this.liuchengDataOld.push(res.data.payList[index].name)
						})
						// 配置获取的值数据
						_this.Detail = res.data.reportforms_admin//数据详情表配置
						for (var i = 0; i < _this.Detail.length; i++) {
							_this.Detail[i].columns = _this.Detail[i].columns.split(',')
							console.log(_this.Detail[i].columns)
							if (_this.Detail[i].columns.length>0 && _this.Detail[i].columns[0] == "") {
								_this.Detail[i].columns = [];
							}
						}
						_this.Trans = res.data.reportforms_operate//流程转换表配置
						for (var i = 0; i < _this.Trans.length; i++) {
							_this.Trans[i].columns = _this.Trans[i].columns.split(',')
							if (_this.Trans[i].columns.length>0 && _this.Trans[i].columns[0] == "") {
								_this.Trans[i].columns = [];
							}
						}
						console.log("---------------------")
						console.log(_this.Detail)
						console.log(_this.Trans)
						console.log("---------------------")
						// 成功时候显示的配置信息可以滑动排序且加载进度提示隐藏
						setTimeout(()=>{
						  _this.comment(_this.Detail,_this.Trans);
						},1000)
				        _this.Statrloading = false;
					}
					if (res.data.status === 1) {
						_this.$router.replace("/login");
					}
				})
			},
			comment(data1,data2){
				// 定义一个空数组来存放两个报表中后台给的唯一标识名字onlytype
				var stringOnlyType = [];
				data1.forEach( function(element, index) {
					stringOnlyType.push(element.onlyType)
				});
				data2.forEach( function(element, index) {
					stringOnlyType.push(element.onlyType)
				});
				console.log(stringOnlyType)
				// 命名一个变量存放获取遍历时候放入标签中的id获取对应的节点放到数组dataArr
				var dataArr = [];
				stringOnlyType.map((value,index)=>{
				    value = document.getElementById(value);
					dataArr.push(value)
				})
				console.log(dataArr)
				var _this = this;
				dataArr.map((val,index)=>{
					// 数据详情表拖动函数
					Sortable.create(val, {
						group: "words",
						animation: 150,
						onAdd: function (evt){ console.log('onAdd.foo:', [evt.item, evt.from]); },

						onUpdate: function (evt){ console.log('onUpdate.foo:', [evt.item, evt.from]); },

						onRemove: function (evt){ console.log('onRemove.foo:', [evt.item, evt.from]); },

						onStart:function(evt){ console.log('onStart.foo:', [evt.item, evt.from]);},

						onSort:function(evt){ console.log('onStart.foo:', [evt.item, evt.from]);},

						onEnd: function(evt){ 
							console.log('onEnd.foo:', [evt.item, evt.from],evt.from.innerText);
							var columnsValue = evt.from.innerText.split("\n").slice(0,-1);
							console.log(evt.from.id)
							// if (evt.from.id === "kaTrans" || evt.from.id === "leTrans") {
							// 	_this.Trans =  columnsValue
							// }
							// 发送字段 产品 配置数据
					    	let saveDate = {
					    		sessionid:_this.userperadmin.sessionid,
					    		onlyType:evt.from.id,
					    		columns:columnsValue,
					    		system:1
					    	}
					    	console.log(saveDate)
					    	// 保存配置
					    	updateTableColumnsReg(saveDate).then(res=>{
					    		console.log(res.data)
					    		if (res.data.status === 0) {
					    			_this.$message({
							          message: '保存配置成功',
							          type: 'success'
							        });
					    		};
					    		if (res.data.status === 1) {
					    			_this.$router.replace("/login");
					    		};
					    	})
						}
					});
				})
			},
			// 点击菜单显示模态框res值代表数据详情表还是流程转化表
			clickDataInfoMenu(res) {
				// 事件点击传入值1代表数据详情表，2代表流程转化表
				if (res == 1) {
				   this.data2 = this.generateData2(this.channelType)
				   this.dataInfoMenuVisible = true;
				}else if (res == 2) {
				   this.data2 = this.generateData2(this.payList)
				   this.liuchenMenuVisible = true;
				}
			},
			// 点击产品修改
			modification(item,dataRes) {
				console.log(item)
				console.log(dataRes)
				if (item.name) {
					this.addInfoData = item.name
					this.valueData = item.columns
				} else {
					this.addInfoData = '';
					this.valueData = [];
				}
				if (dataRes === 1) {
				   this.dataInfoTableVisible = true;
				} else if (dataRes === 2) {
				   this.liuchenTableVisible = true;
				}
			},
			// 数据详情表点击新增产品
			addDataInfo(reqult) {
				console.log(reqult)
				// 新增1代表数据详情表2代表流程转化表,data2还原到原始数据
				if (reqult == 1) {
					// 产品APP值清空配置栏也为空
					this.valueData = [];
					this.addInfoData = '';
					this.data2 = this.generateData2(this.dataInfoDataOld)
					this.dataInfoTableVisible = true;
				};
				if (reqult == 2) {
					this.valueData = [];
					this.addInfoData = '';
					this.data2 = this.generateData2(this.liuchengDataOld)
					this.liuchenTableVisible = true;
				};
			},
			// 穿梭框数据改变执行的函数
			handleChange(value, direction, movedKeys) {
		        console.log(value, direction, movedKeys);
		    },
		    // 原始数据赋值给data2
		    generateData2 (res) {
		        const data = [];
		        const appList = res;
		        appList.forEach((city, index) => {
		          data.push({
		            label: city,
		            key: city
		          });
		        });
		        return data;
		    },
		    // 保存修改新增的表头1数据详情2流程转化
		    saveTableName(index){
		    	var typeValue = null;
		    	console.log(index)
		    	if (index === 1) {
		    		typeValue = 22
		    	} else if (index === 2) {
		    		typeValue = 21
		    	}
		    	if (!this.addInfoData) {
		    		this.$message.error('选择需要配置的产品');
		    		return;
		    	}
		    	console.log(typeValue)
		    	// 发送字段 产品 配置数据
		    	let saveDate = {
		    		sessionid:this.userperadmin.sessionid,
		    		appName:this.addInfoData,
		    		type:typeValue,
		    		columns:this.valueData,
		    		system:1
		    	}
		    	console.log(saveDate)
		    	let _this = this;
		    	// 保存配置
		    	updateTableColumnsReg(saveDate).then(res=>{
		    		console.log(res.data)
		    		if (res.data.status === 0) {
		    			_this.$message({
				          message: '保存配置成功',
				          type: 'success'
				        });
				        _this.dataInfoMenuVisible = false;
						_this.dataInfoTableVisible = false;
						_this.liuchenMenuVisible = false;
						_this.liuchenTableVisible = false;
				        _this.getNeedInfo(_this.userperadmin)
		    		};
		    		if (res.data.status === 1) {
		    			_this.$router.replace("/login");
		    		};
		    	})
		    }
		}
	}
	
</script>
<style scoped lang="scss">
 .clearfix:after{content:'';height: 0px;overflow:hidden;display: block;clear:both}
 .config { text-align: left;}
 .dataInfo{background:#E4E7ED;}
 .ProcessConversTable{background:#E4E7ED }
 .titleDataInfo{font-size:16px;line-height:42px;padding-left: 10px;}
 .addDataInfo{padding:5px;}
 .touchStyle li {
 	background:#E4E7ED; 
 	margin-bottom: 5px;
 	border-radius: 5px;
 	text-align: center;
 	height: 30px;
 	line-height: 30px;
 }
 .modelDataHeight{
 	max-height: 400px;
 	overflow-y: auto;
 }
 .touchStyle li:hover{
 	 cursor:pointer;
 }
 .existMenu li{list-style: none;margin-bottom: 30px;}
 .havedDataInfo{border: 1px solid #C0C4CC;margin-top: 10px;padding:5px 10px 5px 10px;max-height:40px;min-height: 20px;overflow-y: auto}
 .havedSpanStyle{width:25px;height:20px;background:#87CEFA;color: #ffffff;padding:0px 8px 0px 8px;line-height: 20px;border-radius:8px;margin-left: 5px;}
 .btnAddStyle{float: right}
</style>


