<template>
	<div>
		<div class="hearder" v-loading="divHearderInfo" element-loading-text="拼命加载中...">
			<el-row :span="24">
		        <!-- 合作方选择 -->
		        <el-col :span="5">
			         <el-select v-model="partner" filterable clearable allow-create default-first-option size="small" placeholder="输入/选择合作方" class='templeMaring' @focus="focuschoosePartner">
				          <el-option
				            v-for="(item,index) in choosePartner"
				            :key="index"
				            :label="item.company"
				            :value="item.id">
				          </el-option>
			         </el-select>
		        </el-col>
		        <!-- 产品选择 -->
		        <el-col :span="4">
			         <el-select v-model="product"  clearable  size="small" placeholder="选择产品" class='templeMaring'>
				          <el-option
				            v-for="(item,index) in chooseproduct"
				            :key="index"
				            :label="item.name"
				            :value="item.id">
				          </el-option>
			         </el-select>
		        </el-col>
		        <!-- 输入合作内容 -->
		        <el-col :span="4">
			        <el-input v-model="content" placeholder="输入合作内容" clearable size="small" class='templeMaring'></el-input>
		        </el-col>
		        <!-- 选择日期 -->
		        <el-col :span="3" style="margin-left: 1rem;">
			        <div class="block">
			            <el-date-picker
			              v-model="gitTime"
			              type="daterange"
			              start-placeholder="开始日期"
			              end-placeholder="结束日期"
			              unlink-panels
			              value-format="yyyy-MM-dd"
			              @focus = "focusDataTime"
			              :editable='false'
			              size='small'
			              >
			            </el-date-picker>
			         </div>
		        </el-col>
		    </el-row>
		    <el-row :span="24" class='headerSecond'>
		        <!-- 一键删除 -->
		        <el-col :span="2">
			        <el-button type="primary" size='small' @click='deleteCost'>一键删除</el-button>
		        </el-col>
		        <el-col :span="4" class="marginLeft">
			       <el-button type="success" icon="el-icon-plus" size="small" @click='addInfoToModel'></el-button>
			       <el-button type="primary" icon="el-icon-search" size="small" @click="searchInfo"></el-button>
		        </el-col>	
		    </el-row>
	    	<div class="table" v-loading="tableStaus" element-loading-text="数据加载中">
				<el-table
				    ref="multipleTable"
				    size='small'
				    :data="tableData"
				    tooltip-effect="dark"
				    style="width: 100%"
				    border
				    @selection-change="handleSelectionChange"
				    :height="tableData&&tableData.length>0?480:120"
				    >
				    <el-table-column type="selection" width="55"></el-table-column>
				    <el-table-column prop="dateNew" label="日期" width='200'></el-table-column>
				    <el-table-column prop="name" label="产品"></el-table-column>
				    <el-table-column prop="company" label="合作方"></el-table-column>
				    <el-table-column prop="cooperationContent" label="合作内容"></el-table-column>
				    <el-table-column prop="cost" label="成本金额"></el-table-column>
				    <el-table-column prop="remark" label="备注"></el-table-column>
				    <el-table-column  label="操作">
				    	 <template slot-scope="scope">
				    	   <el-switch v-model="scope.row.synchronizeSwitch" active-color="#13ce66" inactive-color="#909399" @change="synchronize(scope.row,scope.$index)"></el-switch>&nbsp;&nbsp;&nbsp;
                           <el-button  type="text" size="small" @click='deleteTableRow(scope.row,scope.$index)'><i class="el-icon-delete"></i></el-button>
                         </template>
				    </el-table-column>
				</el-table>
			</div>
		</div>
		<!-- 添加模态框 -->
		<div class="model">
			<el-dialog title="增加" :visible.sync="addVisible" width="90%">
				<div class="addModel">
					<el-button type="success" icon="el-icon-plus" size="small" @click="addArrPush"></el-button>
				</div>
                <el-table :data="fbsData" :height='fbsData&&fbsData.length>0?300:120' border size="small"  :highlight-current-row='true'>
	                <el-table-column type="index" label="序号"></el-table-column>
	                <el-table-column prop="date" label="日期" min-width="300">
						<template slot-scope="scope">
							<div class="block">
					            <el-date-picker
					              class="dataStyleDate"
					              v-model="scope.row.date"
					              type="daterange"
					              start-placeholder="开始日期"
					              end-placeholder="结束日期"
					              unlink-panels
					              value-format="yyyy-MM-dd"
					              :editable='false'
					              size='small'
					              >
					            </el-date-picker>
					         </div>
	                    </template>
	                </el-table-column>
	                <el-table-column prop="appId" label="产品" min-width="150">
	                	<template slot-scope="scope">
	                        <el-select v-model="scope.row.appId" clearable size="mini" placeholder="选择产品">
							    <el-option	 
							      class="dataStyle"						     
							      v-for="(item,index) in chooseproduct"
							      :key="index"
							      :label="item.name"
							      :value="item.id">
							    </el-option>
							</el-select>
	                    </template>
	                </el-table-column>
	                <el-table-column prop="companyId" label="合作方" min-width="300">
	                	<template slot-scope="scope">
	                        <el-select v-model="scope.row.companyId" clearable size="mini" placeholder="选择合作方"  class="dataStyleDate" >
							    <el-option							     
							      v-for="(item,index) in choosePartner"
							      :key="index"
							      :label="item.company"
							      :value="item.id">
							    </el-option>
							</el-select>
	                    </template>
	                </el-table-column>
	                <el-table-column prop="cooperationContent" label="合作内容" min-width="150">
	                	<template slot-scope="scope">
	                       <el-input v-model="scope.row.cooperationContent" clearable size="mini"></el-input>
	                    </template>
	                </el-table-column>
	                <el-table-column prop="cost" label="成本金额" min-width="100">
	                	<template slot-scope="scope">
	                       <el-input v-model="scope.row.cost" clearable size="mini"></el-input>
	                    </template>
	                </el-table-column>
	                <el-table-column prop="remark" label="备注" min-width="200">
	                	<template slot-scope="scope">
	                       <el-input v-model="scope.row.remark" clearable size="mini"></el-input>
	                    </template>
	                </el-table-column>
	                <el-table-column label="操作">
	                    <template slot-scope="scope">
	                        <el-button  type="text" size="small" @click='deleteRow(scope.row,scope.$index)'><i class="el-icon-delete"></i>删除</el-button>
	                    </template>
	                </el-table-column>
	        </el-table>
		        <span slot="footer">
		          <el-button type="primary" size="small" @click="modelSubmit">提交</el-button>
		        </span>
	        </el-dialog>
		</div>
		<!-- 添加删除模态框 -->
		<div class="model">
			<el-dialog title="是否删除 ?" :visible.sync="deleteVisible" width="60%">
				 <el-table :data="deleteData"  style="width: 100%"  border  size="mini">
	                 <el-table-column prop="dateNew" label="日期" width="200"></el-table-column>
	                 <el-table-column prop="name" label="产品"></el-table-column>
	                 <el-table-column prop="company" label="合作方"></el-table-column>
	                 <el-table-column prop="cooperationContent" label="合作内容"></el-table-column>
	                 <el-table-column prop="cost" label="成本金额"></el-table-column>
	                 <el-table-column prop="remark" label="备注"></el-table-column>
	            </el-table>
            <div style="padding-top:20px;">
            	<span>删除后不可恢复。点击"确认"进行删除，点击"x"关闭窗口</span>
            </div>
		        <span slot="footer">
		          <el-button type="primary" size="small" @click="deleteCost">确定</el-button>
		        </span>
	        </el-dialog>
		</div>
	</div>
</template>
<script>
    import{partnerManagerallselectReg,getOperateCostingReg,addOrUpdateCostingReg,deleteCostingReg,updateCostingReg} from '../../api/api.js'
	export default {
		data() {
			return {
			   userloginInfo:null,//用户信息
			   divHearderInfo:false,//搜索条件加载状态
			   // 输入选择合作方
               partner:'',//选择值
               choosePartner:[],//读取的选择的对象
               //产品选择
               product:'',//选择值
               chooseproduct:[],//读取的选择的对象
               //输入合作内容
               content:'',
               //选择时间
               gitTime:[],//选择的时间值
               //table表格
               tableStaus:false,//加载状态
               tableData:[],//获取后台返回的表格搜索数据
               deleteVisible:false,//删除模态框状态
               deleteData:[],//删除某行的数据
               // 模态框添加
               addVisible: false,//模态框显示隐藏状态
               //模态框表格数据
               // data 日期 appId产品 companyId 合作方 cooperationContent 合作内容 cost 成本金额 remark 备注
               fbsData:[
                 {date:[],appId:'',companyId:'',cooperationContent:'',cost:'',remark:''}
               ]
			}
		},
		mounted() {
			this.userloginInfo = JSON.parse(localStorage.getItem("userperadmin"));
			// 获取搜索内容信息
			this.getSelections()
		},
		methods:{
			// 获取搜索内容信息
			getSelections() {
				let sendSessionid = {sessionid:this.userloginInfo.sessionid};
				console.log('发送参数获取搜索条件')
				console.log(sendSessionid)
				this.divHearderInfo = true;
				let _this = this;
                partnerManagerallselectReg(sendSessionid).then(res=>{
                	console.log("获取到的数据")
                	console.log(res.data)
                	if (res.data.status === 0) {
                		_this.choosePartner = res.data.reportforms_admin
                		_this.chooseproduct = res.data.appList;              
                		_this.divHearderInfo = false;
                		_this.searchInfo();
                	};
                	if (res.data.status === 1) {
                       _this.$router.replace("/login");
                	};
                })
			},
			// 搜索事件
			searchInfo() {
				this.tableStaus = true;
				let searchSend = {
					sessionid:this.userloginInfo.sessionid,
					id:this.partner,
					appId:this.product,
					cooperationContent:this.content,
					minDate:this.gitTime&&this.gitTime.length>0?this.gitTime[0]:'',
					maxDate:this.gitTime&&this.gitTime.length>0?this.gitTime[1]:''
				};
				console.log(searchSend)
				let _this = this;
				getOperateCostingReg(searchSend).then(res=>{
					console.log(res.data)
					if (res.data.status === 0) {
						_this.tableData = res.data.reportforms_admin;
						for (var i = 0; i < _this.tableData.length; i++) {
                    		_this.tableData[i].synchronizeSwitch = _this.tableData[i].synchronizeSwitch==='0'?false:true;
                    	}
						_this.tableStaus = false;
					};
					if (res.data.status === 1) {
					    _this.$router.replace("/login");
					};
				})
			},
			// 选择合作方获取焦点事件
			focuschoosePartner() {
				this.partner = '';
			},
			// 时间选择获取焦点事件
			focusDataTime() {
				this.gitTime = [];
			},
			// 点击增加事件
			addInfoToModel() {
               this.addVisible = true;
               this.fbsData = [{date:[],appId:'',companyId:'',cooperationContent:'',cost:'',remark:''}];
			},
			// 点击表格删除事件
			deleteTableRow(row,index) {
				console.log(row)
				this.deleteData = [row];
				this.deleteVisible = true;
			},
			//确认删除点击确认
			deleteCost() {
				let deleteRowInfo = {
					sessionid:this.userloginInfo.sessionid,
					ruleForm: this.deleteData
				};
				console.log(deleteRowInfo)
				let _this = this;
				deleteCostingReg(deleteRowInfo).then(res=>{
					console.log(res.data)
					if (res.data.status === 0) {
						_this.$message({
				          message: '删除数据成功',
				          type: 'success'
				        });
				        _this.deleteVisible = false;
				        _this.searchInfo();
					};
					if (res.data.status === 1) {
					    _this.$router.replace("/login");
					};
				})
			},
			// 数据同步改变事件
			synchronize(row,index) {
				console.log(row)
				let isSynchronizeSend = {
					sessionid:this.userloginInfo.sessionid,
					id:row.id,
					synchronizeSwitch:row.synchronizeSwitch
				}
				let _this = this;
				updateCostingReg(isSynchronizeSend).then(res=>{
					if (res.data.status === 0) {
						_this.$message({
				          message: '同步成功',
				          type: 'success'
				        });
				        _this.searchInfo()
					};
					if (res.data.status === 1) {
						 _this.$router.replace("/login");
					};
				})
			},
			// 勾选表格中的数据
			handleSelectionChange(val) {
		        this.deleteData = val;
		        console.log(this.deleteData)
		     },
		     //模态框增加事件
		     addArrPush() {
		     	this.fbsData.push({date:[],appId:'',companyId:'',cooperationContent:'',cost:'',remark:''})
		     },
		     // 模态框删除某行
		     deleteRow(row,index) {
               this.fbsData.splice(index, 1);
               console.log(this.fbsData)
		     },
		    // 模态框提交
		    modelSubmit () {
		    	for (var i = 0; i <this.fbsData.length; i++) {
		    		if (!this.fbsData[i].date || !this.fbsData[i].appId || !this.fbsData[i].companyId || !this.fbsData[i].cooperationContent || !this.fbsData[i].cost) {
		    			this.$message.error('日期/产品/合作方/合作内容/成本金额 为必填项');
		    			return;
		    		} else {
		    			this.fbsData[i].dateNew = this.fbsData[i].date[0]+'~'+this.fbsData[i].date[1];
		    		}	
		    	}
		    	let sendAddInfo = {
		    		sessionid:this.userloginInfo.sessionid,
		    		ruleForm: this.fbsData
		    	};
		    	console.log(sendAddInfo)
		    	let _this = this;
		    	addOrUpdateCostingReg(sendAddInfo).then(res=>{
		    		console.log(res.data)
		    		if (res.data.status === 0) {
		    			_this.$message({
				          message: '新增数据成功',
				          type: 'success'
				        });
				        _this.addVisible = false;
				        _this.searchInfo();
		    		};
		    		if (res.data.status === 1) {
		    			_this.$router.replace("/login");
		    		};
		    	})
		    }
		}
	}
</script>
<style lang="scss" scoped>
	.templeMaring{margin-right: 1rem;}
	.headerSecond{margin-top: 1rem;}
	.table{margin-top: 1rem;}
	.marginLeft{margin-left: -2rem;}
	@media screen and (max-width: 1300px) {
	   .marginLeft{margin-left: 3rem;}
	}
	.el-table td, .el-table th {
		padding: 4px 0;
	}
	.dataStyle{
		width: 7rem;
	}
	.dataStyleDate{
		width: 13rem;
	}
	.addModel{
		float: right;
		padding-bottom: 1rem;
		margin-right: 1rem;
	}

</style>


