<template>
	<div class="databackrun">
		<div class="seachInput" v-loading="Statrloading" element-loading-text="拼命加载中...">
			<el-row :span="24">
			    <span class="fontColor">*</span><span class="fontColorSize">选择重跑数据日期:</span>
  			    <el-date-picker size="small"
  			      v-model="gitTime"
  			      type="daterange"
  			      start-placeholder="开始日期"
  			      end-placeholder="结束日期"
  			      unlink-panels
  			      :editable='false'
  			      value-format="yyyy-MM-dd"
                  @focus = "dataTime"
  			      >
  			    </el-date-picker>
			    <!-- 选择APP默认乐贷款 -->
                <el-select v-model="optionsApp" default-first-option clearable placeholder="选择产品"  class='templeMaring' size="small">
	                <el-option
	                  v-for="(item,index) in chooseApp"
	                  :key="index"
	                  :label="item.name"
	                  :value="item.id">
	                </el-option>
                </el-select>
			</el-row>
			<div class="lineHeight"></div>
			<el-row>
			  <el-col :span="3"><span class="fontColor">*</span><span class="fontColorSize">选择重跑数据报表:</span></el-col>
			  <el-col :span="4">
			  	 <el-select v-model="dataTablechange" filterable clearable  default-first-option size="small"  placeholder="输入/选择数据报表" class='templeMaring' @focus="tablechange">
			          <el-option
			            v-for="(item,index) in dataList"
			            :key="index"
			            :label="item.name"
			            :value="item.id">
			          </el-option>
			    </el-select>
			  </el-col>
			  <el-col :span="2" :offset="1"><span class="fontColor">*</span><span class="fontColorSize">优化比例:</span></el-col>
			  <el-col :span="3">
			  	<!-- <input type="text"  v-model="reate" class="inputReate" placeholder="请输入优化比例" maxLength="4" min="0" max="100" oninput="value=value.replace(/[^0-9]/g, '')"> -->
			  	<el-input placeholder="请输入优化比例" v-model:value="reate" clearable class="inputReate" size="small" type="text" @keyup.native="inputReate"></el-input>
			  </el-col>
			  <el-col :span="2"><span class="lilv">%</span></el-col>
			  <el-col :span="2"><span class="fontColor">*</span><span class="fontColorSize">渠道编码:</span></el-col>
			  <el-col :span="6">
			  	 <el-select v-model="qudaoNum" filterable clearable  default-first-option  size="small" placeholder="输入/选择渠道编码" class='templeMaring' @focus="qudaoNumFocus">
			          <el-option
			            v-for="(item,index) in qudaoNumArry"
			            :key="index"
			            :label="item.label"
			            :value="item.value">
			          </el-option>
			    </el-select>
			  </el-col>
			</el-row>
			<div class="lineHeight"></div>
			<el-row>
			  <el-col :span="3"><span class="fontColor">*</span><span class="fontColorSize">备注:</span></el-col>
		  	  <el-col :span="10">
		  	 	<el-input
				  type="textarea"
				  :rows="2"
				  placeholder="输入内容最多200字符"
				  :maxlength="length"
				  v-model="textarea">
				</el-input>
			 </el-col>
			 <el-col :span="9" :offset="2"><el-button type="primary" size="small" class="addDataBackInfo" @click="addDataBackInfo">添加</el-button></el-col>
			</el-row>
			<div class="lineHeight"></div>
			<div class="clickNewData">
				<el-button type="primary" @click="clickGetNewData" size="small">点击刷新</el-button>
			</div>
			<!-- 数据重跑表格 -->
			<div class="table" v-loading="tableStaus" element-loading-text="数据刷新中">
				<el-table
				    ref="multipleTable"
				    :data="tableData3"
				    tooltip-effect="dark"
				    style="width: 100%"
				    border
				    @selection-change="handleSelectionChange">
				    <el-table-column type="selection" width="55"></el-table-column>
				    <el-table-column prop="tableName"  label="重跑报表"></el-table-column>
				    <el-table-column label="重跑数据日期" show-overflow-tooltip width="200">
				       <template slot-scope="scope"><span>{{scope.row.startDate}}</span><span>~</span><span>{{scope.row.endDate}}</span></template>
				    </el-table-column>
				    <el-table-column prop="channelName" label="渠道名称"></el-table-column>
				    <el-table-column prop="channel" label="渠道编码"></el-table-column>
				    <el-table-column prop="optimization" label="优化比例"></el-table-column>
				    <el-table-column prop="comment" label="备注"></el-table-column>
				    <el-table-column prop="adminName" label="创建人"></el-table-column>
				    <el-table-column  label="操作" width="50">
				    	 <template slot-scope="scope">
                           <el-button @click="deletDataBack(scope.row,scope.$index)" type="text" size="small"  v-show="scope.row.status !=1"><i class="el-icon-delete"></i></el-button>
                         </template>
				    </el-table-column>
				    <el-table-column prop="progressMsg" label="进度"></el-table-column>
				</el-table>
			</div>
			<div class="lineHeight"></div>
			<div class="tableButton">
				<el-button type="primary" icon="el-icon-delete" @click="batchDelete">批量删除</el-button>
				<el-button type="primary" @click="AkeyHeavyRun">一键重跑</el-button>
			</div>
		</div>
	</div>
</template>
<script>
   import axios from 'axios';
   import {dataSeachAddInfo,addOptimizationTaskReg,getOptimizationTaskReg,startOptimizationTaskReg,deleteOptimizationTaskReg,getDataDailyReg} from "../../api/api.js"
    var trime;
    var runOptimizationTaskId;
	export default{
		data(){
			return{
			  sessionid:'',
			  //输入最大长度
			  length:100,
			  min:0,
			  max:100,
              // 加载搜索信息时候显示加载动画
              Statrloading:true,
              // 日期默认显示日期
              newDate:'',
              gitTime:'',
              optionsApp:'',//选择的产品App
              chooseApp:[],//获取到的后台配置的app产品
              // 选择数据报表值
              dataTablechange:'',
              dataList:[
                {
		          value: '-1',
		          label: '全部'
		        }
              ],
              //优化比例
              reate:'',
              // 渠道编码
              qudaoNum:'',
              qudaoNumArry:[],
              // 备注信息
              textarea:'',
              // 数据重跑表格数据
              tableData3: [],
		      multipleSelection: [],
		      tableStaus:false
			}
		},
		methods:{
			getSearchInfo(data){
				clearInterval(trime);
				var _data = {sessionid:data}
				_data = JSON.stringify(_data)
				var _this = this;
			    // 页面加载时候获取搜索信息
				function getUserAccount() {
					dataSeachAddInfo(_data).then(res=>{
						console.log(res.data)
						if (res.data.status === 0) {
							for (var i = 0; i < res.data.channelNoList.length; i++) {
								_this.qudaoNumArry.push({label:res.data.channelNoList[i].split(",")[1],value:res.data.channelNoList[i]});
							}
							_this.dataList = res.data.fromTypeList;
						};
						if (res.data.status === 1) {
						  _this.$router.replace("/login");
						  _this.Statrloading = false;
						}
					});
				}
				//App产品信息
				function getUserPermissions() {
					getDataDailyReg(_data).then(res=>{
						console.log('app产品信息',res.data)
						if (res.data.status === 0) {
							_this.chooseApp = res.data.appList;
						};
						if (res.data.status === 1) {
						  _this.$router.replace("/login");
						  _this.Statrloading = false;
						};
					})
				}

				// 两次请求都成功
				axios.all([getUserAccount(), getUserPermissions()])
			        .then(axios.spread(function (acct, perms) {
			        //两个请求现已完成
			         _this.Statrloading = false;
			         _this.searchData();
			      }));
				
			},
			findTime(){
		    	var myDate = new Date();
					//获取当前年
					var year=myDate.getFullYear();
					//获取当前月
					var month=myDate.getMonth()+1;
					//获取当前日
					var date=myDate.getDate(); 
					this.newDate = year+'-'+month+'-'+date;
		    	},
		    // 时间获取焦点
		    dataTime(){},
		    // 选择数据报表时获取焦点清除选择内容
		    tablechange(){
		    	this.dataTablechange = '';
		    },
		    // 选择渠道编码获取焦点时清除选择的内容
		    qudaoNumFocus(){
		    	this.qudaoNum = '';
		    },
		    // 优化比例判断是否满足条件
		    inputReate(){
		    	if (this.reate > 100) {
	    		  this.$message.error('输入必须为100以内的数字');
	    		  this.reate = null;
	    		}
		    	// if (isNaN(this.reate)) {
		    	// 	this.$message.error('输入必须为0~100的数字');
		    	// 	this.reate = null;
		    	// } else {
		    	// 	if (this.reate<0 || this.reate>100) {
		    	// 	  this.$message.error('输入必须为0~100的数字');
		    	// 	  this.reate = null;
		    	// 	} else if (this.reate.indexOf(".") != -1) {
		    	// 		this.$message.error('输入必须为0~100的整数');
		    	// 	    this.reate = null;
		    	// 	  }
		    	// }
		    },
		    // 添加需要跑的数据
		    addDataBackInfo(){
		    	console.log(this.gitTime)
		    	console.log(this.dataTablechange)
		    	console.log(this.reate)
		    	console.log(this.qudaoNum)
		    	console.log(this.textarea)
		    	if (!this.gitTime) {
                  this.$message.error('日期范围为必填项');
                  return;
		    	}else if (!this.optionsApp) {
		    	  this.$message.error('产品App为必选项');
		    	  return;
		    	} else if (!this.dataTablechange) {
		    	  this.$message.error('数据表选择为必填项');
		    	  return;
		    	} else if (!this.reate) {
		    	  this.$message.error('优化比例为必填项');
		    	  return;
		    	} else if (!this.qudaoNum) {
		    	  this.$message.error('渠道编码为必填项');
		    	  return;
		    	}else if (!this.textarea) {
		    	  this.$message.error('备注为必填项');
		    	  return;
		    	}
		    	var dataTime = (new Date()).valueOf();
		    	var qudaoInfo = this.qudaoNum.split(",");
		    	var setDataAdd = {
		    		sessionid:this.sessionid,
		    		id:dataTime,
		    		startDate:this.gitTime[0],
		    		endDate:this.gitTime[1],
		    		tableId:this.dataTablechange,
		    		optimization:this.reate,
		    		channelId:qudaoInfo[0],
		    		channel:qudaoInfo[1],
		    		channelName:qudaoInfo[2],
		    		comment:this.textarea,
		    		adminId:this.sessionid,
		    		appid:this.optionsApp
		    	};
		    	var setDataAddStr = JSON.stringify(setDataAdd);
		    	console.log(setDataAddStr)
		    	addOptimizationTaskReg(setDataAddStr).then(res=>{
		    		console.log(res.data)
		    		if (res.data.status === 0) {
		    			// 添加成功刷新表格
                       this.searchData();
		    		};
                    if (res.data.status === 1) {
		    			this.$message.error('添加失败，请重新添加');
		    		};
		    	});
		    },
		    // 重跑勾选数据
		    handleSelectionChange(val) {
		        this.multipleSelection = val;
		        // console.log(val)
		     },
		   // 删除某个重跑数据
		   deletDataBack(row,index){
		   	// this.$refs.multipleTable.toggleRowSelection(this.tableData3[1])
		   	 var DeletesetDateInfo = {sessionid:this.sessionid,id:row.id};
		     var datasessionid = JSON.stringify(DeletesetDateInfo);
		     this.$confirm('确认删除？')
	            .then(_ => {
		   	      this.deleteFun(datasessionid);
	            })
	            .catch(_ => {});
		   },
		   // 批量删除
		   batchDelete(){
		   	this.$confirm('确认删除？')
	            .then(_ => {
	              if (this.multipleSelection.length>0) {
	              	  var setId = [];
	              	  for (var i = 0; i < this.multipleSelection.length; i++) {
	              	       setId.push(this.multipleSelection[i].id)
	                  };
	                  var DeleteDateInfo = {sessionid:this.sessionid,id:setId.toString()};
	                  var datassionandid = JSON.stringify(DeleteDateInfo);
	                  this.deleteFun(datassionandid);
	              }
	            })
	            .catch(_ => {});
		   },
		   // 一键重跑
		   AkeyHeavyRun(){
		   	// alert("点击一键重跑");
		    var _this = this;
		   	var sessionid = {sessionid:this.sessionid};
		    var datasessionid = JSON.stringify(sessionid);
		    console.log(datasessionid)
		   	startOptimizationTaskReg(datasessionid).then(res=>{
		   		console.log(res.data)
		   		if (res.data.status === 0) {
		   			// 执行搜索函数
		   			_this.searchData();
		   		};
		   		if (res.data.status === 1) {
		   			_this.$message.error('一键重跑失败');
		   			_this.$router.replace("/login");
		   		}
		   	})
		   },
		   // 刷新表格
		   searchData(){
		   	this.tableStaus = true;
		   	var sessionid = {sessionid:this.sessionid};
		    var datasessionid = JSON.stringify(sessionid);
		    console.log(datasessionid)
		    var _this = this;
             getOptimizationTaskReg(datasessionid).then(res=>{
             console.log("请求添加的数据")
             console.log(res.data)
	             if (res.data.status === 0) {
                    _this.tableData3 = res.data.optimizationTaskList;
                    for (var i = 0;  i< _this.tableData3.length; i++) {
                    	_this.tableData3[i].optimization +="%";
                    	if (_this.tableData3[i].status == 0) {
                    		_this.tableData3[i].progressMsg = "暂未开始"
                    	} else if (_this.tableData3[i].status == 1) {
                    		_this.tableData3[i].progressMsg = res.data.optimizationTaskList[i].progressMsg;
                    	} else if (_this.tableData3[i].status == 2) {
                    		_this.tableData3[i].progressMsg = "已完成"
                    	}
                    	for (var j = 0; j <_this.dataList.length; j++) {
                    		if (_this.tableData3[i].tableId === _this.dataList[j].id) {
                    			_this.tableData3[i].tableName = _this.dataList[j].name;
                    		}
                    	}	 
                    };
                    console.log(_this.tableData3);
                    _this.tableStaus = false;
                    // if (_this.tableData3.length>0) {
                    // 	_this.setInter();
                    // } else {
                    //     clearInterval(trime);
                    // }
	             };
	             if (res.data.status === 1) {
                      _this.$router.replace("/login");
	             }
             })
		   },
		   deleteFun(data){
               console.log(data)
               deleteOptimizationTaskReg(data).then(res=>{
               	  if (res.data.status === 0) {
               	  	this.searchData();
               	  }
               })
		   },
		   // 点击刷新事件
		   clickGetNewData(){
		   	 this.searchData();
		   }

		   // 定时器刷新事件
		   // setInter(){
		   // 	  console.log("旧的ID",runOptimizationTaskId)
		   // 	  clearInterval(trime);
		   // 	  var _this = this;
		   //    trime = setInterval(function(){
     //           var sessionid = {sessionid:_this.sessionid};
		   //     var datasessionid = JSON.stringify(sessionid);
     //           getRunOptimizationTaskReg(datasessionid).then(res=>{
     //           	    console.log('获取正在跑的数据')
     //           	    console.log(res.data)
     //           	    if (runOptimizationTaskId && !res.data.runOptimizationTask) {
     //           	    	runOptimizationTaskId = null;
     //                	_this.searchData();
     //                }
     //                if (res.data.status === 0 && res.data.runOptimizationTask) {
     //                	if(runOptimizationTaskId != res.data.runOptimizationTask.id){
     //                		_this.searchData();
     //                	 }
     //                	else{
	    //                 	for (var i = 0; i < _this.tableData3.length; i++) {
	    //                 		if (_this.tableData3[i].id === res.data.runOptimizationTask.id) {
	    //                 			console.log('正在跑的和原有数据进行对比')
	    //                 			console.log(_this.tableData3[i].id )
	    //                 			let changeData = res.data.runOptimizationTask;
	    //                 			for (var j = 0; j <_this.dataList.length; j++) {
			  //                   		if (changeData.tableId === _this.dataList[j].id) {
			  //                   			  changeData.tableName = _this.dataList[j].name;
			  //                   		 }
	    //                 	         };
	    //                 	         console.log('新数据')
	    //                 	         console.log(changeData)
	    //                 	         console.log('原有的所有数据')
	    //                 	         console.log(_this.tableData3)
	    //                 		    _this.$set( _this.tableData3,i,changeData)
	    //                 		}
	    //                 	}

     //                	}
					// 	runOptimizationTaskId =  res.data.runOptimizationTask.id;
     //                } else {
     //                	clearInterval(trime);
     //                }
     //           })
		   // 	},5000)
		   // }
		},
		// beforeRouteLeave(to, from, next) {
		//    // clearInterval(trime);
		//    setTimeout(function(){
		//    	 clearInterval(trime);
		//    },1000)
  //          next()
  //       },
		mounted(){
			var adminInfo = JSON.parse(localStorage.getItem("userperadmin"));
			this.sessionid = adminInfo.sessionid;
			// clearInterval(trime);
			this.getSearchInfo(this.sessionid);
			this.findTime();
		}
	}
</script>
<style lang="scss" scoped>
    .databackrun{text-align: left}
	.seachInput{
		margin-top: 1rem;
	}
	.fontColorSize{
		display:inline-block;
		font-size: 0.7rem;
		color: #666666;
		margin-top:0.2rem;
	}
	.lineHeight{
		height: 1rem;
	}
	.lilv{
		display: inline-block;
		margin-top:0.2rem;
		margin-left: 1rem;
	}
	.addDataBackInfo{
		margin-top:0.5rem;
	}
	.table{
		margin-top:2rem;
	}
	.tableButton{
		text-align: center;
	}
	.fontColor{
		color: red;
	}
	.Datarenovate{
		float: right;
	}
	.clickNewData{
		float:right;
		padding: 0.5rem;
	}
	.templeMaring{
		margin-left:0.267rem;
	}
</style>


