<script>
/* operationData:[],//父传子传的数据运用数据
   operationIndex:6点击的数据下标运用数据
*/
	export default {
		props:['operationData','operationIndex'],
		data() {
			return {
			   tableloading:false,//加载动画
			}
		},
		methods: {
            commentClick(row,index) {
                this.$emit('sendParentcommentRow',row); //主动触发sendParentcommentRow方法，row为向父组件传递的数据
            }
        }
	}
</script>
<style lang="scss" scoped>
	.operationDataShow{display: block}
	.operationDataHide{display: none}
    .colorFont{color:red}
</style>


