<template>
	<div class="resultTheQuery">
		<div v-loading="loading" element-loading-text="拼命加载中...">
			<div class="header">
				<el-row :span="24">
			        <!-- 渠道商搜索 -->
			        <el-col :span="3">
				        <el-select v-model="resultTheQuery.qudaoChannel" filterable clearable    placeholder="渠道商" size="small" class='templeMaring' @visible-change="channelNameBlur(resultTheQuery.qudaoChannel)" @clear='clearChannelName'>
					          <el-option
					            v-for="(item,index) in channelOp"
					            :key="index"
					            :label="item.company"
					            :value="item.id">
					          </el-option>
				    	</el-select>
				    </el-col>
				    <!-- 渠道 -->
				    <el-col :span="3">
				        <el-select v-model="resultTheQuery.qudao" filterable clearable    placeholder="渠道" size="small" class='templeMaring'>
					          <el-option
					            v-for="(item,index) in qudaoOp"
					            :key="index"
					            :label="item.channelName"
					            :value="item.channelName">
					          </el-option>
				    	</el-select>
				    </el-col>
				    <!-- 渠道号 -->
				    <el-col :span="3">
				        <el-select v-model="resultTheQuery.qudaoChannelNum" filterable clearable    placeholder="渠道号" size="small" class='templeMaring'>
					          <el-option
					            v-for="(item,index) in channelNumOp"
					            :key="index"
					            :label="item.channel"
					            :value="item.channel">
					          </el-option>
				    	</el-select>
				    </el-col>
				    <!-- 负责人 -->
				    <el-col :span="3">
				        <el-select v-model="resultTheQuery.fuzheren"  clearable    placeholder="负责人" size="small" class='templeMaring'>
					          <el-option
					            v-for="(item,index) in fuzherenOp"
					            :key="index"
					            :label="item.name"
					            :value="item.id">
					          </el-option>
				    	</el-select>
				    </el-col>
				    <!-- APP -->
				    <el-col :span="3">
				        <el-select v-model="resultTheQuery.appId"   placeholder="选择产品" size="small" class='templeMaring'>
					          <el-option
					            v-for="(item,index) in appOp"
					            :key="index"
					            :label="item.name"
					            :value="item.id">
					          </el-option>
				    	</el-select>
				    </el-col>
				    <!-- 所有渠道 -->
				    <el-col :span="3">
				        <el-select v-model="resultTheQuery.showQudao"   placeholder="显示渠道" size="small" class='templeMaring'>
					          <el-option
					            v-for="(item,index) in showAllQudaoOp"
					            :key="index"
					            :label="item.name"
					            :value="item.value">
					          </el-option>
				    	</el-select>
				    </el-col>
				    <el-col :span="2">
				         <div class="block templeMaring" >
				            <el-date-picker
				              class='dataStyle'
				              v-model="resultTheQuery.getTime"
				              type="daterange"
				              start-placeholder="开始日期"
				              end-placeholder="结束日期"
				              unlink-panels
				              value-format="yyyy-MM-dd"
				              size='small'
				              >
				            </el-date-picker>
				         </div>
			        </el-col>
				</el-row>
				<div class="divheight"></div>
			    <el-row :span='24'>
			        <el-col :span="17" class="cheackTop">
				          <el-checkbox-group v-model="resultTheQuery.checkList">
					            <el-col :span="3">
					               <el-checkbox label="渠道商" @change="fontchangeIsShow(1)"></el-checkbox>
					            </el-col>
					            <el-col :span="3">
					               <el-checkbox label="渠道" @change="fontchangeIsShow(2)"></el-checkbox>
					            </el-col>
					            <!-- <el-col :span="3">
					               <el-checkbox label="渠道号" @change="fontchangeIsShow(3)"></el-checkbox>
					            </el-col> -->
					            <el-col :span="3">
					               <el-checkbox label="负责人" @change="fontchangeIsShow(4)"></el-checkbox>
					            </el-col>
					            <el-col :span="3">
					                <el-checkbox label="外部" @change="fontchangeIsShow(5)"></el-checkbox>
					            </el-col>
					            <el-col :span="3">
					               <el-checkbox label="APP" @change="fontchangeIsShow(6)"></el-checkbox>
					            </el-col>
				          </el-checkbox-group>
			        </el-col> 
			        <el-col :span="6">
				          <el-col :span="8" >
				                <el-button type="info" icon="el-icon-search" size='mini' @click='clickSeach'></el-button>
				          </el-col>
				          <el-col :span="8">
				                <!-- <el-button type="success"  :class="iconImg"  size='mini'></el-button> -->
				                <el-button type="success" :class="iconImg" style="height: 28px;font-size:14px;margin-top: 1px;"  size='mini' @click='tableTimeStyle'></el-button>
				          </el-col>
				          <el-col :span="8" >
				               <el-button type="primary" class="el-icon-download" size='mini' @click='resultTheQueryDown'></el-button>
				          </el-col>
			        </el-col>
			    </el-row>
			</div>
			<div class="tableMargin">
	         <!-- table表格 -->
	            <el-table :data="fbsData"  style="width: 100%"  :height="fbsData&&fbsData.length>0?clientHeight:100" border  size="mini" v-loading="tableloading" element-loading-text="拼命加载中..." :highlight-current-row="true">
	                <el-table-column prop="date"  label="日期" width='120' sortable></el-table-column>
	                <el-table-column prop="company" label="渠道商" width="180" v-if='fontCheack.channelNameF'></el-table-column>
	                <el-table-column prop="app" label="产品" v-if='fontCheack.appTableShow'></el-table-column>
	                <el-table-column prop="channelName" label="渠道"  width="180" v-if='fontCheack.channelQuDao'></el-table-column>
	                <el-table-column prop="channel" label="渠道号"  width="180" v-if='fontCheack.channelQuDao'></el-table-column>
	                <el-table-column prop="admin" label="负责人" v-if='fontCheack.fuzerenTableShow'></el-table-column>
	                <el-table-column prop="outRegister" label="外部注册" v-if='fontCheack.externalTableShow' sortable width="120"></el-table-column>
	                <el-table-column prop="outAccount" label="外部开户" v-if='fontCheack.externalTableShow' sortable width="120"></el-table-column>
	                <el-table-column prop="outUpload" label="外部进件" v-if='fontCheack.externalTableShow' sortable width="120"></el-table-column>                         
	                <el-table-column prop="outFirstGetPer" label="外部首提人数" v-if='fontCheack.externalTableShow' sortable width="120"></el-table-column>                         
	                <el-table-column prop="outFirstGetSum" label="外部首提金额" v-if='fontCheack.externalTableShow' sortable width="120"></el-table-column>                         
	                <el-table-column prop="outChannelSum" label="外部提现总额" v-if='fontCheack.externalTableShow' sortable width="120"></el-table-column>                         
	                <el-table-column prop="channelSum" label="提现总额" sortable width="120"></el-table-column>                         
	                <el-table-column prop="income" label="收入" sortable></el-table-column>                         
	                <el-table-column prop="cost" label="成本" sortable></el-table-column>                         
	                <el-table-column prop="grossProfit" label="毛利" sortable></el-table-column>                         
	                <el-table-column prop="grossProfitRate" label="毛利率"   width="120" sortable>
	                	<template slot-scope="scope">
                         <span>{{scope.row.grossProfitRate}}%</span>
                        </template>
	                </el-table-column>                         
	            </el-table>
	            <br>
	            <!-- 分页 -->
	            <el-pagination
	                background
	                @size-change="pageSizeChange"
	                @current-change="currentPageChange"
	                :current-page="currentPage"
	                :page-sizes="pageSizes"
	                :page-size="pagesize"
	                layout="total, sizes, prev, pager, next, jumper"
	                :total="dataSize" class="paginationWrapper">
	            </el-pagination>
	        </div>
		</div>
	</div>
</template>
<script>
	import {yesterDay,toDay} from '../../api/formatter.js'
	import {getPerformanceParaReg,getChannelsReg,getPerformanceReg,resultdownloadDataReg} from '../../api/api.js'
	export default {
		data() {
			return {
				clientHeight:'',//屏幕可视区域高度
				userInfo:null,//用户信息
				loading:false,//加载动画
				channelOp:[{name:'麦广'}],//渠道商获取所有渠道商的值
				qudaoOp:[{name:'麦广渠道'}],//渠道获取所有渠道的值
				channelNameBecome:[],//备份渠道信息
				channelNumBecome:[],//备份渠道号信息
				channelNumOp:[{name:'maimob'}],//渠道号获取所有渠道号的值
				fuzherenOp:[{name:'负责人'}],//负责人获取所有负责人的值
				appOp:[{name:'app'}],//APP获取所有APP的值
				showAllQudaoOp:[{name:'显示所有渠道',value:1},{name:'仅显示一级渠道',value:2}],//APP获取所有APP的值
				resultTheQuery:{
					qudaoChannel:'',//渠道商选择的值
					qudao:'',//渠道的值
					qudaoChannelNum:'',//渠道号值
					fuzheren:'',//负责人值
					appId:1,//app选择的ID的值
					showQudao:1,//显示渠道的值
					getTime:[],//选择时间的值
					checkList:['渠道商','渠道','负责人','外部','APP'],//勾选的字段
				},
                iconImgIndex:1,// 命名一个变量来判断icon是日月不选
                // 字段
                fontCheack:{
                	channelNameF:true,//渠道商表格列默认显示
	                channelQuDao:true,//渠道表格列默认显示
	                channelNumber:true,//渠道号表格列默认显示
	                fuzerenTableShow:true,//负者人表格列默认显示
	                externalTableShow:true,//负者人表格列默认显示
	                appTableShow:true,//负者人表格列默认显示
                },
                iconImg:'fa fa-sun-o',//默认日图标
				fbsData:[],//表格数据
				tableloading:false,//表格加载动画
				// 分页信息
		        currentPage: 1,
		        pageSizes: [50,150,300,500],
		        dataSize: 0,
		        pagesize: 50
			}
		},
		mounted() {
			setTimeout(()=>{
				this.clientHeightFun()
				this.userInfo = JSON.parse(localStorage.getItem("userperadmin"));
				this.resultTheQuery.getTime = [yesterDay()[0],toDay()[0]];//默认选择昨天到今天
				this.getSearchSelectInfo(this.userInfo.sessionid)
			},100)
			
		},
		methods:{
			clientHeightFun() {
				var clientHeight = document.documentElement.clientHeight;
				 this.clientHeight = (clientHeight-300);
			},
			// 页面首次进入获取搜索条件内容
			getSearchSelectInfo(sessionid) {
				this.loading = true;
                let sendSessionid = {sessionid:sessionid};
                let _this = this;
                getPerformanceParaReg(sendSessionid).then(res=>{
                	console.log('搜索条件信息',res.data)
                	if (res.data.status === 0) {
                		_this.channelOp = res.data.proxyList;
                		_this.qudaoOp = res.data.reportforms_operate;
                		_this.channelNameBecome = res.data.reportforms_operate;//备份渠道信息
                		_this.channelNumBecome = res.data.reportforms_operate;//备份渠道号信息
                		_this.channelNumOp = res.data.reportforms_operate;
                		_this.fuzherenOp = res.data.reportforms_admin;
                		_this.appOp = res.data.appList;
                		_this.loading = false;
                		_this.clickSeach();
                	};
                	if (res.data.status === 1) {
                		_this.$router.replace("/login");
                	};
                })
			},
			 // 选择页发生改变
			pageSizeChange(res){
				console.log('选择页发生改变',res)
				this.pagesize = res;
	            this.clickSeach();
			},
			// 当前页发生改变
		     currentPageChange(res){
		     	console.log('当前页发生改变',res)
		     	this.currentPage = res;
	            this.clickSeach();
		    },
		    // 日月不选点击事件
		    tableTimeStyle(){
	           this.iconImgIndex++;
	           if (this.iconImgIndex<=3) {
		            if (this.iconImgIndex === 1) {
		              this.iconImg = 'fa fa-sun-o'
		            }
		            if (this.iconImgIndex === 2) {
		              this.iconImg = 'fa fa-moon-o'
		            }
		            if (this.iconImgIndex === 3) {
		              this.iconImg = ' el-icon-circle-close'
	             	}
	           }else {
	             this.iconImgIndex = 1;
	             this.iconImg = 'fa fa-sun-o'
	           }
	           this.currentPage = 1;
	           this.clickSeach();   
	        },
	        // 字段勾选表格某部分显示
	        fontchangeIsShow(num) {
	        	console.log(num)
	        	switch (num) {
		          case 1:
		            this.fontCheack.channelNameF = ! this.fontCheack.channelNameF;
		            break;
		          case 2:
		            this.fontCheack.channelQuDao = ! this.fontCheack.channelQuDao;
		            break;
		          case 3:
		            this.fontCheack.channelNumber = ! this.fontCheack.channelNumber;
		            break;
		          case 4:
		            this.fontCheack.fuzerenTableShow = ! this.fontCheack.fuzerenTableShow;
		            break;
		          case 5:
		            this.fontCheack.externalTableShow = ! this.fontCheack.externalTableShow;
		            break;
		          case 6:
		            this.fontCheack.appTableShow = ! this.fontCheack.appTableShow;
		            break;
		        }
	        },
	        // 点击渠道商上的清除按钮
	        clearChannelName() {
	        	this.qudaoOp = this.channelNameBecome;
	        	this.channelNumOp = this.channelNumBecome;
	        },
	        // 渠道商选择失去焦点根据选择渠道商请求后台获取对应的渠道信息
	        channelNameBlur(res) {
	        	console.log(res)
	        	if (res) {
	        		let sendChannelChoose = {
	        			sessionid:this.userInfo.sessionid,
	        			proxyId:res
	        		};
	        		let _this = this;
	        		getChannelsReg(sendChannelChoose).then(res=>{
	        			console.log(res.data)
	        			if (res.data.status === 0) {
	        				_this.qudaoOp = res.data.reportforms_admin;
	        				_this.channelNumOp = res.data.reportforms_admin;
	        			    _this.resultTheQuery.qudao = '';
	        		        _this.resultTheQuery.qudaoChannelNum = '';
	        			};
	        			if (res.data.status === 1) {
	        				_this.$router.replace("/login");
	        			};
	        		})
	        	}
	        },
	        // 点击搜索
	        clickSeach() {
	        	console.log(this.resultTheQuery.getTime)
	        	this.loading = true;
	        	let searchSend = {
	        		sessionid:this.userInfo.sessionid,
	        		proxyId:this.resultTheQuery.qudaoChannel,
	        		appId:this.resultTheQuery.appId,
	        		adminId:this.resultTheQuery.fuzheren,
	        		channelName:this.resultTheQuery.qudao,
	        		channel:this.resultTheQuery.qudaoChannelNum,
	        		minDate:this.resultTheQuery.getTime.length>0?this.resultTheQuery.getTime[0]:'',
	        		maxDate:this.resultTheQuery.getTime.length>0?this.resultTheQuery.getTime[1]:'',
	        		pageId:this.currentPage-1,
	        		pageSize:this.pagesize,
	        		flag:this.resultTheQuery.showQudao,
	        		dateType: this.iconImgIndex,
	        		tag:this.resultTheQuery.checkList
	        	};
	        	console.log('搜索发送参数',searchSend)
	        	let _this = this;
	        	getPerformanceReg(searchSend).then(res=>{
	        		console.log('搜索获取数据',res.data)
	        		if (res.data.status === 0) {
	        			if (res.data.reportforms_admin && res.data.reportforms_admin[0] && res.data.reportforms_admin[0].total) {
	        				_this.fbsData = res.data.reportforms_admin;
		        			_this.dataSize = parseInt(res.data.reportforms_admin[0].total);
		        			for (var i = 0; i < _this.fbsData.length; i++) {
		        				_this.fbsData[i].channelSum = parseFloat(_this.fbsData[i].channelSum);
		        				_this.fbsData[i].cost = parseFloat(_this.fbsData[i].cost);
		        				_this.fbsData[i].grossProfit = parseFloat(_this.fbsData[i].grossProfit);
		        				_this.fbsData[i].grossProfitRate = parseFloat(_this.fbsData[i].grossProfitRate);
		        				_this.fbsData[i].income = parseFloat(_this.fbsData[i].income);
		        				_this.fbsData[i].outAccount = parseFloat(_this.fbsData[i].outAccount);
		        				_this.fbsData[i].outChannelSum = parseFloat(_this.fbsData[i].outChannelSum);
		        				_this.fbsData[i].outFirstGetPer = parseFloat(_this.fbsData[i].outFirstGetPer);
		        				_this.fbsData[i].outFirstGetSum = parseFloat(_this.fbsData[i].outFirstGetSum);
		        				_this.fbsData[i].outRegister = parseFloat(_this.fbsData[i].outRegister);
		        				_this.fbsData[i].outUpload = parseFloat(_this.fbsData[i].outUpload);
		        			}
	        			} else {
	        				_this.fbsData = [];
	        				_this.dataSize = 0;
	        			}
	        			
	        			_this.loading = false;
	        		};
	        		if (res.data.status === 1) {
	        			_this.$router.replace("/login");
	        		};
	        	})
	        },
	        //点击下载
	        resultTheQueryDown() {
	        	let downSend = {
	        		sessionid:this.userInfo.sessionid,
	        		proxyId:this.resultTheQuery.qudaoChannel,
	        		appId:this.resultTheQuery.appId,
	        		adminId:this.resultTheQuery.fuzheren,
	        		channelName:this.resultTheQuery.qudao,
	        		channel:this.resultTheQuery.qudaoChannelNum,
	        		minDate:this.resultTheQuery.getTime.length>0?this.resultTheQuery.getTime[0]:'',
	        		maxDate:this.resultTheQuery.getTime.length>0?this.resultTheQuery.getTime[1]:'',
	        		pageId:this.currentPage-1,
	        		flag:this.resultTheQuery.showQudao,
	        		dateType: this.iconImgIndex,
	        		tag:this.resultTheQuery.checkList
	        	};
	        	console.log('下载发送参数',downSend)
	        	let _this = this;
	        	resultdownloadDataReg(downSend).then(res=>{
	        		console.log(res.data)
	        		// if (res.data.status === 0) {
	        			const content = res.data;
				        const blob = new Blob([content])
				        const fileName = '业绩查询统计表.xls'
				        if ('download' in document.createElement('a')) { // 非IE下载
				            const elink = document.createElement('a')
				            elink.download = fileName
				            elink.style.display = 'none'
				            elink.href = URL.createObjectURL(blob)
				            document.body.appendChild(elink)
				            elink.click()
				            URL.revokeObjectURL(elink.href) // 释放URL 对象
				            document.body.removeChild(elink)
				        } else { // IE10+下载
				            navigator.msSaveBlob(blob, fileName)
				        }
	        		// };
	        		// if (res.data.status === 1) {
	        		// 	_this.$router.replace("/login");
	        		// };
	        	})
	        }
		}
	}
</script>
<style lang="scss" scoped>
  *{text-align: left;}
  .templeMaring{margin-left:10px;}
  .dataStyle{width: 220px;}
  .divheight{height: 15px;}
  .cheackTop{margin-left: 10px;}
  .tableMargin{margin-top: 30px;}
</style>


