<template>
	<div class="page" v-loading="pageLoading" element-loading-text="正在同步，请稍后">
		<div class="bread-nav">
			<span class="current-text">当前位置：财务中心 > </span>
			<span v-if="currentPosition.page >= item.id" :class="{menuActive: currentPosition.page > item.id}" v-for="(item, index) in currentPosition.menu" @click="toggleCurrentMenu(item.id)"><span v-if="index > 0">></span> {{item.name}}</span>
		</div>
		<div class="page1">
			<el-row class="search-wrapper">
				<div class="myRangeMonth">
					<label for="">开始</label>
					<el-date-picker size="mini" v-model="rangeDate.minDate" type="date" placeholder="开始日期" value-format="yyyy-MM-dd" :picker-options="startLimit" style="width:140px;"></el-date-picker>
					<label for="">结束</label>
					<el-date-picker size="mini" v-model="rangeDate.maxDate" type="date" placeholder="结束日期" value-format="yyyy-MM-dd" :picker-options="endLimit" style="width:140px;"></el-date-picker>
					<label>名称</label>
				    <el-input v-model='rangeDate.synName' size="mini" placeholder="输入名称" style="width:150px;"></el-input>
				    <el-button size="mini" type="primary" @click="synchrFinance"><i class="fa fa-refresh"></i> 同步</el-button>
				</div>
			</el-row>
			<div class="content-wrapper">
				<p class="content-title"><span>同步反馈</span></p>
				<div class="content">
					<p v-for="(item, index) in returnMsgArr" :key="index"><span class="index">{{index + 1}}</span>{{item}}</p>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import {FinanceTaskReg} from '../../api/api';
	export default {
		data() {
			return {
				userperadmin: '',
				// 页面loading
				pageLoading: false,
				// 当前显示的页面
				currentPosition: {
					page: 1,
					menu: [
						{id: 1 ,name: '财务同步'}
					]
				},
				startLimit: {
					disabledDate(time) {
						var theDay = new Date('2018-02-28');
						var today = new Date();
			            return time.getTime() < theDay.getTime() || time.getTime() > today.getTime();
			        }
				},
				endLimit: {
					disabledDate(time) {
						var theDay = new Date('2018-02-28');
						var today = new Date();
			            return time.getTime() < theDay.getTime() || time.getTime() > today.getTime();
			        }
				},
				rangeDate: {
					minDate: '',
					maxDate: '',
					synName: ''
				},
				returnMsg: '',
				returnMsgArr: []
			}
		},
		mounted() {
			this.userperadmin = JSON.parse(localStorage.getItem('userperadmin'));
		},
		methods: {
			synchrFinance() {
				if (this.rangeDate.minDate == '' && this.rangeDate.maxDate == '') {
					this.toast('请选择同步的日期期间', 'error');
					return;
				}
				if (this.rangeDate.minDate != '' && this.rangeDate.maxDate == '') {
					this.toast('请选择同步的结束月份', 'error');
					return;
				}
				if (this.rangeDate.minDate == '' && this.rangeDate.maxDate != '') {
					this.toast('请选择同步的起始月份', 'error');
					return;
				}
				if (this.rangeDate.minDate > this.rangeDate.maxDate) {
					this.toast('起始月份不能大于终止月份', 'error');
					return;
				}
				// 判断是否填写输入名称调用不同接口同步，如果不存在只传参数时间范围，否则相反
					var parameter = {
						sessionid: this.userperadmin.sessionid,
						minDate: this.rangeDate.minDate,
						maxDate: this.rangeDate.maxDate,
					    name: this.rangeDate.synName,
					};
					console.log(parameter);
					this.pageLoading = true;
					FinanceTaskReg(parameter).then((res) => {
						console.log(res);
						if(res.data.status === 0) {
							this.pageLoading = false;
							this.returnMsg = res.data.statusMsg;
							this.returnMsgArr = res.data.statusMsg.split(';');
							this.returnMsgArr.pop();
							console.log(this.returnMsgArr);
						}else if(res.data.status === 1){
							this.$router.replace("/login");
						}else{
							this.toast(res.data.statusMsg, 'error');
						}
					}).catch((err) => {
						this.pageLoading = false;
						console.error(error);
					});
			},
			toast(msg, type) {
				this.$message({
		            type: type,
		            message: msg
		        });
			}
		}
	}
</script>
<style lang="scss" scoped>
	@import url(../../assets/style/common.css);
	.myRangeMonth{
		display: inline-block;
		label{
			font-size: 12px;
		}
	}
	.content-wrapper{
		width: 100%;
		
		margin-top: 20px;
		border: 1px solid #EBEEF5;
		.content-title{
			height: 40px;
			line-height: 40px;
			background: #E5E9F2;
			font-size: 14px;
			font-weight: 700;
			span{
				border-left: 4px solid #409EFF;
				margin-left: 20px;
				padding-left: 10px;
			}
		}
		.content{
			height: 400px;
			overflow: auto;
			padding: 15px;
			font-size: 16px;
			p{
				margin-bottom: 6px;
			}
			.index{
				display: inline-block;
				width: 50px;
				padding-right: 20px;
				text-align: right;
			}
		}
	}
</style>
<style>
	.grid-content, .content-wrapper{
		height: 100%;
	}
	.el-table--border th{
		background: #F5F7FA !important;
	}
	.el-dialog__header{
		background: #F5F7FA;
		padding-top: 10px;
		font-weight: 700;
	}
	.el-dialog__headerbtn{
		top: 12px;
	}
	.el-dialog__footer{
		/*background: #F5F7FA;*/
	}
	.billState{
		height: 30px;
		border-radius: 2px !important;
	}
	.el-input::-webkit-scrollbar-thumb{
		border-radius: 2px !important;
	}
	.handler-panel .el-date-editor.el-input{
		width: 120px;
	}
</style>


